export const getGameSpec = (puzName: string): GameSpec => {
  const sharedGameSpec = ALL_SHARED_GAME_SPECS[puzName];
  const gameSpec = ALL_SERVER_ONLY_GAME_SPECS[puzName];

  if (sharedGameSpec === undefined && gameSpec === undefined) {
    throw new Error(`unknown puzzle name ${puzName} requested`);
  }

  return {
    ...(sharedGameSpec ?? {}),
    ...(gameSpec ?? {}),
  };
};
