import { Player } from "engine/types/game-state";
import { AbilityType, EffectOptType } from "engine/types/effects";
import { GameSpecServerFragment } from "engine/types/game-specs";

// basic battle: summon a robot each turn, attack with all robots
export const RobotSpec: GameSpecServerFragment = {
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;

    // TODO: give the AI as many gems as they need to attack

    // play the robot somewhere
    // TODO: retry if blocked and there's space somewhere else
    // TODO: use some heuristic to play to 'most annoying' column

    ai.trySpawn(
      "robot",
      Math.floor(Math.random() * 3),
      Math.floor(Math.random() * 5)
    );

    // attack with all ready robots down their column
    inspector.getPermanentsOf(Player.P2).forEach((permanent) => {
      if (!permanent.ready) return;
      const column = permanent.slot.column;

      // use robot's special ability on first enemy below it
      // TODO: reword card effect that robot only attacks this way, can't pick targets regularly
      for (let row = permanent.slot.row + 1; row < 6; row++) {
        const defender = inspector.getPermanentAtIfExists({
          row,
          column,
        });
        if (!defender || defender.owner !== Player.P1) continue;

        const attackResolved = ai.tryActivateAbility(
          permanent,
          AbilityType.SPECIAL,
          [
            {
              type: EffectOptType.PERMANENT,
              permanentId: defender.id,
            },
          ]
        );
      }
    });
  },
};
