import { CardType, ColorSymbol } from "engine/types/card-data";
import { CounterType } from "engine/types/counters";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Player } from "engine/types/game-state";

// attacking mint creatures deals damage based on attacker color & mint creature category
type DamageMap = { [color: string]: number };
const DAMAGE_MAPS: { [category: string]: DamageMap } = {
  CAD: {
    [ColorSymbol.PURPLE]: 10,
    [ColorSymbol.GREEN]: 20,
    [ColorSymbol.RED]: 50,
  },
  EUR: {
    [ColorSymbol.WHITEBLACK]: 5,
    [ColorSymbol.RED]: 10,
    [ColorSymbol.GREEN]: 100,
    [ColorSymbol.YELLOW]: 200,
    [ColorSymbol.PURPLE]: 500,
  },
  AUD: {
    [ColorSymbol.PURPLE]: 5,
    [ColorSymbol.RED]: 20,
    [ColorSymbol.YELLOW]: 50,
    [ColorSymbol.GREEN]: 100,
  },
  KRW: {
    [ColorSymbol.RED]: 5000,
    [ColorSymbol.GREEN]: 10000,
    [ColorSymbol.YELLOW]: 50000,
  },
  CHF: {
    [ColorSymbol.YELLOW]: 10,
    [ColorSymbol.RED]: 20,
    [ColorSymbol.GREEN]: 50,
    [ColorSymbol.PURPLE]: 1000,
  },
  SEK: {
    [ColorSymbol.PURPLE]: 20,
    [ColorSymbol.GREEN]: 200,
    [ColorSymbol.RED]: 500,
  },
  THB: {
    [ColorSymbol.GREEN]: 20,
    [ColorSymbol.RED]: 100,
    [ColorSymbol.PURPLE]: 500,
  },
  BRL: {
    [ColorSymbol.PURPLE]: 5,
    [ColorSymbol.RED]: 10,
    [ColorSymbol.YELLOW]: 20,
    [ColorSymbol.WHITEBLACK]: 200,
  },
};

// map mintable creatures to their weaknesses
const mintCreatures: { [name: string]: DamageMap } = {
  goose: DAMAGE_MAPS.CAD,
  poutine: DAMAGE_MAPS.CAD,
  wisent: DAMAGE_MAPS.EUR,
  "twelve-stars": DAMAGE_MAPS.EUR,
  "consort-on-a-bull-deity": DAMAGE_MAPS.EUR,
  "spicy-fermented-cabbage": DAMAGE_MAPS.KRW,
  hallyu: DAMAGE_MAPS.KRW,
  "wombat-mob": DAMAGE_MAPS.AUD,
  "jar-of-vegemite": DAMAGE_MAPS.AUD,
  didgeridoo: DAMAGE_MAPS.AUD,
  "toblerone-dipped-in-fondue": DAMAGE_MAPS.CHF,
  "plus-sign": DAMAGE_MAPS.CHF,
  "army-knife": DAMAGE_MAPS.CHF,
  "pungent-canned-herring": DAMAGE_MAPS.SEK,
  "dala-horse": DAMAGE_MAPS.SEK,
  "green-curry": DAMAGE_MAPS.THB,
  "red-curry": DAMAGE_MAPS.THB,
  "ka-api-ara": DAMAGE_MAPS.BRL,
  "toasted-cassava": DAMAGE_MAPS.BRL,
  "order-and-progress": DAMAGE_MAPS.BRL,
};

export const Puzzle8Spec: GameSpecServerFragment = {
  adjustDamageReceived: (permanent, damage, attacker, ctx) => {
    const { inspector, engine } = ctx;

    if (!attacker) return damage;

    const name = inspector.getCardName(permanent);
    const weaknesses = mintCreatures[name];
    if (!weaknesses) return damage; // all other creatures take normal damage

    const defenderColor = inspector.getCardColor(permanent);
    const attackerColor = inspector.getCardColor(attacker);

    // non-existent color
    if (!attackerColor || !defenderColor) return 0;

    // if attacker same color, take 0
    if (defenderColor === attackerColor) return 0;

    // check damageMap
    if (weaknesses[attackerColor]) {
      return weaknesses[attackerColor];
    }

    // deal 0 damage otherwise
    return 0;
  },
  afterStep: (step, ctx) => {
    const { engine, inspector } = ctx;
    const p2Creatures = inspector.getPermanents({
      owner: Player.P2,
      isBase: false,
    });
    if (p2Creatures.length === 0) {
      const mints = inspector.getPermanents({
        owner: Player.P2,
        isBase: true,
      });
      for (const mint of mints) {
        engine.removeCounterType(mint, CounterType.INVULNERABLE);
      }
    }
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      ai.speak(
        "How did you GET in here? I swear this boar treasury is OFF-LIMITS to VISITORS, or my name isn’t MISTER PENNY!"
      );
    }

    // TODO: if lost, say "nooo!! my life’s work... ruined! please don’t tell my wife..."

    // mints spawn
    let mints = inspector.getPermanents({
      owner: Player.P2,
      name: "mint",
    });

    // spawn 1 creature for first 5 turns, then spawn 2
    let numToSpawn = 1;

    if (inspector.getTurnNumber() / 2 === 5) {
      mints.forEach((mint) => {
        engine.getBackdoorEngine().transformPermanent(mint, "mint--overdrive-");
      });
      ai.speak("ENTERING MINTY OVERDRIVE!!!");
    }

    if (inspector.getTurnNumber() / 2 >= 5) {
      numToSpawn = 2;
      mints = inspector.getPermanents({
        owner: Player.P2,
        name: "mint--overdrive-",
      });
    }

    const mintsRandom = mints.sort(() => Math.random() - 0.5);

    let spawned = 0;

    for (const mint of mintsRandom) {
      const { row, column } = mint.slot;
      const creatureNameRandom =
        Object.keys(mintCreatures)[
          Math.floor(Math.random() * Object.keys(mintCreatures).length)
        ];
      const success = ai.trySpawn(creatureNameRandom, row + 1, column);
      if (success) {
        spawned++;
      }
      if (spawned >= numToSpawn) {
        break;
      }
    }

    // Update mint invuln
    if (spawned > 0) {
      for (const mint of mints) {
        engine.removeCounterType(mint, CounterType.INVULNERABLE);
        engine.addCounter(mint, { type: CounterType.INVULNERABLE });
      }
    }

    // move / attack with all units
    const creatures = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.CREATURE,
    });

    for (const creature of creatures) {
      let moved = false;
      // move down to row 5, if unblocked
      if (creature.slot.row < 4) {
        moved = ai.tryMoveRel(creature, 1, 0);
      }
      if (!moved) {
        ai.tryAttackFirst(creature);
      }
    }
  },
};
