import { MasteryTree } from "game-server/masteries";
import {
  BackendDeck,
  BackendAnswerSubmission,
  BackendUnlock,
  BackendCardUnlock,
  BackendCutsceneChoice,
} from "game-server/backend-interface/BackendInterface";
import { GameServerConfig } from "game-server/GameServer";
import { DirtyStateType, DirtyState } from "game-server/monitoring";
import { Faction } from "engine/types/factions";
import { SerializedCheckpointState } from "engine/types/game-state";

/**
 * Amount of time to throttle the slow sync. For now, don't throttle
 * it; the sync period will depend on the periodic sync frequency.
 */
export const SLOW_SYNC_THROTTLE_MS = 0;

export type BackendSyncReqPacket = {
  /** Inactive state to update. */
  isInactive?: boolean;
  /** Hidden state to update. */
  isHidden?: boolean;
  /** Display name to update */
  displayName?: string;
  /** Faction to update. */
  faction?: Faction | null;
  /** Faction score contributions to update. */
  factionScoreContributions?: { [faction in Faction]?: number };
  /** Decks to create or overwrite. */
  decks?: { [slot: number]: BackendDeck };
  /** Mastery tree to create or overwrite. */
  masteryTree?: MasteryTree;
  /** Answer submissions to add/fish puzzle solves to delete. */
  answerSubmissions?: {
    [puzName: string]: {
      [answer: string]: BackendAnswerSubmission;
    } | null;
  };
  /** Unlocks to add/delete. */
  unlocks?: { [puzName: string]: BackendUnlock | null };
  /** Card unlocks to add/delete. */
  cardUnlocks?: { [cardName: string]: BackendCardUnlock | null };
  /** View times to update. Only used for battles. */
  viewTimes?: { [puzName: string]: number };
  /** Slots of preferred decks to set. */
  preferredDecks?: { [puzName: string]: number };
  /** Puzzle selections for instancer rooms. */
  puzzleSelections?: { [puzName: string]: string | null };
  /** Saved checkpoints for each puzzle. */
  checkpoints?: {
    [puzName: string]: { [slot: number]: SerializedCheckpointState };
  };
  /** teamIds of blocked teams to add/delete. */
  blockedTeams?: { [teamId: string]: boolean };
  /** Whether we're in the ring, to set or unset. */
  isInRing?: boolean;
  /** Choices made during cutscene keyframes with dialog options. */
  cutsceneChoices?: BackendCutsceneChoice[];
};

export class BackendSyncReqController {
  packet: BackendSyncReqPacket;
  tracker: SyncTracker;

  constructor(
    serverConfig: GameServerConfig,
    syncType: SyncObjectType,
    onDirtyStateChange: (dirtyState: DirtyState) => void
  ) {
    this.packet = {};
    this.tracker = new SyncTracker(serverConfig, syncType, onDirtyStateChange);
  }

  isDirty(): boolean {
    return Object.keys(this.packet).length > 0;
  }

  /**
   * Merge a new packet into the pending packet, taking care to ensure
   * that the final state reflects the application of the incoming
   * state after the initial state. If there are conflicts within
   * the incoming packet itself, deletes take priority over adds.
   */
  mergePacket({
    isInactive,
    isHidden,
    displayName,
    faction,
    factionScoreContributions,
    decks,
    masteryTree,
    answerSubmissions,
    unlocks,
    cardUnlocks,
    viewTimes,
    preferredDecks,
    puzzleSelections,
    checkpoints,
    blockedTeams,
    isInRing,
    cutsceneChoices,
  }: BackendSyncReqPacket): void {
    if (isInactive !== undefined) this.packet.isInactive = isInactive;
    if (isHidden !== undefined) this.packet.isHidden = isHidden;
    if (displayName !== undefined) this.packet.displayName = displayName;
    if (faction !== undefined) this.packet.faction = faction;
    this.packet.factionScoreContributions = Object.assign(
      this.packet.factionScoreContributions ?? {},
      factionScoreContributions ?? {}
    );
    this.packet.decks = Object.assign(this.packet.decks ?? {}, decks ?? {});
    if (masteryTree !== undefined) this.packet.masteryTree = masteryTree;
    for (const [puzName, puzSubmissions] of Object.entries(
      answerSubmissions ?? {}
    )) {
      this.packet.answerSubmissions ??= {};
      if (puzSubmissions === null) {
        this.packet.answerSubmissions[puzName] = null;
        continue;
      }
      this.packet.answerSubmissions[puzName] = Object.assign(
        this.packet.answerSubmissions[puzName] ?? {},
        puzSubmissions
      );
    }
    this.packet.unlocks = Object.assign(
      this.packet.unlocks ?? {},
      unlocks ?? {}
    );
    this.packet.cardUnlocks = Object.assign(
      this.packet.cardUnlocks ?? {},
      cardUnlocks ?? {}
    );
    this.packet.viewTimes = Object.assign(
      this.packet.viewTimes ?? {},
      viewTimes ?? {}
    );
    this.packet.preferredDecks = Object.assign(
      this.packet.preferredDecks ?? {},
      preferredDecks ?? {}
    );
    this.packet.puzzleSelections = Object.assign(
      this.packet.puzzleSelections ?? {},
      puzzleSelections ?? {}
    );
    this.packet.checkpoints = Object.assign(
      this.packet.checkpoints ?? {},
      checkpoints ?? {}
    );
    this.packet.blockedTeams = Object.assign(
      this.packet.blockedTeams ?? {},
      blockedTeams ?? {}
    );
    if (isInRing !== undefined) this.packet.isInRing = isInRing;
    if (cutsceneChoices !== undefined) {
      this.packet.cutsceneChoices ??= [];
      this.packet.cutsceneChoices.push(...cutsceneChoices);
    }

    // Remove empty entries.
    this.resolvePacket({});

    if (this.isDirty()) this.tracker.handleModify();
  }

  /**
   * Takes in a snapshot representing synced data and removes it from
   * the pending packet, taking into consideration the fact that
   * the pending packet might have been modified while the sync
   * was in process.
   */
  private resolvePacket({
    isInactive,
    isHidden,
    displayName,
    faction,
    factionScoreContributions,
    decks,
    masteryTree,
    answerSubmissions,
    unlocks,
    cardUnlocks,
    viewTimes,
    preferredDecks,
    puzzleSelections,
    checkpoints,
    blockedTeams,
    isInRing,
    cutsceneChoices,
  }: BackendSyncReqPacket): void {
    if (
      this.packet.isInactive !== undefined &&
      isInactive === this.packet.isInactive
    )
      delete this.packet.isInactive;
    if (this.packet.isHidden !== undefined && isHidden === this.packet.isHidden)
      delete this.packet.isHidden;
    if (
      this.packet.displayName !== undefined &&
      displayName === this.packet.displayName
    )
      delete this.packet.displayName;
    if (this.packet.faction !== undefined && faction === this.packet.faction)
      delete this.packet.faction;
    for (const [kFaction, factionScoreContribution] of Object.entries(
      factionScoreContributions ?? {}
    )) {
      const faction = kFaction as Faction;
      if (this.packet.factionScoreContributions === undefined) continue;
      if (this.packet.factionScoreContributions[faction] === undefined)
        continue;
      if (
        this.packet.factionScoreContributions[faction] !==
        factionScoreContribution
      )
        continue;
      delete this.packet.factionScoreContributions[faction];
    }
    if (Object.keys(this.packet.factionScoreContributions ?? {}).length === 0)
      delete this.packet.factionScoreContributions;

    // For time-based syncs (decks/mastery trees), we are guaranteed
    // that a new object will not be added if an existing object (with
    // the same ID) is already in the packet.

    for (const slot of Object.keys(decks ?? {})) {
      if (this.packet.decks === undefined) continue;
      delete this.packet.decks[Number(slot)];
    }
    if (Object.keys(this.packet.decks ?? {}).length === 0)
      delete this.packet.decks;

    if (masteryTree !== undefined) delete this.packet.masteryTree;

    // If we ever e.g. find ourselves requesting to sync two different
    // solve records for the same puzzle, then it does not matter which
    // one gets synced. So, we can just check e.g. whether we want a
    // puzzle solved or unsolved to resolve its pending sync state.

    for (const [puzName, puzSubmissions] of Object.entries(
      answerSubmissions ?? {}
    )) {
      if (this.packet.answerSubmissions === undefined) continue;
      const pendingPuzSubmissions = this.packet.answerSubmissions[puzName];
      if (pendingPuzSubmissions === undefined) continue;
      if (puzSubmissions === null) {
        if (pendingPuzSubmissions !== null) continue;
        delete this.packet.answerSubmissions[puzName];
      } else {
        if (pendingPuzSubmissions === null) continue;
        for (const [answer, submissions] of Object.entries(
          puzSubmissions ?? {}
        )) {
          delete pendingPuzSubmissions[answer];
        }
        if (Object.keys(pendingPuzSubmissions).length === 0)
          delete this.packet.answerSubmissions[puzName];
      }
    }
    if (Object.keys(this.packet.answerSubmissions ?? {}).length === 0)
      delete this.packet.answerSubmissions;

    for (const [puzName, unlock] of Object.entries(unlocks ?? {})) {
      if (this.packet.unlocks === undefined) continue;
      const pendingUnlock = this.packet.unlocks[puzName];
      if (pendingUnlock === undefined) continue;
      if ((pendingUnlock === null) !== (unlock === null)) continue;
      delete this.packet.unlocks[puzName];
    }
    if (Object.keys(this.packet.unlocks ?? {}).length === 0)
      delete this.packet.unlocks;

    for (const [puzName, cardUnlock] of Object.entries(cardUnlocks ?? {})) {
      if (this.packet.cardUnlocks === undefined) continue;
      const pendingCardUnlock = this.packet.cardUnlocks[puzName];
      if (pendingCardUnlock === undefined) continue;
      if ((pendingCardUnlock === null) !== (cardUnlock === null)) continue;
      if (
        (pendingCardUnlock?.puzName ?? null) !== (cardUnlock?.puzName ?? null)
      )
        continue;
      delete this.packet.cardUnlocks[puzName];
    }
    if (Object.keys(this.packet.cardUnlocks ?? {}).length === 0)
      delete this.packet.cardUnlocks;

    // View times will only ever be set once, so we have nothing to check.
    for (const puzName of Object.keys(viewTimes ?? {})) {
      if (this.packet.viewTimes === undefined) continue;
      delete this.packet.viewTimes[puzName];
    }
    if (Object.keys(this.packet.viewTimes ?? {}).length === 0)
      delete this.packet.viewTimes;

    for (const [puzName, slot] of Object.entries(preferredDecks ?? {})) {
      if (this.packet.preferredDecks === undefined) continue;
      const pendingSlot = this.packet.preferredDecks[puzName];
      if (pendingSlot === undefined) continue;
      if (pendingSlot !== slot) continue;
      delete this.packet.preferredDecks[puzName];
    }
    if (Object.keys(this.packet.preferredDecks ?? {}).length === 0)
      delete this.packet.preferredDecks;

    for (const [puzName, selection] of Object.entries(puzzleSelections ?? {})) {
      if (this.packet.puzzleSelections === undefined) continue;
      const pendingSelection = this.packet.puzzleSelections[puzName];
      if (pendingSelection === undefined) continue;
      if (pendingSelection !== selection) continue;
      delete this.packet.puzzleSelections[puzName];
    }
    if (Object.keys(this.packet.puzzleSelections ?? {}).length === 0)
      delete this.packet.puzzleSelections;

    for (const [puzName, states] of Object.entries(checkpoints ?? {})) {
      if (this.packet.checkpoints === undefined) continue;
      const pendingStates = this.packet.checkpoints[puzName];
      if (pendingStates === undefined) continue;
      (() => {
        if (Object.keys(pendingStates).length !== Object.keys(states).length)
          return;
        for (const [slot, state] of Object.entries(states)) {
          const pendingState = pendingStates[Number(slot)];
          if (pendingState === undefined) return;
          if (pendingState.turnNumber !== state.turnNumber) return;
          if (pendingState.roomId !== state.roomId) return;
          if (pendingState.timestamp !== state.timestamp) return;
          if (pendingState.gainedScore !== state.gainedScore) return;
          if (pendingState.serializedState !== state.serializedState) return;
        }
        delete this.packet.checkpoints[puzName];
      })();
    }
    if (Object.keys(this.packet.checkpoints ?? {}).length === 0)
      delete this.packet.checkpoints;

    for (const [teamId, isBlocked] of Object.entries(blockedTeams ?? {})) {
      if (this.packet.blockedTeams === undefined) continue;
      const pendingIsBlocked = this.packet.blockedTeams[teamId];
      if (pendingIsBlocked === undefined) continue;
      if (pendingIsBlocked !== isBlocked) continue;
      delete this.packet.blockedTeams[teamId];
    }
    if (Object.keys(this.packet.blockedTeams ?? {}).length === 0)
      delete this.packet.blockedTeams;

    if (this.packet.isInRing !== undefined && isInRing === this.packet.isInRing)
      delete this.packet.isInRing;

    if (cutsceneChoices !== undefined) {
      if (this.packet.cutsceneChoices === undefined)
        throw new Error("expect cutscene choices to be prefix of packet");
      this.packet.cutsceneChoices.splice(0, cutsceneChoices.length);
    }
    if ((this.packet.cutsceneChoices ?? []).length === 0)
      delete this.packet.cutsceneChoices;
  }

  /**
   * Starts a sync, and returns a snapshot of the current packet,
   * which is a clone of the current packet at the appropriate depth.
   *
   * Returns null if there is nothing to sync. In this case, a sync
   * is not considered to have started.
   */
  startSync(): BackendSyncReqPacket | null {
    if (!this.tracker.snapshot()) return null;
    if (!this.isDirty()) {
      this.tracker.handleSyncResult(true);
      return null;
    }

    const {
      factionScoreContributions,
      decks,
      answerSubmissions,
      unlocks,
      cardUnlocks,
      viewTimes,
      preferredDecks,
      puzzleSelections,
      checkpoints,
      blockedTeams,
    } = this.packet;
    return {
      ...this.packet,
      factionScoreContributions:
        factionScoreContributions === undefined
          ? undefined
          : { ...factionScoreContributions },
      decks: decks === undefined ? undefined : { ...decks },
      answerSubmissions:
        answerSubmissions === undefined
          ? undefined
          : Object.fromEntries(
              Object.entries(answerSubmissions).map(
                ([puzName, puzSubmissions]) => [
                  puzName,
                  puzSubmissions === null ? null : { ...puzSubmissions },
                ]
              )
            ),
      unlocks: unlocks === undefined ? undefined : { ...unlocks },
      cardUnlocks: cardUnlocks === undefined ? undefined : { ...cardUnlocks },
      viewTimes: viewTimes === undefined ? undefined : { ...viewTimes },
      preferredDecks:
        preferredDecks === undefined ? undefined : { ...preferredDecks },
      puzzleSelections:
        puzzleSelections === undefined ? undefined : { ...puzzleSelections },
      checkpoints: checkpoints === undefined ? undefined : { ...checkpoints },
      blockedTeams:
        blockedTeams === undefined ? undefined : { ...blockedTeams },
    };
  }

  /**
   * Takes in a snapshot from a sync. After a sync is complete,
   * removes the synced data from the pending packet, taking into
   * consideration the fact that the pending packet might have been
   * modified while the sync was in process.
   *
   * A null snapshot indicates that the sync failed.
   */
  endSync(snapshot: BackendSyncReqPacket | null): void {
    if (snapshot !== null) this.resolvePacket(snapshot);
    this.tracker.handleSyncResult(snapshot !== null);
  }
}

/**
 * Utility class to manage sync loops. A user of this controller
 * independently manages a queue of sync tasks, and provides a
 * runSyncAsync method that performs a single sync of some subset
 * of the sync tasks. startLoopAsync can then be used to repeatedly
 * run the provided runSyncAsync until there is nothing left to sync.
 *
 * The user may also add callbacks that wait on pending syncs.
 * These are called once a single iteration of the sync loop completes,
 * because those callbacks do not care if any additional sync tasks
 * are added while that iteration was running.
 */
export class SyncLoopController {
  isSyncInFlight: boolean;
  /** Callbacks waiting on the sync. */
  pendingCallbacks: (() => void)[];
  /**
   * Performs a sync if there is anything to sync at call time.
   * Returns true if the loop is complete, i.e. we have tried to
   * sync all pending sync items.
   */
  runSyncAsync: () => Promise<boolean>;

  constructor(runSyncAsync: () => Promise<boolean>) {
    this.isSyncInFlight = false;
    this.pendingCallbacks = [];
    this.runSyncAsync = runSyncAsync;
  }

  /**
   * Run the sync loop, if one isn't already running.
   * This does not guarantee the completion of any sync tasks.
   * If we need to wait for sync tasks, use triggerLoopAndWaitAsync.
   */
  async triggerLoopAsync() {
    if (this.isSyncInFlight) return;
    this.isSyncInFlight = true;

    // Keep syncing until the loop is complete.
    while (true) {
      // Don't actually modify pendingCallbacks until runSyncAsync
      // completes without errors.
      const numCallbacks = this.pendingCallbacks.length;

      let isLoopComplete = false;
      try {
        isLoopComplete = await this.runSyncAsync();
      } catch (err) {
        // If we hit an error with this sync, break out of the loop
        // to ensure that we release the lock, and try again later
        // as part of slow sync.
        console.error(err);
        break;
      }

      // Complete the callbacks even if nothing was synced.
      for (const callback of this.pendingCallbacks.splice(0, numCallbacks)) {
        try {
          callback();
        } catch (err) {
          // If we fail here, ensure that we fully clean up state
          // by continuing to process the other callbacks.
          console.error(err);
        }
      }

      if (isLoopComplete) break;
    }

    this.isSyncInFlight = false;
  }

  /**
   * Trigger the loop and wait for current sync tasks to finish syncing.
   * Note that the loop may continue running even after this function
   * returns, if more tasks got added to the queue in the meantime.
   */
  async triggerLoopAndWaitAsync() {
    await new Promise<void>((resolve, reject) => {
      this.pendingCallbacks.push(resolve);
      // Don't block on the loop completion.
      this.triggerLoopAsync();
    });
  }
}

export enum SyncObjectType {
  FAST = "fast",
  MISC = "misc",
  MASTERY_TREE = "mastery_tree",
  DECK = "deck",
  ROOM = "room",
  CHECKPOINTS = "checkpoints",
  ANSWER = "answer",
}

export class SyncTracker {
  serverConfig: GameServerConfig;
  syncType: SyncObjectType;
  onDirtyStateChange: (dirtyState: DirtyState) => void;
  revNumber: number;
  syncedRevNumber: number;
  modifyTime: number;
  syncTime: number;

  /** The revision number that is currently being synced, if any. */
  syncingRevNumber: number | null;

  constructor(
    serverConfig: GameServerConfig,
    syncType: SyncObjectType,
    onDirtyStateChange: (dirtyState: DirtyState) => void
  ) {
    this.serverConfig = serverConfig;
    this.syncType = syncType;
    this.onDirtyStateChange = onDirtyStateChange;
    this.revNumber = 0;
    this.syncedRevNumber = 0;
    const timeNow = new Date().getTime();
    this.modifyTime = timeNow;
    this.syncTime = timeNow;

    this.syncingRevNumber = null;
  }

  /** Returns if a sync is needed. */
  snapshot(): boolean {
    if (this.revNumber === this.syncedRevNumber) return false;
    if (
      this.modifyTime - this.syncTime <
      this.serverConfig.slowSyncThrottle[this.syncType]
    )
      return false;
    // Note that we may request a sync despite syncingRevNumber
    // already being set, due to a previous failed sync. In this
    // case, just overwrite it.
    this.syncingRevNumber = this.revNumber;
    this.onDirtyStateChange({
      type: DirtyStateType.SYNCING,
      lastSyncTime: this.syncTime,
    });
    return true;
  }

  handleModify() {
    const timeNow = Date.now();
    if (this.revNumber === this.syncedRevNumber) this.syncTime = timeNow;
    this.modifyTime = timeNow;
    this.revNumber++;
    this.onDirtyStateChange({
      type: DirtyStateType.DIRTY,
      lastSyncTime: this.syncTime,
    });
  }

  handleSyncResult(success: boolean) {
    if (success) {
      this.syncTime = new Date().getTime();
      if (this.syncingRevNumber === null)
        throw new Error("no sync in progress");
      this.syncedRevNumber = this.syncingRevNumber;
    }
    this.syncingRevNumber = null;
    this.onDirtyStateChange({
      type: success ? DirtyStateType.SYNCED : DirtyStateType.FAILED,
      lastSyncTime: this.syncTime,
    });
  }
}
