import {
  ServerInterface,
  ServerInterfaceCallbacks,
} from "game-server/server-interface/ServerInterface";
import { GameServer } from "game-server/GameServer";

export class MockServerInterface implements ServerInterface {
  gameServer: GameServer;
  callbacks?: ServerInterfaceCallbacks;
  connUid?: string;
  handleMsg: (msg: string) => void;
  disconnect: () => void;
  isClosed: boolean;

  constructor(gameServer: GameServer, connUid?: string) {
    this.gameServer = gameServer;
    this.connUid = connUid;
    this.handleMsg = (msg) => {
      throw new Error("connection not initialized yet");
    };
    this.disconnect = () => {
      throw new Error("connection not initialized yet");
    };
    this.isClosed = false;
  }

  init(callbacks: ServerInterfaceCallbacks) {
    this.callbacks = callbacks;
    this.connect();
  }

  private connect(): void {
    if (this.callbacks === undefined)
      throw new Error("expect callbacks to be defined");
    const { onOpen, onResp, afterDisconnect } = this.callbacks;
    const { handleMsg, removeClient } = this.gameServer.addClient(
      {
        send: (msg) => {
          if (this.isClosed) return;
          Promise.resolve().then(() => {
            onResp(msg);
          });
        },
        close: () => {
          this.close();
        },
      },
      this.connUid
    );
    this.handleMsg = handleMsg;
    this.disconnect = () => {
      removeClient();
      afterDisconnect({
        numReconnections: 0,
        reconnectDelayInSeconds: 0,
      });
    };

    onOpen();
  }

  send(msg: string) {
    Promise.resolve().then(() => {
      this.handleMsg(msg);
    });
  }

  reconnect() {
    this.disconnect();
    this.connect();
  }

  close() {
    this.disconnect();
    this.isClosed = true;
  }
}
