const shuffle = <T>(a: T[]) => {
  const array = a.slice();
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
};

const makePuzzle50Spec = (
  INFO: {
    global_constraint: number[][];
    puzzles: {
      clues: number[][];
      answer: boolean[][];
    }[];
  },
  showSolution: boolean,
  isInvulnerable: boolean
): GameSpecServerFragment => ({
  ...Puzzle50CommonSpec,
  beforeNewGame: (ctx) => {
    // TODO: seed?
    // choose a puzzle nondeterministically
    const puzzleId = Math.floor(Math.random() * INFO.puzzles.length);
    ctx.engine.initExtraState({
      globalConstraints: INFO.global_constraint.map((a) => ({
        id: `puzzle-50-global-${a[0]}`,
        health: a[1],
      })),
      // shuffle clues
      clues: shuffle(
        INFO.puzzles[puzzleId].clues.map((a) => ({
          id: `puzzle-50-local-${a[0]}`,
          health: a[1],
          row: a[2],
          column: a[3],
        }))
      ),
      cluesPlaced: 0,
      answer: INFO.puzzles[puzzleId].answer,
      showSolution: showSolution,
      passed: false,
      isInvulnerable: isInvulnerable,
    } as Puzzle50ExtraState);
  },
});

