import { DeckSelection } from "engine/types/decks";
import {
  Player,
  DevKnobs,
  PlayerStateTeamData,
  MutableGameState,
} from "engine/types/game-state";
import { PUZ_NAME_PVP } from "engine/puzzles/puzzle-data";
import { GameSpec } from "engine/types/game-specs";
import {
  EffectsDB,
  CountersDB,
  getEffectsDB,
  getCountersDB,
} from "engine/cards/card-effects";
import { getPrimaryPuzName } from "engine/puzzles/puzzle-data";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";
import { PUZ_NAME_COMMON } from "engine/game-specs/common-shared";
import { getGameSpec } from "engine/all-game-specs";
import { StaticInspector } from "engine/Inspector";
import { Engine } from "engine/Engine";
import { UpdatesMulticaster } from "game-server/UpdatesMulticaster";
import { GameServer } from "game-server/GameServer";
import {
  ActiveRoomsSyncController,
  CompletedRoomsSyncController,
} from "game-server/sync-controllers";
import {
  TeamCard,
  makeBattleSolveAnswerSubmission,
} from "game-server/backend-interface/BackendInterface";
import {
  SyncTracker,
  SyncObjectType,
} from "game-server/backend-interface/backend-sync";
import { GameRoom } from "game-server/Room";
import { DirtyStateType, ServerDirtyStateUpdate } from "game-server/monitoring";

export class GameRoomsController {
  server: GameServer;

  /**
   * Map from roomId to room objects, and a refcount to ensure that
   * the room gets cleared once it is no longer referenced by any
   * active games.
   */
  rooms: Map<
    string,
    {
      room: GameRoom;
      refcount: number;
    }
  >;
  /** Map from teamId and puzName to the active roomId. */
  activeRoomIds: Map<string, Map<string, string>>;

  slowSyncTracking: {
    activeRoomsSyncController: ActiveRoomsSyncController;
    completedRoomsSyncController: CompletedRoomsSyncController;
  };

  puzDataCache: {
    [puzName: string]: {
      allSpecs: ReadonlyArray<GameSpec>;
      effectsDB: EffectsDB;
      countersDB: CountersDB;
      inspector: StaticInspector;
    };
  };

  constructor(server: GameServer) {
    this.server = server;
    this.rooms = new Map();
    this.activeRoomIds = new Map();
    this.slowSyncTracking = {
      activeRoomsSyncController: new ActiveRoomsSyncController(this.server),
      completedRoomsSyncController: new CompletedRoomsSyncController(
        this.server
      ),
    };
    this.puzDataCache = {};
  }

  getPuzData(puzName: string) {
    const existingPuzData = this.puzDataCache[puzName];
    if (existingPuzData !== undefined) return existingPuzData;

    const allSpecs = [PUZ_NAME_COMMON, ...puzName.split(",")].map((specName) =>
      getGameSpec(specName)
    );
    const effectsDB = getEffectsDB(allSpecs, this.server.cardsDB);
    const countersDB = getCountersDB(allSpecs, effectsDB);
    const inspector = new StaticInspector(
      allSpecs,
      this.server.cardsDB,
      effectsDB
    );

    const puzData = {
      allSpecs,
      effectsDB,
      countersDB,
      inspector,
    };
    this.puzDataCache[puzName] = puzData;
    return puzData;
  }

  getInspector(puzName: string): StaticInspector {
    return this.getPuzData(puzName).inspector;
  }

  createRoomWithTeamData(
    puzName: string,
    hostPuzName: string,
    teamData: { [player in Player]?: PlayerStateTeamData },
    deckSelections: { [player in Player]?: DeckSelection },
    devKnobs?: DevKnobs,
    gameState?: MutableGameState,
    roomId?: string
  ): GameRoom {
    roomId ??= this.server.genGameRoomId();
    const multicaster = new UpdatesMulticaster();
    const { allSpecs, effectsDB, countersDB } = this.getPuzData(puzName);
    const engine = new Engine(
      multicaster,
      puzName,
      roomId,
      allSpecs,
      (player) => [],
      this.server.cardsDB,
      effectsDB,
      countersDB,
      this.server.keyframes,
      teamData,
      deckSelections,
      (engine, slots) => {
        // this should only be one team, since we are in an AI battle
        for (const { teamId } of Object.values(teamData))
          this.server
            .getBattleController(teamId, hostPuzName)
            .makeCheckpoint(engine, slots);
      },
      (engine, keyframeId, dialogOptionIndex) => {
        for (const { teamId } of Object.values(teamData)) {
          // HACK: We now depend on the cutscene choice sync to render
          // the victory page, so this needs to be blocking. We don't
          // need to block right here, though, since we will block
          // later anyway to sync the final cutscene solve.
          // No need to trigger the sync loop either, since we already
          // trigger the fast sync loop periodically (in addition to
          // immediately on normal fast syncs).
          this.server.getTeamController(teamId).mergeFastSyncPacket({
            cutsceneChoices: [
              {
                keyframeId,
                dialogOptionIndex,
              },
            ],
          });
        }
      },
      devKnobs,
      gameState
    );
    if (gameState === undefined) engine.init();
    const room: GameRoom = {
      engine,
      roomId,
      hostPuzName,
      createTime: new Date().getTime(),
      members: new Map(),
      multicaster,
      tracker: new SyncTracker(
        this.server.config,
        SyncObjectType.ROOM,
        (dirtyState) => {
          const dirtyStateUpd: ServerDirtyStateUpdate = { teams: {} };
          // Only merge the dirty state update if the room is still
          // active. Otherwise, the dirty state should be marked as
          // synced and reponsibility for syncing the room state should
          // transfer to the completed rooms sync path.
          for (const { teamId } of Object.values(teamData)) {
            const activeRoom = this.getActiveRoom(teamId, hostPuzName);
            if (activeRoom === null || activeRoom.roomId !== roomId) continue;
            const battleController = this.server.getBattleController(
              teamId,
              hostPuzName
            );
            if (!battleController.isRoomActive()) continue;
            dirtyStateUpd.teams[teamId] = {
              rooms: { [hostPuzName]: dirtyState },
            };
          }
          this.server.dirtyStateTracker.update(dirtyStateUpd);
        }
      ),
    };
    this.server.prepareRoom(room);
    this.rooms.set(roomId, {
      room,
      refcount: 0,
    });
    return room;
  }

  createRoomFromGameState(
    puzName: string,
    hostPuzName: string,
    gameState: MutableGameState,
    roomId?: string
  ): GameRoom {
    const teamData = Object.fromEntries(
      [Player.P1, Player.P2].flatMap((player) => {
        const playerTeamData = gameState[player].teamData;
        if (playerTeamData === undefined) return [];
        return [[player, playerTeamData]];
      })
    );

    return this.server.gameRoomsController.createRoomWithTeamData(
      puzName,
      hostPuzName,
      teamData,
      gameState.engineOnly.deckSelections,
      gameState.devKnobs,
      gameState,
      roomId
    );
  }

  createRoom(
    puzName: string,
    hostPuzName: string,
    teamIds: { [player in Player]?: string },
    deckSelections: { [player in Player]?: DeckSelection },
    devKnobs?: DevKnobs
  ): GameRoom {
    const teamData = Object.fromEntries(
      Object.entries(teamIds).map(([player, teamId]) => {
        const teamController = this.server.getTeamController(teamId);
        return [player, teamController.makeTeamDataForBattle(puzName)];
      })
    );
    return this.createRoomWithTeamData(
      puzName,
      hostPuzName,
      teamData,
      deckSelections,
      devKnobs
    );
  }

  getActiveRoom(teamId: string, puzName: string): GameRoom | null {
    const teamRooms = this.activeRoomIds.get(teamId);
    if (teamRooms === undefined) return null;
    const roomId = teamRooms.get(puzName);
    if (roomId === undefined) return null;
    const roomTracker = this.rooms.get(roomId);
    if (roomTracker === undefined)
      throw new Error("expect active room to be available");
    return roomTracker.room;
  }

  setActiveRoom(teamId: string, puzName: string, roomId: string) {
    const roomTracker = this.rooms.get(roomId);
    if (roomTracker === undefined)
      throw new Error("expect room to exist if setting active");
    roomTracker.refcount++;
    const teamRooms = (() => {
      const existingTeamRooms = this.activeRoomIds.get(teamId);
      if (existingTeamRooms !== undefined) return existingTeamRooms;
      const newTeamRooms = new Map();
      this.activeRoomIds.set(teamId, newTeamRooms);
      return newTeamRooms;
    })();
    const prevRoomId = teamRooms.get(puzName);
    if (prevRoomId !== undefined) {
      const prevRoomTracker = this.rooms.get(prevRoomId);
      if (prevRoomTracker === undefined)
        throw new Error("expect prev room to exist if unsetting");
      prevRoomTracker.refcount--;
      if (prevRoomTracker.refcount <= 0) this.rooms.delete(prevRoomId);
    }
    teamRooms.set(puzName, roomId);
  }

  getNumRoomMembers(teamId: string, puzName: string) {
    const room = this.getActiveRoom(teamId, puzName);
    if (room === null) return 0;
    return [...room.members.values()].filter(
      (otherMember) => otherMember.conn.teamId === teamId
    ).length;
  }

  handleRoomMembersUpdate(teamId: string, puzName: string) {
    this.server.bigBoardStateTracker.updateTeam(teamId, {
      unlocks: {
        [puzName]: {
          numMembers: this.getNumRoomMembers(teamId, puzName),
        },
      },
    });
  }

  async handleRoomCompleteAsync(
    room: GameRoom,
    solvedTeamIds: string[],
    cardUnlocks: TeamCard[]
  ): Promise<void> {
    const { roomId, hostPuzName, engine, createTime } = room;
    const { inspector } = engine;
    const { puzName: fullPuzName, gameState } = room.engine;
    const puzName = getPrimaryPuzName(fullPuzName);
    const isPvP = puzName === PUZ_NAME_PVP;
    const timeNow = new Date().getTime();

    // Deduplicate the teamIds so that we only report solves once.
    const p1TeamData = gameState[Player.P1].teamData;
    const p2TeamData = gameState[Player.P2].teamData;
    const p1TeamId = p1TeamData?.teamId;
    const p2TeamId = p2TeamData?.teamId;
    const teamIds = new Set([
      ...(p1TeamId === undefined ? [] : [p1TeamId]),
      ...(p2TeamId === undefined ? [] : [p2TeamId]),
    ]);
    const teamControllers = Array.from(teamIds, (teamId) =>
      this.server.getTeamController(teamId)
    );
    /**
     * Just treat it as solved if any player solved, since it sounds like
     * we're only ever going to have P1 solving anyway.
     */
    const isSolved = solvedTeamIds.length > 0;
    const totalTime = inspector.getSpeedrunTime();
    const speedrunPlayers = isSolved ? [Player.P1] : [];

    const playerScores: { [player in Player]?: number } = {};
    for (const player of [Player.P1, Player.P2]) {
      let factionScore = inspector.calcFactionScore(player, isPvP, isSolved);
      if (!isPvP) {
        if (factionScore > gameState.engineOnly.gainedScore) {
          factionScore -= gameState.engineOnly.gainedScore;
          gameState.engineOnly.gainedScore += factionScore;
        } else factionScore = 0;
      }
      if (factionScore !== 0) playerScores[player] = factionScore;
    }

    const { winner, loseReason } = gameState;
    if (winner === undefined || loseReason === undefined)
      throw new Error("winner/loseReason should be defined on game end");

    const roomToSync = {
      roomId,
      puzName,
      hostPuzName,
      createTime,
      p1TeamId,
      p2TeamId,
      p1Faction: p1TeamData?.faction ?? undefined,
      p2Faction: p2TeamData?.faction ?? undefined,
      savedRoom: { gameState },
      gameEndInfo: {
        totalTime,
        isSolved,
        winner,
        loseReason,
        p1FactionScoreContribution: playerScores[Player.P1],
        p2FactionScoreContribution: playerScores[Player.P2],
      },
    };

    /**
     * Detect immediate-retries ("mulligans") in AI rooms and don't
     * bother adding those rooms to sync.
     */
    if (room.tracker.revNumber > 0 || !inspector.hasAI()) {
      // Treat the room as synced, since we've transferred responsibility
      // for syncing it to the completed rooms sync controller.
      const teamDirtyStateUpd = {
        rooms: {
          [hostPuzName]: {
            type: DirtyStateType.SYNCED,
            lastSyncTime: Date.now(),
          },
        },
      };
      this.server.dirtyStateTracker.update({
        teams: Object.fromEntries(
          [...teamIds].map((teamId) => [teamId, teamDirtyStateUpd])
        ),
      });

      const { completedRoomsSyncController } = this.slowSyncTracking;
      this.slowSyncTracking.completedRoomsSyncController.addRoom(roomToSync);
    }

    await Promise.all(
      Array.from(teamControllers, (teamController) =>
        teamController.handleTeamUpdatesAsync({
          answerSubmissionsToAdd: solvedTeamIds
            .filter((teamId) => teamId === teamController.teamId)
            .map((teamId) =>
              makeBattleSolveAnswerSubmission(teamId, puzName, timeNow)
            ),
          cardUnlocksToAdd: cardUnlocks
            .filter(({ teamId }) => teamId === teamController.teamId)
            .map(({ teamId, cardName }) => ({
              teamId,
              puzName,
              cardName,
              timestamp: timeNow,
            })),
        })
      )
    );

    for (const player of speedrunPlayers) {
      const { teamData } = gameState[player];
      if (teamData === undefined)
        throw new Error("expect speedrunning players to belong to a team");
      const { teamId, enabledMasteries } = teamData;
      this.server
        .getTeamController(teamId)
        .handleNewSpeedrunTime(puzName, totalTime);
    }

    for (const [kPlayer, score] of Object.entries(playerScores)) {
      const player = kPlayer as Player;
      const { teamData } = gameState[player];
      if (teamData === undefined)
        throw new Error("expect scored players to belong to teams");
      const { teamId, faction, enabledMasteries } = teamData;
      const teamController = this.server.getTeamController(teamId);
      if (!(enabledMasteries[FACTIONS_MASTERY_ID] ?? false)) continue;
      if (faction === null) continue;
      teamController.handleChangeFactionScoreContributions({
        [faction]: score,
      });
    }

    for (const teamController of teamControllers) {
      teamController
        .getBattleController(hostPuzName)
        .handleRoomComplete(roomId, gameState.engineOnly.gainedScore);
    }
  }

  async triggerSlowSyncAsync(): Promise<void> {
    await this.slowSyncTracking.completedRoomsSyncController.syncLoopController.triggerLoopAsync();
    await this.slowSyncTracking.activeRoomsSyncController.syncLoopController.triggerLoopAsync();
  }
}
