import {
  Player,
  DevKnobs,
  PlayerStateTeamData,
  CheckpointState,
} from "engine/types/game-state";
import {
  PUZ_NAME_MASTERY_TREE,
  PUZ_NAME_MASTERY_TREE_PROMPT,
  PUZ_NAME_PRE_FINAL_BATTLE,
  PUZ_NAME_POST_FINAL_BATTLE,
  PUZ_NAME_FIRST_ANSWER,
  PUZ_NAME_FIRST_LEGENDARY,
  BATTLE_GROUP_NAME_INSTANCER,
  BATTLE_GROUP_NAME_TUTORIAL,
  isUsedForSolveCount,
  isUsedForGlobalSpeedrunTime,
} from "engine/puzzles/puzzle-data";
import { Faction } from "engine/types/factions";
import {
  REPUTATION_BOOST_MASTERY_ID,
  PVP_MASTERY_ID,
  SOLVE_COUNTS_MASTERY_ID,
  FACTIONS_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import { WSErrorCode, AdminModifyTeamParams } from "game-server/ws";
import { ClientConnection, GameServer } from "game-server/GameServer";
import {
  BackendAnswerSubmission,
  BackendUnlock,
  BackendCardUnlock,
  BackendRoom,
  GetInitTeamDataBackendResp,
  BackendOwnedTeamState,
  makeBattleSolveAnswerSubmission,
} from "game-server/backend-interface/BackendInterface";
import {
  BackendSyncReqPacket,
  BackendSyncReqController,
  SyncLoopController,
  SyncObjectType,
} from "game-server/backend-interface/backend-sync";
import {
  GlobalUpdatesScopeType,
  GlobalUpdateType,
  GlobalUpdate,
  GlobalStateBattleRoomStatus,
  ClientTeamAdminDataGlobalState,
  ClientTeamSpeedrunInfoGlobalState,
  ClientPuzzleGlobalState,
  ClientBattleGroupGlobalState,
} from "game-server/global-updates";
import { EnterRoomParams, UnjoinedRoomMember } from "game-server/Room";
import { HuntNotificationType } from "game-server/notifications";
import {
  BigBoardTeamState,
  TeamSummaryState,
  IntervalMetrics,
  HEALTH_MONITORING_INTERVAL_MS,
} from "game-server/monitoring";
import { TeamBattleController } from "./battle-controller";
import { TeamPvPController } from "./pvp-controller";
import {
  DeckController,
  TeamMasteryTreeController,
  TeamFishPuzzleController,
} from "./team-aux-controller";

export class TeamController {
  /** Whether connections from this team are blocked. */
  isInactive: boolean;
  /** Whether the team should be exposed on leaderboards, etc. */
  isHidden: boolean;
  /** Whether the team is temporarily blocked for maintenance */
  isTempBlocked: boolean;

  /** The Django user ID, for impersonation. */
  userId: string;

  teamId: string;
  displayName: string;
  faction: Faction | null;
  factionScoreContributions: { [faction in Faction]: number };
  members: string[];
  server: GameServer;

  deckControllers: (DeckController | null)[];
  masteryTreeController: TeamMasteryTreeController;
  /** Map of puzNames to battle controllers. */
  battleControllers: Map<string, TeamBattleController>;
  /** Map of puzNames to fish puzzle controllers. */
  fishPuzzleControllers: Map<string, TeamFishPuzzleController>;
  pvpController: TeamPvPController;

  /** puzNames of the puzzles viewed by the team. */
  views: Set<string>;
  /**
   * cardNames of the cards unlocked by the team, mapped to the
   * puzName of the puzzles that unlocked them, if any.
   */
  cardUnlocks: Map<string, string | null>;

  /** Time when each puzzle was solves, mapped by puzName. */
  unlockTimes: Map<string, number>;
  /** Time when each puzzle was solves, mapped by puzName. */
  solveTimes: Map<string, number>;
  /** Best speedrun time for each battle, mapped by puzName. */
  speedrunTimes: Map<string, number>;

  cachedSpeedrunInfo?: ClientTeamSpeedrunInfoGlobalState;

  /**
   * Map of connId to connections for connections subscribed to
   * team updates for this team.
   */
  subscriptions: Map<string, ClientConnection>;
  /** Global updates subscriptions for the decks. */
  decksSubscriptions: Map<string, ClientConnection>;
  /** Global updates subscriptions for the team members. */
  membersSubscriptions: Map<string, ClientConnection>;

  /* Hints given and used. This state is owned by Django and maintained
   * here through state syncing */
  backendOwned: BackendOwnedTeamState;

  numAuthsMetrics: IntervalMetrics;
  numRequestsMetrics: IntervalMetrics;

  /**
   * Tracking state for the fast backend sync.
   * By default, the data here is synced as soon as possible, as
   * there would be clients waiting on the sync.
   * If there is a request in flight, new data is synced immediately
   * after the request completes.
   * Unlike the periodic sync, pending changes here are broadcast and
   * applied only after the backend sync is complete.
   */
  fastSyncTracking: {
    syncLoopController: SyncLoopController;
    syncController: BackendSyncReqController;
  };

  /**
   * Tracking state for the slow backend sync.
   * The data here is only synced periodically.
   */
  slowSyncTracking: {
    syncController: BackendSyncReqController;
  };

  constructor(
    teamId: string,
    initTeamData: GetInitTeamDataBackendResp,
    server: GameServer
  ) {
    const {
      userId,
      displayName,
      isHidden,
      isInactive,
      faction,
      factionScoreContributions,
      members,
      decks,
      masteryTree,
      views,
      cardUnlocks,
      unlockTimes,
      solveTimes,
      speedrunTimes,
      numWrongGuesses,
      preferredDecks,
      puzzleSelections,
      checkpoints,
      blockedTeams,
      isInRing,
      backendOwned,
    } = initTeamData;
    this.userId = userId;
    this.teamId = teamId;
    this.displayName = displayName;
    this.faction = faction;
    this.factionScoreContributions = factionScoreContributions;
    this.members = members;
    this.server = server;

    this.deckControllers = new Array(NUM_DECK_SLOTS).fill(null);
    for (const { slot, deck } of decks) {
      if (slot < 0 || slot >= this.deckControllers.length)
        throw new Error(`deck slot ${slot} from backend out of bounds`);
      this.deckControllers[slot] = new DeckController(
        this.teamId,
        slot,
        deck,
        this.server
      );
    }

    this.masteryTreeController = new TeamMasteryTreeController(
      teamId,
      masteryTree ?? {
        placements: {},
        connectedAnswers: [],
      },
      server
    );

    this.battleControllers = new Map();
    this.fishPuzzleControllers = new Map();
    this.pvpController = new TeamPvPController(
      teamId,
      Object.keys(blockedTeams),
      server
    );
    this.views = new Set(views);
    this.cardUnlocks = new Map(Object.entries(cardUnlocks));
    this.unlockTimes = new Map(Object.entries(unlockTimes));
    this.solveTimes = new Map(Object.entries(solveTimes));
    this.speedrunTimes = new Map(Object.entries(speedrunTimes));
    this.subscriptions = new Map();
    this.decksSubscriptions = new Map();
    this.membersSubscriptions = new Map();
    this.numAuthsMetrics = new IntervalMetrics(HEALTH_MONITORING_INTERVAL_MS);
    this.numRequestsMetrics = new IntervalMetrics(
      HEALTH_MONITORING_INTERVAL_MS
    );
    this.fastSyncTracking = {
      syncLoopController: new SyncLoopController(async () => {
        try {
          return await this.runFastSyncAsync();
        } catch (err) {
          // Fail gracefully. Fast syncs should be designed to be
          // retried periodically if unsuccessful.
          console.error(err);
          return false;
        }
      }),
      syncController: new BackendSyncReqController(
        this.server.config,
        SyncObjectType.FAST,
        (dirtyState) => {
          this.server.dirtyStateTracker.update({
            teams: {
              [this.teamId]: {
                fastSync: dirtyState,
              },
            },
          });
        }
      ),
    };
    this.slowSyncTracking = {
      syncController: new BackendSyncReqController(
        this.server.config,
        SyncObjectType.MISC,
        (dirtyState) => {
          this.server.dirtyStateTracker.update({
            teams: {
              [this.teamId]: {
                misc: dirtyState,
              },
            },
          });
        }
      ),
    };

    for (const [
      puzName,
      { answer },
    ] of this.server.puzzlesDB.puzzles.entries()) {
      if (answer === null) continue;
      const numWrongGuessesForPuzzle = numWrongGuesses[puzName] ?? 0;
      const numExtraGuessesForPuzzle = backendOwned.extraGuesses[puzName] ?? 0;
      if (numWrongGuessesForPuzzle > 0 || numExtraGuessesForPuzzle !== 0) {
        this.fishPuzzleControllers.set(
          puzName,
          new TeamFishPuzzleController(
            this.teamId,
            puzName,
            numWrongGuessesForPuzzle,
            numExtraGuessesForPuzzle,
            this.server
          )
        );
      }
    }

    for (const [puzName, slot] of Object.entries(preferredDecks)) {
      this.getBattleController(puzName).selectedDeckSlot = slot;
    }
    for (const [puzName, selection] of Object.entries(puzzleSelections)) {
      this.getBattleController(puzName).selectedPuzName = selection;
    }
    for (const [puzName, states] of Object.entries(checkpoints)) {
      this.getBattleController(puzName).checkpoints = states;
    }

    // Add the team to the ring only if the mastery is enabled, in case
    // the mastery tree state got out of sync with the in-ring state.
    // We can't use enterRing as the teamController is not fully
    // initialized and registered with GameServer yet.
    if (
      isInRing &&
      this.masteryTreeController.getEnabledMasteries().has(PVP_MASTERY_ID)
    )
      this.server.pvpLobbyController.ring.push(teamId);

    this.backendOwned = backendOwned;

    this.isInactive = isInactive;
    this.isHidden = isHidden;
    this.isTempBlocked = false;
  }

  /**
   * Init the team with the stats controller. This must always be
   * called after construction, once the team controller is registered
   * with the game server.
   */
  initStats() {
    this.server.statsController.handleTeamUpdates(this.teamId, {
      factionScoreChanges: { ...this.factionScoreContributions },
      updateFactionHistory: false,
      unlocksToAdd: [...this.unlockTimes.keys()],
      solvesToAdd: [...this.solveTimes.keys()],
      speedrunTimes: Object.fromEntries(this.speedrunTimes.entries()),
      solveTimes: Object.fromEntries(this.solveTimes.entries()),
    });
  }

  initRooms(rooms: { [puzName: string]: BackendRoom }) {
    for (const roomEntry of Object.values(rooms)) {
      const { puzName, hostPuzName } = roomEntry;
      // Don't restore a saved room if its puzzle was set locked,
      // as doing so in the instancer would set the selected puzzle.
      if (this.unlockTimes.has(puzName))
        this.getBattleController(hostPuzName).restoreSavedRoom(roomEntry);
    }
  }

  broadcastTeamGlobalUpdate(upd: GlobalUpdate): void {
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM,
        teamId: this.teamId,
      },
      upd
    );
  }

  getDeckController(slot: number): DeckController {
    // TODO: Make sure slot bounds are validated in callers
    const existingController = this.deckControllers[slot];
    if (existingController !== null) return existingController;

    const timeNow = new Date().getTime();

    const deck: Deck = createNewDeck(slot);
    const deckController = new DeckController(
      this.teamId,
      slot,
      deck,
      this.server
    );
    this.deckControllers[slot] = deckController;
    // Broadcast the deck creation to subscribers by pushing a null update.
    deckController.handleDeckUpdate({});
    return deckController;
  }

  isValidBattle(puzName: string): boolean {
    // Either the puzName is invalid, or the team has not unlocked it.
    if (!this.unlockTimes.has(puzName)) return false;
    const { battleGroupName } = this.server.puzzlesDB.getPuzzle(puzName);

    // Fish puzzles cannot be used for battles.
    // Check the answer from the battle group instead of the puzzle
    // directly to filter out the tracker mastery tree "puzzles" too.
    const { answer } = this.server.puzzlesDB.getPuzzle(battleGroupName);
    if (answer !== null) return false;

    const { numPuzzles } =
      this.server.puzzlesDB.getBattleGroup(battleGroupName);
    // Dummy battles (for groups of multiple sub-battles) are only
    // relevant for full puzzle accounting, and cannot be used
    // to actually start a battle.
    if (numPuzzles > 1 && puzName === battleGroupName) return false;

    return true;
  }

  isValidFishPuzzle(puzName: string): boolean {
    // Either the puzName is invalid, or the team has not unlocked it.
    if (!this.unlockTimes.has(puzName)) return false;

    const { answer, battleGroupName } =
      this.server.puzzlesDB.getPuzzle(puzName);
    // Ignore battles, and the fake mastery tree state tracker puzzles.
    // Fish puzzle controllers are only used for answer submission.
    if (answer === null) return false;

    // The team should not have access to answer submission for the
    // mastery tree until they have unlocked the prompt.
    if (
      puzName === PUZ_NAME_MASTERY_TREE &&
      !this.unlockTimes.has(PUZ_NAME_MASTERY_TREE_PROMPT)
    )
      return false;

    return true;
  }

  // If there doesn't exist a battle controller for the given slug, this will
  // attempt to create one, and throw an error if the puzzle name doesn't exist.
  getBattleController(puzName: string): TeamBattleController {
    const existingController = this.battleControllers.get(puzName);
    if (existingController !== undefined) return existingController;

    const { battleGroupName } = this.server.getBattleGroup(puzName); // throws error if puzName doesn't exist
    const isInstancer = battleGroupName === BATTLE_GROUP_NAME_INSTANCER;
    const controller = new TeamBattleController(
      this.teamId,
      puzName,
      isInstancer ? null : puzName,
      null,
      this.server
    );
    this.battleControllers.set(puzName, controller);
    return controller;
  }

  getFishPuzzleController(puzName: string): TeamFishPuzzleController {
    const existingController = this.fishPuzzleControllers.get(puzName);
    if (existingController !== undefined) return existingController;

    const controller = new TeamFishPuzzleController(
      this.teamId,
      puzName,
      0,
      0,
      this.server
    );
    this.fishPuzzleControllers.set(puzName, controller);
    return controller;
  }

  isPuzzleUnlocked(puzName: string): boolean {
    return this.unlockTimes.has(puzName);
  }

  getClientPuzzleState(puzName: string): ClientPuzzleGlobalState {
    const puzData = this.server.puzzlesDB.getPuzzle(puzName);
    const { answer } = puzData;
    const unlockTime = this.unlockTimes.get(puzName);
    if (unlockTime === undefined)
      throw new Error(`tried to get client state for locked puzzle ${puzName}`);

    const { battleGroupName, order } = puzData;
    const puzState: ClientPuzzleGlobalState = {
      puzName,
      battleGroupName,
      order,
      hasAnswer: answer !== null ? true : undefined,
      isNew: !this.views.has(puzName),
      speedrunTime: this.speedrunTimes.get(puzName),
      unlockTime,
      solveTime: this.solveTimes.get(puzName),
      puzPrompt:
        puzName === PUZ_NAME_MASTERY_TREE_PROMPT
          ? {
              header: "Restoring peace",
              text: "With your newfound mastery over the cards, you realize what you need to do next. What do you need to restore peace among the factions?",
            }
          : undefined,
    };

    if (answer !== null && this.solveTimes.has(puzName)) {
      puzState.answer = answer;
    }

    const battleController = this.battleControllers.get(puzName);
    if (battleController !== undefined) {
      const roomStatus = battleController.getRoomStatus();
      if (roomStatus !== GlobalStateBattleRoomStatus.NONE)
        puzState.roomStatus = roomStatus;
    }

    return puzState;
  }

  getClientBattleGroupState(
    battleGroupName: string
  ): ClientBattleGroupGlobalState {
    const {
      displayName,
      mapPos,
      isLegendary,
      preBattleDialogue,
      postBattleDialogue,
      numPuzzles,
    } = this.server.getBattleGroup(battleGroupName);
    const puzData = this.server.puzzlesDB.getPuzzle(battleGroupName);
    const { isCutscene, answer } = puzData;
    const battleGroupState: ClientBattleGroupGlobalState = {
      battleGroupName,
      displayName,
      isCutscene: isCutscene ? true : undefined,
      isLegendary: isLegendary ? true : undefined,
      preBattleDialogue,
      postBattleDialogue: this.solveTimes.has(battleGroupName)
        ? postBattleDialogue
        : undefined,
      numPuzzles: numPuzzles !== 1 ? numPuzzles : undefined,
      mapPos,
    };
    if (
      this.masteryTreeController
        .getEnabledMasteries()
        .has(SOLVE_COUNTS_MASTERY_ID)
    )
      battleGroupState.stats =
        this.server.statsController.getPuzzleStats(battleGroupName);
    return battleGroupState;
  }

  getLeaderboardNumSolves(): number {
    return [...this.solveTimes.keys()].filter((puzName) => {
      const puzData = this.server.puzzlesDB.puzzles.get(puzName);
      if (puzData === undefined)
        throw new Error("expect solved puzzles to be in puzzlesDB");
      return isUsedForSolveCount(
        puzName,
        puzData.battleGroupName,
        puzData.isCutscene
      );
    }).length;
  }

  getClientAdminData(): ClientTeamAdminDataGlobalState {
    const { userId, displayName, isHidden, isInactive, isTempBlocked } = this;
    return { userId, displayName, isHidden, isInactive, isTempBlocked };
  }

  getHintsUsedMap(): { [puzName: string]: number } {
    const hintsUsedMap: { [puzName: string]: number } = {};
    for (const puzName of this.backendOwned.hintsUsed) {
      hintsUsedMap[puzName] = (hintsUsedMap[puzName] ?? 0) + 1;
    }
    return hintsUsedMap;
  }

  getClientBigBoardState(): BigBoardTeamState {
    const hintsUsedMap = this.getHintsUsedMap();
    return {
      numHintsTotal: this.backendOwned.hintsTotal,
      unlocks: Object.fromEntries(
        Array.from(this.unlockTimes.keys(), (puzName) => {
          const { numWrongGuesses } =
            this.fishPuzzleControllers.get(puzName) ?? {};
          return [
            puzName,
            {
              solveTime: this.solveTimes.get(puzName),
              numHintsUsed: hintsUsedMap[puzName],
              numWrongGuesses: numWrongGuesses,
              numMembers: this.server.gameRoomsController.getNumRoomMembers(
                this.teamId,
                puzName
              ),
            },
          ];
        })
      ),
      cardUnlocks: Object.fromEntries(
        Array.from(this.cardUnlocks.keys(), (cardName) => [cardName, true])
      ),
      selectedPuzzles: Object.fromEntries(
        [...this.battleControllers.values()].flatMap(
          ({ puzName, selectedPuzName }) =>
            selectedPuzName === null || selectedPuzName === puzName
              ? []
              : [[puzName, selectedPuzName]]
        )
      ),
      enabledMasteries: Object.fromEntries(
        Array.from(
          this.masteryTreeController.getEnabledMasteries(),
          (masteryId) => [masteryId, true]
        )
      ),
    };
  }

  getLastSolveTime(): number | undefined {
    return this.solveTimes.size === 0
      ? undefined
      : Math.max(
          0,
          ...[...this.solveTimes.entries()]
            .filter(([puzName, solveTime]) => {
              const { battleGroupName, isCutscene } =
                this.server.puzzlesDB.getPuzzle(puzName);
              return isUsedForSolveCount(puzName, battleGroupName, isCutscene);
            })
            .map(([puzName, solveTime]) => solveTime)
        );
  }

  getCompletionTime(): number | undefined {
    return this.solveTimes.get(PUZ_NAME_POST_FINAL_BATTLE);
  }

  getTeamSummaryState(): TeamSummaryState {
    const {
      teamId,
      displayName,
      faction,
      factionScoreContributions,
      members,
      masteryTreeController,
      pvpController,
      isHidden,
    } = this;
    const enabledMasteries = masteryTreeController.getEnabledMasteries();
    return {
      teamId,
      displayName,
      faction,
      factionScoreContribution:
        faction === null ? 0 : factionScoreContributions[faction],
      hasReputationBoost: enabledMasteries.has(REPUTATION_BOOST_MASTERY_ID)
        ? true
        : undefined,
      hasPvP: enabledMasteries.has(PVP_MASTERY_ID) ? true : undefined,
      isPvPGameActive: pvpController.isGameActive() ? true : undefined,
      numSolves: this.getLeaderboardNumSolves(),
      lastSolveTime: this.getLastSolveTime(),
      completionTime: this.getCompletionTime(),
      speedrunInfo: this.computeSpeedrunInfo(),
      isHidden: isHidden ? true : undefined,
    };
  }

  makeTeamDataForBattle(puzName: string): PlayerStateTeamData {
    const { teamId, displayName, faction } = this;
    return {
      teamId,
      displayName,
      faction,
      factionScoreContribution:
        faction === null ? 0 : this.factionScoreContributions[faction],
      enabledMasteries: Object.fromEntries(
        Array.from(
          this.masteryTreeController.getEnabledMasteries(),
          (masteryId) => [masteryId, true]
        )
      ),
      isReplay: this.solveTimes.has(puzName),
      prevBestSpeedrunTime: this.speedrunTimes.get(puzName),
    };
  }

  startAIBattle(
    conn: ClientConnection,
    puzName: string,
    slot?: number,
    restart?: boolean,
    reuseDeck?: boolean,
    devKnobs?: DevKnobs
  ): void {
    const battleController = this.getBattleController(puzName);

    const { selectedPuzName } = battleController;
    if (selectedPuzName === null) {
      // Return an error if no puzzle is selected.
      this.server.sendErr(conn, { errCode: WSErrorCode.NEED_PUZZLE });
      return;
    }

    const inspector = battleController.getInspector();
    if (!inspector.hasAI() && selectedPuzName !== "vanilla") {
      // This endpoint should only be used for AI puzzles, or the
      // dev-only vanilla puzzle.
      this.server.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
      return;
    }

    if (battleController.isRoomActive()) {
      if (!(restart ?? false)) {
        // If no restart is requested, and a battle is already running,
        // then the request acts as a no-op.
        return;
      }
      const err = battleController.resign();
      if (err !== null) throw new Error("expect resign to succeed");
    }

    if (slot !== undefined) {
      const err = battleController.selectDeck(slot);
      if (err !== null) {
        this.server.sendErr(conn, err);
        return;
      }
    }

    // TODO: Validate puzName, role, and requestedRoomId
    // are good for the team at their current unlock state.

    const teamIds: { [player in Player]?: string } = {};

    // Keep non-AI battle dev functionality around until we have
    // brought up non-AI battle support.
    const players = [Player.P1, Player.P2].filter((player) => {
      return !(inspector.hasAI() && player === Player.P2);
    });
    for (const player of players) {
      teamIds[player] = this.teamId;
    }

    const { deckSelections, err } = battleController.commitDeckSelection(
      players,
      reuseDeck,
      devKnobs?.allowInvalidDecks
    );
    if (err !== undefined) {
      this.server.sendErr(conn, err);
      return;
    }

    const room = this.server.gameRoomsController.createRoom(
      selectedPuzName,
      puzName,
      teamIds,
      deckSelections,
      devKnobs
    );
    battleController.activateGame(room);

    // The requester will eventually join the room in a separate
    // request once it sees that the game has started.
  }

  handleEnterRoom(
    unjoinedMember: UnjoinedRoomMember,
    params: EnterRoomParams
  ): void {
    const { conn } = unjoinedMember;
    const { puzName } = params;
    const battleController = this.battleControllers.get(puzName);
    if (battleController === undefined) {
      this.server.sendEnterRoomErr(unjoinedMember, {
        errCode: WSErrorCode.NO_ROOM,
      });
      return;
    }
    battleController.handleEnterGameRoom(unjoinedMember);
  }

  /**
   * Actually run the fast sync.
   * Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. there are no sync
   * tasks in the buffers to run).
   */
  private async runFastSyncAsync(): Promise<boolean> {
    const { syncController } = this.fastSyncTracking;
    const { packet } = syncController;

    // Remove redundant entries from the pending packet.
    // This is functionally necessary for consistency, since
    // the snapshot is later used to update solve counts.
    if (packet.unlocks !== undefined) {
      for (const [puzName, val] of Object.entries(packet.unlocks)) {
        if ((val !== null) === this.unlockTimes.has(puzName))
          delete packet.unlocks[puzName];
      }
    }
    if (Object.keys(packet.unlocks ?? {}).length === 0) delete packet.unlocks;

    if (packet.cardUnlocks !== undefined) {
      for (const [cardName, val] of Object.entries(packet.cardUnlocks)) {
        if (
          (val !== null) === this.cardUnlocks.has(cardName) &&
          (val?.puzName ?? null) === (this.cardUnlocks.get(cardName) ?? null)
        )
          delete packet.cardUnlocks[cardName];
      }
    }
    if (Object.keys(packet.cardUnlocks ?? {}).length === 0)
      delete packet.cardUnlocks;

    if (packet.answerSubmissions !== undefined) {
      for (const [puzName, puzSubmissions] of Object.entries(
        packet.answerSubmissions
      )) {
        if (puzSubmissions === null && !this.solveTimes.has(puzName)) {
          delete packet.answerSubmissions[puzName];
        } else if (puzSubmissions !== null && this.solveTimes.has(puzName)) {
          for (const [answer, submission] of Object.entries(puzSubmissions)) {
            if (submission.isCorrect) delete puzSubmissions[answer];
          }
          if (Object.keys(puzSubmissions ?? {}).length === 0)
            delete packet.answerSubmissions[puzName];
        }
      }
    }
    if (Object.keys(packet.answerSubmissions ?? {}).length === 0)
      delete packet.answerSubmissions;

    const snapshot = syncController.startSync();
    if (snapshot === null) return true;
    let didSyncSucceed = false;
    this.server.fastSyncMetrics.record();
    // If this errors out, the SyncLoopController will handle it.
    try {
      await this.server.syncDataThrottledAsync({
        teamState: {
          [this.teamId]: snapshot,
        },
      });
      didSyncSucceed = true;
    } finally {
      syncController.endSync(didSyncSucceed ? snapshot : null);
    }

    const { puzzlesDB } = this.server;
    const submissionSolves = Object.fromEntries(
      Object.entries(snapshot.answerSubmissions ?? {}).flatMap<
        [string, BackendAnswerSubmission | null]
      >(([puzName, puzSubmissions]) => {
        if (puzSubmissions === null) return [[puzName, null]];
        const correctSubmission = Object.values(puzSubmissions).find(
          (submission) => submission.isCorrect
        );
        if (correctSubmission === undefined) return [];
        return [[puzName, correctSubmission]];
      })
    );
    const answers = Object.fromEntries(
      Object.entries(submissionSolves).flatMap(([puzName, submission]) => {
        const { answer } = this.server.puzzlesDB.getPuzzle(puzName);
        if (answer === null) return [];
        return [[puzName, answer]];
      })
    );
    const battleGroupUnlocks = Object.fromEntries(
      Object.entries(snapshot.unlocks ?? {}).flatMap(([puzName, unlock]) =>
        unlock !== null &&
        puzzlesDB.getBattleGroup(puzName).battleGroupName === puzName
          ? [[puzName, this.getClientBattleGroupState(puzName)]]
          : []
      )
    );
    const cardUnlocks = Object.fromEntries(
      Object.entries(snapshot.cardUnlocks ?? {}).map(
        ([cardName, cardUnlock]) => {
          if (cardUnlock === null) return [cardName, null];
          const { puzName } = cardUnlock;
          return [cardName, { cardName, puzName }];
        }
      )
    );

    const solveTimes = Object.fromEntries(
      Object.entries(submissionSolves).map(([puzName, solve]) =>
        solve === null ? [puzName, null] : [puzName, solve.timestamp]
      )
    );

    // Clear all newly locked puzzles from the instancer.
    const puzzlesToLock = new Set(
      Object.entries(snapshot.unlocks ?? {})
        .filter(([puzName, unlock]) => unlock === null)
        .map(([puzName, unlock]) => puzName)
    );
    if (puzzlesToLock.size > 0) {
      const instancers = [...this.server.puzzlesDB.puzzles.values()].filter(
        ({ puzName, battleGroupName }) =>
          battleGroupName === BATTLE_GROUP_NAME_INSTANCER &&
          puzName !== BATTLE_GROUP_NAME_INSTANCER
      );
      for (const puzName of puzzlesToLock) {
        const firstSubpuzzle =
          puzzlesDB.getFirstSubpuzzleInBattleGroup(puzName).puzName;
        for (const { puzName: instancerPuzName } of instancers) {
          const battleController = this.getBattleController(instancerPuzName);
          if (
            !(
              battleController.selectedPuzName !== null &&
              battleController.selectedPuzName === puzName
            )
          )
            continue;
          if (battleController.isRoomActive()) {
            if (battleController.resign() !== null)
              throw new Error("failed to resign");
          }
          if (
            battleController.selectPuzzle(
              this.unlockTimes.has(firstSubpuzzle) &&
                !puzzlesToLock.has(firstSubpuzzle)
                ? firstSubpuzzle
                : null
            ) !== null
          )
            throw new Error("failed to clear selected puzzle");

          // Clear checkpoints with that selected puzzle.
          for (const [slot, serializedCheckpoint] of Object.entries(
            battleController.checkpoints
          )) {
            if (serializedCheckpoint === undefined) continue;
            const checkpoint = JSON.parse(
              serializedCheckpoint.serializedState
            ) as CheckpointState;
            if (checkpoint.selectedPuzName === puzName) {
              delete battleController.checkpoints[Number(slot)];
            }
          }
        }
      }
    }

    // Apply the updates.
    for (const [puzName, solveTime] of Object.entries(solveTimes)) {
      // We should have guaranteed this by removing redundant
      // entries before firing off the sync.
      if ((solveTime !== null) === this.solveTimes.has(puzName))
        console.error("packet has repeat data from solve state");
      if (solveTime === null) this.solveTimes.delete(puzName);
      else this.solveTimes.set(puzName, solveTime);
    }
    for (const [puzName, unlock] of Object.entries(snapshot.unlocks ?? {})) {
      // We should have guaranteed this by removing redundant
      // entries before firing off the sync.
      if ((unlock !== null) === this.unlockTimes.has(puzName))
        console.error("packet has repeat data from unlock state");
      if (unlock === null) this.unlockTimes.delete(puzName);
      else this.unlockTimes.set(puzName, unlock.timestamp);
    }
    for (const [cardName, cardUnlock] of Object.entries(
      snapshot.cardUnlocks ?? {}
    )) {
      // We should have guaranteed this by removing redundant
      // entries before firing off the sync.
      if (
        (cardUnlock !== null) === this.cardUnlocks.has(cardName) &&
        (cardUnlock?.puzName ?? null) ===
          (this.cardUnlocks.get(cardName) ?? null)
      )
        console.error("packet has repeat data from card unlock state");
      if (cardUnlock === null) this.cardUnlocks.delete(cardName);
      else this.cardUnlocks.set(cardName, cardUnlock.puzName);
    }

    // Broadcast the updates.
    const allUnlocks = Object.fromEntries(
      Object.entries(snapshot.unlocks ?? {}).map(([puzName, unlock]) => [
        puzName,
        unlock === null ? null : this.getClientPuzzleState(puzName),
      ])
    );
    this.broadcastTeamGlobalUpdate({
      type: GlobalUpdateType.TEAM_UPDATE_STATE,
      solveTimes,
      answers,
      unlocks: allUnlocks,
      battleGroupUnlocks,
      cardUnlocks,
      masteries: Object.fromEntries(
        Object.entries(solveTimes).flatMap<
          [string, { displayName: string; effectText: string } | null]
        >(([masteryId, solveTime]) => {
          const mastery = this.server.masteriesDB[masteryId];
          if (mastery === undefined) return [];
          if (solveTime === null) return [[masteryId, null]];
          const { displayName, effectText } = mastery;
          return [
            [
              masteryId,
              {
                displayName,
                effectText,
              },
            ],
          ];
        })
      ),
    });
    // Unlock post-battle dialogue.
    for (const [puzName, solveTime] of Object.entries(solveTimes)) {
      const puzData = this.server.puzzlesDB.getPuzzle(puzName);
      const { battleGroupName } = puzData;
      if (puzName !== battleGroupName) continue;
      const { postBattleDialogue } =
        this.server.puzzlesDB.getBattleGroup(battleGroupName);
      if (postBattleDialogue === undefined) continue;
      this.broadcastTeamGlobalUpdate({
        type: GlobalUpdateType.TEAM_UPDATE_BATTLE_GROUP,
        battleGroupName,
        postBattleDialogue,
      });
    }
    if (Object.keys(solveTimes).length >= 0) {
      this.server.handleTeamSummaryUpdate(this.teamId, {
        numSolves: this.getLeaderboardNumSolves(),
        lastSolveTime: this.getLastSolveTime(),
        completionTime: this.getCompletionTime(),
      });
    }

    const hintsUsedMap = this.getHintsUsedMap();
    this.server.bigBoardStateTracker.updateTeam(this.teamId, {
      // No need to worry about diffing here since this doesn't happen
      // too often, and we already diff in the tracker.
      unlocks: Object.fromEntries(
        Array.from(this.unlockTimes.keys(), (puzName) => [
          puzName,
          {
            solveTime: this.solveTimes.get(puzName),
            // Not sure if we would have reason to do un-unlocking hijinks
            // that could cause these to become non-zero on unlock,
            // but send them anyway just in case.
            numHintsUsed: hintsUsedMap[puzName],
            numWrongGuesses:
              this.fishPuzzleControllers.get(puzName)?.numWrongGuesses,
          },
        ])
      ),
      cardUnlocks: Object.fromEntries(
        Object.entries(cardUnlocks).map(([cardName, cardUnlock]) => [
          cardName,
          cardUnlock !== null,
        ])
      ),
    });

    // This has to come after broadcasting the unlocks, to ensure
    // that teams have the puzzle global state to merge the
    // stats into.
    this.server.statsController.handleTeamUpdates(this.teamId, {
      unlocksToAdd: [
        ...Object.keys(allUnlocks).filter(
          (puzName) => allUnlocks[puzName] !== null
        ),
      ],
      solvesToAdd: Object.keys(submissionSolves).filter(
        (puzName) => submissionSolves[puzName] !== null
      ),
      unlocksToDelete: [
        ...Object.keys(allUnlocks).filter(
          (puzName) => allUnlocks[puzName] === null
        ),
      ],
      solvesToDelete: Object.keys(submissionSolves).filter(
        (puzName) => submissionSolves[puzName] === null
      ),
      solveTimes,
    });

    // Broadcast the victory notification.
    if (
      solveTimes[PUZ_NAME_POST_FINAL_BATTLE] !== undefined &&
      solveTimes[PUZ_NAME_POST_FINAL_BATTLE] !== null
    ) {
      this.server.broadcastNotifToTeam(this.teamId, {
        type: HuntNotificationType.VICTORY,
      });
    }

    return false;
  }

  async handleTeamUpdatesAsync(opts: {
    solvesToDelete?: string[];
    unlocksToDelete?: string[];
    cardUnlocksToDelete?: string[];
    answerSubmissionsToAdd?: BackendAnswerSubmission[];
    unlocksToAdd?: BackendUnlock[];
    cardUnlocksToAdd?: BackendCardUnlock[];
  }) {
    const { puzzlesDB } = this.server;
    const { puzzles, battleGroups } = puzzlesDB;
    const timeNow = new Date().getTime();
    const { syncController, syncLoopController } = this.fastSyncTracking;

    if (opts.solvesToDelete !== undefined) {
      // If a battle group is set unsolved, propagate it to
      // all subpuzzles as well.
      const solvesToDeleteSet = new Set(opts.solvesToDelete);
      opts.solvesToDelete.push(
        ...[...puzzlesDB.puzzles.values()]
          .filter(
            ({ puzName, battleGroupName }) =>
              !solvesToDeleteSet.has(puzName) &&
              solvesToDeleteSet.has(battleGroupName)
          )
          .map(({ puzName }) => puzName)
      );
    }

    if (opts.unlocksToDelete !== undefined) {
      // If a battle group is set locked, propagate it to
      // all subpuzzles as well.
      const unlocksToDeleteSet = new Set(opts.unlocksToDelete);
      opts.unlocksToDelete.push(
        ...[...puzzlesDB.puzzles.values()]
          .filter(
            ({ puzName, battleGroupName }) =>
              !unlocksToDeleteSet.has(puzName) &&
              unlocksToDeleteSet.has(battleGroupName)
          )
          .map(({ puzName }) => puzName)
      );

      // If an unlock is deleted, delete the solve too.
      opts.solvesToDelete ??= [];
      opts.solvesToDelete.push(...opts.unlocksToDelete);
    }

    // Queue the changes for sync.
    const firstPacket: BackendSyncReqPacket = {
      answerSubmissions: {},
      unlocks: {
        ...Object.fromEntries(
          (opts.unlocksToAdd ?? []).map((unlock) => [unlock.puzName, unlock])
        ),
        ...Object.fromEntries(
          (opts.unlocksToDelete ?? []).map((puzName) => [puzName, null])
        ),
      },
      cardUnlocks: {
        ...Object.fromEntries(
          (opts.cardUnlocksToAdd ?? []).map((cardUnlock) => [
            cardUnlock.cardName,
            cardUnlock,
          ])
        ),
        ...Object.fromEntries(
          (opts.cardUnlocksToDelete ?? []).map((cardName) => [cardName, null])
        ),
      },
    };
    for (const answerSubmission of opts.answerSubmissionsToAdd ?? []) {
      const { answer, puzName } = answerSubmission;
      firstPacket.answerSubmissions ??= {};
      firstPacket.answerSubmissions[puzName] = Object.assign(
        firstPacket.answerSubmissions[puzName] ?? {},
        { [answer]: answerSubmission }
      );
    }
    for (const puzName of opts.solvesToDelete ?? []) {
      firstPacket.answerSubmissions ??= {};
      firstPacket.answerSubmissions[puzName] = null;
    }
    this.mergeFastSyncPacket(firstPacket);

    // Build up a packet of generated changes to additionally merge.
    const genPacket: BackendSyncReqPacket = {};

    // Compute the combined solves, including pending changes.
    const combinedSolves: Set<string> = new Set([
      ...this.solveTimes.keys(),
      ...Object.entries(syncController.packet.answerSubmissions ?? {}).flatMap(
        ([puzName, puzSubmissions]) =>
          puzSubmissions === null
            ? []
            : Object.values(puzSubmissions).some(
                (submission) => submission.isCorrect
              )
            ? [puzName]
            : []
      ),
    ]);

    // Helper function to set a battle solved.
    const addBattleSolve = (puzName: string): void => {
      if (combinedSolves.has(puzName)) return;
      const submission = makeBattleSolveAnswerSubmission(
        this.teamId,
        puzName,
        timeNow
      );
      genPacket.answerSubmissions ??= {};
      genPacket.answerSubmissions[puzName] = Object.assign(
        genPacket.answerSubmissions[puzName] ?? {},
        { [submission.answer]: submission }
      );
      combinedSolves.add(puzName);
    };

    // Propagate battle group solves to dummy puzzles.
    const solveTriggersPerBattleGroup: Map<string, number> = new Map();
    for (const puzName of combinedSolves) {
      const { battleGroupName, triggersBattleGroupSolve } =
        puzzlesDB.getPuzzle(puzName);
      if (!triggersBattleGroupSolve) continue;
      solveTriggersPerBattleGroup.set(
        battleGroupName,
        (solveTriggersPerBattleGroup.get(battleGroupName) ?? 0) + 1
      );
    }
    for (const [
      battleGroupName,
      numSolveTriggers,
    ] of solveTriggersPerBattleGroup) {
      const { solveReq } = puzzlesDB.getBattleGroup(battleGroupName);
      if (numSolveTriggers < solveReq) continue;
      addBattleSolve(battleGroupName);
    }
    for (const puzName of Object.entries(
      syncController.packet.answerSubmissions ?? {}
    ).flatMap(([puzName, val]) => (val === null ? [puzName] : []))) {
      combinedSolves.delete(puzName);
    }

    // Compute the combined unlocks, including pending changes.
    const combinedUnlocks: Set<string> = new Set([
      ...this.unlockTimes.keys(),
      ...Object.entries(syncController.packet.unlocks ?? {}).flatMap(
        ([puzName, val]) => (val === null ? [] : [puzName])
      ),
    ]);
    for (const puzName of Object.entries(
      syncController.packet.unlocks ?? {}
    ).flatMap(([puzName, val]) => (val === null ? [puzName] : []))) {
      combinedUnlocks.delete(puzName);
    }

    // Helper function to unlock a new puzzle.
    const addUnlock = (puzName: string): void => {
      if (combinedUnlocks.has(puzName)) return;
      genPacket.unlocks ??= {};
      genPacket.unlocks[puzName] = {
        teamId: this.teamId,
        puzName,
        timestamp: timeNow,
      };
      combinedUnlocks.add(puzName);
    };

    // Since the tutorial has card unlocks tied to subpuzzles,
    // set all subpuzzles solved when the battle group is set solved.
    if (combinedSolves.has(BATTLE_GROUP_NAME_TUTORIAL)) {
      for (const { puzName, battleGroupName } of puzzlesDB.puzzles.values()) {
        if (battleGroupName !== BATTLE_GROUP_NAME_TUTORIAL) continue;
        addUnlock(puzName);
        addBattleSolve(puzName);
      }
    }

    // Compute the combined card unlocks, including pending changes.
    // This is recorded as a map from cardName to their attributed
    // puzName (or null, if unattributed).
    const combinedCardUnlocks: Map<string, string | null> = new Map([
      ...this.cardUnlocks.entries(),
      ...Object.entries(syncController.packet.cardUnlocks ?? {}).flatMap<
        [string, string | null]
      >(([cardName, val]) => (val === null ? [] : [[cardName, val.puzName]])),
    ]);
    for (const cardName of Object.entries(
      syncController.packet.cardUnlocks ?? {}
    ).flatMap(([cardName, val]) => (val === null ? [cardName] : []))) {
      combinedCardUnlocks.delete(cardName);
    }

    // Compute the "unlock points" for each unlock group.
    const progressPerUnlockGroup: { [unlockGroupId: string]: number } = {};
    // For each unlocked card unlock group, holds the battleGroupName
    // of the battle group that unlocked it.
    const cardUnlockGroups: { [cardUnlockGroupId: string]: string } = {};
    for (const { puzName, unlockMult, dstUnlockGroupId } of puzzles.values()) {
      if (!combinedSolves.has(puzName)) continue;
      if (progressPerUnlockGroup[dstUnlockGroupId] === undefined)
        progressPerUnlockGroup[dstUnlockGroupId] = 0;
      progressPerUnlockGroup[dstUnlockGroupId] += unlockMult;
    }
    for (const { puzName, cardUnlockGroupId } of puzzles.values()) {
      if (!combinedSolves.has(puzName)) continue;
      if (cardUnlockGroupId === null) continue;
      cardUnlockGroups[cardUnlockGroupId] = puzName;
    }

    // Unlock new puzzles as necessary.
    for (const { puzName, unlockReq, srcUnlockGroupId } of puzzles.values()) {
      // An unlock requirement of -1 implies that the puzzle
      // does not get unlocked by round solves.
      if (unlockReq === -1) continue;
      if ((progressPerUnlockGroup[srcUnlockGroupId] ?? 0) < unlockReq) continue;
      addUnlock(puzName);
    }

    // Unlock the first set of battles for unlocked multi-part battles.
    for (const { puzName, battleGroupName, unlockReq } of puzzles.values()) {
      if (unlockReq !== -1) continue;
      if (!combinedUnlocks.has(battleGroupName)) continue;
      addUnlock(puzName);
    }

    // Unlock new cards as necessary.
    for (const cardData of Object.values(this.server.cardsDB)) {
      const { name: cardName, cardUnlockGroupId } = cardData;
      if (cardUnlockGroupId === null) continue;
      const puzName = cardUnlockGroups[cardUnlockGroupId];
      if (puzName === undefined) continue;
      const existingUnlock = combinedCardUnlocks.get(cardName);
      if (existingUnlock !== undefined && existingUnlock === puzName) continue;
      genPacket.cardUnlocks ??= {};
      genPacket.cardUnlocks[cardName] = {
        teamId: this.teamId,
        puzName,
        cardName,
        timestamp: timeNow,
      };
      combinedCardUnlocks.set(cardName, puzName);
    }

    if (!combinedUnlocks.has(PUZ_NAME_FIRST_ANSWER)) {
      if (
        [...puzzles.values()].filter(
          ({ puzName, answer }) =>
            answer !== null &&
            puzName !== PUZ_NAME_MASTERY_TREE &&
            combinedSolves.has(puzName)
        ).length > 0
      ) {
        addUnlock(PUZ_NAME_FIRST_ANSWER);
      }
    }

    if (!combinedUnlocks.has(PUZ_NAME_FIRST_LEGENDARY)) {
      if (
        [...battleGroups.values()].filter(
          ({ battleGroupName, isLegendary }) =>
            isLegendary && combinedSolves.has(battleGroupName)
        ).length > 0
      ) {
        addUnlock(PUZ_NAME_FIRST_LEGENDARY);
      }
    }

    // Unlock the cutscene to the final battle if we have
    // all the legendaries.
    if (!combinedUnlocks.has(PUZ_NAME_PRE_FINAL_BATTLE)) {
      const inspector = this.getBattleController(
        PUZ_NAME_PRE_FINAL_BATTLE
      ).getInspector();
      if (
        [...battleGroups.values()]
          .filter(({ isLegendary }) => isLegendary)
          .every(({ battleGroupName }) => combinedSolves.has(battleGroupName))
      ) {
        addUnlock(PUZ_NAME_PRE_FINAL_BATTLE);
      }
    }

    this.mergeFastSyncPacket(genPacket);

    await syncLoopController.triggerLoopAndWaitAsync();
  }

  async adminModifyTeamAsync(params: AdminModifyTeamParams) {
    const {
      isInactive,
      isHidden,
      isTempBlocked,
      displayName,
      overrideSolved,
      overrideUnlocked,
      overrideCardUnlocked,
      overrideMasteriesList,
      overrideDecks,
      overrideMasteryTree,
    } = params;
    if (isInactive !== undefined) this.setTeamInactive(isInactive);
    if (isHidden !== undefined) this.setTeamHidden(isHidden);
    if (isTempBlocked !== undefined) this.setTeamTempBlocked(isTempBlocked);
    if (displayName !== undefined) this.setDisplayName(displayName);

    this.masteryTreeController.handleMasteryTreeUpdate({
      masteriesListOverride: overrideMasteriesList,
    });
    if (overrideMasteryTree !== undefined)
      this.masteryTreeController.overrideMasteryTree(overrideMasteryTree);
    for (const [slot, deck] of Object.entries(overrideDecks ?? {})) {
      this.getDeckController(Number(slot)).overrideDeck(deck);
    }

    await this.handleTeamUpdatesAsync({
      answerSubmissionsToAdd: Object.entries(overrideSolved ?? {}).flatMap(
        ([puzName, isSolved]) =>
          isSolved
            ? [
                {
                  teamId: this.teamId,
                  puzName,
                  answer: this.server.puzzlesDB.getPuzzle(puzName).answer ?? "",
                  isCorrect: true,
                  timestamp: new Date().getTime(),
                  usedFreeAnswer: false,
                },
              ]
            : []
      ),
      solvesToDelete: Object.entries(overrideSolved ?? {}).flatMap(
        ([puzName, isSolved]) => (!isSolved ? [puzName] : [])
      ),
      unlocksToAdd: Object.entries(overrideUnlocked ?? {}).flatMap(
        ([puzName, isUnlocked]) =>
          isUnlocked
            ? [
                {
                  teamId: this.teamId,
                  puzName,
                  timestamp: new Date().getTime(),
                },
              ]
            : []
      ),
      unlocksToDelete: Object.entries(overrideUnlocked ?? {}).flatMap(
        ([puzName, isUnlocked]) => (!isUnlocked ? [puzName] : [])
      ),
      cardUnlocksToAdd: Object.entries(overrideCardUnlocked ?? {}).flatMap(
        ([cardName, isUnlocked]) =>
          isUnlocked
            ? [
                {
                  teamId: this.teamId,
                  puzName: null,
                  cardName,
                  timestamp: new Date().getTime(),
                },
              ]
            : []
      ),
      cardUnlocksToDelete: Object.entries(overrideCardUnlocked ?? {}).flatMap(
        ([cardName, isUnlocked]) => (!isUnlocked ? [cardName] : [])
      ),
    });
  }

  async overrideIsSolvedAsync(puzName: string, isSolved: boolean) {
    await this.adminModifyTeamAsync({
      overrideSolved: {
        [puzName]: isSolved,
      },
    });
  }

  async overrideIsUnlockedAsync(puzName: string, isUnlocked: boolean) {
    await this.adminModifyTeamAsync({
      overrideUnlocked: {
        [puzName]: isUnlocked,
      },
    });
  }

  async overrideIsCardUnlockedAsync(cardName: string, isUnlocked: boolean) {
    await this.adminModifyTeamAsync({
      overrideCardUnlocked: {
        [cardName]: isUnlocked,
      },
    });
  }

  setTeamInactive(isInactive: boolean) {
    if (isInactive === this.isInactive) return;
    this.isInactive = isInactive;
    this.mergeSlowSyncPacket({ isInactive });
    if (isInactive) {
      const teamConns = this.server.teamConns.get(this.teamId);
      if (teamConns !== undefined) {
        for (const conn of teamConns.values()) {
          conn.client.close();
        }
      }
    }
    this.server.broadcastAdminGlobalUpdate({
      type: GlobalUpdateType.SERVER_UPDATE_TEAM,
      teamId: this.teamId,
      isInactive,
    });
  }

  setTeamHidden(isHidden: boolean) {
    // Don't update stats if there is no change.
    if (isHidden === this.isHidden) return;
    this.isHidden = isHidden;
    this.mergeSlowSyncPacket({ isHidden });
    if (isHidden) {
      this.server.statsController.hideTeam(this.teamId);
    } else {
      this.server.statsController.unhideTeam(this.teamId);
    }
    this.server.broadcastAdminGlobalUpdate({
      type: GlobalUpdateType.SERVER_UPDATE_TEAM,
      teamId: this.teamId,
      isHidden,
    });
    this.server.handleTeamSummaryUpdate(this.teamId, {
      isHidden,
    });
  }

  setTeamTempBlocked(isTempBlocked: boolean) {
    if (isTempBlocked === this.isTempBlocked) return;
    this.isTempBlocked = isTempBlocked;
    if (isTempBlocked) {
      const teamConns = this.server.teamConns.get(this.teamId);
      if (teamConns !== undefined) {
        for (const conn of teamConns.values()) {
          if (!conn.isAdmin) {
            this.server.sendErr(conn, {
              errCode: WSErrorCode.TEAM_TEMP_BLOCKED,
            });
            conn.client.close();
          }
        }
      }
    }
  }

  setDisplayName(displayName: string) {
    this.displayName = displayName;
    this.mergeSlowSyncPacket({
      displayName,
    });
    this.broadcastTeamGlobalUpdate({
      type: GlobalUpdateType.TEAM_UPDATE_STATE,
      displayName,
    });
    this.server.handleTeamSummaryUpdate(this.teamId, {
      displayName,
    });
    this.server.broadcastAdminGlobalUpdate({
      type: GlobalUpdateType.SERVER_UPDATE_TEAM,
      teamId: this.teamId,
      displayName,
    });
  }

  handleSetFaction(conn: ClientConnection, faction: Faction | null) {
    if (
      !this.masteryTreeController.getEnabledMasteries().has(FACTIONS_MASTERY_ID)
    ) {
      this.server.sendErr(conn, { errCode: WSErrorCode.NO_MASTERY });
      return;
    }
    this.faction = faction;
    this.mergeSlowSyncPacket({
      faction,
    });
    this.broadcastTeamGlobalUpdate({
      type: GlobalUpdateType.TEAM_UPDATE_STATE,
      faction,
    });
    this.server.handleTeamSummaryUpdate(this.teamId, {
      faction,
      factionScoreContribution:
        faction === null ? 0 : this.factionScoreContributions[faction],
    });
  }

  handleChangeFactionScoreContributions(changes: {
    [faction in Faction]?: number;
  }): void {
    for (const [kFaction, change] of Object.entries(changes)) {
      const faction = kFaction as Faction;
      this.factionScoreContributions[faction] += change;
    }
    const changedContributions = Object.fromEntries(
      Object.entries(this.factionScoreContributions).filter(
        ([kFaction, contribution]) => changes[kFaction as Faction] ?? 0 !== 0
      )
    );
    this.mergeSlowSyncPacket({
      factionScoreContributions: changedContributions,
    });
    this.broadcastTeamGlobalUpdate({
      type: GlobalUpdateType.TEAM_UPDATE_STATE,
      factionScoreContributions: changedContributions,
    });
    const teamFaction = this.faction;
    if (teamFaction !== null) {
      const teamFactionScoreChange = changes[teamFaction] ?? 0;
      if (teamFactionScoreChange !== 0) {
        this.server.handleTeamSummaryUpdate(this.teamId, {
          factionScoreContribution: this.factionScoreContributions[teamFaction],
        });
      }
    }
    this.server.statsController.handleTeamUpdates(this.teamId, {
      factionScoreChanges: changes,
      updateFactionHistory: true,
    });
  }

  handleSetMembers(members: string[]) {
    this.members = members;
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM_MEMBERS,
        teamId: this.teamId,
      },
      {
        type: GlobalUpdateType.TEAM_MEMBERS_UPDATE_STATE,
        members,
      }
    );
  }

  handleNewSpeedrunTime(puzName: string, speedrunTime: number) {
    const prevBestTime = this.speedrunTimes.get(puzName);
    if (prevBestTime !== undefined && speedrunTime >= prevBestTime) return;

    this.speedrunTimes.set(puzName, speedrunTime);
    // Invalidate the cached speedrunInfo.
    delete this.cachedSpeedrunInfo;

    this.broadcastTeamGlobalUpdate({
      type: GlobalUpdateType.TEAM_UPDATE_STATE,
      speedrunTimes: { [puzName]: speedrunTime },
    });

    this.server.statsController.handleTeamUpdates(this.teamId, {
      speedrunTimes: { [puzName]: speedrunTime },
    });

    this.server.handleTeamSummaryUpdate(this.teamId, {
      speedrunInfo: this.computeSpeedrunInfo(),
    });
  }

  computeSpeedrunInfo() {
    if (this.cachedSpeedrunInfo !== undefined) return this.cachedSpeedrunInfo;
    const times = [...this.server.puzzlesDB.puzzles.values()]
      .filter(({ puzName, battleGroupName, isCutscene }) => {
        return isUsedForGlobalSpeedrunTime(
          puzName,
          battleGroupName,
          isCutscene
        );
      })
      .map(({ puzName }) => this.speedrunTimes.get(puzName) ?? 0);
    this.cachedSpeedrunInfo = {
      numTimes: times.filter((time) => !!time).length,
      totalTime: times.reduce((acc, cur) => acc + cur, 0),
    };
    return this.cachedSpeedrunInfo;
  }

  viewPuzzle(conn: ClientConnection, puzName: string): void {
    if (!this.unlockTimes.has(puzName)) {
      this.server.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
      return;
    }

    const { battleGroupName } = this.server.getBattleGroup(puzName);
    const timeNow = new Date().getTime();
    const viewTimes: { [puzName: string]: number } = {};
    if (!this.views.has(puzName)) viewTimes[puzName] = timeNow;
    // If we view any puzzle in the battle group, then we view
    // the whole battle group.
    if (!this.views.has(battleGroupName)) viewTimes[battleGroupName] = timeNow;

    for (const puzName of Object.keys(viewTimes)) {
      this.views.add(puzName);
      this.broadcastTeamGlobalUpdate({
        type: GlobalUpdateType.TEAM_UPDATE_PUZZLE,
        puzName,
        isNew: false,
      });
    }
    this.mergeSlowSyncPacket({
      viewTimes,
    });
  }

  mergeFastSyncPacket(packet: BackendSyncReqPacket) {
    this.fastSyncTracking.syncController.mergePacket(packet);
  }

  mergeSlowSyncPacket(packet: BackendSyncReqPacket) {
    this.slowSyncTracking.syncController.mergePacket(packet);
  }
}
