import {
  Player,
  Slot,
  Permanent,
  areSlotsEqual,
} from "engine/types/game-state";
import { EngineContext, GameSpecServerFragment } from "engine/types/game-specs";
import { Step, StepType } from "engine/types/steps";
import { AbilityType, EffectOptResolved } from "engine/types/effects";
import { EffectOptType } from "engine/types/effects";
import { EffectOpt } from "engine/types/effects";
import { Inspector } from "engine/Inspector";
import { CounterType } from "engine/types/counters";
import { Role } from "engine/types/updates";
import { StepMaker } from "engine/StepMaker";

const reflectSlot = (slot: Slot, inspector: Inspector) =>
  inspector.getReflectedSlot(slot);

const reflectPermanent = (permanent: Permanent, inspector: Inspector) =>
  inspector
    .getPermanentsAt(reflectSlot(permanent.slot, inspector))
    .find(({ owner }) => owner !== permanent.owner);

// check that reflected permanent is the same card
const areReflectionsEqual = (
  permanent: Permanent,
  reflection: Permanent,
  inspector: Inspector
) => inspector.getCardName(permanent) === inspector.getCardName(reflection);

const reflectOpt = (effectOpt: EffectOpt, inspector: Inspector): EffectOpt => {
  if (effectOpt.type === EffectOptType.SLOT) {
    const { slot } = effectOpt;
    return {
      type: EffectOptType.SLOT,
      slot: reflectSlot(slot, inspector),
    };
  }
  if (effectOpt.type === EffectOptType.PERMANENT) {
    const { permanentId } = effectOpt;
    const permanent = inspector.getPermanent(permanentId);
    const newPermanent = reflectPermanent(permanent, inspector);
    if (newPermanent) {
      return {
        type: EffectOptType.PERMANENT,
        permanentId: newPermanent.id,
      };
    }
  }
  return effectOpt;
};

const reflectOptResolved = (
  effectOpt: EffectOptResolved,
  inspector: Inspector
): EffectOpt => {
  if (effectOpt.type === EffectOptType.SLOT) {
    const { slot } = effectOpt;
    return {
      type: EffectOptType.SLOT,
      slot: reflectSlot(slot, inspector),
    };
  }
  if (effectOpt.type === EffectOptType.PERMANENT) {
    const { permanent } = effectOpt;
    const newPermanent = reflectPermanent(permanent, inspector);
    if (newPermanent) {
      return {
        type: EffectOptType.PERMANENT,
        permanentId: newPermanent.id,
      };
    }
  }
  return effectOpt;
};

// (naively) checks if the defender is about to die from an attack from attacker
//  should not affect the game state
//  most of the logic is from engine.damagePermanent()
const isDefenderAboutToDie = (
  attacker: Permanent,
  defender: Permanent,
  ctx: EngineContext
) => {
  const { inspector } = ctx;
  const damage = inspector.getPower(attacker);
  const health = inspector.getHealth(defender);
  const isAlreadyDead = health <= 0;

  // const effects = engine.getBackdoorEngine().getEffects(defender);
  // const effectsAdjust = effects.adjustDamageReceived;
  // const insteadOfDamaged = effects.insteadOfDamaged;

  // NAH: beethoven's effect will trigger if this hook is called, so we have to be naive
  // if (effectsAdjust) {
  //   damage = effectsAdjust(defender, damage, attacker, ctx);
  // }

  if (damage <= 0) return isAlreadyDead;

  if (
    inspector.doesPermanentHaveCounterType(defender, CounterType.INVULNERABLE)
  ) {
    return isAlreadyDead;
  }
  // check for divine shield or cannot be destroyed
  // divine shield pops on the first instance of damage
  if (
    inspector.doesPermanentHaveCounterType(
      defender,
      CounterType.DIVINE_SHIELD
    ) ||
    inspector.doesPermanentHaveCounterType(
      defender,
      CounterType.CANNOT_BE_DESTROYED
    )
  ) {
    return isAlreadyDead;
  }

  // at this point, assume the attack will go through
  return damage >= health;
};

// Hard-coded, needs to be kept in sync with card effects.
const UNITS_WITH_ATTACK_EFFECTS = new Set([
  "nice-cow",
  "lady-jane",
  "murray",
  "rude-cow",
  "giraffatitan",
  "realistic-rex",
  "touch-grass",
  "roe-doe-dendron",
  "switch-boar-d",
  "oklahoma-tusks",
  "quail-aboard-fiery-steeds",
  "beeowulf",
  "nightshade",
]) as ReadonlySet<string>;

type ExtraState = {
  didCopyBeforeStep: boolean; // whether we copied an attack/special in the beforeStep
};

export const Puzzle4Spec: GameSpecServerFragment = {
  // if both players have no bases, then P1 loses
  disableP2NoBasesLossCondition: true,
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;
    const doesPlayerHaveBases = (player: Player) =>
      inspector.getBases(player).length > 0;
    if (player === Player.P2) {
      // Check if P2 has no bases left and P1 has some base.
      // This is necessary because afterStep gets evaluated
      // before the lose checks.
      return (
        !doesPlayerHaveBases(player) &&
        doesPlayerHaveBases(inspector.getOpponentOf(player))
      );
    }
    return false;
  },

  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      didCopyBeforeStep: false,
    } as ExtraState);
  },
  beforeNewGame: (ctx) => {
    // copy P1's bases
    const { inspector, engine } = ctx;
    const bases = inspector.getDefaultBases(Player.P1);
    const baseColumns = inspector.getBasePositions(Player.P2, bases.length);
    for (let i = 0; i < bases.length; i++) {
      const slot = {
        row: inspector.getPlayerRowIndex(0, Player.P2),
        column: baseColumns[i],
      };
      engine.spawn(bases[i], slot, Player.P2);
    }
  },
  beforeStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const ext = engine.getExtraState() as ExtraState;
    const tryStep = (step: Step) => engine.getBackdoorEngine().aiTryStep(step);
    const sm = new StepMaker(inspector, Player.P2, Role.GOD);

    switch (step.type) {
      case StepType.SUMMON: {
        // const { player, slot, cardName } = step;
        const { player, slot, handCardId, cardName } = step;
        if (player !== Player.P1) break;

        const resolvedCardName = (() => {
          if (handCardId !== undefined) {
            const handCard = inspector.getCardInHandIfExists(
              player,
              handCardId
            );
            if (!handCard) return null;
            return inspector.getCardName(handCard);
          }
          return null;
        })();

        const spawnCardName = resolvedCardName ?? cardName;
        if (spawnCardName) {
          tryStep(sm.spawn(spawnCardName, reflectSlot(slot, inspector)));
        }

        // NAH: possibly we want two New Boarns to still attack each other from different slots?
        // if so, then we can store state to create a 1:1 map between player's units and the copied units
        break;
      }
      case StepType.MOVE: {
        const { permanentId, slot } = step;
        const permanent = inspector.getPermanent(permanentId);
        if (permanent.owner !== Player.P1) break;
        const reflectedPermanent = reflectPermanent(permanent, inspector);
        const reflectedSlot = reflectSlot(slot, inspector);

        if (
          reflectedPermanent &&
          areReflectionsEqual(permanent, reflectedPermanent, inspector)
        ) {
          tryStep(sm.move(reflectedPermanent, reflectedSlot));
        }
        break;
      }
      case StepType.ATTACK: {
        const { attackerId, defenderId } = step;
        const attacker = inspector.getPermanent(attackerId);
        const defender = inspector.getPermanent(defenderId);
        if (attacker.owner !== Player.P1) break;

        // AI attacks first. however, if the AI is about to die, then it lets the player attack first, then clones to attack.
        if (isDefenderAboutToDie(attacker, defender, ctx)) break;

        const reflectAttacker = reflectPermanent(attacker, inspector);
        const reflectDefender = reflectPermanent(defender, inspector);
        let isSuccess = false;
        if (reflectAttacker && reflectDefender) {
          isSuccess = tryStep(sm.attack(reflectAttacker, reflectDefender));
        }
        if (isSuccess) ext.didCopyBeforeStep = true;

        break;
      }
      case StepType.ACTIVATE_ABILITY: {
        const { permanentId, abilityType, effectOpts } = step;
        const permanent = inspector.getPermanent(permanentId);
        if (permanent.owner !== Player.P1) break;

        // TODO: uncurse this

        // --- WARNING -- EXTREMELY CURSED ---
        // check if the special involves an attack
        // if so, assume that the permanent is attacking the first opt
        //  and check isDefenderAboutToDie
        // if so, then let the player attack first, then clone the special
        const effects = engine.getBackdoorEngine().getEffects(permanent);
        const effect =
          abilityType === AbilityType.FLEX
            ? effects.onRepeatable
            : effects.onSpecial;

        if (UNITS_WITH_ATTACK_EFFECTS.has(permanent.card.name || "")) {
          const firstOpt = effectOpts[0];
          if (!firstOpt) break;
          if (firstOpt.type !== EffectOptType.PERMANENT) break;
          const defender = inspector.getPermanent(firstOpt.permanentId);
          if (isDefenderAboutToDie(permanent, defender, ctx)) break;
        }

        // --- END OF CURSED CODE ---

        const reflectedPermanent = reflectPermanent(permanent, inspector);
        const reflectedEffectOpts = effectOpts.map((opt) =>
          reflectOpt(opt, inspector)
        );
        let isSuccess = false;
        if (reflectedPermanent) {
          isSuccess = tryStep(
            sm.activateAbility(
              reflectedPermanent,
              abilityType,
              reflectedEffectOpts
            )
          );
        }
        if (isSuccess) ext.didCopyBeforeStep = true;
      }
    }

    // Revalidate whether we can execute the step, since the actions
    // performed here may cause the step to no longer be valid.
    const stepResolved = engine.getBackdoorEngine().resolve(step, Role.GOD);
    return stepResolved !== null;
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const ext = engine.getExtraState() as ExtraState;

    const tryStep = (step: Step) => engine.getBackdoorEngine().aiTryStep(step);
    const sm = new StepMaker(inspector, Player.P2, Role.GOD);

    // Spawn a card to slot without announcing or triggering any SUMMON effects.
    const silentSpawn = (
      permToCopy: Permanent,
      slot: Slot,
      player: Player
    ): boolean => {
      if (!inspector.isSlotInBounds(slot)) return false;
      if (inspector.isSlotOccupied(slot)) return false;
      const cardName = permToCopy.card.name;
      const permanent = engine
        .getBackdoorEngine()
        .cardToPermanent({ name: cardName }, player, slot);
      engine.getBackdoorController().createPermanent(permanent);
      for (const c of permToCopy.counters) {
        engine.addCounter(permanent, c);
      }
      return true;
    };

    const reportCopyFailed = () => {
      ai.speak("...copy failed... 🥀");
    };

    switch (step.type) {
      case StepType.SUMMON: {
        // handled in beforeStep
        break;
      }
      case StepType.ATTACK: {
        // only execute if the attack wasn't already copied in the beforeStep
        if (ext.didCopyBeforeStep) {
          ext.didCopyBeforeStep = false;
          break;
        }
        const { attacker, defender } = step;
        if (attacker.owner !== Player.P1) break;

        const newAttacker = reflectPermanent(attacker, inspector);
        const newDefender = reflectPermanent(defender, inspector);

        let isSuccess = false;
        if (
          newAttacker &&
          newDefender &&
          tryStep(sm.attack(newAttacker, newDefender))
        )
          isSuccess = true;

        if (isSuccess) break;

        const isDefenderAboutToDie =
          defender.damage >= inspector.getMaxHealth(defender);
        const isDefenderReflectedAttacker = areSlotsEqual(
          defender.slot,
          reflectSlot(attacker.slot, inspector)
        );

        if (
          isDefenderAboutToDie &&
          isDefenderReflectedAttacker &&
          newDefender // need a target to attack
        ) {
          // spawn a temporary attacker to copy the attack, then kill it
          // don't trigger any summon effects
          if (
            silentSpawn(
              attacker,
              reflectSlot(attacker.slot, inspector),
              Player.P2
            )
          ) {
            const tempAttacker = inspector.getPermanentAtIfExists(
              reflectSlot(attacker.slot, inspector)
            );

            if (tempAttacker) {
              engine.ready(tempAttacker);
              if (tryStep(sm.attack(tempAttacker, newDefender)))
                isSuccess = true;

              // destroy temp attacker, attributed to P1's attacker
              engine.destroy(attacker, tempAttacker);
            }
          }
        }
        if (!isSuccess) reportCopyFailed();
        break;
      }
      case StepType.MOVE: {
        // handled in beforeStep
        break;
      }
      case StepType.ACTIVATE_ABILITY: {
        // only execute if the attack wasn't already copied in the beforeStep
        if (ext.didCopyBeforeStep) {
          ext.didCopyBeforeStep = false;
          break;
        }
        const { permanent, abilityType, effectOptsResolved } = step;
        if (permanent.owner !== Player.P1) break;
        const newPermanent = reflectPermanent(permanent, inspector);
        const newEffectOpts = effectOptsResolved.map((opt) =>
          reflectOptResolved(opt, inspector)
        );

        let isSuccess = false;
        if (
          newPermanent &&
          tryStep(sm.activateAbility(newPermanent, abilityType, newEffectOpts))
        )
          isSuccess = true;

        if (isSuccess) break;

        // spawn a temporary attacker to copy the attack, then kill it
        // don't trigger any summon effects
        if (
          silentSpawn(
            permanent,
            reflectSlot(permanent.slot, inspector),
            Player.P2
          )
        ) {
          const tempAttacker = inspector.getPermanentAtIfExists(
            reflectSlot(permanent.slot, inspector)
          );

          if (tempAttacker) {
            engine.ready(tempAttacker);
            if (
              tryStep(
                sm.activateAbility(tempAttacker, abilityType, newEffectOpts)
              )
            )
              isSuccess = true;

            // destroy temp attacker, attributed to P1's permanent
            engine.destroy(permanent, tempAttacker);
          }
        }

        if (!isSuccess) reportCopyFailed();

        break;
      }
    }
  },
  aiTurn: (ctx) => {
    // do nothing, say something
    const { inspector, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      ai.speak(
        "...watchful gnomes... 🌹 ...mimic foe...\n...leave no openings... 🌺 ...none shall pass..."
      );
    }
  },
  afterTurnEnd: (player, ctx) => {
    const { inspector, engine, ai } = ctx;

    if (player === Player.P2) {
      // P2 just ended turn. Ready all P2's units
      const permanents = inspector.getPermanents({ owner: Player.P2 });
      for (const permanent of permanents) {
        engine.ready(permanent);
      }
    }
  },
};
