import { Slot, Permanent } from "engine/types/game-state";
import { Inspector } from "engine/Inspector";
import { WALL_NAME } from "./puzzle-22-data";

const N = 100; // must be bigger than board size
export function slotToIndex(slot: Slot) {
  return N * slot.row + slot.column;
}
export function indexToSlot(index: number) {
  return {
    row: Math.floor(index / N),
    column: index % N,
  };
}

const WALLS = new Set([WALL_NAME]);

export enum SokobanUpdateType {
  MOVE,
  APPLY_POWERUP,
}
export type MoveDescription = {
  type: SokobanUpdateType.MOVE;
  permanent: Permanent;
  rowDiff: number;
  colDiff: number;
};
export type ApplyPowerupDescription = {
  type: SokobanUpdateType.APPLY_POWERUP;
  permanent: Permanent;
  powerupPermanent: Permanent;
};
export type UpdateDescription = MoveDescription | ApplyPowerupDescription;

export class SokobanState {
  // key = location (index), value = list of permanents at that slot
  constructor(
    private actors: { [index: number]: Permanent[] },
    private inspector: Inspector
  ) {}

  static makeStateFromInspector(inspector: Inspector) {
    const locations: { [index: number]: Permanent[] } = {};
    for (const perm of inspector.getAllPermanents()) {
      locations[slotToIndex(perm.slot)] ||= [];
      locations[slotToIndex(perm.slot)].push(perm);
    }
    return new SokobanState(locations, inspector);
  }

  private get rows() {
    return this.inspector.getNumRows();
  }

  private get cols() {
    return this.inspector.getNumColumns();
  }

  // Return a list of updates, in order, that must be made to apply the push.
  getPushResult(
    maxPower: number,
    permanent: Permanent,
    rowDiff: number,
    colDiff: number
  ): UpdateDescription[] {
    if (Math.abs(rowDiff) + Math.abs(colDiff) !== 1) {
      // Unit is magically hopping, no pushing!
      const nextSlot = this.getResultingSlot(permanent.slot, rowDiff, colDiff);
      if (nextSlot == null || this.slotHasWall(nextSlot)) {
        // wall (or boundary)! can't move.
        return [];
      }
      const actorInWay = this.getActorAt(nextSlot);
      if (actorInWay) {
        return [];
      }
      return [
        {
          type: SokobanUpdateType.MOVE,
          permanent,
          rowDiff,
          colDiff,
        },
      ];
    }

    const startSlot = permanent.slot;
    let currSlot = startSlot;
    const allSlots = [startSlot];
    let currWeight = 0;
    let canMove = false;
    while (true) {
      const nextSlot = this.getResultingSlot(currSlot, rowDiff, colDiff);
      if (nextSlot == null || this.slotHasWall(nextSlot)) {
        // wall (or boundary)! can't move.
        break;
      }

      allSlots.push(nextSlot);

      const thingToAttemptToPush = this.getActorAt(nextSlot);
      if (!thingToAttemptToPush) {
        canMove = true;
        break;
      }
      // Attempt to push the next thing
      const newWeight = currWeight + this.getWeightOf(thingToAttemptToPush);
      if (newWeight > maxPower) {
        break;
      }
      currWeight = newWeight;
      currSlot = nextSlot;
    }
    if (!canMove) {
      return [];
    }
    const allUpdates = [] as UpdateDescription[];
    for (let i = 0; i < allSlots.length; i++) {
      // Skip last spot
      if (i == allSlots.length - 1) {
        break;
      }
      const slot = allSlots[i];
      const perm = this.getActorAt(slot);
      if (!perm) {
        throw new Error("error in sokoban logic :(");
      }
      allUpdates.push({
        type: SokobanUpdateType.MOVE,
        permanent: perm,
        rowDiff,
        colDiff,
      });
    }
    // Reverse the order, because moves should start from farther away
    allUpdates.reverse();
    return allUpdates;
  }

  getWeightOf(perm: Permanent) {
    // please don't move camp
    if (perm.card.name === "camp") {
      return 99;
    }
    // This used to be the weight
    return 1;
  }

  getActorsAt(slot: Slot) {
    return this.actors[slotToIndex(slot)] || [];
  }

  getActorAt(slot: Slot) {
    // return this.getActorsAt(slot).find(
    //   (perm) => !STACKABLE.has(perm.card.name || "")
    // );

    // when stackables don't exist, this is just the actor on the slot.
    return this.getActorsAt(slot).find((perm) => true);
  }

  slotHasWall(slot: Slot) {
    return !!this.getActorsAt(slot).find((perm) =>
      WALLS.has(perm.card.name || "")
    );
  }

  isSlotInBounds(slot: Slot) {
    return (
      0 <= slot.row &&
      slot.row < this.rows &&
      0 <= slot.column &&
      slot.column < this.cols
    );
  }

  // The slot that moving in the given relative amount will result in,
  // or null if that's out of bounds.
  getResultingSlot(slot: Slot, rowDiff: number, colDiff: number) {
    const newSlot = {
      row: slot.row + rowDiff,
      column: slot.column + colDiff,
    };
    if (!this.isSlotInBounds(newSlot)) {
      return null;
    }
    return newSlot;
  }
}
