import { Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { PermanentEventType } from "engine/server-hooks";

export const Puzzle98SplitterSpec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  cardEffects: {
    "wild-weed": {
      handlers: {
        [PermanentEventType.OTHER_UNIT_SPAWN]: (event) => {
          const { permanent: me, spawnedPermanent } = event;
          if (me.owner !== spawnedPermanent.owner) {
            const { engine, inspector } = event.ctx;
            let toSpawn = inspector.getPower(spawnedPermanent);
            if (!toSpawn) return;
            const emptySlots = inspector.getUnoccupiedSlotsClosestTo(
              Player.P2,
              me.slot
            );
            for (const emptySlot of emptySlots) {
              if (engine.spawn("wild-weed", emptySlot, Player.P2)) {
                toSpawn--;
                if (toSpawn === 0) break;
              }
            }
          }
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      ai.trySpawn("wild-weed", 0, 2);
    }

    inspector.getPermanentsOf(Player.P2).forEach((permanent) =>
      ai.tryAttackFirst(permanent, {
        sort: [
          {
            type: "weighted",
            weights: [
              // prioritize higher power, since they are scarier
              { criterion: "-power", weight: 1 },
              // prioritize lower health, since they are faster to kill off
              { criterion: "health", weight: 1 },
            ],
          },
          // tiebreak by nearest first
          { type: "euclideanDistance", slot: permanent.slot },
        ],
      })
    );
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;

    // if P2 has no creatures they lose
    return (
      player === Player.P2 && inspector.getPermanentsOf(player).length === 0
    );
  },
};
