import { CardColor, ColorSymbol } from "engine/types/card-data";
import { Permanent, Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Inspector } from "engine/Inspector";
import {
  PermanentEventHandlers,
  PermanentEventType,
} from "engine/server-hooks";

type Puzzle80ExtraState = {
  // Transient state (cleared at the end of each turn) to store
  // damage mappings to announce.
  damageMappings: {
    [gostakName: string]: {
      targetName: string;
      damage: number;
    }[];
  };
};

function manhattanDistance(a: Permanent, b: Permanent) {
  return (
    Math.abs(a.slot.row - b.slot.row) + Math.abs(a.slot.column - b.slot.column)
  );
}

function puzzleLength(s: string) {
  let count = 0;
  for (const c of s) {
    if (/[0-9a-zA-Z]/.test(c)) {
      count += 1;
    }
  }
  return count;
}

function a1color(color: CardColor | null) {
  if (color === null) return 0;
  // WHITEBLACK is Gray
  if (color === ColorSymbol.WHITEBLACK) return 7;
  if (!/^[A-Z]/.test(color)) return 0;
  return color.charCodeAt(0) - "A".charCodeAt(0) + 1;
}

const damageFuncs = [
  [
    function (inspector: Inspector, _me: Permanent, target: Permanent) {
      return inspector.getPower(target);
    },
  ],
  [
    function (inspector: Inspector, _me: Permanent, target: Permanent) {
      return inspector.getMaxHealth(target);
    },
  ],
  [
    function (inspector: Inspector, _me: Permanent, target: Permanent) {
      return Math.abs(
        inspector.getPower(target) - inspector.getMaxHealth(target)
      );
    },
  ],
  [
    function (inspector: Inspector, _me: Permanent, target: Permanent) {
      return Math.abs(
        inspector.getPower(target) -
          puzzleLength(inspector.getCardDisplayName(target))
      );
    },
  ],
  [
    function (inspector: Inspector, me: Permanent, target: Permanent) {
      return Math.abs(
        manhattanDistance(me, target) - a1color(inspector.getCardColor(target))
      );
    },
  ],
  [
    function (inspector: Inspector, me: Permanent, target: Permanent) {
      return Math.abs(
        puzzleLength(inspector.getCardDisplayName(target)) -
          manhattanDistance(me, target) *
            a1color(inspector.getCardColor(target))
      );
    },
  ],
  [
    function (inspector: Inspector, me: Permanent, target: Permanent) {
      return Math.abs(
        inspector.getMaxHealth(target) *
          puzzleLength(inspector.getCardDisplayName(target)) -
          manhattanDistance(me, target)
      );
    },
    function (inspector: Inspector, _me: Permanent, target: Permanent) {
      return (
        Math.floor(inspector.getTurnNumber() / 2) *
        Math.abs(
          inspector.getMaxHealth(target) -
            puzzleLength(inspector.getCardDisplayName(target))
        )
      );
    },
  ],
];

function makeGostakHandler(
  damageFuncs: ((
    inspector: Inspector,
    me: Permanent,
    target: Permanent
  ) => number)[]
): PermanentEventHandlers {
  return {
    [PermanentEventType.START_TURN]: (event) => {
      const me = event.permanent;
      const { inspector, engine } = event.ctx;
      const ext = engine.getExtraState() as Puzzle80ExtraState;
      const gostakName = inspector.getCardDisplayName(me);
      for (const func of damageFuncs) {
        const targets = inspector
          .getPermanentsOf(inspector.getOpponentOf(me.owner))
          .filter((perm) => !inspector.isProtectedFrom(perm, me));
        for (const perm of targets) {
          const damage = func(inspector, me, perm);
          const targetName = inspector.getCardDisplayName(perm);
          engine.announce(`The gostak attacks ${targetName} for ${damage}...`);
          ext.damageMappings[gostakName] ??= [];
          ext.damageMappings[gostakName].push({
            targetName,
            damage,
          });
          engine.damage(me, perm, damage);
        }
      }
    },
  };
}

export const Puzzle80Specs: GameSpecServerFragment[] = damageFuncs.map(
  (f, i) => {
    const gostakName = `gostak${i + 1}`;
    return {
      disableP2NoBasesLossCondition: true,
      counterData: {},
      cardEffects: {
        [gostakName]: {
          handlers: makeGostakHandler(f),
        },
      },
      afterInitEngine: (ctx) => {
        ctx.engine.initExtraState({
          damageMappings: {},
        } as Puzzle80ExtraState);
      },
      aiTurn: (ctx) => {
        const { inspector, engine, ai } = ctx;
        const ext = engine.getExtraState() as Puzzle80ExtraState;
        if (inspector.getTurnNumber() === 0) {
          ai.trySpawn(gostakName, 0, 2);
        }
        if (Object.keys(ext.damageMappings).length > 0) {
          ai.speak(
            `A portion of damage to every foe!\n${Object.entries(
              ext.damageMappings
            )
              .map(([gostakName, damages]) =>
                damages
                  .map(({ targetName, damage }) => `${targetName}, ${damage}.`)
                  .join(" ")
              )
              .join("\n")}`
          );
          ext.damageMappings = {};
        } else {
          engine.clearKeyframe();
        }
      },
      extraLoseChecks: (player, ctx) => {
        const { inspector } = ctx;

        if (player === Player.P2) {
          // if P2 has no creatures they lose
          return inspector.getPermanentsOf(Player.P2).length === 0;
        }
        return false;
      },
    };
  }
);
