import { CardType } from "engine/types/card-data";
import { Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";

export const Tutorial6Spec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  beforeNewGame: (ctx) => {
    const { engine, ai } = ctx;
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const { keyframe } = inspector.gameState;
    if (
      inspector.getBases(Player.P2).length === 0 &&
      keyframe !== undefined &&
      keyframe.id === "tutorial6-start-2"
    ) {
      // show a final keyframe before game end
      ai.setKeyframe("tutorial6-end");
    }
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();

    switch (turnNumber) {
      case 0: {
        const aiCamps = inspector.getPermanents({
          name: "camp",
          owner: Player.P2,
        });
        for (const camp of aiCamps) {
          engine.adjustPermanentStats(camp, 0, -2);
        }
        if (inspector.gameState[Player.P1].hand.length <= 0) {
          ai.setKeyframe("tutorial6-start-no-deck");
        } else {
          ai.setKeyframe("tutorial6-start-1");
        }
        return;
      }
      case 2: {
        ai.trySpawn("o", 1, 0);
        ai.trySpawn("payne", 1, 1);
        ai.trySpawn("vanilla-calf", 1, 3);
        ai.trySpawn("re", 1, 4);
        ai.trySpawn("beethoven", 2, 0);
        ai.trySpawn("chicken", 2, 1);
        ai.trySpawn("petroleum-jelly", 2, 3);
        ai.trySpawn("log", 2, 4);
        ai.setKeyframe("tutorial6-start-2");
        break;
      }
    }

    const aiCreatures = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.CREATURE,
    });

    aiCreatures.forEach((creature) => {
      ai.tryAttackFirst(creature);
    });
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine } = ctx;
    if (solves[Player.P1] !== undefined) engine.clearKeyframe();
  },
};
