import { CounterType } from "engine/types/counters";
import { Permanent, Player, Slot } from "engine/types/game-state";
import { GameSpecServerFragment, AIContext } from "engine/types/game-specs";
import { Inspector } from "engine/Inspector";
import { CardType } from "engine/types/card-data";
import { GAMES, Puzzle28Game } from "engine/game-specs/puzzle-28-SPOILERS-data";

const BLACK_NAME = "chocolate";
const WHITE_NAME = "vanilla";
const COW_NAME = "cyclow";

// values double as cardname suffix
enum cowsState {
  SLEEPING = "--sleeping-", // do nothing
  AWAKE = "--awake-", // about to attack
  TIRED = "--tired-", // just attacked, weak
  WEAK = "--weak-", // not used as state, just card art: (weak + vulnerable)
  ANGRY = "--angry-", // final state, angry cow
}

type ExtraState = {
  cows: cowsState;
  level: number; // there is no cow level
  game: Puzzle28Game;
};

const cowQuery = (inspector: Inspector) => ({
  owner: Player.P2,
  custom: (permanent: Permanent) => {
    return isCow(inspector, permanent);
  },
});

const isCow = (inspector: Inspector, permanent: Permanent) => {
  return inspector.getCardName(permanent).includes(COW_NAME);
};

const transformCows = (
  state: cowsState,
  ctx: AIContext,
  cows?: Permanent[]
) => {
  const { inspector, engine } = ctx;
  if (!cows) {
    cows = inspector.getPermanents(cowQuery(inspector));
  }
  cows.forEach((cow) => {
    if (inspector.getCardName(cow).startsWith(WHITE_NAME)) {
      engine
        .getBackdoorEngine()
        .transformPermanent(cow, WHITE_NAME + "-" + COW_NAME + state);
    } else {
      engine
        .getBackdoorEngine()
        .transformPermanent(cow, BLACK_NAME + "-" + COW_NAME + state);
    }
  });
};

const getAttacks = (level: number, game: Puzzle28Game) => {
  if (level === 6) {
    return game.attks_6;
  } else if (level === 5) {
    return game.attks_5;
  } else if (level === 4) {
    return game.attks_4;
  }

  throw new Error("No attacks found for invalid level: " + level);
};

const getKillableSlot = (level: number, game: Puzzle28Game): Slot => {
  let move = "";

  if (level === 6) {
    move = game.moves65;
  } else if (level === 5) {
    move = game.moves54;
  }

  for (let row = 0; row < 6; row++) {
    for (let column = 0; column < 5; column++) {
      const index = row * 5 + column;
      if (move[index] === "X") {
        return { row, column };
      }
    }
  }

  throw new Error("No killable slot found for level: " + level);
};

// move (permanent) to the closest empty slot on their side
// prioritizes unsafe spots to shove
const shovePermanent = (ctx: AIContext, permanent: Permanent) => {
  const { inspector, engine } = ctx;
  const ext = engine.getExtraState() as ExtraState;

  const emptySlots = inspector.getUnoccupiedSlotsClosestTo(
    permanent.owner,
    permanent.slot
  );
  for (const slot of emptySlots) {
    const attacks = getAttacks(ext.level - 1, ext.game); // check the next level's attacks
    const index = slot.row * inspector.getNumColumns() + slot.column;
    // first try to shove to unsafe spots
    if (attacks[index] === "!" && engine.move(permanent, slot)) return;
  }
  for (const slot of emptySlots) {
    // now try any slot
    if (engine.move(permanent, slot)) return;
  }
};

const spawnCows = (ctx: AIContext, game: Puzzle28Game) => {
  const { inspector, engine } = ctx;

  for (let row = 0; row < 6; row++) {
    for (let column = 0; column < 5; column++) {
      const slot = { row, column };
      const char = game.final_6[row * 5 + column];
      if (char !== ".") {
        // check if blocked
        const blockingPermanent = inspector.getPermanentAtIfExists(slot);
        if (blockingPermanent) {
          shovePermanent(ctx, blockingPermanent);
        }
        // spawn
        const cowName =
          (char === "b" ? BLACK_NAME : WHITE_NAME) +
          "-" +
          COW_NAME +
          cowsState.SLEEPING;
        engine.spawn(cowName, slot, Player.P2);
      }
    }
  }

  const units = inspector.getPermanentsOf(Player.P2);
  units.forEach((unit) => {
    engine.addCounter(unit, {
      type: CounterType.INVULNERABLE,
    });
  });
};

const moveCows = (ctx: AIContext, game: Puzzle28Game) => {
  const { inspector, engine } = ctx;
  const ext = engine.getExtraState() as ExtraState;
  // move now
  let moves = "";
  if (ext.level === 6) {
    moves = game.moves65;
  } else if (ext.level === 5) {
    moves = game.moves54;
  }

  type cowToMove = {
    cow: Permanent;
    from: Slot;
    to: Slot;
  };

  const cowsToMove: cowToMove[] = [];

  for (let row = 0; row < 6; row++) {
    for (let column = 0; column < 5; column++) {
      const from = { row, column };
      const move = moves[row * 5 + column];
      let to;
      switch (move) {
        case "L":
          to = { row, column: column - 1 };
          break;
        case "R":
          to = { row, column: column + 1 };
          break;
        case "U":
          to = { row: row - 1, column };
          break;
        case "D":
          to = { row: row + 1, column };
          break;
        default: // XS. all skip
          continue;
      }

      // move the cow
      const cow = inspector.getPermanentAtIfExists(from);
      if (cow && isCow(inspector, cow)) {
        cowsToMove.push({ cow, from, to });
      }
    }
  }

  // fix infinite loop
  let tries = 0;
  // queue to prevent shoving other cows
  while (cowsToMove.length > 0 && tries < 10) {
    tries++;
    const cowToMove = cowsToMove.shift();
    if (!cowToMove) continue;

    const { cow, from, to } = cowToMove;
    // check if blocked
    const blockingPermanent = inspector.getPermanentAtIfExists(to);
    if (blockingPermanent) {
      if (isCow(inspector, blockingPermanent)) {
        // blocked by a cow, skip
        cowsToMove.push(cowToMove);
        continue;
      }
      shovePermanent(ctx, blockingPermanent);
    }
    // move
    engine.move(cow, to);
  }

  // this should never happen, but if it does, just let the cows be in bad positions instead of crashing server.
  // if (cowsToMove.length > 0) {
  //   console.log(game);
  //   throw new Error(`Failed to move cows`);
  // }
};

export const Puzzle28Spec: GameSpecServerFragment = {
  counterData: {},
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const ext = engine.getExtraState() as ExtraState;

    const turnNumber = inspector.getTurnNumber();

    const cows = inspector.getPermanents(cowQuery(inspector));

    if (turnNumber === 0) {
      spawnCows(ctx, ext.game);
      ai.speak(
        "Howdy! The name’s Miss Yu. This here’s my ranch! Mind the cows."
      );
    } else {
      switch (ext.cows) {
        case cowsState.SLEEPING: {
          ext.cows = cowsState.AWAKE;
          transformCows(cowsState.AWAKE, ctx);
          if (ext.level === 6) {
            ai.speak(
              "They like to curl up into li’l balls if ya wake ’em, ya see. Watch yer step!"
            );
          } else {
            ai.speak("Hope you know what’s comin’.");
          }
          break;
        }
        case cowsState.AWAKE: {
          const attacks = getAttacks(ext.level, ext.game);

          // attack!!!
          for (let row = 0; row < 6; row++) {
            for (let column = 0; column < 5; column++) {
              const i = row * 5 + column;
              const attack = attacks[i];
              if (attack === "!") {
                // TODO show ALL slots attacked with explosions
                // maybe we can hack this by adding a explosion creature to empty slots?
                const permanent = inspector.getPermanentAtIfExists({
                  row,
                  column,
                });
                if (
                  permanent &&
                  permanent.owner === Player.P1 &&
                  inspector.getCardType(permanent) === CardType.CREATURE
                ) {
                  engine.destroy(permanent, permanent);
                }
              }
            }
          }

          if (ext.level === 6) {
            ai.speak(
              "My husband always said, the cows won’t pay the bills!\nLemme tell ya, he sure shut up after he saw\nthis ’ere cow-ordinated lasso attack!"
            );
          } else {
            ai.speak("Take that!");
          }

          ext.cows = cowsState.TIRED;
          transformCows(cowsState.TIRED, ctx);

          // remove counter from killable
          if (ext.level <= 4) {
            // all are killable
            cows.forEach((cow) => {
              engine.removeCounterType(cow, CounterType.INVULNERABLE);
            });
            transformCows(cowsState.WEAK, ctx);
            break;
          }
          const killableSlot = getKillableSlot(ext.level, ext.game);
          const killableCow = inspector.getPermanentAtIfExists(killableSlot);
          if (killableCow) {
            engine.removeCounterType(killableCow, CounterType.INVULNERABLE);
            transformCows(cowsState.WEAK, ctx, [killableCow]);
          }

          break;
        }
        case cowsState.TIRED: {
          if (ext.level > cows.length) {
            // a cow was killed, switch to MOVING to move on the next turn

            if (ext.level <= 4) {
              // less than 4 cows left
              // final angry transition
              const barn: Permanent = inspector.getPermanents({
                name: "barn",
              })[0];

              engine.removeCounterType(barn, CounterType.INVULNERABLE);

              ext.level = 3;
              ext.cows = cowsState.ANGRY;
              transformCows(cowsState.ANGRY, ctx);
              ai.speak("I’ve had it with you! Get off my ranch!");

              break;
            }

            moveCows(ctx, ext.game);
            ext.level -= 1;

            ext.cows = cowsState.SLEEPING;
            transformCows(cowsState.SLEEPING, ctx);
            ai.speak(
              "What in tarnation didya do to my cow? Yer gonna get milked!"
            );
          } else {
            ai.speak("They’re tired, don’t give ’em any trouble now.");
          }
          break;
        }
        case cowsState.ANGRY: {
          // all cows attack bases
          for (const cow of cows) {
            ai.tryAttackFirst(cow, {
              sort: [
                // attack bases, lowest health, closest
                "structuresFirst",
                "health",
                { type: "euclideanDistance", slot: cow.slot },
              ],
            });
          }
        }
      }
    }
  },
  afterInitEngine: (ctx) => {
    // GOOD STATE FOR TESTING
    // const final_6 = ".......b...w.....b......w.bw..";
    // const moves65 = ".......R...L.....X......S.LR..";
    // const moves54 = "........D.R.............UR..X.";
    // const attks_6 = "!!!!!!!!!!s!!sss!!!!s!ss!.!!!!";
    // const attks_5 = ".s.s.!!!!s!ss!s!ss!!!sss!!!!!!";
    // const attks_4 = ".s.!!sss!!!!!!!!!ss!s!s!!.!!!.";
    // KEEP THE ABOVE ONE

    // pick a random game
    const gamesArr = Object.values(GAMES);
    const gameIndex = Math.floor(Math.random() * gamesArr.length);
    const game = gamesArr[gameIndex];
    // const { final_6, moves65, moves54, attks_6, attks_5, attks_4 } = game;

    ctx.engine.initExtraState({
      cows: cowsState.SLEEPING,
      level: 6,
      game,
    } as ExtraState);
  },
};
