import { GameSpecServerFragment } from "engine/types/game-specs";
import {
  COMMON_COUNTER_DATA,
  COMMON_CARD_EFFECTS,
} from "engine/cards/card-effects";

// Note: If this ever uses ExtraState, it needs a separate extra state
// buffer and a new hook to access it, since it is used simultaneously
// with other game specs.

/**
 * Common "game spec" used in all puzzles. Note that only some subset
 * of the game spec hooks are supported for the common spec.
 */
export const CommonSpec: GameSpecServerFragment = {
  counterData: COMMON_COUNTER_DATA,
  cardEffects: COMMON_CARD_EFFECTS,
};
