import { Permanent, Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Inspector } from "engine/Inspector";
import { ColorSymbol } from "engine/types/card-data";
import {
  COLORS,
  CURRENCY_CODES,
  DAY_OF_WEEK_ABBREVIATIONS,
  FIRST_151_POKEMON,
  FRUITS,
  IATA_AIRPORT_CODES_FLORIDA,
  KENTUCKY_DERBY_WINNERS,
  LEAGUE_OF_LEGENDS_CHAMPIONS,
  MONTHS,
  NFL_TEAM_NAMES,
  OLYMPIC_WINTER_SPORTS,
  ONES_DIGITS,
  OPTICAL_COMPONENTS,
  STANDALONE_NUMBERS,
  SUMMER_OLYMPIC_SPORTS,
  SYNONYMS_FOR_TRICKED,
  TENS_DIGITS,
  TREES,
  US_STATES,
  WEAPONS,
} from "./puzzle-51-server-data";
import { CounterType } from "engine/types/counters";
import { EffectsDB, extractOptChoiceString } from "engine/cards/card-effects";
import { PUZZLE_51_CARDS } from "./puzzle-51-shared";
import {
  PermanentEventHandlers,
  PermanentEventType,
} from "engine/server-hooks";
import { StepType } from "engine/types/steps";
import { AbilityType } from "engine/types/effects";

const AI_BASE_ID = "bearhive";
const BEEHIVE_ID = "beehive";
const LARVA_ID = "larva";
const NUM_STARTING_GEMS = 15;
const AI_CARD_IDS = [
  "luge-bear", // 0
  "stufful-bear", // 1
  "black-bear", // 2
  "banana-bear", // 3
  "sun-bear", // 4
  "ord-bear", // 5
  "bears-bear", // 6
  "confused-bear", // 7
  "eur-bear", // 8
  "three-bear", // 9
  "bludgeon-bear", // 10
  "four-bear", // 11
  "grism-bear", // 12
  "assault-bear", // 13
  "archery-bear", // 14
  "oak-bear", // 15
  "december-bear", // 16
  "alabama-bear", // 17
  "rammus-bear", // 18
];
const NUMBERS = ONES_DIGITS.concat(TENS_DIGITS).concat(STANDALONE_NUMBERS);

type Puzzle51ExtraState = {
  firstNumber: undefined | number;
  usedDraw: boolean;
  usedSpecial: boolean;
};

/**
 * Returns all strings available by reading across or down. Blank grid spaces break strings.
 * All letters are in upper case.
 *
 * @param inspector
 * @returns
 */
function getAllStrings(inspector: Inspector): Set<string> {
  const rows = inspector.getNumRows();
  const cols = inspector.getNumColumns();
  const permanents = inspector.getAllPermanents();
  const grid: (Permanent | undefined)[][] = [];
  for (let r = 0; r < rows; r++) {
    const row: (Permanent | undefined)[] = [];
    for (let c = 0; c < cols; c++) {
      row.push(undefined);
    }
    grid.push(row);
  }
  for (const p of permanents) {
    grid[p.slot.row][p.slot.column] = p;
  }
  // Read across.
  const res = new Set<string>();
  for (let r = 0; r < rows; r++) {
    let string = "";
    for (let c = 0; c < cols; c++) {
      const p = grid[r][c];
      if (p === undefined || p.card.name === LARVA_ID) {
        res.add(string);
        string = "";
      } else {
        string += p.card.name?.charAt(0).toUpperCase();
      }
    }
    // When you reach the end of the column, add whatever you have.
    res.add(string);
  }
  // Read down.
  for (let c = 0; c < cols; c++) {
    let string = "";
    for (let r = 0; r < rows; r++) {
      const p = grid[r][c];
      if (p === undefined || p.card.name === LARVA_ID) {
        res.add(string);
        string = "";
      } else {
        string += p.card.name?.charAt(0).toUpperCase();
      }
    }
    // When you reach the end of the row, add whatever you have.
    res.add(string);
  }
  return res;
}

/**
 * Returns true iff one of the items in shouldContain is contained within the grid (reading across or down, breaking at empty space).
 * Requires that all entries of shouldContain are in upper case.
 *
 * @param shouldContain The set of strings where we want to know if one of them is present on the board.
 * @param allStringsBeforeAnyDespawns The set of all strings that are present before any despawns.
 */
function containsOneOf(
  shouldContain: Set<string>,
  allStringsBeforeAnyDespawns: Set<string>
): boolean {
  for (const string of allStringsBeforeAnyDespawns) {
    for (const substring of shouldContain) {
      if (string.includes(substring)) {
        return true;
      }
    }
  }
  return false;
}

const beeOrLarvaHandlers: PermanentEventHandlers = {
  [PermanentEventType.CREATED]: (ev) => {
    const { ctx, permanent } = ev;
    ctx.engine.addCounter(permanent, { type: CounterType.CANNOT_CREATE });
    ctx.engine.addCounter(permanent, { type: CounterType.CANNOT_ATTACK });
    ctx.engine.addCounter(permanent, { type: CounterType.CANNOT_MOVE });
  },
};
const cardEffects: EffectsDB = {
  larva: {
    onSpecial: (step, ctx) => {
      const { engine } = ctx;
      const { permanent } = step;
      const newPermanentName = extractOptChoiceString(step, 0);
      const newPermanentId = newPermanentName.toLowerCase().replace(/ /g, "-");
      const slot = permanent.slot;
      const owner = permanent.owner;

      engine.despawn(permanent);
      engine.spawn(newPermanentId, slot, owner);
    },
    handlers: beeOrLarvaHandlers,
  },
};

for (const cardName of PUZZLE_51_CARDS) {
  cardEffects[cardName.toLocaleLowerCase().replace(/ /g, "-")] = {
    onRepeatable: (step, ctx) => {
      const { engine } = ctx;
      engine.despawn(step.permanent);
    },
    handlers: beeOrLarvaHandlers,
  };
}

export const Puzzle51Spec: GameSpecServerFragment = {
  afterInitEngine: (ctx) => {
    const { engine } = ctx;
    engine.initExtraState({
      firstNumber: undefined,
      usedDraw: false,
      usedSpecial: false,
    } as Puzzle51ExtraState);
  },
  afterTurnEnd: (player, ctx) => {
    const { inspector, engine } = ctx;
    if (player === Player.P2) {
      const amt = inspector
        .getPermanentsOf(Player.P1)
        .filter((p) => p.card.name !== BEEHIVE_ID).length;
      engine.addGems(Player.P1, new Array(amt).fill(ColorSymbol.YELLOW));
    }
    // if (player === Player.P1) {
    //   engine.checkpoint();
    // }
  },
  beforeNewGame: (ctx) => {
    const { ai, engine } = ctx;
    engine.addGems(
      Player.P1,
      new Array(NUM_STARTING_GEMS).fill(ColorSymbol.YELLOW)
    );
    ai.speak(
      "The B, E, A, R, S are looking rather down today. Help us C, H, E, E, R them up!\nWe’ll keep the H, O, N, E, Y fresh, so it won’t reset between T, U, R, N, S!"
    );
  },
  cardEffects,
  aiTurn: (ctx) => {
    const { ai, engine, inspector } = ctx;
    const extraState = engine.getExtraState() as Puzzle51ExtraState;
    const aiTurnNumber = inspector.getTurnNumber() / 2;
    const allStringsBeforeAnyDespawns = getAllStrings(inspector);

    const bearhive = inspector
      .getPermanentsOf(Player.P2)
      .find((p) => p.card.name === AI_BASE_ID);

    // Set up the bearhive.
    if (aiTurnNumber === 0) {
      if (bearhive === undefined) return;
      engine.addCounter(bearhive, { type: CounterType.HAPPY_BEARS, val: 0 });
      engine.addCounter(bearhive, { type: CounterType.SAD_BEARS, val: 0 });
    }

    const bearResults: string[] = [];
    const completeBear = (isHappy: boolean, permanent?: Permanent) => {
      if (bearhive === undefined) return;
      const bearName =
        permanent === undefined
          ? "Bear"
          : inspector.getCardDisplayName(permanent);
      engine.mergeValueCounter(
        bearhive,
        isHappy ? CounterType.HAPPY_BEARS : CounterType.SAD_BEARS,
        1
      );
      bearResults.push(
        `${bearName} is ${isHappy ? "H, A, P, P, Y!" : "S, A, D :("}`
      );
    };
    function despawnAndUpdateCounters(cardId: string, category: Set<string>) {
      const cardToDespawn = inspector
        .getPermanentsOf(Player.P2)
        .find((p) => p.card.name === cardId);
      const bearName =
        cardToDespawn === undefined
          ? "Bear"
          : inspector.getCardDisplayName(cardToDespawn);

      completeBear(
        containsOneOf(category, allStringsBeforeAnyDespawns),
        cardToDespawn
      );

      if (cardToDespawn !== undefined) {
        engine.despawn(cardToDespawn);
      }
    }

    // De-spawn everything.
    switch (aiTurnNumber) {
      case 2:
        despawnAndUpdateCounters(AI_CARD_IDS[0], OLYMPIC_WINTER_SPORTS);
        break;
      case 5:
        despawnAndUpdateCounters(AI_CARD_IDS[1], FIRST_151_POKEMON);
        break;
      case 6:
        despawnAndUpdateCounters(AI_CARD_IDS[2], COLORS);
        break;
      case 7:
        despawnAndUpdateCounters(AI_CARD_IDS[3], FRUITS);
        break;
      case 8:
        despawnAndUpdateCounters(AI_CARD_IDS[4], DAY_OF_WEEK_ABBREVIATIONS);
        despawnAndUpdateCounters(AI_CARD_IDS[5], IATA_AIRPORT_CODES_FLORIDA);
        break;
      case 9:
        despawnAndUpdateCounters(AI_CARD_IDS[6], NFL_TEAM_NAMES);
        break;
      case 10:
        despawnAndUpdateCounters(AI_CARD_IDS[7], SYNONYMS_FOR_TRICKED);
        despawnAndUpdateCounters(AI_CARD_IDS[8], CURRENCY_CODES);
        break;
      case 11: {
        // Get the smallest number present on the board.
        const allStrings = getAllStrings(inspector);
        let minimumValue = undefined;
        for (const [number, value] of NUMBERS) {
          for (const string of allStrings) {
            if (
              string.includes(number) &&
              (minimumValue === undefined || minimumValue > value)
            ) {
              minimumValue = value;
            }
          }
        }
        // If a number was spelled out, despawn the correct AI card and store the firstNumber in extraState.
        const cardToDespawn = inspector
          .getPermanentsOf(Player.P2)
          .find((p) => p.card.name === AI_CARD_IDS[9]);

        const isSatisfied = minimumValue !== undefined;
        if (isSatisfied) extraState.firstNumber = minimumValue;
        completeBear(isSatisfied, cardToDespawn);

        if (cardToDespawn !== undefined) engine.despawn(cardToDespawn);
        // Despawn based on spelled weapons.
        despawnAndUpdateCounters(AI_CARD_IDS[10], WEAPONS);
        break;
      }
      case 13: {
        // Get the largest number present on the board.
        const allStrings2 = getAllStrings(inspector);
        let maximumValue = undefined;
        const generatedNumbers: [string, number][] = [];
        for (const [tensDigit, tensValue] of TENS_DIGITS) {
          for (const [onesDigit, onesValue] of ONES_DIGITS) {
            generatedNumbers.push([
              tensDigit + onesDigit,
              tensValue + onesValue,
            ]);
          }
        }
        for (const [number, value] of NUMBERS.concat(generatedNumbers)) {
          for (const string of allStrings2) {
            if (
              string.includes(number) &&
              (maximumValue === undefined || maximumValue < value)
            ) {
              maximumValue = value;
            }
          }
        }
        // If a larger number was spelled out, despawn the correct AI card.
        const cardToDespawn = inspector
          .getPermanentsOf(Player.P2)
          .find((p) => p.card.name === AI_CARD_IDS[11]);

        const isSatisfied =
          maximumValue !== undefined &&
          extraState.firstNumber !== undefined &&
          maximumValue > extraState.firstNumber;
        completeBear(isSatisfied, cardToDespawn);

        if (cardToDespawn !== undefined) engine.despawn(cardToDespawn);

        // Despawn based on geometric shapes & Kentucky Derby winners.
        despawnAndUpdateCounters(AI_CARD_IDS[12], OPTICAL_COMPONENTS);
        despawnAndUpdateCounters(AI_CARD_IDS[13], KENTUCKY_DERBY_WINNERS);
        break;
      }
      case 15:
        despawnAndUpdateCounters(AI_CARD_IDS[14], SUMMER_OLYMPIC_SPORTS);
        despawnAndUpdateCounters(AI_CARD_IDS[15], TREES);
        despawnAndUpdateCounters(AI_CARD_IDS[16], MONTHS);
        despawnAndUpdateCounters(AI_CARD_IDS[17], US_STATES);
        despawnAndUpdateCounters(AI_CARD_IDS[18], LEAGUE_OF_LEGENDS_CHAMPIONS);
        break;
    }

    const showHint =
      aiTurnNumber === 3 &&
      !(engine.getExtraState() as Puzzle51ExtraState).usedDraw;

    const hintDialogue =
      "Oh yeah, a certain M, A, S, T, E, R, Y might bee very helpful for this battle!";
    if (bearResults.length > 0 || showHint) {
      ai.speak(
        [
          ...(bearResults.length > 0
            ? [
                bearResults.join(
                  showHint || bearResults.length > 4 ? " " : "\n"
                ),
              ]
            : []),
          ...(showHint ? [hintDialogue] : []),
        ].join("\n")
      );
    }

    function clear(row: number, col: number) {
      inspector
        .getAllPermanents()
        .filter((p) => p.slot.column === col && p.slot.row === row)
        .forEach((p) => engine.despawn(p));
    }

    // Now do new spawns.
    switch (aiTurnNumber) {
      case 1:
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[0], 2, 2);
        break;
      case 4:
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[1], 2, 2);
        break;
      case 5:
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[2], 2, 2);
        break;
      case 6:
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[3], 2, 2);
        break;
      case 7:
        clear(2, 2);
        clear(2, 3);
        ai.trySpawn(AI_CARD_IDS[4], 2, 2);
        ai.trySpawn(AI_CARD_IDS[5], 2, 3);
        break;
      case 8:
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[6], 2, 2);
        break;
      case 9:
        clear(2, 2);
        clear(2, 3);
        ai.trySpawn(AI_CARD_IDS[7], 2, 2);
        ai.trySpawn(AI_CARD_IDS[8], 2, 3);
        break;
      case 10:
        clear(2, 3);
        clear(2, 2);
        ai.trySpawn(AI_CARD_IDS[9], 2, 3);
        ai.trySpawn(AI_CARD_IDS[10], 2, 2); // Needed for 1st letter.
        break;
      case 12:
        clear(2, 2);
        clear(2, 3);
        clear(2, 4);
        ai.trySpawn(AI_CARD_IDS[11], 2, 2);
        ai.trySpawn(AI_CARD_IDS[12], 2, 3);
        ai.trySpawn(AI_CARD_IDS[13], 2, 4);
        break;
      case 14:
        clear(1, 2);
        clear(2, 2);
        clear(0, 5);
        clear(1, 5);
        clear(2, 5);
        // Letter placements important.
        ai.trySpawn(AI_CARD_IDS[14], 1, 2);
        ai.trySpawn(AI_CARD_IDS[15], 2, 2);
        ai.trySpawn(AI_CARD_IDS[16], 0, 5);
        ai.trySpawn(AI_CARD_IDS[17], 1, 5);
        ai.trySpawn(AI_CARD_IDS[18], 2, 5);
        break;
    }
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;
    const bearhive = inspector
      .getPermanentsOf(Player.P2)
      .find((p) => p.card.name === AI_BASE_ID);
    const noSadBears =
      (bearhive?.counters.find((c) => c.type === CounterType.SAD_BEARS)?.val ??
        0) === 0;
    return (
      inspector.getTurnNumber() >= 31 &&
      // P2 loses if there are no sad bears;
      // P1 loses if there are sad bears.
      noSadBears === (player === Player.P2)
    );
  },
  afterStep: (step, ctx) => {
    if (step.type === StepType.DRAW) {
      (ctx.engine.getExtraState() as Puzzle51ExtraState).usedDraw = true;
      return;
    }
    const { ai, engine } = ctx;
    const extraState = engine.getExtraState() as Puzzle51ExtraState;
    if (extraState.usedSpecial) return;
    if (step.type !== StepType.ACTIVATE_ABILITY) return;
    if (step.abilityType !== AbilityType.SPECIAL) return;
    extraState.usedSpecial = true;
    ai.speak(
      "This battle has a S, P, E, C, I, A, L hotkey! When transforming a Larva into a bee, you can type any letter key to select the bee type whose first letter matches!"
    );
  },
};
