import { PermanentEventType } from "engine/server-hooks";
import { CardType, ColorSymbol } from "engine/types/card-data";
import { AbilityType, EffectOptType } from "engine/types/effects";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Player, getSlotsEuclideanDistance } from "engine/types/game-state";

export const Puzzle53Spec: GameSpecServerFragment = {
  cardEffects: {
    yurt: {
      handlers: {
        [PermanentEventType.START_TURN]: (event) => {
          const { permanent, ctx } = event;
          const { inspector, engine } = ctx;
          if (inspector.isTurnPending(permanent.owner)) {
            engine.addGems(permanent.owner, [ColorSymbol.YELLOW]);
          }
        },
      },
    },
  },
  adjustDamageReceived: (permanent, damage, attacker, ctx) => {
    const { inspector, engine, ai } = ctx;
    if (!attacker) {
      return damage;
    }
    if (Math.abs(attacker.slot.row - permanent.slot.row) > 6) {
      engine.announce(
        `${inspector.getCardDisplayName(
          attacker
        )} failed to damage ${inspector.getCardDisplayName(
          permanent
        )} because it was too far.`
      );
      return 0;
    }
    return damage;
  },
  disableP2NoBasesLossCondition: true,
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;
    // ai loses iff no permanents
    return (
      player === Player.P2 && inspector.getPermanentsOf(player).length === 0
    );
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;

    // first turn: spawn a bunch of stuff
    if (inspector.getTurnNumber() === 0) {
      for (let row = 0; row <= 6; row++) {
        for (let column = 0; column <= 4; column++) {
          ai.trySpawn("bee-rider", { row, column });
        }
      }
      return;
    }

    // try spawning horse riders next to yurts
    inspector
      .getPermanentsOf(Player.P2)
      .filter((permanent) => inspector.getSharedEffects(permanent).isBase)
      .forEach((yurt) => {
        const { row, column } = yurt.slot;
        for (let dr = -1; dr <= 1; dr++) {
          for (let dc = -1; dc <= 1; dc++) {
            ai.trySpawn("bee-rider", { row: row + dr, column: column + dc });
          }
        }
      });

    // permanents sorted from bottom to top
    inspector
      .getPermanents({
        owner: Player.P2,
        type: CardType.CREATURE,
        sort: [(permanent) => -permanent.slot.row],
      })
      .forEach((permanent) => {
        const { row, column } = permanent.slot;
        // try move down
        ai.tryActivateAbility(permanent, AbilityType.FLEX, [
          {
            type: EffectOptType.SLOT,
            slot: { row: row + 1, column },
          },
        ]);
        // try move down (again)
        ai.tryMoveRel(permanent, 1, 0);
        // if still ready, try attacking something near
        ai.tryAttackFirst(permanent, {
          owner: Player.P1,
          notProtectedFrom: permanent,
          custom: (target) =>
            getSlotsEuclideanDistance(permanent.slot, target.slot) < 2,
        });
      });
  },
};
