import { CursorEvent, CursorEventType } from "engine/types/presence";

export const safeParseCursorEvent = (
  event: ReturnType<typeof JSON.parse>
): CursorEvent | null => {
  if (typeof event !== "object" || typeof event.type !== "string") return null;
  switch (event.type) {
    case CursorEventType.REMOVE: {
      return event;
    }
    case CursorEventType.MOVE:
    case CursorEventType.CLICK: {
      const { rX, rY } = event;
      if (typeof rX !== "number" || typeof rY !== "number") return null;
      return event;
    }
  }
  return null;
};
