import {
  Card,
  Permanent,
  Slot,
  Player,
  DevKnobs,
  GamePhase,
  GameState,
} from "engine/types/game-state";
import { Counter } from "engine/types/counters";
import {
  Role,
  UpdateType,
  Update,
  UpdateDrawCard,
  UpdateRemoveFromDrawPile,
} from "engine/types/updates";
import { GemColor } from "engine/types/card-data";
import { WSErrorCode, WSError } from "game-server/ws";
import { UpdatesMulticaster } from "game-server/UpdatesMulticaster";
import { Keyframe } from "./types/keyframes";

/**
 * Controller is a thin utility wrapper around UpdatesMulticaster.
 * It handles raw Update creation, as well as ensuring that the
 * right broadcastAndApply functions are called for asymmetric updates.
 *
 * Each Controller function should generate at most a single update.
 */
export class Controller {
  multicaster: UpdatesMulticaster;

  constructor(multicaster: UpdatesMulticaster) {
    this.multicaster = multicaster;
  }

  stepAck(roomMemberId: string, err?: WSError) {
    this.multicaster.send(roomMemberId, {
      type: UpdateType.STEP_ACK,
      err,
    });
  }

  respSuccess(roomMemberId: string) {
    this.stepAck(roomMemberId);
  }

  respInvalidStep(roomMemberId: string) {
    this.stepAck(roomMemberId, { errCode: WSErrorCode.INVALID_STEP });
  }

  advancePhase(phase: GamePhase) {
    this.broadcastAndApply({
      type: UpdateType.ADVANCE_PHASE,
      phase,
    });
  }

  startTurn(player: Player) {
    this.broadcastAndApply({
      type: UpdateType.START_TURN,
      player: player,
      startTurnTime: new Date().getTime(),
    });
  }

  endTurn(player: Player) {
    this.broadcastAndApply({
      type: UpdateType.END_TURN,
      player: player,
      endTurnTime: new Date().getTime(),
    });
  }

  setDrawPile(player: Player, drawPile: ReadonlyArray<Card>) {
    // TODO: This should error out if it is ever used while clients
    // are connected, since players should not have access to even
    // their own draw pile. If we ever really need to use this
    // while users are connected, make sure to only send the
    // pile size/top card to clients, and the full draw pile only
    // to the reducer.
    this.broadcastAndApply({
      type: UpdateType.SET_DRAW_PILE,
      player,
      drawPile,
    });
  }

  setDevKnobs(devKnobs: DevKnobs) {
    this.broadcastAndApply({
      type: UpdateType.SET_DEV_KNOBS,
      devKnobs: devKnobs,
    });
  }

  addGems(player: Player, gems: GemColor[]) {
    this.broadcastAndApply({
      type: UpdateType.ADD_GEMS,
      player,
      gems,
    });
  }

  removeGems(player: Player, gems: GemColor[]) {
    this.broadcastAndApply({
      type: UpdateType.REMOVE_GEMS,
      player,
      gems,
    });
  }

  clearGems(player: Player) {
    this.broadcastAndApply({
      type: UpdateType.CLEAR_GEMS,
      player,
    });
  }

  /**
   * Create a permanent on the board. This just adds the permanent to
   * the board and does not trigger any effects.
   */
  createPermanent(permanent: Permanent, handCardId?: string) {
    // This function does not propagate in-game effects that care about
    // summoning, e.g. on-summon effects. To do those things, call
    // Engine.summonPermanent instead.
    this.broadcastAndApply({
      type: UpdateType.CREATE_PERMANENT,
      handCardId,
      permanent: JSON.parse(JSON.stringify(permanent)) as Permanent,
    });
  }

  readyPermanent(permanentId: string) {
    this.broadcastAndApply({
      type: UpdateType.READY_PERMANENT,
      permanentId,
    });
  }

  unreadyPermanent(permanentId: string) {
    this.broadcastAndApply({
      type: UpdateType.UNREADY_PERMANENT,
      permanentId,
    });
  }

  changePermanentOwner(permanentId: string, owner: Player) {
    this.broadcastAndApply({
      type: UpdateType.CHANGE_PERMANENT_OWNER,
      permanentId,
      owner,
    });
  }

  transformPermanent(permanentId: string, newCardName: string) {
    this.broadcastAndApply({
      type: UpdateType.TRANSFORM_PERMANENT,
      permanentId,
      newCardName,
    });
  }

  move(permanentId: string, slot: Slot) {
    this.broadcastAndApply({
      type: UpdateType.MOVE,
      permanentId,
      slot,
    });
  }

  swap(permanent1Id: string, permanent2Id: string) {
    this.broadcastAndApply({
      type: UpdateType.SWAP,
      permanent1Id,
      permanent2Id,
    });
  }

  drawCard(
    player: Player,
    card: Card,
    handCardId: string,
    nextTopCard?: Card | null,
    shouldPopDrawPile?: boolean
  ) {
    const fullUpd: UpdateDrawCard = {
      type: UpdateType.DRAW_CARD,
      player,
      card,
      handCardId,
      nextTopCard,
      shouldPopDrawPile: shouldPopDrawPile ?? true, // pop from draw pile by default
    };
    this.broadcastAndApplyAsymmetric(player, fullUpd, {
      ...fullUpd,
      nextTopCard: undefined,
      card: {},
    });
  }

  removeFromDrawPile(
    player: Player,
    drawPileIndex: number,
    nextTopCard?: Card | null
  ) {
    const fullUpd: UpdateRemoveFromDrawPile = {
      type: UpdateType.REMOVE_FROM_DRAW_PILE,
      player,
      drawPileIndex,
      nextTopCard,
    };
    this.broadcastAndApplyAsymmetric(player, fullUpd, {
      ...fullUpd,
      nextTopCard: undefined,
    });
  }

  discardCard(player: Player, handCardId: string) {
    this.broadcastAndApply({
      type: UpdateType.DISCARD_CARD,
      player,
      handCardId,
    });
  }

  damagePermanent(permanentId: string, damage: number, attackerId?: string) {
    this.broadcastAndApply({
      type: UpdateType.DAMAGE_PERMANENT,
      permanentId,
      damage,
      attackerId,
    });
  }

  removePermanent(permanentId: string, discard?: boolean) {
    this.broadcastAndApply({
      type: UpdateType.REMOVE_PERMANENT,
      permanentId,
      discard,
    });
  }

  addCounter(permanentId: string, counter: Counter) {
    this.broadcastAndApply({
      type: UpdateType.ADD_COUNTER,
      permanentId,
      counter,
    });
  }

  removeCounter(permanentId: string, counterIndex: number) {
    this.broadcastAndApply({
      type: UpdateType.REMOVE_COUNTER,
      permanentId,
      counterIndex,
    });
  }

  setCounter(
    permanentId: string,
    counterIndex: number,
    counterVal?: number,
    explanation?: string
  ) {
    this.broadcastAndApply({
      type: UpdateType.SET_COUNTER,
      permanentId,
      counterIndex,
      counterVal,
      explanation,
    });
  }

  setKeyframe(keyframe: Keyframe) {
    this.broadcastAndApply({
      type: UpdateType.SET_KEYFRAME,
      keyframe,
    });
  }

  clearKeyframe() {
    this.broadcastAndApply({
      type: UpdateType.SET_KEYFRAME,
      keyframe: null,
    });
  }

  announce(message: string) {
    this.broadcastAndApply({
      type: UpdateType.ANNOUNCE,
      message,
    });
  }

  announceSummonPermanent(player: Player, cardName: string) {
    this.broadcastAndApply({
      type: UpdateType.ANNOUNCE_SUMMON_PERMANENT,
      player,
      cardName,
    });
  }

  announceMove(player: Player, cardName: string) {
    this.broadcastAndApply({
      type: UpdateType.ANNOUNCE_MOVE,
      player: player,
      cardName: cardName,
    });
  }

  announceAttackCard(cardName: string, damage: number, targetName: string) {
    this.broadcastAndApply({
      type: UpdateType.ANNOUNCE_ATTACK_CARD,
      cardName: cardName,
      target: targetName,
      damage: damage,
    });
  }

  overrideState(
    roomId: string,
    fullState: GameState,
    p1State: GameState,
    p2State: GameState
  ) {
    this.multicaster.broadcastPerRoleAndApply({
      [Role.GOD]: {
        type: UpdateType.OVERRIDE_STATE,
        roomId,
        state: fullState,
      },
      [Role.P1]: {
        type: UpdateType.OVERRIDE_STATE,
        roomId,
        state: p1State,
      },
      [Role.P2]: {
        type: UpdateType.OVERRIDE_STATE,
        roomId,
        state: p2State,
      },
    });
  }

  respOverrideState(roomMemberId: string, roomId: string, state: GameState) {
    this.multicaster.send(roomMemberId, {
      type: UpdateType.OVERRIDE_STATE,
      roomId,
      state: state,
    });
  }

  // Internal methods

  notifySystem(upd: Update): void {
    this.multicaster.notifySystem(upd);
  }

  broadcastAndApply(upd: Update): void {
    this.multicaster.broadcastAndApply(upd);
  }

  // Broadcast and apply an asymmetric update.
  // The full update `upd` is broadcast to all roles with full
  // information about `player`, while `updHidden` is broadcast
  // to all other roles. If `updHidden` is null, then nothing
  // is broadcast to other roles.
  broadcastAndApplyAsymmetric(
    player: Player,
    upd: Update,
    updHidden: Update | null
  ): void {
    this.multicaster.broadcastAndApplyAsymmetric(player, upd, updHidden);
  }
}
