import localforage from "localforage";
import ALL_PUZZLES from "engine/puzzles/all-puzzles-client";
import ALL_CARDS from "engine/cards/all-cards-client";
import ALL_MASTERIES from "engine/puzzles/all-masteries-client";
import ALL_KEYFRAMES from "engine/puzzles/all-keyframes-client";
import { PuzzlesDB } from "engine/puzzles/puzzle-data";
import { makeCardsDB } from "engine/cards/CardsDB";
import { makeMasteriesDB } from "engine/puzzles/mastery-data";
import { GameServer } from "game-server/GameServer";
import { MockBackendInterface } from "game-server/backend-interface/MockBackendInterface";
import { MockAuthenticationInterface } from "game-server/authentication-interface/MockAuthenticationInterface";

export type MockServerOpts = {
  dumpBackendRequests?: boolean;
};

/** The key that the mock DB is stored into in local storage. */
const LOCAL_FORAGE_KEY = "__game_mockdb";

let gameServer: GameServer | undefined = undefined;
let isForceResetPending = false;

const makeMockServer = async (
  opts: MockServerOpts,
  forceReset?: boolean
): Promise<GameServer> => {
  forceReset ??= false;

  if (gameServer === undefined || (forceReset && !isForceResetPending)) {
    // No localStorage in worker context, so we use IndexedDB.
    localforage.config({
      driver: localforage.INDEXEDDB,
    });

    if (forceReset) {
      isForceResetPending = true;
      await localforage.clear();
    }

    const prevGameServer = gameServer;

    const puzzlesDB = new PuzzlesDB(ALL_PUZZLES);
    const cardsDB = makeCardsDB(ALL_CARDS);
    const masteriesDB = makeMasteriesDB(ALL_MASTERIES);
    gameServer = new GameServer(
      puzzlesDB,
      cardsDB,
      masteriesDB,
      ALL_KEYFRAMES,
      {
        backend: new MockBackendInterface(
          puzzlesDB,
          cardsDB,
          masteriesDB,
          await localforage.getItem(LOCAL_FORAGE_KEY),
          async (db) => {
            await localforage.setItem(LOCAL_FORAGE_KEY, db);
          },
          !forceReset
        ),
        // We'll be dumping WS messages from the client side,
        // so don't dump them from the server side.
        dumpWsMessages: false,
        dumpBackendRequests: opts.dumpBackendRequests ?? false,
        periodicSyncInterval: 200, // 0.2s
        authInterface: new MockAuthenticationInterface(),
        // @ts-expect-error: Pretend all WSReqs are valid to skip typia
        // in offline mode.
        parseWSReq: (req) => true,
      }
    );
    await gameServer.initAsync();

    if (prevGameServer !== undefined) {
      prevGameServer.teardown();
    }

    if (forceReset) isForceResetPending = false;
  }
  return gameServer;
};
export default makeMockServer;
