import { AIContext, GameSpecServerFragment } from "engine/types/game-specs";
import { Permanent, Player } from "engine/types/game-state";

const round1Specs: GameSpecServerFragment = {
  beforeNewGame: (ctx) => {
    const { engine } = ctx;
    engine.spawn("hog-factory", { row: 0, column: 1 }, Player.P2);
    engine.spawn("log-factory", { row: 0, column: 3 }, Player.P2);
  },
  aiTurn: (ctx) => {
    if (ctx.inspector.getTurnNumber() === 0) {
      return;
    }
    spawnHogsAndLogs(ctx);
    performAttacksOrHogMoves(ctx);
  },
};
const round2Specs: GameSpecServerFragment = {
  beforeNewGame: (ctx) => {
    const { engine } = ctx;
    engine.spawn("hog-factory", { row: 0, column: 0 }, Player.P2);
    engine.spawn("log-factory", { row: 0, column: 4 }, Player.P2);
    engine.spawn("stacktory", { row: 0, column: 2 }, Player.P2);
  },
  aiTurn: (ctx) => {
    const bases = getAIBases(ctx);
    // Spawn.
    if (bases.length == 3) {
      spawnStacks(ctx);
    } else {
      spawnHogsAndLogs(ctx);
    }
    // Attack!
    if (ctx.inspector.getTurnNumber() === 0) {
      return;
    }
    performAttacksOrHogMoves(ctx);
  },
};
const round4Specs: GameSpecServerFragment = {
  beforeNewGame: (ctx) => {
    const { engine } = ctx;
    engine.spawn("stackstack-tory", { row: 0, column: 2 }, Player.P2);
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      return;
    }

    const stacktory = inspector.getPermanents({
      owner: Player.P2,
      name: "stackstack-tory",
    })[0];

    if (stacktory) {
      const col = stacktory.slot.column;
      for (const row of [0, 1, 2]) {
        const success = ai.trySpawn("hog-on-a-log-on-a", row, col);
        if (success) {
          break;
        }
      }
    }
    performAttacksOrHogMoves(ctx);
  },
};
const round3Specs: GameSpecServerFragment = {
  beforeNewGame: (ctx) => {
    const { engine } = ctx;
    engine.spawn("hog-factorytorytory", { row: 0, column: 0 }, Player.P2);
    engine.spawn("log-factorytorytory", { row: 0, column: 1 }, Player.P2);
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      return;
    }
    const aiUnitsInOrder = inspector.getPermanents({
      owner: Player.P2,
      sort: [(perm: Permanent) => 100 * perm.slot.row + perm.slot.column],
    });
    // spawn stuff
    for (const unit of aiUnitsInOrder) {
      const unitToSpawn = getFactoryOutput(unit.card.name);
      if (unitToSpawn) {
        let spawned = false;
        for (const slotToSpawn of emptyAiSlotsInOrder(ctx)) {
          const summonResolved = ai.trySpawn(unitToSpawn, slotToSpawn);
          if (summonResolved) {
            spawned = true;
            break;
          }
        }
        if (!spawned) {
          // Stop trying to spawn all other units
          break;
        }
      }
    }
    performAttacksOrHogMoves(ctx);
  },
};
export const Puzzle101Specs: GameSpecServerFragment[] = [
  round1Specs,
  round2Specs,
  round3Specs,
  round4Specs,
];

function hogAttackAI(unit: Permanent, ctx: AIContext) {
  // Boar Smart. Boar Attack down column.
  const success = ctx.ai.tryAttackFirst(unit, {
    column: unit.slot.column,
    sort: [{ type: "frontToBack", perspective: Player.P2 }],
  });
  if (success) {
    return true;
  }

  const attackableUnits = ctx.inspector.getPermanents({
    owner: Player.P1,
    notProtectedFrom: unit,
    sort: [(p: Permanent) => Math.abs(p.slot.column - unit.slot.column)],
  });

  if (attackableUnits[0]) {
    const colDelta = attackableUnits[0].slot.column > unit.slot.column ? 1 : -1;
    ctx.ai.tryMoveRel(unit, 0, colDelta);
  }

  return false;
}

function logAttackAI(unit: Permanent, ctx: AIContext) {
  return ctx.ai.tryAttackFirst(unit);
}

function attackAI(unit: Permanent, ctx: AIContext) {
  if ((unit.card.name || "").startsWith("hog")) {
    return hogAttackAI(unit, ctx);
  }
  return logAttackAI(unit, ctx);
}

function performAttacksOrHogMoves(ctx: AIContext) {
  const { inspector } = ctx;
  // hogs (or hog on x)s first, then logs
  const units = inspector.getPermanents({
    owner: Player.P2,
    sort: [
      (p) => ((p.card.name || "").startsWith("h") ? 0 : 1),
      (p) => -10 * p.slot.row + p.slot.column,
    ],
  });
  for (const unit of units) {
    if (inspector.getPower(unit) <= 0) {
      continue;
    }
    if (!unit.ready) {
      continue;
    }
    attackAI(unit, ctx);
  }
}

const BASES = new Set([
  "hog-factory",
  "log-factory",
  "stacktory",
  "stackstack-tory",
]);

function getAIBases(ctx: AIContext) {
  const { inspector } = ctx;
  return inspector
    .getPermanentsOf(Player.P2)
    .filter((p) => BASES.has(p.card.name || ""));
}

function* slotsInPreferenceOrder(ctx: AIContext) {
  const bases = getAIBases(ctx);
  const baseCols = new Set(bases.map((p) => p.slot.column));

  for (const row of [1, 2, 0]) {
    for (let column = 0; column <= 4; column++) {
      if (baseCols.has(column)) {
        yield { row, column };
      }
    }
  }
  for (const row of [1, 2, 0]) {
    for (let column = 0; column <= 4; column++) {
      if (!baseCols.has(column)) {
        yield { row, column };
      }
    }
  }
}

function* emptyAiSlotsInOrder(ctx: AIContext) {
  for (let row = 0; row < 3; row++) {
    for (let column = 0; column < 5; column++) {
      if (!ctx.inspector.isSlotOccupied({ row, column })) {
        yield { row, column };
      }
    }
  }
}

function getFactoryOutput(name: string | undefined) {
  switch (name) {
    case "hog-factory":
      return "hog";
    case "hog-factorytory":
      return "hog-factory";
    case "hog-factorytorytory":
      return "hog-factorytory";
    case "log-factory":
      return "log";
    case "log-factorytory":
      return "log-factory";
    case "log-factorytorytory":
      return "log-factorytory";
    default:
      return null;
  }
}

function spawnHogsAndLogs(ctx: AIContext) {
  const { inspector, ai } = ctx;
  const units = inspector.getPermanentsOf(Player.P2);
  let mustPlayHog = !!units.find((unit) => unit.card.name === "hog-factory");
  let mustPlayLog = !!units.find((unit) => unit.card.name === "log-factory");

  for (const slot of slotsInPreferenceOrder(ctx)) {
    if (!mustPlayHog && !mustPlayLog) {
      break;
    }
    const summonResolved = ai.trySpawn(mustPlayHog ? "hog" : "log", slot);
    if (summonResolved) {
      if (mustPlayHog) {
        mustPlayHog = false;
      } else {
        mustPlayLog = false;
      }
    }
  }
}

function spawnStacks(ctx: AIContext) {
  const { inspector, ai } = ctx;

  const turnNumber = inspector.getTurnNumber();
  const cardName = (turnNumber / 2) % 2 === 0 ? "hog-on-a-log" : "log-on-a-hog";

  for (const slot of slotsInPreferenceOrder(ctx)) {
    const summonResolved = ai.trySpawn(cardName, slot);

    if (summonResolved) {
      break;
    }
  }
}
