import { Player } from "engine/types/game-state";
import {
  UpdateType,
  Update,
  Role,
  canRoleControlPlayer,
} from "engine/types/updates";
import { RoomMember } from "game-server/Room";

// Any room activity with state shared between the client and server
// should never mutate its own state directly. Instead, it should
// formulate mutations in the form of Updates, which are both
// applied to the server's state, as well as broadcast to clients
// to apply to their local state. This ensures that server and client
// state always remains in sync.

export enum UpdateBroadcastSpecType {
  /** Update targets the game server system, rather than clients. */
  SYSTEM = "system",
  /** Only send to a specific room member ID. */
  ROOM_MEMBER_ID = "room_member_id",
  /** Send to all room members. */
  BROADCAST = "broadcast",
  /** Send to members of a given role. */
  ROLE = "role",
  /** Send to roles satisfying a given filter predicate. */
  ROLE_FILTER = "role_filter",
}

/** Spec for broadcasting an update to some subset of members in a room. */
export type UpdateBroadcastSpec = {
  upd: Update;
} & (
  | {
      type: UpdateBroadcastSpecType.SYSTEM;
    }
  | {
      type: UpdateBroadcastSpecType.ROOM_MEMBER_ID;
      roomMemberId: string;
    }
  | {
      type: UpdateBroadcastSpecType.BROADCAST;
    }
  | {
      type: UpdateBroadcastSpecType.ROLE;
      role: Role;
    }
  | {
      type: UpdateBroadcastSpecType.ROLE_FILTER;
      pred: (role: Role) => boolean;
    }
);

/**
 * All Updates should be sent through an UpdatesMulticaster. This
 * ensures that any Updates that get applied to server state also
 * gets broadcast to clients and applied to client state.
 *
 * While a room activity is in the process of applying a step (or
 * async task), UpdatesMulticaster accumulates all requested updates
 * in an update buffer. After processing the step, the room activity
 * uses readOutUpdates to grab the accumulated updates and clear the
 * buffer.
 */
export class UpdatesMulticaster {
  /**
   * Transient buffer to accumulate updates while in the process of
   * applying a step. It should always be empty except while the
   * room activity is in the process of applying a step.
   */
  updates: UpdateBroadcastSpec[];
  /**
   * Function to apply an update to the server's copy of the state.
   */
  applyUpdateFunc?: (upd: Update) => void;

  constructor() {
    this.updates = [];
  }

  setApplyUpdateFunc(applyUpdateFunc: (upd: Update) => void): void {
    this.applyUpdateFunc = applyUpdateFunc;
  }

  applyUpdate(upd: Update): void {
    if (this.applyUpdateFunc === undefined) {
      throw new Error("apply update func not initialized");
    }
    this.applyUpdateFunc(upd);
  }

  /** Emit a system update. */
  notifySystem(upd: Update): void {
    this.updates.push({
      type: UpdateBroadcastSpecType.SYSTEM,
      upd,
    });
  }

  /**
   * Send an update to only a specific room member. Such updates
   * should not affect shared state.
   */
  send(roomMemberId: string, upd: Update): void {
    this.updates.push({
      type: UpdateBroadcastSpecType.ROOM_MEMBER_ID,
      upd,
      roomMemberId,
    });
  }

  broadcastAndApply(upd: Update): void {
    // Note that since the broadcast only happens after all updates from
    // a step is accumulated, the order of these operations doesn't
    // actually matter.
    this.updates.push({
      type: UpdateBroadcastSpecType.BROADCAST,
      upd,
    });
    this.applyUpdate(upd);
  }

  /**
   * Broadcast and apply an asymmetric update.
   * The full update `upd` is broadcast to all roles with full
   * information about `player`, while `updHidden` is broadcast
   * to all other roles. If `updHidden` is null, then nothing
   * is broadcast to other roles.
   */
  broadcastAndApplyAsymmetric(
    player: Player,
    upd: Update,
    updHidden: Update | null
  ): void {
    this.applyUpdate(upd);
    this.updates.push({
      type: UpdateBroadcastSpecType.ROLE_FILTER,
      upd: upd,
      pred: (role) => canRoleControlPlayer(role, player),
    });
    if (updHidden != null) {
      this.updates.push({
        type: UpdateBroadcastSpecType.ROLE_FILTER,
        upd: updHidden,
        pred: (role) => !canRoleControlPlayer(role, player),
      });
    }
  }

  /**
   * Broadcast and apply an update that is different for every role.
   * The GOD role update is the one applied to the server state.
   */
  broadcastPerRoleAndApply(upds: { [role in Role]?: Update }): void {
    const fullUpd = upds[Role.GOD];
    if (fullUpd === undefined) throw new Error("full update not provided");
    this.applyUpdate(fullUpd);
    for (const [role, upd] of Object.entries(upds)) {
      this.updates.push({
        type: UpdateBroadcastSpecType.ROLE,
        upd,
        role: role as Role,
      });
    }
  }

  readOutUpdates(): UpdateBroadcastSpec[] {
    const updates = this.updates;
    // reset
    this.updates = [];

    return updates;
  }

  enableMember(roomMember: RoomMember): void {
    this.notifySystem({
      type: UpdateType.SYSTEM_ENABLE_MEMBER,
      roomMember,
    });
  }
}
