import { CounterType } from "engine/types/counters";
import { AbilityType } from "engine/types/effects";
import { Slot, Permanent, Player } from "engine/types/game-state";
import { StepType } from "engine/types/steps";
import { Speaker } from "engine/types/keyframes";
import { GameSpecServerFragment, AIContext } from "engine/types/game-specs";
import { PermanentEventType } from "engine/server-hooks";
import { specialThreatened } from "engine/game-specs/puzzle-9-shared";

type ExtraState = {
  lastKnownKeepSlot: Slot;
};

const UNIT_NAME_WHITE_PIECE = "vanilla-calf";
const UNIT_NAME_B = "bell-flower";
const UNIT_NAME_P = "petroleum-jelly";
const UNIT_NAME_Q = "quail-aboard-fiery-steeds";
const UNIT_NAME_R = "realistic-rex";

const unitAI: {
  [cardName: string]: (unit: Permanent, ctx: AIContext) => void;
} = {
  [UNIT_NAME_P]: (unit, ctx) => {
    const { inspector, engine, ai } = ctx;
    let here = unit.slot;
    let unitAttacked = false;
    for (const defender of inspector.getPermanentsOf(Player.P1)) {
      if (ai.tryAttack(unit, defender)) {
        unitAttacked = true;
        break;
      }
    }
    if (!unitAttacked) {
      ai.tryMoveRel(unit, 1, 0);
    }
    here = unit.slot;
    if (here.row == 5) {
      engine.despawn(unit);
      engine.spawn(UNIT_NAME_Q, here, Player.P2);
    }
  },
  [UNIT_NAME_WHITE_PIECE]: (unit, ctx) => {
    // Attack if possible, otherwise move toward center column, otherwise move forward
    const { inspector, ai } = ctx;
    ai.tryActivateAbility(unit, AbilityType.FLEX);
    for (const defender of inspector.getPermanentsOf(Player.P1)) {
      if (ai.tryAttack(unit, defender)) {
        return true;
      }
    }
    // Move toward center if possible, otherwise move forward
    if (ai.tryMove(unit, unit.slot.row, [1, 2, 2, 2, 3][unit.slot.column]))
      return true;
    if (ai.tryMoveRel(unit, 1, 0)) return true;
    return;
  },
  [UNIT_NAME_B]: (unit, ctx) => {
    // Attack if possible, otherwise move away from center column, otherwise move forward
    const { inspector, ai } = ctx;
    ai.tryActivateAbility(unit, AbilityType.FLEX);
    for (const defender of inspector.getPermanentsOf(Player.P1)) {
      if (ai.tryAttack(unit, defender)) {
        return true;
      }
    }
    // Move away from center if possible, otherwise move forward

    if (ai.tryMove(unit, unit.slot.row, [0, 0, 1, 4, 4][unit.slot.column]))
      return true;
    if (ai.tryMoveRel(unit, 1, 0)) return true;
    return;
  },
  [UNIT_NAME_R]: (unit, ctx) => {
    // Attack if possible, otherwise move toward center column, otherwise move forward
    // TODO: Refine this? Maybe try to line up against the enemy Keep specifically
    const { inspector, ai } = ctx;
    for (const defender of inspector.getPermanentsOf(Player.P1)) {
      if (ai.tryAttack(unit, defender)) {
        return true;
      }
    }
    // Move toward odd positions if possible, otherwise move backward

    if (ai.tryMove(unit, unit.slot.row, 3)) {
      return true;
    }
    if (ai.tryMoveRel(unit, -1, 0)) {
      return true;
    }
    return;
  },
  [UNIT_NAME_Q]: (unit, ctx) => {
    // Attack if possible, otherwise move toward center column, otherwise move forward
    // TODO: Refine this? Maybe try to line up against the enemy Keep specifically
    const { inspector, ai } = ctx;
    for (const defender of inspector.getPermanentsOf(Player.P1)) {
      if (ai.tryAttack(unit, defender)) {
        return true;
      }
    }
    // Move toward odd positions if possible, otherwise move backward

    if (ai.tryMove(unit, unit.slot.row, 3)) {
      return true;
    }
    if (ai.tryMoveRel(unit, -1, 0)) {
      return true;
    }
    return;
  },
};

export const Puzzle9Spec: GameSpecServerFragment = {
  counterData: {
    [CounterType.BODY_SLAM]: {
      handlers: {
        [PermanentEventType.AFTER_DESTROY_UNIT]: (event) => {
          const { permanent, defender, ctx } = event;
          const { engine } = ctx;
          engine.move(permanent, defender.slot);
        },
      },
    },
  },

  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();
    const units = inspector.getPermanentsOf(Player.P2);
    if (turnNumber == 0) {
      for (let c = 0; c < 5; c++) {
        ai.trySpawn(UNIT_NAME_P, 1, c);
      }
      ai.speak(
        "Behold my confounding moo-gicks! Your Units won’t behave the same as normal here!"
      );
      return;
    }
    // act with all present units
    for (const unit of units) {
      const unitAIFunc = unitAI[inspector.getCardName(unit)];
      if (unitAIFunc) {
        unitAIFunc(unit, ctx);
      }
    }
    // TODO: Spawn more units on certain turns
    switch (turnNumber) {
      case 4: // After player's second turn
        for (let c = 1; c < 4; c++) {
          ai.trySpawn(UNIT_NAME_WHITE_PIECE, 1, c);
        }
        break;
      case 8: // After the player's fourth turn
        for (const col of [0, 4]) {
          for (const row of [0, 1]) {
            ai.trySpawn(UNIT_NAME_B, row, col);
          }
        }
        break;
      case 12: // After the player's sixth turn
        for (let c = 0; c < 5; c++) {
          ai.trySpawn(UNIT_NAME_P, 1, c);
        }
        break;
      case 14: // After the player's seventh turn
        for (let c = 0; c < 5; c++) {
          if (ai.trySpawn(UNIT_NAME_R, 0, c)) {
            break;
          }
        }
        for (let c = 4; c >= 0; c--) {
          if (ai.trySpawn(UNIT_NAME_R, 0, c)) {
            break;
          }
        }
        break;
      default:
        // Every three turns starting after the player's ninth turn
        if (turnNumber >= 18 && turnNumber % 6 == 0) {
          for (let c = 0; c < 5; c++) {
            ai.trySpawn(UNIT_NAME_P, 1, c);
            ai.trySpawn(
              [UNIT_NAME_R, UNIT_NAME_B, UNIT_NAME_Q, UNIT_NAME_B, UNIT_NAME_R][
                c
              ],
              0,
              c
            );
          }
        }
    }
    return true;
  },
  afterSummon: (permanent, ctx) => {
    const { inspector, engine } = ctx;
    if (
      inspector
        .getCardName(permanent)
        .toLowerCase()
        .match(/^[kqrbnp].*/)
    ) {
      engine.adjustPermanentStats(permanent, 1, 0);
      engine.addCounter(permanent, { type: CounterType.BODY_SLAM });
    }
  },
  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      lastKnownKeepSlot: { row: 0, column: 2 },
    });
  },
  afterStep: (step, ctx) => {
    const { inspector, engine } = ctx;
    const ourKeep = inspector
      .getPermanentsOf(Player.P2)
      .filter(
        (permanent) => inspector.getSharedEffects(permanent).isBase ?? false
      )[0];
    if (!ourKeep) {
      return; // this is the step that killed it!
    }
    const ext = engine.getExtraState() as ExtraState;
    ext.lastKnownKeepSlot = ourKeep.slot;

    // check for player pawn promotion
    if (
      step.type == StepType.MOVE &&
      step.slot.row == 0 &&
      step.permanent.owner == Player.P1 &&
      (inspector.getCardName(step.permanent).toLowerCase().match(/^p.*/) ??
        false)
    ) {
      engine.despawn(step.permanent);
      engine.spawn(UNIT_NAME_Q, step.slot, Player.P1);
    } else if (
      step.type == StepType.ATTACK &&
      step.attacker.owner == Player.P1 &&
      step.attacker.slot.row == 0 &&
      (inspector.getCardName(step.attacker).toLowerCase().match(/^p.*/) ??
        false)
    ) {
      engine.despawn(step.attacker);
      engine.spawn(UNIT_NAME_Q, step.attacker.slot, Player.P1);
    }
  },
  extraSolveChecks: (player, winner, ctx) => {
    const { inspector, engine } = ctx;
    const ext = engine.getExtraState() as ExtraState;
    const slot = ext.lastKnownKeepSlot;

    // now check all 8-adjacent things of slot
    for (const dr of [-1, 0, 1]) {
      for (const dc of [-1, 0, 1]) {
        if (dr === 0 && dc === 0) {
          continue;
        }
        const adjSlot = { row: slot.row + dr, column: slot.column + dc };
        if (!inspector.isSlotInBounds(adjSlot)) {
          continue;
        }
        const blocker = inspector.getPermanentsAt(adjSlot);
        if (blocker.length > 0) {
          if (blocker[0].owner == Player.P2) {
            // King blocked by ally
            continue;
          }
        }
        // TODO: If there is a Player.P1 piece blocking the space, we should
        // check if the King can safely take it -- which is to say, the King can
        // capture the piece without taking damage, and the piece is not
        // guarded. The latter check just falls down to the logic below, but the
        // check on whether the King can actually capture gets weird (weirder
        // than a straight power-vs-health check) when random defensive
        // abilities come into play.

        // fake permanent, so we can reuse logic
        // TODO: Make specialThreatened take in slots instead.
        // Backdoor access to engine.engine here is a hack and is
        // not supported.
        const defender = engine
          .getBackdoorEngine()
          .cardToPermanent({ name: UNIT_NAME_P }, Player.P2, adjSlot);

        let foundAttacker = false;
        for (const attacker of inspector.getPermanentsOf(Player.P1)) {
          if (specialThreatened(attacker, defender, ctx)) {
            foundAttacker = true;
            break;
          }
        }

        if (!foundAttacker) {
          return false;
        }
      }
    }

    return true;
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine, ai } = ctx;
    if (!(solves[Player.P1] ?? false) && winner == Player.P1) {
      ai.speak(
        "As you raised the uwuand, Blancmange slid away! It seems you failed to befriend it. How puzzling!",
        {
          hideBattler: true,
          speaker: Speaker.NARRATOR,
        }
      );
    } else {
      engine.clearKeyframe();
    }
  },
};
