export type OnAbilityHook = (
  step: ActivateAbilityStepResolved,
  ctx: EngineContext
) => void;

/** Card effects that the server knows. */
export type CardEffects = CardEffectsShared & {
  // These are split up to make the effects spec shorter.
  onRepeatable?: OnAbilityHook;
  onSpecial?: OnAbilityHook;
  /**
   * Counter data for counters specifically defined for the card.
   * Counters can also be defined under COMMON_COUNTER_DATA, e.g.
   * for counters that are used by multiple effects.
   */
  counterData?: { [type in CounterType]?: CounterData };
  /**
   * Adjust the damage received by permanent.
   * The damage may optionally be attributed to an attacker.
   * This may generate updates, e.g. to track adjustments applied,
   * but MUST NOT perform any game state updates.
   */
  adjustDamageReceived?: (
    permanent: Permanent,
    damage: number,
    attacker: Permanent | undefined,
    ctx: EngineContext
  ) => number;
  /**
   * If permanent would get damaged, run this hook instead.
   * If this hook returns true, the permanent gets damaged anyway.
   * If this hook returns false or does not return, the permanent
   * does not get damaged.
   */
  insteadOfDamaged?: (
    permanent: Permanent,
    damage: number,
    attacker: Permanent | undefined,
    ctx: EngineContext
  ) => boolean | undefined;
  /**
   * Actions to run in response to game events.
   */
  handlers?: PermanentEventHandlers;
};

export type EffectsDB = { [cardName: string]: CardEffects };

export const thornsHandler = (ev: DamagedPermanentEvent) => {
  const { permanent, attacker, ctx } = ev;
  const { inspector, engine } = ctx;
  // Attack back only if the damage comes from an attacker.
  if (attacker) {
    engine.announce(
      `${inspector.getCardDisplayName(permanent)} attacks back for 1 damage!`
    );
    // Do not attribute damage to prevent triggering other card effects.
    engine.damageUnattributed(attacker, 1);
  }
};

type PowerHealthBuff = { power?: number; health?: number };

const buffPermanent = (
  target: Permanent,
  buff: PowerHealthBuff,
  source: Permanent,
  ctx: EngineContext
) => {
  const { inspector, engine } = ctx;
  const power = buff.power || 0;
  const health = buff.health || 0;
  const msgParts = [];
  if (power) msgParts.push(`+${power} power`);
  if (health) msgParts.push(`+${health} health`);
  engine.announce(
    `${inspector.getCardDisplayName(
      source
    )} buffs ${inspector.getCardDisplayName(target)} with ${msgParts.join(
      "/"
    )}!`
  );

  engine.adjustPermanentStats(target, power, health);
};

// buff adjacent cards to a permanent
const buffAdjacentHandler = (
  buff: PowerHealthBuff,
  condition?: (permanent: Permanent, ctx: EngineContext) => boolean
) => {
  return (ev: CreatedPermanentEvent) => {
    const { permanent, ctx } = ev;
    ctx.inspector
      .getAdjacentFriendlyPermanents(permanent)
      .forEach((adjacentPermanent) => {
        if (condition && !condition(adjacentPermanent, ctx)) {
          return;
        }
        buffPermanent(adjacentPermanent, buff, permanent, ctx);
      });
  };
};

/**
 * Counter data for counters used by game specs or shared by
 * multiple card effects. If a counter is defined and used only
 * for a specific card, it can be defined as part of the card
 * effect's counterData property and will be collated with these
 * counters to form the final counters database.
 */
export const COMMON_COUNTER_DATA: { [type in CounterType]?: CounterData } = {
  [CounterType.BASE_GEMS]: {
    handlers: {
      [PermanentEventType.START_TURN]: (event) => {
        const { permanent, counter, ctx } = event;
        const { inspector, engine } = ctx;
        if (counter === undefined) {
          throw new Error("expect counter in counter event");
        }
        if (counter.val === undefined) {
          throw new Error("counter should have value");
        }
        if (inspector.isTurnPending(permanent.owner)) {
          engine.addGems(
            permanent.owner,
            new Array(counter.val).fill(ColorSymbol.RAINBOW)
          );
        }
        return { expire: false };
      },
    },
  },
  [CounterType.POISON]: {
    handlers: {
      [PermanentEventType.START_TURN]: (event) => {
        const { permanent, counter, ctx } = event;
        const { inspector, engine } = ctx;
        if (counter === undefined) {
          throw new Error("expect counter in counter event");
        }
        if (counter.val === undefined) {
          throw new Error("poison counter should have value");
        }

        // deal poison damage to the owning permanent on the start of its turn
        if (inspector.isTurnPending(permanent.owner)) {
          engine.damageUnattributed(permanent, counter.val);
        }
      },
    },
  },
  // basic doom counter decrements by 1 each turn
  // should handle val = 0 on a per-case basis
  [CounterType.DOOM]: {
    handlers: {
      [PermanentEventType.END_TURN]: (event) => {
        const { permanent, counter, counterIndex, ctx } = event;
        const { engine, inspector } = ctx;
        if (!inspector.isTurnPending(permanent.owner)) {
          // Only apply on unit owner's turn
          return;
        }
        if (counter == null || counterIndex == null) {
          throw new Error("counter event should have counter specified");
        }
        // decrement
        const newVal = (counter.val || 0) - 1;

        if (newVal <= 0) {
          // implementer note, per-case basis: handle the behavior and remove counter manually on END_TURN
          if (counter.val !== 0) {
            engine.updateCounterVal(permanent, counterIndex, 0);
          }
        } else {
          engine.updateCounterVal(permanent, counterIndex, newVal);
        }
      },
    },
  },
  [CounterType.DESTROY_ON_END_OF_TURN]: {
    handlers: {
      [PermanentEventType.END_TURN]: (event) => {
        const { permanent, ctx } = event;
        const { engine } = ctx;
        engine.destroy(permanent, permanent);
      },
    },
  },
};

// Utility functions to quickly extract opt values.
// These should already be validated during the validation phase.
export const extractOptPermanent = (
  step: ActivateAbilityStepResolved,
  index: number
): Permanent => {
  const opt = step.effectOptsResolved[index];
  if (opt === undefined || opt.type !== EffectOptType.PERMANENT) {
    throw new Error("expected permanent opt");
  }
  return opt.permanent;
};
const extractOptSlot = (
  step: ActivateAbilityStepResolved,
  index: number
): Slot => {
  const opt = step.effectOptsResolved[index];
  if (opt === undefined || opt.type !== EffectOptType.SLOT) {
    throw new Error("expected slot opt");
  }
  return opt.slot;
};
export const extractOptChoiceString = (
  step: ActivateAbilityStepResolved,
  index: number
): string => {
  const opt = step.effectOptsResolved[index];
  if (opt === undefined || opt.type !== EffectOptType.ONE_CHOICE) {
    throw new Error("expected one-choice string opt");
  }
  return opt.choice;
};

/** Common ability hook that just performs an attack. */
const onAbilityAttack: OnAbilityHook = (step, opts) => {
  const { engine } = opts;
  const target = extractOptPermanent(step, 0);

  engine.attack(step.permanent, target);
};

