// --- Programmatic addition of cards for puzzles. ---

// Puzzle 51
for (const cardName of PUZZLE_51_CARDS) {
  COMMON_CARD_EFFECTS[cardName.toLocaleLowerCase().replace(/ /g, "-")] = {
    onRepeatable: (step, ctx) => {
      const { engine } = ctx;
      engine.despawn(step.permanent);
    },
  };
}

/** Merges auxEffectsDB into effectsDB. */
const mergeEffectsDB = (
  effectsDB: EffectsDB,
  auxEffectsDB?: {
    [cardName: string]: CardEffectsShared | CardEffects;
  }
): void => {
  if (auxEffectsDB === undefined) return;
  for (const [cardName, auxEffects] of Object.entries(auxEffectsDB)) {
    effectsDB[cardName] = {
      ...(effectsDB[cardName] ?? {}),
      ...auxEffects,
    };
  }
};

export const getEffectsDB = (
  allSpecs: ReadonlyArray<GameSpec>,
  cardsDB: CardsDB
): EffectsDB => {
  const effectsDB: EffectsDB = {};
  for (const spec of allSpecs) {
    mergeEffectsDB(effectsDB, spec.cardEffects);
    mergeEffectsDB(effectsDB, spec.makeCardEffectsShared?.(cardsDB));
    mergeEffectsDB(effectsDB, spec.cardEffectsShared);
  }
  return effectsDB;
};

export type CountersDB = { [type in CounterType]?: CounterData };

/**
 * Construct the counters DB from the game spec and effectsDB.
 * The effectsDB should already contain the effects defined in
 * the game spec.
 */
export const getCountersDB = (
  allSpecs: ReadonlyArray<GameSpec>,
  effectsDB: EffectsDB
): CountersDB => {
  const countersDB: CountersDB = {};
  for (const spec of allSpecs) {
    if (spec.counterData !== undefined) {
      Object.assign(countersDB, spec.counterData);
    }
  }
  for (const [, effects] of Object.entries(effectsDB)) {
    if (effects.counterData) {
      for (const [counterType, counterData] of Object.entries(
        effects.counterData
      )) {
        countersDB[counterType as CounterType] = counterData;
      }
    }
  }
  return countersDB;
};
