import { Player } from "engine/types/game-state";
import { CounterType } from "engine/types/counters";
import { StepType } from "engine/types/steps";
import { GameSpecServerFragment } from "engine/types/game-specs";

export const Tutorial2Spec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  beforeNewGame: (ctx) => {
    const { inspector, engine, ai } = ctx;
    ai.setKeyframe("tutorial2-start-1");
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    if (inspector.getTurnNumber() === 0) return;

    const enemyUnits = inspector.getPermanentsOf(Player.P2);
    for (const enemyUnit of enemyUnits) {
      ai.tryAttackFirst(enemyUnit);
    }

    switch (inspector.getTurnNumber()) {
      case 2: {
        ai.setKeyframe("tutorial2-start-2");
        break;
      }
      case 4: {
        ai.trySpawn("hog", 1, 0);
        ai.trySpawn("hamlet", 2, 0);

        const hog = inspector.getPermanents({
          name: "hog",
        })[0];
        const hamlet = inspector.getPermanents({
          name: "hamlet",
        })[0];
        engine.addCounter(hog, { type: CounterType.CANNOT_MOVE });
        engine.addCounter(hog, { type: CounterType.CANNOT_CREATE });
        engine.addCounter(hamlet, { type: CounterType.CANNOT_MOVE });
        engine.addCounter(hamlet, { type: CounterType.CANNOT_CREATE });
        ai.setKeyframe("tutorial2-start-3");
        break;
      }
    }
  },
  beforeStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    if (
      step.type === StepType.ADVANCE_KEYFRAME &&
      step.keyframeId === "tutorial2-before-summon"
    ) {
      engine.clearKeyframe();
      engine.spawn("new-boarn", 4, 0, Player.P1);
      const playerUnit = inspector.getPermanents({
        name: "new-boarn",
      })[0];
      engine.addCounter(playerUnit, { type: CounterType.CANNOT_MOVE });
      engine.addCounter(playerUnit, { type: CounterType.CANNOT_CREATE });
    }
    return true;
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const { keyframe } = inspector.gameState;
    if (step.type === StepType.ATTACK && inspector.getTurnNumber() === 3) {
      ai.setKeyframe("tutorial2-end-turn-2");
    }
    if (
      inspector.getBases(Player.P2).length === 0 &&
      (keyframe === undefined || keyframe.id === "tutorial2-free-play")
    ) {
      // this triggers game win for player 1
      ai.setKeyframe("tutorial2-start-4");
    }
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine } = ctx;
    engine.clearKeyframe();
  },
};
