import { Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { CardType } from "engine/types/card-data";

export const Tutorial5Spec: GameSpecServerFragment = {
  beforeNewGame: (ctx) => {
    const { engine, ai } = ctx;
    engine.spawn("roe-doe-dendron", 1, 0, Player.P2);
    engine.spawn("flora-the-explora", 2, 2, Player.P2);
    engine.spawn("nepeta-legion", 1, 4, Player.P2);
    ai.setKeyframe("tutorial5-start");
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;

    const turnVal = inspector.getTurnNumber() / 2;
    // Don't do anything on the first turn
    if (turnVal == 0) return;

    const card = ["flora-the-explora", "roe-doe-dendron", "nepeta-legion"][
      (turnVal - 1) % 3
    ];

    // TODO: Tune this preference ordering better
    const rowPref = [1, 2, 0];
    const colPref = [1, 3, 2, 0, 4];

    // Play a random dryad if possible
    let cardPlayed = false;

    // Strong preference for playing in column 1 and 3
    for (let r = 0; r < 3; r++) {
      for (let c = 0; c < 2; c++) {
        const summonResolved = ai.trySpawn(card, rowPref[r], colPref[c]);
        if (summonResolved) {
          cardPlayed = true;
          break;
        }
      }
      if (cardPlayed) break;
    }

    // If columns 1 and 3 are unplayable, try playing in the other columns
    if (!cardPlayed) {
      for (let r = 0; r < 3; r++) {
        for (let c = 2; c < 5; c++) {
          const summonResolved = ai.trySpawn(card, rowPref[r], colPref[c]);
          if (summonResolved) {
            cardPlayed = true;
            break;
          }
        }
        if (cardPlayed) break;
      }
    }

    // basic attack
    const aiCreatures = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.CREATURE,
    });

    aiCreatures.forEach((creature) => {
      ai.tryAttackFirst(creature);
    });
  },
};
