import { CounterType } from "engine/types/counters";
import { Player } from "engine/types/game-state";
import { Speaker } from "engine/types/keyframes";
import {
  AIContext,
  EngineContext,
  GameSpecServerFragment,
} from "engine/types/game-specs";
import {
  PermanentEventType,
  StartTurnPermanentEvent,
} from "engine/server-hooks";
import {
  ASTEROID_1_NAME,
  ASTEROID_2_NAME,
  ASTEROID_3_NAME,
} from "./puzzle-82-shared";

const announceAsteroidLand = (ctx: AIContext, plural: boolean) => {
  const { ai } = ctx;
  ai.speak(
    `Good heavens! The ${
      plural ? "asteroids are" : "asteroid is"
    } here! Doom, doom to us all!`,
    { hideBattler: true, speaker: Speaker.DINO }
  );
};

const announceAsteroidTime = (ctx: AIContext, val: number) => {
  const { ai } = ctx;
  ai.speak(
    `The asteroid approches. ${val} ${
      val === 1 ? "turn remains" : "turns remain"
    }!\n${Array(val).fill("⏳").join(" ")}`,
    { speaker: Speaker.DINO }
  );
};

// basic battle: DPS race
export const Puzzle82_1Spec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  extraLoseChecks: loseCheck,
  beforeNewGame(ctx) {
    const { engine } = ctx;
    engine.spawn(ASTEROID_1_NAME, { row: 0, column: 2 }, Player.P2);
  },
  counterData: {
    [CounterType.DOOM]: {
      handlers: {
        [PermanentEventType.START_TURN]: makeDecrementer(asteroidExplanation),
      },
    },
  },
  cardEffects: {
    [ASTEROID_1_NAME]: {
      handlers: {
        [PermanentEventType.CREATED]: (event) => {
          event.ctx.engine.addCounter(event.permanent, {
            type: CounterType.DOOM,
            val: 11, // first tick immediately goes to val - 1
          });
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const perms = inspector.getPermanentsOf(Player.P2);
    const asteroidWithDoom = perms.find((p) =>
      inspector.doesPermanentHaveCounterType(p, CounterType.DOOM)
    );
    if (asteroidWithDoom) {
      announceAsteroidTime(
        ctx,
        inspector.getCounterValSum(asteroidWithDoom, CounterType.DOOM)
      );
      return;
    }
    const asteroid = perms.find((p) => p.card.name === ASTEROID_1_NAME);
    for (const perm of inspector.getPermanentsOf(Player.P1)) {
      if (asteroid) {
        engine.destroy(asteroid, perm);
      } else {
        engine.despawn(perm);
      }
    }
    announceAsteroidLand(ctx, false);
  },
};

