export const Puzzle82_3Spec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  extraLoseChecks: loseCheck,
  beforeNewGame(ctx) {
    const { engine } = ctx;
    engine.spawn(ASTEROID_3_NAME, { row: 0, column: 2 }, Player.P2);
  },
  counterData: {
    [CounterType.DOOM]: {
      handlers: {
        [PermanentEventType.START_TURN]: makeDecrementer(asteroidExplanation),
      },
    },
  },
  cardEffects: {
    [ASTEROID_3_NAME]: {
      handlers: {
        [PermanentEventType.CREATED]: (event) => {
          event.ctx.engine.addCounter(event.permanent, {
            type: CounterType.DOOM,
            val: 11, // first tick immediately goes to val - 1
          });
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { inspector, engine } = ctx;
    const perms = inspector.getPermanentsOf(Player.P2);
    const asteroidWithDoom = perms.find((p) =>
      inspector.doesPermanentHaveCounterType(p, CounterType.DOOM)
    );
    if (asteroidWithDoom) {
      announceAsteroidTime(
        ctx,
        inspector.getCounterValSum(asteroidWithDoom, CounterType.DOOM)
      );
      return;
    }
    const asteroid = perms.find((p) => p.card.name === ASTEROID_3_NAME);
    if (!asteroid) {
      return;
    }
    const power = inspector.getPower(asteroid);
    engine.removeDamage(asteroid, inspector.getBaseHealth(asteroid));
    const startingHealth = inspector.getHealth(asteroid);
    const adjustment = power - startingHealth;
    if (adjustment !== 0) {
      engine.adjustPermanentStats(asteroid, 0, adjustment);
    }
    let currentHealth = power;
    for (const perm of inspector.getPermanents({
      owner: Player.P1,
      sort: ["creaturesFirst", { type: "frontToBack", perspective: Player.P1 }],
    })) {
      const healthToRemove = inspector.getHealth(perm);
      if (healthToRemove > currentHealth) {
        engine.announce(`${perm.card.name} catches the Meaty Ore!`);
        engine.damage(perm, asteroid, healthToRemove);
        break;
      }
      engine.damage(perm, asteroid, healthToRemove);
      engine.adjustPermanentStats(asteroid, -healthToRemove, 0);
      engine.destroy(asteroid, perm);
      currentHealth -= healthToRemove;
    }
    if (inspector.getPermanents({ name: ASTEROID_3_NAME }).length !== 0)
      announceAsteroidLand(ctx, false);
  },
};

function loseCheck(player: Player, ctx: EngineContext): boolean {
  const { inspector } = ctx;

  // player is P2, the AI. Check for any units
  return player === Player.P2 && inspector.getPermanentsOf(player).length === 0;
}

function makeDecrementer(explanation: (val: number) => string) {
  return (event: StartTurnPermanentEvent) => {
    const { permanent, counter, counterIndex, ctx } = event;
    const { engine, inspector } = ctx;
    if (!inspector.isTurnPending(permanent.owner)) {
      // Only apply on unit owner's turn
      return;
    }
    if (counter == null || counterIndex == null) {
      throw new Error("counter event should have counter specified");
    }
    const newVal = (counter.val || 0) - 1;
    if (newVal <= 0) {
      return { expire: true };
    } else {
      engine.updateCounterVal(permanent, counterIndex, newVal, explanation);
    }
  };
}

function asteroidExplanation(val: number) {
  switch (val) {
    case 0:
      return "BOOM!!!!!!!";
    case 1:
      return "This asteroid lands in 1 turn!";
    default:
      return `This asteroid lands in ${val} turns.`;
  }
}

function asteroidAnnounce1(val: number) {
  switch (val) {
    case 0:
      return "BOOM!!!!!!!";
    case 1:
      return "The asteroid lands in 1 turn!";
    default:
      return `The asteroid lands in ${val} turns!`;
  }
}

function asteroidAnnounce2(val: number) {
  switch (val) {
    case 0:
      return "BOOM!!!!!!!";
    case 1:
      return "The asteroids land in 1 turn!";
    default:
      return `The asteroids land in ${val} turns!`;
  }
}
