import { Permanent, Player, Slot } from "engine/types/game-state";
import { AIContext, GameSpecServerFragment } from "engine/types/game-specs";
import { PermanentEventType } from "engine/server-hooks";
import { BASE1, BASE2, BOAR } from "./puzzle-109-shared";
import { CardType } from "engine/types/card-data";

export const Puzzle109Spec: GameSpecServerFragment = {
  cardEffects: {
    [BASE1]: {
      handlers: {
        [PermanentEventType.END_TURN]: (ev) => {
          const { engine, inspector } = ev.ctx;
          const { permanent } = ev;
          for (const otherP of inspector.getPermanents({
            column: permanent.slot.column,
            type: CardType.CREATURE,
          })) {
            engine.adjustPermanentStats(otherP, 1, 0);
          }
        },
      },
    },
    [BOAR]: {
      handlers: {
        [PermanentEventType.END_TURN]: (ev) => {
          const { engine } = ev.ctx;
          const { permanent } = ev;
          engine.adjustPermanentStats(permanent, 1, 0);
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { ai, engine, inspector } = ctx;
    if (inspector.getTurnNumber() === 0) {
      ai.speak(
        "What are y’all trespassers DOING on my FARM?\nGIT yer butts OUTTA here and keep yer hands off my dang BOARRIES!"
      );
      ai.trySpawn(BOAR, { row: 2, column: 2 });
      return;
    }

    const boar = inspector.getPermanents({ name: BOAR, owner: Player.P2 })[0];

    // boar is dead. sad :(
    if (!boar) {
      return;
    }
    const boarAtk = inspector.getPower(boar);

    const bases = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.STRUCTURE,
    });
    const hasTrain = !!bases.find((p) => p.card.name === BASE2);

    // Prioritize
    // 1: Units
    // 2: Invading units (in friendly territory)
    // 3: Killable
    // 4: Highest Power
    // 5: Closest, column-wise (mostly for if we must move)
    const attackableUnits = inspector.getPermanents({
      owner: Player.P1,
      notProtectedFrom: boar,
      sort: [
        "creaturesFirst",
        (p: Permanent) =>
          inspector.getTerrainOf(p.slot) === Player.P2 ? 0 : 1,
        (p: Permanent) => (inspector.getHealth(p) <= boarAtk ? 0 : 1),
        "-power",
        (p: Permanent) => Math.abs(p.slot.column - boar.slot.column),
      ],
    });

    if (hasTrain) {
      const victim = attackableUnits[0];
      if (!victim) {
        // um, how could this happen ???
        return;
      }
      if (inspector.getTerrainOf(victim.slot) === Player.P2) {
        // free to move anywhere! let's take a power boost, from our weakest farm.
        const farms = inspector.getPermanents({
          owner: Player.P2,
          name: BASE1,
          sort: ["health"],
        });
        let success = false;
        for (const farm of farms) {
          for (const row of [2, 1, 0]) {
            success = tryFreeMove(ctx, boar, { column: farm.slot.column, row });
            if (success) {
              break;
            }
          }
          if (success) {
            break;
          }
        }
        ai.tryAttack(boar, victim);
        return;
      }
      // Unit is on P1's terrain. Move to attack this unit!
      if (victim.slot.column !== boar.slot.column) {
        let success = false;
        for (const row of [2, 1, 0]) {
          success = tryFreeMove(ctx, boar, { column: victim.slot.column, row });
          if (success) {
            break;
          }
        }
      }
      ai.tryAttack(boar, victim);
    } else {
      // doesn't have train.
      // If we can attack without moving, let's do that.
      const attackableWithoutMovingUnit = attackableUnits.find(
        (p) =>
          p.slot.column === boar.slot.column ||
          inspector.getTerrainOf(p.slot) === Player.P2
      );
      if (attackableWithoutMovingUnit) {
        ai.tryAttack(boar, attackableWithoutMovingUnit);
        return;
      }
      // Move towards closest unit
      const victim = attackableUnits[0];
      if (!victim) {
        // um, how could this happen ???
        return;
      }
      const colDelta = victim.slot.column > boar.slot.column ? 1 : -1;
      ai.tryMoveRel(boar, 0, colDelta);
    }
  },
};

function tryFreeMove(ctx: AIContext, boar: Permanent, slot: Slot): boolean {
  const success = ctx.ai.tryMove(boar, slot);
  if (success) {
    ctx.engine.ready(boar);
  }
  return success;
}
