import { UpdatesMulticaster } from "game-server/UpdatesMulticaster";

//
// constants
//

const STARTING_HAND_SIZE = 4;

type CheckpointHandler = (engine: Engine, slots: number[]) => void;
type CutsceneChoiceHandler = (
  engine: Engine,
  keyframeId: string,
  dialogOptionIndex: number
) => void;

export class Engine {
  multicaster: UpdatesMulticaster;
  puzName: string;
  roomId: string;

  /** The primary game spec. */
  gameSpec: GameSpec;
  /** All the mods to apply, including the common spec and primary spec. */
  allSpecs: ReadonlyArray<GameSpec>;
  /** Function to get the card unlocks for player if solved. */
  getCardUnlocks: (player: Player) => string[];
  cardsDB: CardsDB;
  effectsDB: EffectsDB;
  countersDB: CountersDB;
  keyframes: Keyframe[];

  // This should be treated as readonly outside controller methods.
  // TODO: should this be removed in favor of inspector?
  gameState: EngineGameState;

  inspector: Inspector;
  controller: Controller;

  hooksApi: HooksEngineAPI;
  hooksAiApi: HooksAIAPI;
  hooksOutOfTurnAiApi: HooksOutOfTurnAIAPI;

  /**
   * Whether a checkpoint should be created once the current step
   * has finished processing.
   * This should only be transiently used while processing a step.
   */
  needCheckpoint: number[];
  checkpointHandler: CheckpointHandler;

  cutsceneChoiceHandler: CutsceneChoiceHandler;

  constructor(
    multicaster: UpdatesMulticaster,
    puzName: string,
    roomId: string,
    allSpecs: ReadonlyArray<GameSpec>,
    getCardUnlocks: (player: Player) => string[],
    cardsDB: CardsDB,
    effectsDB: EffectsDB,
    countersDB: CountersDB,
    keyframes: Keyframe[],
    teamData: { [player in Player]?: PlayerStateTeamData },
    deckSelections: { [player in Player]?: DeckSelection },
    checkpointHandler: CheckpointHandler,
    cutsceneChoiceHandler: CutsceneChoiceHandler,
    devKnobs?: DevKnobs,
    gameStateOpt?: MutableGameState
  ) {
    this.multicaster = multicaster;
    this.puzName = puzName;
    this.roomId = roomId;
    this.allSpecs = allSpecs;
    this.gameSpec = this.allSpecs[1];
    this.getCardUnlocks = getCardUnlocks;

    this.cardsDB = cardsDB;
    this.effectsDB = effectsDB;
    this.countersDB = countersDB;
    this.keyframes = keyframes;

    // Safety check that all card effects correspond to actual cards.
    // This would help catch problems with card renaming.
    for (const cardName of Object.keys(this.effectsDB)) {
      if (this.cardsDB[cardName] === undefined) {
        throw new Error(
          `card ${cardName} not found in cards database -- perhaps the card names in the deck sheet and code got out of sync, or a card name was misspelled.`
        );
      }
    }

    const gameState =
      gameStateOpt ??
      (() => {
        const gameState = makeInitGameState(this.puzName, this.gameSpec);
        for (const [kPlayer, playerTeamData] of Object.entries(teamData)) {
          const player = kPlayer as Player;
          gameState[player].teamData = playerTeamData;
        }
        gameState.devKnobs = devKnobs;
        gameState.engineOnly.deckSelections = deckSelections;
        return gameState;
      })();
    this.gameState = gameState;

    // The engine uses a separate Inspector from its Reducer
    // since it has access to all card and effects data.
    this.inspector = new Inspector(
      this.gameSpec,
      this.allSpecs,
      this.cardsDB,
      this.effectsDB,
      this.gameState
    );

    const reducer = new Reducer(this.gameSpec, this.allSpecs, gameState);
    this.multicaster.setApplyUpdateFunc((upd) => {
      reducer.applyUpdate(upd);
    });
    this.controller = new Controller(this.multicaster);

    this.hooksApi = new HooksEngineAPI(this);
    this.hooksAiApi = new HooksAIAPI(this);
    this.hooksOutOfTurnAiApi = new HooksOutOfTurnAIAPI(this.hooksAiApi);

    this.needCheckpoint = [];
    this.checkpointHandler = checkpointHandler;
    this.cutsceneChoiceHandler = cutsceneChoiceHandler;
  }

  /**
   * A separate init allows creating the Engine with a pre-defined
   * gameState, as required by backend-saved game loading.
   */
  init() {
    for (const spec of this.allSpecs) {
      if (spec.afterInitEngine) {
        spec.afterInitEngine(this.makeContext());
      }
    }

    this.startNewGame();

    if (!this.inspector.isGameActive())
      throw new Error("expect game to still be active after starting");
  }

  //
  // helpers
  //

  /**
   * Access point to allow game specs to trigger game behavior on specified
   * events.
   */
  makeContext(): EngineContext {
    return {
      inspector: this.inspector,
      engine: this.hooksApi,
      ai: this.hooksOutOfTurnAiApi,
    };
  }

  makeAIContext(): AIContext {
    return {
      ...this.makeContext(),
      ai: this.hooksAiApi,
    };
  }

  getEffects(arg: CardLike): CardEffects {
    const effects = this.effectsDB[this.inspector.getCardName(arg)];
    return effects ? effects : {};
  }

  /**
   * Get the engine-only portion of the game state.
   * Since this is not used by the client, we are allowed to
   * mutate it directly in the Engine.
   */
  getEngineOnly(): MutableEngineOnlyGameState {
    const engineOnly = this.gameState.engineOnly;
    if (engineOnly === undefined) {
      throw new Error("engine should have access to engine-only state");
    }
    return engineOnly;
  }

  /**
   * If the game should end or has ended, returns the game's winner.
   * Otherwise, returns null.
   */
  getWinner(): Player | null {
    const isLoser = (player: Player): boolean => {
      if (
        !(this.gameSpec.disableNoBasesLossCondition ?? false) &&
        !(
          player === Player.P1 &&
          (this.gameSpec.disableP1NoBasesLossCondition ?? false)
        ) &&
        !(
          player === Player.P2 &&
          (this.gameSpec.disableP2NoBasesLossCondition ?? false)
        ) &&
        this.inspector.getBases(player).length === 0
      )
        return true;
      if (
        this.gameSpec.extraLoseChecks &&
        this.gameSpec.extraLoseChecks(player, this.makeContext())
      ) {
        return true;
      }
      return false;
    };

    if (isLoser(Player.P2)) return Player.P1;
    if (isLoser(Player.P1)) return Player.P2;
    return null;
  }

  /**
   * Checks if the puzzle is solved, once the game has ended.
   */
  getIsSolved(player: Player, winner: Player): boolean {
    // Currently, only AI battles have a "solved" concept.
    if (!this.inspector.hasAI()) return false;
    if (player !== Player.P1) return false;
    if (!this.gameSpec.disableBattleWonSolvedCondition && winner !== player) {
      return false;
    }
    if (
      this.gameSpec.extraSolveChecks &&
      !this.gameSpec.extraSolveChecks(player, winner, this.makeContext())
    ) {
      return false;
    }
    return true;
  }

  /**
   * Turn a card to an initially-played permanent.
   * This has to go here since it might be overriden by a game spec.
   */
  cardToPermanent(card: Card, owner: Player, slot: Slot): Permanent {
    return {
      id: (this.getEngineOnly().nextPermanentId++).toString(),
      card,
      damage: 0,
      counters: [],
      // Cards are summoned unready by default ("summoning sickness").
      // In "always ready" mode, cards are summoned ready.
      ready: this.inspector.isAlwaysReady(),
      owner,
      slot,
    };
  }

  /** Draw a card from the top of player's deck. */
  drawCard(player: Player): void {
    const { topCard, nextTopCard } =
      this.inspector.getTopCardsInDrawPile(player);
    if (topCard !== null) {
      this.controller.drawCard(
        player,
        topCard,
        `${this.getEngineOnly().nextHandCardId++}`,
        nextTopCard === null ? undefined : nextTopCard
      );
    }
  }

  /**
   * Add a given card to the player's hand, without modifying
   * the draw pile.
   */
  addCardToHand(player: Player, card: Card): void {
    this.controller.drawCard(
      player,
      card,
      `${this.getEngineOnly().nextHandCardId++}`,
      undefined,
      false
    );
  }

  /** Remove a card by index from the player's deck. */
  removeCardIndex(player: Player, drawPileIndex: number): void {
    const drawPile = this.inspector.getDrawPile(player);
    this.controller.removeFromDrawPile(
      player,
      drawPileIndex,
      drawPileIndex === drawPile.length - 1 &&
        drawPile.length > 1 &&
        this.inspector.doesPlayerHaveMastery(player, SCRY_TOP_CARD_MASTERY_ID)
        ? drawPile[drawPile.length - 2]
        : undefined
    );
  }

  /** Initialize a new game. */
  startNewGame(): void {
    // Signal client to reset any client-only game state.
    // This does not affect the shared game state, since this is
    // taken care of by the override-state update on join.
    this.controller.broadcastAndApply({
      type: UpdateType.RESET,
    });

    const devKnobs = this.inspector.getDevKnobs();
    const noShuffle = devKnobs.noShuffle !== undefined && devKnobs.noShuffle;
    const shuffleDeck = (deck: string[]): void => {
      // Fisher-Yates shuffle
      for (let i = deck.length - 1; i >= 1; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [deck[j], deck[i]] = [deck[i], deck[j]];
      }
    };

    const loadouts: { [player in Player]?: LoadoutResolved } = {};
    for (const player of [Player.P1, Player.P2]) {
      const {
        deckCardList,
        priorityCard: deckPriorityCard,
      }: {
        deckCardList: string[];
        priorityCard?: string;
      } = (() => {
        const deckOverride = this.inspector.getDeckOverride(player);
        if (deckOverride !== null) return { deckCardList: [...deckOverride] };
        // If all cards are available, then there's no use setting a deck.
        if (this.inspector.isAllCardsAvailable()) return { deckCardList: [] };
        const deckSelection = this.getEngineOnly().deckSelections[player];
        if (deckSelection === undefined) {
          if (this.inspector.isNoDeckAllowed()) return { deckCardList: [] };
          throw new Error(`expect deck to be defined for ${player}`);
        }
        const { deck } = deckSelection;
        return {
          deckCardList: deckToCardList(deck),
          priorityCard: deck.priorityCard,
        };
      })();

      const hasPriorityCardMastery = this.inspector.doesPlayerHaveMastery(
        player,
        PRIORITY_CARD_MASTERY_ID
      );
      const priorityCard = hasPriorityCardMastery
        ? deckPriorityCard
        : undefined;
      const priorityCardIndex =
        priorityCard === undefined ? -1 : deckCardList.indexOf(priorityCard);
      if (priorityCardIndex !== -1) deckCardList.splice(priorityCardIndex, 1);

      if (!noShuffle) shuffleDeck(deckCardList);

      if (priorityCardIndex !== -1) {
        if (priorityCard === undefined)
          throw new Error(
            "expect there to be a priority card if we removed one"
          );
        deckCardList.push(priorityCard);
      }

      this.controller.setDrawPile(
        player,
        deckCardList.map((name) => ({
          name,
        }))
      );
    }

    for (const spec of this.allSpecs) {
      if (spec.beforeNewGame) {
        spec.beforeNewGame(this.makeContext());
      }
    }

    // Place bases.
    const width = this.inspector.getNumColumns();
    const height = this.inspector.getNumRows();
    for (const player of [Player.P1, Player.P2]) {
      const bases = this.inspector.getDefaultBases(player);
      if (bases.length > width) {
        throw new Error(
          "invalid number of bases (no more than one per column)"
        );
      }
      const baseColumns = this.inspector.getBasePositions(player, bases.length);
      if (baseColumns.length !== bases.length) {
        throw new Error(
          "the number of base positions specified does not equal the number of bases"
        );
      }
      for (let i = 0; i < bases.length; i++) {
        const slot = {
          row: this.inspector.getPlayerRowIndex(0, player),
          column: baseColumns[i],
        };
        this.spawnPermanent(bases[i], player, slot);
      }
    }

    // Draw initial hands.
    for (const player of [Player.P1, Player.P2]) {
      for (let i = 0; i < STARTING_HAND_SIZE; i++) {
        this.drawCard(player);
      }
    }

    this.getEngineOnly().hasGameStarted = true;

    // P2 goes first.
    this.startTurn(Player.P2);
  }

  endGame(
    winner: Player,
    loseReason: LoseReason,
    solves: { [player in Player]?: boolean },
    cardUnlocks: { [player in Player]?: string[] }
  ): void {
    this.controller.broadcastAndApply({
      type: UpdateType.SET_GAME_RESULT,
      winner,
      loseReason,
      solves,
      cardUnlocks,
      endTime: new Date().getTime(),
    });
    this.controller.advancePhase(GamePhase.ENDED);
    if (this.gameSpec.afterGameEnd) {
      this.gameSpec.afterGameEnd(winner, solves, this.makeContext());
    }
    const solvedTeamIds: string[] = [];
    const cardUnlocksData: TeamCard[] = [];
    for (const player of [Player.P1, Player.P2]) {
      if (solves[player] ?? false) {
        const teamId = this.gameState[player].teamData?.teamId;
        if (teamId === undefined)
          throw new Error("if puzzle is solved, expect a team to exist");
        solvedTeamIds.push(teamId);
      }
      for (const cardName of cardUnlocks[player] ?? []) {
        const teamId = this.gameState[player].teamData?.teamId;
        if (teamId === undefined)
          throw new Error(
            "if there are cards unlocked, expect a team to exist"
          );
        cardUnlocksData.push({
          teamId,
          cardName,
        });
      }
    }
    this.controller.notifySystem({
      type: UpdateType.SYSTEM_ROOM_COMPLETE,
      solvedTeamIds: solvedTeamIds,
      cardUnlocks: cardUnlocksData,
    });
  }

  destroyPermanent(permanent: Permanent, attacker?: Permanent): void {
    // can't be destroyed
    if (
      permanent !== attacker &&
      this.inspector.doesPermanentHaveCounterType(
        permanent,
        CounterType.INVULNERABLE
      )
    )
      return;

    this.handlePermanentEvent({
      type: PermanentEventType.BEFORE_DESTROYED,
      permanent,
      attacker,
      ctx: this.makeContext(),
    });
    if (attacker) {
      this.handlePermanentEvent({
        type: PermanentEventType.BEFORE_DESTROY_UNIT,
        permanent: attacker,
        defender: permanent,
        ctx: this.makeContext(),
      });
    }
    this.controller.removePermanent(permanent.id);
    if (attacker) {
      this.controller.broadcastAndApply({
        type: UpdateType.MODIFY_STATS,
        player: attacker.owner,
        diffDestroys: 1,
      });
      this.handlePermanentEvent({
        type: PermanentEventType.AFTER_DESTROY_UNIT,
        permanent: attacker,
        defender: permanent,
        ctx: this.makeContext(),
      });
    }
    this.handlePermanentEvent({
      type: PermanentEventType.AFTER_DESTROYED,
      permanent,
      attacker,
      ctx: this.makeContext(),
    });
    // Handle OTHER_UNIT_DESTROYED.
    for (const otherPermanent of this.inspector.getAllPermanents()) {
      if (otherPermanent.id === permanent.id) continue; // no self trigger
      this.handlePermanentEvent({
        type: PermanentEventType.OTHER_UNIT_DESTROYED,
        permanent: otherPermanent,
        destroyedPermanent: permanent,
        attacker,
        ctx: this.makeContext(),
      });
    }
  }

  /**
   * Apply damage to permanent. The damage may optionally be attributed
   * to an attacker, for card effects.
   */
  damagePermanent(
    permanent: Permanent,
    damage: number,
    attacker?: Permanent,
    /** Ignore the INVULNERABLE counter. */
    ignoreInvulnerability?: boolean
  ): void {
    const effects = this.getEffects(permanent);
    const effectsAdjust = effects.adjustDamageReceived;
    const insteadOfDamaged = effects.insteadOfDamaged;

    if (effectsAdjust) {
      damage = effectsAdjust(permanent, damage, attacker, this.makeContext());
    }

    for (const spec of this.allSpecs) {
      if (spec.adjustDamageReceived) {
        damage = spec.adjustDamageReceived(
          permanent,
          damage,
          attacker,
          this.makeContext()
        );
      }
    }

    if (damage <= 0) {
      // Return early if no damage is actually dealt.
      // This is relevant for card effects.
      return;
    }

    // can't be damaged
    if (
      !(ignoreInvulnerability ?? false) &&
      this.inspector.doesPermanentHaveCounterType(
        permanent,
        CounterType.INVULNERABLE
      )
    )
      return;

    // --- AFTER THIS POINT, SOME DAMAGE WOULD BE DEALT ---

    if (insteadOfDamaged) {
      const shouldDamageAnyway = insteadOfDamaged(
        permanent,
        damage,
        attacker,
        this.makeContext()
      );
      if (!shouldDamageAnyway) return;
    }

    // check for divine shield
    // divine shield pops on the first instance of damage
    if (
      this.inspector.doesPermanentHaveCounterType(
        permanent,
        CounterType.DIVINE_SHIELD
      )
    ) {
      this.removeCounterType(permanent, CounterType.DIVINE_SHIELD);
      return;
    }

    const undamagedShell = this.inspector.getUndamagedShell(permanent);
    const maxDamageReceivable =
      this.inspector.getHealth(permanent) + undamagedShell;
    // Prevent a permanent's health from going negative, even
    // instantaneously. This is necessary to prevent thorns
    // from recursing infinitely.
    const netDamageReceived = Math.min(damage, maxDamageReceivable);
    const shellDamage = Math.min(netDamageReceived, undamagedShell);
    if (shellDamage > 0) {
      this.controller.addCounter(permanent.id, {
        type: CounterType.SHELL_DAMAGE_TAKEN,
        val: shellDamage,
        explanation: `shell received ${shellDamage} damage`,
        expiry: PermanentEventType.START_TURN,
      });
    }
    const healthDamage = netDamageReceived - shellDamage;
    if (healthDamage > 0) {
      const isIndestructable = this.inspector.doesPermanentHaveCounterType(
        permanent,
        CounterType.CANNOT_BE_DESTROYED
      );
      const health = this.inspector.getHealth(permanent);
      this.controller.damagePermanent(
        permanent.id,
        isIndestructable ? Math.min(healthDamage, health - 1) : healthDamage,
        attacker?.id
      );
    }

    if (attacker) {
      this.controller.broadcastAndApply({
        type: UpdateType.MODIFY_STATS,
        player: attacker.owner,
        diffDamageDealt: netDamageReceived,
      });
      this.handlePermanentEvent({
        type: PermanentEventType.AFTER_DEAL_DAMAGE,
        permanent: attacker,
        damage: netDamageReceived,
        defender: permanent,
        ctx: this.makeContext(),
      });
    }
    this.handlePermanentEvent({
      type: PermanentEventType.AFTER_DAMAGED,
      permanent,
      damage: netDamageReceived,
      attacker,
      ctx: this.makeContext(),
    });

    if (!this.inspector.doesPermanentExist(permanent)) return;

    if (this.inspector.getHealth(permanent) <= 0) {
      this.destroyPermanent(permanent, attacker);
      return;
    }

    this.controller.addCounter(permanent.id, {
      type: CounterType.DAMAGE_TAKEN,
      val: netDamageReceived,
      expiry: PermanentEventType.START_TURN,
    });
  }

  /**
   * Remove (damage) from (permanent).
   * (damage) must be positive.
   * (permanent)'s health will not go above max health.
   * On-damage effects are not triggered, but after-damage-removed is.
   */
  removeDamageFromPermanent(permanent: Permanent, damage: number): void {
    // amount of damage that is actually removed, never more than current damage
    // eg. permanent at 3/5 health, permanent.damage = 2
    //     damage = 5, realDamage = min(2, 5) = 2
    const realDamage = Math.min(permanent.damage, damage);
    this.controller.damagePermanent(permanent.id, -realDamage);

    this.handlePermanentEvent({
      type: PermanentEventType.AFTER_DAMAGE_REMOVED,
      permanent,
      damage: realDamage,
      ctx: this.makeContext(),
    });
  }

  /**
   * If it doesn't exist, add a value counter to (permanent) with (val), (explanation(val)) and (expiry).
   * If it exists, increment the value of the existing counter by (val) and set the explanation to (explanation(new value))
   */
  mergeValueCounter(
    permanent: Permanent,
    counterType: ValueCounterType,
    val: number,
    explanation?: (val: number) => string,
    expiry?: PermanentEventType
  ): void {
    if (!this.inspector.doesPermanentExist(permanent)) return;

    for (const [idx, counter] of permanent.counters.entries()) {
      if (counter.type === counterType && counter.expiry === expiry) {
        this.controller.setCounter(
          permanent.id,
          idx,
          counter.val + val,
          explanation && explanation(counter.val + val)
        );
        return;
      }
    }

    this.controller.addCounter(permanent.id, {
      type: counterType,
      val: val,
      explanation: explanation && explanation(val),
      expiry,
    });
  }

  adjustPermanentStats(
    permanent: Permanent,
    powerDelta: number,
    healthDelta: number
  ): void {
    if (powerDelta !== 0) {
      this.mergeValueCounter(
        permanent,
        CounterType.POWER_ADJUSTMENT,
        powerDelta
      );
    }
    if (healthDelta !== 0) {
      this.mergeValueCounter(
        permanent,
        CounterType.MAX_HEALTH_ADJUSTMENT,
        healthDelta
      );
    }
    this.handlePermanentEvent({
      type: PermanentEventType.AFTER_STATS_CHANGED,
      permanent,
      powerDelta,
      healthDelta,
      ctx: this.makeContext(),
    });
  }

  /**
   * Remove the first counter of (CounterType) on (permanent).
   * Returns whether successful.
   */
  removeCounterType(permanent: Permanent, counterType: CounterType): boolean {
    for (const [idx, counter] of permanent.counters.entries()) {
      if (counter.type === counterType) {
        this.controller.removeCounter(permanent.id, idx);
        return true;
      }
    }
    return false;
  }

  // Attack defender with attacker.
  attack(attacker: Permanent, defender: Permanent): void {
    const power = this.inspector.getPower(attacker);
    this.damagePermanent(defender, power, attacker);
  }

  /** Unready a permanent as part of performing an action. */
  unreadyPermanentForAction(permanent: Permanent): void {
    // In "always ready" dev mode, don't unready after an action.
    if (this.inspector.isAlwaysReady()) return;
    this.controller.unreadyPermanent(permanent.id);
  }

  transformPermanent(permanent: Permanent, newCardName: string) {
    this.controller.transformPermanent(permanent.id, newCardName);
  }

  /**
   * Trigger all handlers in handlers listening on ev.
   */
  handlePermanentEventWithHandlers(
    ev: PermanentEvent,
    handlers: PermanentEventHandlers
  ): PermanentEventHandlerResult {
    switch (ev.type) {
      case PermanentEventType.CREATED: {
        const handler = handlers[PermanentEventType.CREATED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.BEFORE_DESTROYED: {
        const handler = handlers[PermanentEventType.BEFORE_DESTROYED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_DESTROYED: {
        const handler = handlers[PermanentEventType.AFTER_DESTROYED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.START_TURN: {
        const handler = handlers[PermanentEventType.START_TURN];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.END_TURN: {
        const handler = handlers[PermanentEventType.END_TURN];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.BEFORE_DESTROY_UNIT: {
        const handler = handlers[PermanentEventType.BEFORE_DESTROY_UNIT];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_DESTROY_UNIT: {
        const handler = handlers[PermanentEventType.AFTER_DESTROY_UNIT];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_DAMAGED: {
        const handler = handlers[PermanentEventType.AFTER_DAMAGED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_DAMAGE_REMOVED: {
        const handler = handlers[PermanentEventType.AFTER_DAMAGE_REMOVED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_STATS_CHANGED: {
        const handler = handlers[PermanentEventType.AFTER_STATS_CHANGED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.AFTER_DEAL_DAMAGE: {
        const handler = handlers[PermanentEventType.AFTER_DEAL_DAMAGE];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.OTHER_UNIT_DESTROYED: {
        const handler = handlers[PermanentEventType.OTHER_UNIT_DESTROYED];
        if (handler) return handler(ev);
        break;
      }
      case PermanentEventType.OTHER_UNIT_SPAWN: {
        const handler = handlers[PermanentEventType.OTHER_UNIT_SPAWN];
        if (handler) return handler(ev);
        break;
      }
      default: {
        throw new Error("unknown permanent event type");
      }
    }
  }

  /**
   * Take care of all custom event handlers arising from ev.
   */
  handlePermanentEvent(ev: PermanentEvent): void {
    const { permanent } = ev;

    // Fire handlers attached to the permanent itself.
    const handlers = this.getEffects(permanent).handlers;
    if (handlers !== undefined) {
      this.handlePermanentEventWithHandlers(ev, handlers);
    }

    // Fire handlers attached to counters on the permanent.
    // Be careful since removing counters changes the index.
    let counterIndex = 0;
    while (this.inspector.doesPermanentExist(permanent)) {
      const counter = permanent.counters[counterIndex];
      if (counter === undefined) {
        break;
      }
      const counterData = this.countersDB[counter.type];
      let shouldExpire = false;
      if (counterData !== undefined) {
        if (counterData.handlers !== undefined) {
          const res = this.handlePermanentEventWithHandlers(
            {
              ...ev,
              counter,
              counterIndex,
            },
            counterData.handlers
          );
          if (res && res.expire !== undefined && res.expire) {
            shouldExpire = true;
          }
        }
      }
      if (counter.expiry && counter.expiry === ev.type) {
        shouldExpire = true;
      }
      if (shouldExpire && this.inspector.doesPermanentExist(permanent)) {
        this.controller.removeCounter(permanent.id, counterIndex);
        // Do not increment the index!
        continue;
      }
      counterIndex++;
    }
  }

  startTurn(player: Player): void {
    // untap!
    this.inspector.getPermanentsOf(player).forEach((permanent) => {
      // Don't ready frozen permanents.
      if (
        this.inspector.doesPermanentHaveCounterType(
          permanent,
          CounterType.FROZEN
        )
      )
        return;
      if (permanent.ready) return;
      this.controller.readyPermanent(permanent.id);
    });

    // upkeep!
    const devExtraGemsPerTurn = this.inspector.getDevKnobs().extraGemsPerTurn;
    if (devExtraGemsPerTurn !== undefined) {
      const extraGems = new Array(devExtraGemsPerTurn).fill(
        ColorSymbol.RAINBOW
      );
      this.controller.addGems(player, extraGems);
      this.controller.addGems(this.inspector.getOpponentOf(player), extraGems);
    }

    // draw!
    this.drawCard(player);

    for (const permanent of this.inspector.getPermanentsOf(player)) {
      this.handlePermanentEvent({
        type: PermanentEventType.START_TURN,
        permanent,
        ctx: this.makeContext(),
      });
    }

    // first player balancing
    if (this.gameState.turnNumber === 0) {
      // Sometimes, the AI starts with <3 bases so it cannot pay.
      // This is fine.
      const payment = this.inspector.resolvePayment(player, [
        ColorSymbol.ROCK,
        ColorSymbol.ROCK,
      ]);
      if (payment !== null) {
        this.controller.removeGems(player, payment);
      } else if (!this.inspector.hasAI()) {
        throw new Error("only AI can be exempt from first-turn handicap");
      }
    }

    // Inform ui and tests that we're ready to accept steps.
    this.controller.startTurn(player);

    // Run the AI turn if necessary.
    if (this.gameSpec.hasAI && player === Player.P2) {
      for (const spec of this.allSpecs) {
        if (spec.aiTurn) {
          spec.aiTurn(this.makeAIContext());
        }
      }

      // Only end the turn if the game is still active.
      if (this.inspector.isGameActive()) this.endTurn(Player.P2);
      // Only start the next turn if the game is still active.
      if (this.inspector.isGameActive()) this.startTurn(Player.P1);
    }
  }

  endTurn(player: Player): void {
    for (const permanent of this.inspector.getPermanentsOf(player)) {
      this.handlePermanentEvent({
        type: PermanentEventType.END_TURN,
        permanent,
        ctx: this.makeContext(),
      });
    }

    this.inspector.getPermanentsOf(player).forEach((permanent) => {
      if (!permanent.ready) return;
      this.controller.unreadyPermanent(permanent.id);
    });

    if (!this.inspector.isGemClearingDisabled()) {
      this.controller.clearGems(player);
    }
    if (this.inspector.isNoTurnCheck()) {
      const otherPlayer = this.inspector.getOpponentOf(player);
      this.controller.clearGems(otherPlayer);
    }

    // change current player and increment turn number
    this.controller.endTurn(player);

    if (this.gameSpec.afterTurnEnd) {
      this.gameSpec.afterTurnEnd(player, this.makeContext());
    }
  }

  //
  // STEPS, VALIDATORS AND APPLIERS
  // these should be paired with each other
  //
  // we only need to check things that the inspector doesn't already check
  // (so the server-only checks)
  //

  applySummon(step: SummonStepResolved): void {
    const { player, handCardId, slot, cardName, payment } = step;

    // announce before executing any actions
    this.controller.announceSummonPermanent(
      player,
      this.inspector.getCardDisplayName(cardName)
    );

    // deduct gem cost
    this.controller.removeGems(player, payment);

    const faction = this.gameState[player].teamData?.faction ?? null;
    const isFactionCard = doesCardColorBelongToFaction(
      this.inspector.getCardColor(cardName),
      faction
    );
    this.controller.broadcastAndApply({
      type: UpdateType.MODIFY_STATS,
      player,
      diffSummons: 1,
      diffFactionSummons: isFactionCard ? 1 : undefined,
    });

    // place in field
    this.spawnPermanent(cardName, player, slot, handCardId);
  }

  spawnPermanent(
    cardName: string,
    owner: Player,
    slot: Slot,
    handCardId?: string
  ): void {
    // TODO: Return boolean to indicate success?
    const permanent = this.cardToPermanent({ name: cardName }, owner, slot);

    this.controller.createPermanent(permanent, handCardId);

    this.handlePermanentEvent({
      type: PermanentEventType.CREATED,
      permanent,
      ctx: this.makeContext(),
    });

    // Handle OTHER_UNIT_SPAWN.
    for (const otherPermanent of this.inspector.getAllPermanents()) {
      if (otherPermanent.id === permanent.id) continue; // no self trigger
      this.handlePermanentEvent({
        type: PermanentEventType.OTHER_UNIT_SPAWN,
        permanent: otherPermanent,
        spawnedPermanent: permanent,
        ctx: this.makeContext(),
      });
    }

    for (const spec of this.allSpecs) {
      if (spec.afterSummon) {
        spec.afterSummon(permanent, this.makeContext());
      }
    }
  }

  applyRemove(step: RemoveStepResolved): void {
    const { permanent } = step;
    this.controller.removePermanent(permanent.id);
  }

  applyMove(step: MoveStepResolved): void {
    const { permanent, slot } = step;
    const cardData = this.inspector.getCardData(permanent);

    // announce before executing any actions
    this.controller.announceMove(permanent.owner, cardData.displayName);

    this.unreadyPermanentForAction(permanent);
    this.controller.move(permanent.id, slot);
  }

  applyAttack(step: AttackStepResolved): void {
    const { attacker, defender, payment } = step;
    const cardData = this.inspector.getCardData(attacker);
    const defenderData = this.inspector.getCardData(defender);

    // TODO: Don't announce the power here. The damage is already
    // announced when the damage is applied.
    this.controller.announceAttackCard(
      cardData.displayName,
      this.inspector.getPower(attacker),
      defenderData.displayName
    );

    this.unreadyPermanentForAction(attacker);
    this.controller.removeGems(attacker.owner, payment);
    this.attack(attacker, defender);
  }

  applyCreateGems(step: CreateGemsStepResolved): void {
    const { permanent, gemColors } = step;

    this.unreadyPermanentForAction(permanent);
    this.controller.addGems(permanent.owner, gemColors);
  }

  applyDraw(step: DrawStepResolved): void {
    const { player, payment } = step;

    this.controller.removeGems(player, payment);
    this.drawCard(player);
  }

  applyActivateAbility(step: ActivateAbilityStepResolved): void {
    const { permanent, abilityType, payment } = step;

    switch (abilityType) {
      case AbilityType.SPECIAL: {
        this.unreadyPermanentForAction(permanent);
        break;
      }
      // Flex does not unready the permanent and can be activated
      // multiple times.
    }

    this.controller.removeGems(permanent.owner, payment);

    const effects = this.getEffects(permanent);
    const onAbility = (() => {
      switch (abilityType) {
        case AbilityType.FLEX: {
          return effects.onRepeatable;
        }
        case AbilityType.SPECIAL: {
          return effects.onSpecial;
        }
      }
    })();

    if (onAbility) {
      onAbility(step, this.makeContext());
    }
  }

  applyEndTurn(step: EndTurnStep): void {
    const { player } = step;

    if (this.gameSpec.hasAI && player !== Player.P1) {
      throw new Error("validations should only allow P1 steps for AI games");
    }

    this.endTurn(player);
    // Only start the next turn if the game is still active.
    this.startTurn(this.inspector.getOpponentOf(player));
  }

  applyAdvanceKeyframe(step: AdvanceKeyframeStepResolved): void {
    const { keyframe, dialogOptionIndex } = step;
    const { dialogOptions } = keyframe;

    if (dialogOptions !== undefined && dialogOptions.length > 1)
      this.cutsceneChoiceHandler(this, keyframe.id, dialogOptionIndex ?? 0);

    // Returns if end game was activated.
    const activateKeyframeEndGame = (keyframe: Keyframe): boolean => {
      if (keyframe.endGame ?? false) {
        this.endGame(Player.P1, LoseReason.NORMAL, { [Player.P1]: true }, {});
        return true;
      }
      if (keyframe.endGameUnsolved ?? false) {
        this.endGame(Player.P2, LoseReason.NORMAL, {}, {});
        return true;
      }
      return false;
    };

    if (activateKeyframeEndGame(keyframe)) return;

    // advance to next keyframe, if exists and okay:
    if (!keyframe.endCutscene) {
      const { nextIds, dialogOptions } = keyframe;
      const keyframes = this.keyframes;

      const getKeyframeById = (keyframeId: string) => {
        return keyframes.find(({ id }) => id === keyframeId);
      };
      const getKeyframeIndexById = (keyframeId: string) => {
        return keyframes.findIndex(({ id }) => id === keyframeId);
      };

      const nextKeyframe = ((): Keyframe | null => {
        if (nextIds === undefined) {
          const index = getKeyframeIndexById(keyframe.id);
          if (index === -1)
            throw new Error(
              `could not find index of current keyframe ${keyframe.id}`
            );
          return keyframes[index + 1];
        }

        const nextId = ((): string | null => {
          if (nextIds.length === 1) return nextIds[0];

          // Don't throw as the user might have adversarially omitted
          // a dialog option index.
          if (dialogOptionIndex === undefined) return null;

          // Don't throw as the user might have adversarially specified
          // an invalid dialog option index.
          const nextId = nextIds[dialogOptionIndex];
          if (nextId === undefined) return null;

          return nextId;
        })();

        if (nextId === null) return null;

        const nextKeyframe = getKeyframeById(nextId);
        if (nextKeyframe === undefined)
          throw new Error(`could not find next keyframe ${nextId}`);
        return nextKeyframe;
      })();

      if (nextKeyframe !== null) {
        this.controller.setKeyframe(nextKeyframe);
        if (
          !(nextKeyframe.blocking ?? false) &&
          activateKeyframeEndGame(nextKeyframe)
        )
          return;
        return;
      }
    }

    // otherwise, just clear keyframe:
    this.controller.clearKeyframe();
  }

  // validate and apply a step

  /** Checks if a step is valid, and if so, return it resolved. */
  resolve(step: Step, role: Role): StepResolved | null {
    const { resolved, isValid } = this.inspector.resolveAndValidate(step, role);
    if (!isValid) {
      return null;
    }
    return resolved;
  }

  /** Execute a player's step. Does not validate. */
  execute(step: StepResolved): void {
    for (const spec of this.allSpecs) {
      if (spec.overrideStep) {
        if (spec.overrideStep(step, this.makeContext())) {
          return;
        }
      }
    }

    switch (step.type) {
      // Game steps
      case StepType.SUMMON: {
        this.applySummon(step);
        break;
      }
      case StepType.ATTACK: {
        this.applyAttack(step);
        break;
      }
      case StepType.CREATE_GEMS: {
        this.applyCreateGems(step);
        break;
      }
      case StepType.MOVE: {
        this.applyMove(step);
        break;
      }
      case StepType.DRAW: {
        this.applyDraw(step);
        break;
      }
      case StepType.ACTIVATE_ABILITY: {
        this.applyActivateAbility(step);
        break;
      }
      case StepType.END_TURN: {
        this.applyEndTurn(step);
        break;
      }
      case StepType.ADVANCE_KEYFRAME: {
        this.applyAdvanceKeyframe(step);
        break;
      }

      // Non-game steps
      case StepType.REMOVE: {
        this.applyRemove(step);
        break;
      }
      case StepType.CREATE_CHECKPOINT: {
        this.needCheckpoint.push(step.slot);
        break;
      }
      default: {
        if (this.gameSpec.applyStep) {
          this.gameSpec.applyStep(step, this.makeContext());
        }
      }
    }
  }

  /**
   * Resolve and execute a step.
   * Returns whether resolve was successful.
   * AIs should use aiTryStep.
   */
  tryStep(step: Step, role: Role): boolean {
    // The room may be left open after the game has ended to support
    // checkpoint restore. However, clients cannot restore checkpoints
    // directly with steps, so any step made by clients after the game
    // has ended is invalid.
    // We need to allow system steps, though, since clients may be
    // joined to the room after the game has ended, to allow them
    // to receive updates upon checkpoint restore.
    if (!this.inspector.isGameActive()) {
      return false;
    }

    if (
      this.inspector.isTurnPending(Player.P1) &&
      (this.inspector.getDevKnobs().instasolve ?? false)
    ) {
      this.endGame(Player.P1, LoseReason.NORMAL, { [Player.P1]: true }, {});
      return true;
    }

    const stepResolved = this.resolve(step, role);
    if (stepResolved === null) {
      return false;
    }

    let shouldSkipExecute = false;
    for (const spec of this.allSpecs) {
      if (spec.beforeStep) {
        if (!spec.beforeStep(step, this.makeContext()))
          shouldSkipExecute = true;
      }
    }

    if (!shouldSkipExecute) this.execute(stepResolved);

    for (const spec of this.allSpecs) {
      if (spec.afterStep) {
        spec.afterStep(stepResolved, this.makeContext());
      }
    }

    const player = stepToPlayer(stepResolved);
    if (
      player !== null &&
      // Don't track step numbers for the AI, since there are
      // no race conditions or syncs dependent on it.
      !(this.inspector.hasAI() && player === Player.P2)
    ) {
      this.controller.broadcastAndApply({
        type: UpdateType.SET_STEP_NUMBER,
        player,
        stepNumber: this.gameState[player].stepNumber + 1,
      });
    }

    // AI steps executed after a player's end turn step may cause
    // the game to have already ended. Exit early if so to
    // prevent running game completion actions twice.
    if (!this.inspector.isGameActive()) return true;

    const winner = this.getWinner();
    if (winner) {
      const solves: { [player in Player]?: boolean } = {};
      const cardUnlocks: { [player in Player]?: string[] } = {};
      for (const player of [Player.P1, Player.P2]) {
        if (!this.getIsSolved(player, winner)) continue;
        solves[player] = true;
        const playerCardUnlocks = this.getCardUnlocks(player);
        if (playerCardUnlocks.length > 0)
          cardUnlocks[player] = playerCardUnlocks;
      }
      this.endGame(winner, LoseReason.NORMAL, solves, cardUnlocks);
      return true;
    }

    if (
      this.inspector.getDevKnobs().checkpointAfterEveryStep ||
      (this.inspector.areCheckpointsAllowed() &&
        stepResolved.type == StepType.END_TURN)
    )
      this.needCheckpoint.push(0); // autosave slot
    if (this.needCheckpoint.length) {
      this.checkpointHandler(this, this.needCheckpoint);
      this.needCheckpoint.length = 0;
    }

    return true;
  }

  /** Get the player state visible to its opponent. */
  getPublicPlayerState(player: Player, gameState: FullGameState): PlayerState {
    const playerState = gameState[player];
    const {
      teamData,
      stepNumber,
      drawPile,
      prevTurnsTime,
      startTurnTime,
      stats,
    } = playerState;
    return {
      teamData,
      stepNumber,
      // hide cards in hand
      hand: playerState.hand.map((handCard) => ({
        ...handCard,
        card: { ...handCard.card, name: undefined },
      })),
      gems: playerState.gems,
      drawPileSize: drawPile.length,
      prevTurnsTime,
      startTurnTime,
      stats,
    };
  }

  /** Get the player state visible to the player itself. */
  getClientPlayerState(player: Player, gameState: FullGameState): PlayerState {
    const playerState = gameState[player];
    const { drawPile } = playerState;
    const { topCard } = this.inspector.getTopCardsInDrawPile(player);
    return {
      ...playerState,
      drawPile: undefined,
      discardPile: undefined,
      drawPileSize: drawPile.length,
      topCard: this.inspector.doesPlayerHaveMastery(
        player,
        SCRY_TOP_CARD_MASTERY_ID
      )
        ? topCard
        : undefined,
    };
  }

  // Returns the client view of the game state.
  // Note that this is not a deep copy. Any use of an
  // OVERRIDE_STATE update should not be followed by
  // any more basic updates, since further updates would
  // be reflected in the state object used here.
  getClientGameStateForRole(role: Role, gameState: FullGameState): GameState {
    switch (role) {
      case Role.GOD: {
        return {
          ...gameState,
        };
      }
      case Role.P1: {
        const { engineOnly, ...clientGameState } = {
          ...gameState,
          [Player.P1]: this.getClientPlayerState(Player.P1, gameState),
          [Player.P2]: this.getPublicPlayerState(Player.P2, gameState),
        };
        return clientGameState;
      }
      case Role.P2: {
        const { engineOnly, ...clientGameState } = {
          ...gameState,
          [Player.P1]: this.getPublicPlayerState(Player.P1, gameState),
          [Player.P2]: this.getClientPlayerState(Player.P2, gameState),
        };
        return clientGameState;
      }
    }
  }

  // Respond with override state. This is only used when a client
  // first joins.
  respOverrideState(roomMember: RoomMember): void {
    this.controller.respOverrideState(
      roomMember.id,
      this.roomId,
      this.getClientGameStateForRole(roomMember.role, this.gameState)
    );
  }

  // This should only be used to save and load game states.
  broadcastOverrideState(gameState: FullGameState): void {
    this.controller.overrideState(
      this.roomId,
      gameState,
      this.getClientGameStateForRole(Role.P1, gameState),
      this.getClientGameStateForRole(Role.P2, gameState)
    );
  }

  /**
   * Accept a member into the room and send it an OVERRIDE_STATE
   * update. No updates should be sent after this call, since
   * the state set in OVERRIDE_STATE is a reference that would
   * get updated before it is serialized. If we ever want to
   * add updates after the OVERRIDE_STATE (such as to show fancy
   * animations), we can make a deep copy of the state in controller
   * (potentially expensive), but even then only one player would
   * get to see it if multiple players join the room at once.
   */
  acceptMember(roomMember: RoomMember): void {
    this.multicaster.enableMember(roomMember);
    this.respOverrideState(roomMember);
  }

  applySystemStep(step: SystemStep): void {
    switch (step.type) {
      case StepType.SYSTEM_JOIN: {
        const { teamId, roomMember, role } = step;

        this.acceptMember(roomMember);

        break;
      }
      case StepType.SYSTEM_RESIGN: {
        if (!this.inspector.isGameActive()) break;

        this.endGame(
          this.inspector.getOpponentOf(step.player),
          LoseReason.RESIGN,
          {},
          {}
        );
        break;
      }
      case StepType.SYSTEM_TIME_OUT: {
        if (!this.inspector.isGameActive()) break;

        this.endGame(
          this.inspector.getOpponentOf(step.player),
          LoseReason.TIME,
          {},
          {}
        );
        break;
      }
      default: {
        throw new Error("Invalid system step type.");
      }
    }
  }

  applyStep(sender: RoomMember, step: Step): void {
    if (this.needCheckpoint.length)
      throw new Error(
        "needCheckpoint should only be used during the processing of a step, and consumed when the step is done"
      );

    if (this.tryStep(step, sender.role)) {
      this.controller.respSuccess(sender.id);
    } else {
      this.controller.respInvalidStep(sender.id);
    }
  }

  // Utility functions for AIs.

  /**
   * Resolve and execute a step as an AI.
   * Returns whether resolve was successful.
   */
  aiTryStep(step: Step): boolean {
    return this.tryStep(step, Role.GOD);
  }
}
