import { Player, areSlotsEqual } from "engine/types/game-state";
import { StepType } from "engine/types/steps";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { CardType } from "engine/types/card-data";

type ExtraState = {
  phase: number;
  wrongMove: boolean;
  stampedeTurnStart?: number;
  summonedAll: boolean;
};

export const Tutorial3Spec: GameSpecServerFragment = {
  disableP1NoBasesLossCondition: true,
  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      phase: 1,
      wrongMove: false,
      summonedAll: false,
    });
  },
  beforeNewGame: (ctx) => {
    const { engine, ai } = ctx;
    engine.spawn("yoshisaur-munchakoopas", 3, 3, Player.P1);
    engine.spawn("beeowulf", 1, 1, Player.P2);
    engine.spawn("beeowulf", 1, 3, Player.P2);
    ai.setKeyframe("tutorial3-start-1");
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();
    const { keyframe } = inspector.gameState;
    const ext = engine.getExtraState() as ExtraState;

    if (
      step.type === StepType.ADVANCE_KEYFRAME &&
      step.keyframeId === "tutorial3-lose-1" &&
      ext.wrongMove
    ) {
      ai.setKeyframe("tutorial3-lose-1-wrong-move");
      return;
    }

    if (step.type === StepType.MOVE && turnNumber === 1) {
      if (areSlotsEqual(step.slot, { row: 3, column: 2 })) {
        ai.setKeyframe("tutorial3-after-move");
      } else {
        ext.wrongMove = true;
      }
      return;
    }

    if (
      step.type === StepType.ADVANCE_KEYFRAME &&
      step.keyframeId === "tutorial3-before-give-cards"
    ) {
      engine.addCardToHand(Player.P1, { name: "new-boarn" });
      engine.addCardToHand(Player.P1, { name: "hog" });
      engine.addCardToHand(Player.P1, { name: "hamlet" });
    }

    if (
      ext.phase === 3 &&
      inspector.getPermanents({
        name: "new-boarn",
      }).length > 0
    ) {
      ai.setKeyframe("tutorial3-after-summon-new-boarn");
      ext.phase = 4;
      return;
    }
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();
    const ext = engine.getExtraState() as ExtraState;

    // Turn 1: Set player camp health to 1
    if (inspector.getTurnNumber() == 0) {
      const camp = inspector.getPermanentAtIfExists({ row: 5, column: 2 });
      if (!camp) throw new Error("expect camp to exist");
      engine.adjustPermanentStats(camp, 0, -3);
      return;
    }

    const aiCreatures = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.CREATURE,
    });

    for (const creature of aiCreatures) {
      if (inspector.getBases(Player.P1).length === 0) break;
      ai.tryAttackFirst(creature);
    }

    if (turnNumber === 2) {
      if (inspector.getBases(Player.P1).length === 0) {
        ai.setKeyframe("tutorial3-lose-1");
      } else {
        ai.setKeyframe("tutorial3-start-2");
      }
      return;
    }

    if (ext.phase === 1 && aiCreatures.length === 0) {
      ai.setKeyframe("tutorial3-start-3");
      ext.stampedeTurnStart = turnNumber + 3 * 2;
      ext.phase = 2;
      return;
    }

    if (turnNumber === ext.stampedeTurnStart) {
      engine.spawn("chocolate-calf", 1, 0, Player.P2);
      engine.spawn("chocolate-calf", 1, 2, Player.P2);
      engine.spawn("chocolate-calf", 1, 4, Player.P2);
      if (ext.phase === 4) {
        ai.setKeyframe("tutorial3-stampede");
        ext.summonedAll = true;
      } else {
        ai.setKeyframe("tutorial3-stampede-fail");
      }
      ext.phase = 5;
      return;
    }

    const playerCreatures = inspector.getPermanents({
      owner: Player.P1,
      type: CardType.CREATURE,
    });

    if (ext.phase === 2 && playerCreatures.length === 3) {
      ai.setKeyframe("tutorial3-start-5");
      ext.phase = 3;
      return;
    }
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine, ai } = ctx;
    const ext = engine.getExtraState() as ExtraState;
    if (winner === Player.P2 && ext.summonedAll) {
      ai.setKeyframe("tutorial3-lose-2");
    }
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;
    const { keyframe } = inspector.gameState;
    const turnNumber = inspector.getTurnNumber();
    if (player === Player.P1) {
      return (
        inspector.getBases(player).length === 0 &&
        (turnNumber > 3 ||
          (keyframe !== undefined &&
            ["tutorial3-lose-1-wrong-move", "tutorial3-lose-1-other"].includes(
              keyframe.id
            )))
      );
    }
    return false;
  },
};
