import { Player } from "engine/types/game-state";
import { CounterType } from "engine/types/counters";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { PermanentEventType } from "engine/server-hooks";

export const Puzzle98YinYangSpec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  cardEffects: {
    "spirit-of-the-vines": {
      handlers: {
        [PermanentEventType.START_TURN]: (event) => {
          const { permanent } = event;
          const { engine } = event.ctx;
          engine.adjustPermanentStats(permanent, 0, 2);
        },
        [PermanentEventType.OTHER_UNIT_DESTROYED]: (event) => {
          const { permanent: me, destroyedPermanent } = event;
          const { engine } = event.ctx;
          if (destroyedPermanent.owner === me.owner) {
            engine.adjustPermanentStats(me, 10000, 10000);
          }
        },
      },
    },
    "overgrown-vines": {
      handlers: {
        [PermanentEventType.OTHER_UNIT_SPAWN]: (event) => {
          const { spawnedPermanent } = event;
          const { engine } = event.ctx;
          engine.addCounter(spawnedPermanent, {
            type: CounterType.CANNOT_MOVE,
            explanation: "Immobile.",
          });
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    if (inspector.getTurnNumber() === 0) {
      ai.trySpawn("spirit-of-the-vines", 0, 2);
      ai.trySpawn("overgrown-vines", 1, 2);
    }

    inspector
      .getPermanentsOf(Player.P2)
      .forEach((permanent) => ai.tryAttackFirst(permanent));
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;

    // if P2 has no creatures they lose
    return (
      player === Player.P2 && inspector.getPermanentsOf(player).length === 0
    );
  },
};
