import { Inspector } from "engine/Inspector";
import { PermanentEventType } from "engine/server-hooks";
import { Check } from "engine/types/action-validation";
import { ColorSymbol } from "engine/types/card-data";
import { CounterType } from "engine/types/counters";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Permanent, Player, Slot } from "engine/types/game-state";

export type Puzzle50ExtraState = {
  globalConstraints: { id: string; health: number }[];
  clues: { id: string; health: number; row: number; column: number }[];
  answer: boolean[][];
  showSolution: boolean;
  passed: boolean;
  isInvulnerable: boolean;
};

export const Puzzle50CommonSpec: GameSpecServerFragment = {
  cardEffects: {
    "b-w-copier": {
      handlers: {
        [PermanentEventType.CREATED]: (event) => {
          event.ctx.engine.addCounter(event.permanent, {
            type: CounterType.BASE_GEMS,
            val: 3,
          });
        },
      },
      onSpecial: (step, ctx) => {
        const { inspector, engine } = ctx;
        const { permanent } = step;
        const currSlot = permanent.slot;
        const r = currSlot.row;
        for (let c = 0; c < inspector.getNumColumns(); c++) {
          if (c != currSlot.column) {
            const permanent = inspector.getPermanentAtIfExists({
              row: r,
              column: c,
            });
            if (!permanent) continue;
            const color = inspector.getCardColor(permanent);
            // Don't do anything to WHITEBLACK cards
            if (
              color !== null &&
              [ColorSymbol.WHITE, ColorSymbol.BLACK].includes(color)
            ) {
              engine.addCardToHand(permanent.owner, permanent.card);
            }
          }
        }
      },
    },
    "pasture-planner": {
      adjustAttackChecks: (failed) => {
        // this base can attack!!
        failed.delete(Check.CARD_TYPE);
      },
      handlers: {
        [PermanentEventType.CREATED]: (ev) => {
          const { permanent, ctx } = ev;
          const { engine } = ctx;
          engine.addCounter(permanent, {
            type: CounterType.INVULNERABLE,
          });
        },
        [PermanentEventType.OTHER_UNIT_SPAWN]: (ev) => {
          const { permanent, ctx } = ev;
          const { engine } = ctx;
          const played = ev.spawnedPermanent;
          const extraState = engine.getExtraState() as Puzzle50ExtraState;
          if (permanent.owner == played.owner) {
            if (extraState.isInvulnerable) {
              engine.addCounter(played, {
                type: CounterType.INVULNERABLE,
              });
            } else {
              engine.addCounter(played, {
                type: CounterType.CANNOT_BE_DESTROYED,
              });
            }
            engine.addCounter(played, {
              type: CounterType.CANNOT_MOVE,
            });
          }
        },
        [PermanentEventType.END_TURN]: (ev) => {
          const { permanent, ctx } = ev;
          const { inspector, ai } = ctx;
          // check for doom counter
          if (
            !inspector.doesPermanentHaveCounterType(
              permanent,
              CounterType.DOOM
            ) ||
            !inspector.isTurnPending(Player.P2)
          ) {
            return;
          }
          const counterIndex = permanent.counters.findIndex(
            (counter) => counter.type === CounterType.DOOM
          );
          const counter = permanent.counters[counterIndex];
          // decrement counter
          const newVal = (counter.val || 0) - 1;
          const timer = Array(newVal).fill("⏳").join(" ");
          if (newVal === 1) {
            ai.speak(`Audit is coming!\n${timer}`);
          } else if (newVal > 1) {
            ai.speak(`Audit in ${newVal} turns!\n${timer}`);
          }
        },
      },
    },
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const extraState = engine.getExtraState() as Puzzle50ExtraState;
    const { globalConstraints, clues, answer, showSolution } = extraState;
    const turn = inspector.getTurnNumber() / 2;
    const base = inspector
      .getPermanentsOf(Player.P2)
      .filter((permanent) => inspector.getSharedEffects(permanent).isBase)[0];
    const playerBase = inspector
      .getPermanentsOf(Player.P1)
      .filter((permanent) => inspector.getSharedEffects(permanent).isBase)[0];

    if (turn === 0) {
      // Turn 0: place all global constraint cards on their home row (guaranteed that there are at most 4)
      const slots: Slot[] = [
        { row: 0, column: 0 },
        { row: 0, column: 1 },
        { row: 0, column: 3 },
        { row: 0, column: 4 },
      ];
      for (const { id, health } of globalConstraints) {
        const slot = slots.shift();
        if (!slot) throw new Error("oops");
        ai.trySpawn(id, slot);
        const permanent = inspector.getPermanentsAt(slot)[0];
        engine.adjustPermanentStats(permanent, 0, health - 1);
        // engine.addCounter(permanent, {
        //   type: CounterType.MAX_HEALTH_ADJUSTMENT,
        //   val: health - 1, // all start with 1 health already
        // });
      }
    }

    if (turn >= 1 && turn <= clues.length) {
      // Turn 1~N: place one clue in the center per turn
      const { id, health, row, column } = clues[turn - 1];
      const slot = { row: row + 1, column };
      for (const unit of inspector.getPermanentsAt(slot)) {
        // destroying anything there first if occupied
        engine.destroy(base, unit);
      }
      if (!ai.trySpawn(id, slot)) {
        throw new Error(`why ${id} ${JSON.stringify(slot)}`);
      }
      const permanent = inspector.getPermanentsAt(slot)[0];
      engine.adjustPermanentStats(permanent, 0, health - 1);
      // engine.addCounter(permanent, {
      //   type: CounterType.MAX_HEALTH_ADJUSTMENT,
      //   val: health - 1, // all start with 1 health already
      // });
    }

    if (turn === clues.length) {
      // Turn N: add a DOOM counter to the AI base
      if (clues.length <= 2) {
        // If there are very few clues then give more turns
        engine.addCounter(base, {
          type: CounterType.DOOM,
          val: 7 - clues.length,
          explanation: "Audit is coming",
        });
      } else {
        engine.addCounter(base, {
          type: CounterType.DOOM,
          val: 4,
          explanation: "Audit is coming",
        });
      }
    }

    if (
      turn > clues.length &&
      inspector.getCounterValSum(base, CounterType.DOOM) === 1
    ) {
      /*
      When DOOM counter hits zero, the AI base will "activate" all friendly cards,
      and each card will check for the constraint it correspond to.
      (Maybe it is easier to implement this check under each card instead of within the base?)
      (If the center field is not completely filled with black or white cards, the base itself fails)
      */
      const failedPermanents = [];
      const grid = convertToGrid(inspector);
      if (grid === null) {
        failedPermanents.push(base);
      } else {
        const components = splitComponents(grid);
        for (const permanent of inspector.getPermanentsOf(Player.P2)) {
          const getCheckerMakerId = (a: string) => {
            const i = parseInt(a.split("-").pop() ?? "");
            return i % 2 === 1 ? `${i}-${i + 1}` : `${i - 1}-${i}`;
          };
          let ok = true;
          if (permanent.card.name?.startsWith("puzzle-50-global-")) {
            ok = globalCheckerMakers[getCheckerMakerId(permanent.card.name)](
              permanentToColor(inspector, permanent) ?? false
            )(grid, components, inspector.getHealth(permanent), clues);
          }
          if (permanent.card.name?.startsWith("puzzle-50-local-")) {
            ok = localCheckerMakers[getCheckerMakerId(permanent.card.name)](
              permanentToColor(inspector, permanent) ?? false
            )(
              grid,
              components,
              inspector.getHealth(permanent),
              permanent.slot.row - 1,
              permanent.slot.column
            );
          }
          if (!ok) {
            failedPermanents.push(permanent);
          }
        }
      }
      /*
      Each failed card (local or global) gains 100 power.
      If showSolution is set to True, the AI also destroys all incorrect cards that we placed
      and replaces them with "resident" of the correct color (this includes any empty slots).
      */
      for (const permanent of failedPermanents) {
        // Bypassing adjustPermanentStats because custom explanation
        engine.addCounter(permanent, {
          type: CounterType.POWER_ADJUSTMENT,
          val: 100,
          explanation: "Failed audit!",
        });
      }
      if (showSolution) {
        for (let i = 0; i < 5; i++) {
          for (let j = 0; j < 5; j++) {
            const slot = { row: i + 1, column: j };
            const correct = answer[i][j];
            for (const permanent of inspector.getPermanentsAt(slot)) {
              if (permanentToColor(inspector, permanent) !== correct) {
                engine.destroy(base, permanent);
              }
            }
            ai.trySpawn(`puzzle-50-local-${correct ? 1 : 2}`, slot);
          }
        }
      }

      /*
      All failed cards then attack our base (presumably destroying it).
      If no card attacked, the AI base self-destructs (so we win).
      */
      for (const permanent of failedPermanents) {
        ai.tryAttack(permanent, playerBase);
      }
      if (failedPermanents.length === 0) {
        engine.destroy(base, base);
      }
    }
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine, ai } = ctx;
    if (!(solves[Player.P1] ?? false)) {
      ai.speak(
        "Audit failed! Violators shall be punished in accordance with the law!"
      );
    } else {
      engine.clearKeyframe();
    }
  },
};

// a connected component is a list of cells, each of the same color
type Cell = { row: number; column: number };
type Component = { color: boolean; cells: Cell[] };

type GlobalChecker = (
  grid: boolean[][],
  components: Component[],
  health: number,
  clues: Cell[]
) => boolean;

type LocalChecker = (
  grid: boolean[][],
  components: Component[],
  health: number,
  row: number,
  column: number
) => boolean;

// a CheckerMaker takes a color and makes its corresponding Checker
type GlobalCheckerMaker = (color: boolean) => GlobalChecker;
type LocalCheckerMaker = (color: boolean) => LocalChecker;

const permanentToColor = (inspector: Inspector, permanent: Permanent) => {
  const color = inspector.getCardColor(permanent);
  if (color === null) return null;
  switch (color) {
    case ColorSymbol.WHITE:
      return false;
    case ColorSymbol.BLACK:
      return true;
    default:
      return null;
  }
};

const isInBounds = (cell: Cell): boolean => {
  const { row, column } = cell;
  return 0 <= row && row < 5 && 0 <= column && column < 5;
};

const convertToGrid = (inspector: Inspector): boolean[][] | null => {
  const grid: (boolean | null)[][] = Array(5)
    .fill(null)
    .map((_) => Array(5).fill(null));

  inspector.getAllPermanents().forEach((permanent) => {
    // Convert gameboard coordinate to grid coordinate (top-left = (1,0))
    const row = permanent.slot.row - 1;
    const column = permanent.slot.column;
    if (isInBounds({ row, column })) {
      grid[row][column] = permanentToColor(inspector, permanent);
    }
  });

  return grid.every((a): a is boolean[] => a.every((a) => a !== null))
    ? grid
    : null;
};

const isCellEqual = (a: Cell, b: Cell): boolean => {
  return a.row === b.row && a.column === b.column;
};
const hasEqualCell = (a: Cell[], b: Cell): boolean => {
  return a.some((cell) => isCellEqual(cell, b));
};

const splitComponentsMaker =
  (neighborsOf: (cell: Cell) => Cell[]) =>
  (grid: boolean[][]): Component[] => {
    const colorOf = (grid: boolean[][], cell: Cell): boolean => {
      return grid[cell.row][cell.column];
    };
    const result: Component[] = [];
    const unseenCells: Cell[] = [];
    for (let row = 0; row < 5; row++) {
      for (let column = 0; column < 5; column++) {
        unseenCells.push({ row, column });
      }
    }
    while (unseenCells.length > 0) {
      const cell = unseenCells[0];
      const color = colorOf(grid, cell);
      const cells: Cell[] = [];
      const add = (cell: Cell) => {
        const idx = unseenCells.findIndex((unseen) =>
          isCellEqual(cell, unseen)
        );
        if (idx === -1) return;
        unseenCells.splice(idx, 1);
        cells.push(cell);
        neighborsOf(cell)
          .filter((neighbor) => colorOf(grid, neighbor) === color)
          .forEach((neighbor) => add(neighbor));
      };
      add(cell);
      result.push({ color, cells });
    }
    return result;
  };

const splitComponents = splitComponentsMaker((cell: Cell) => {
  const { row, column } = cell;
  return [
    { row: row + 1, column },
    { row: row - 1, column },
    { row, column: column + 1 },
    { row, column: column - 1 },
  ].filter(isInBounds);
});

const globalCheckerMakers: { [id: string]: GlobalCheckerMaker } = {
  // Must be orthogonally connected
  "1-2": (color) => (grid, components, health, clues) => {
    let count = 0;
    for (let i = 0; i < components.length; i++) {
      if (components[i].color == color) count++;
    }
    return count <= 1;
  },
  // Must be diagonally (including orthogonally) connected
  "3-4": (color) => (grid) => {
    const components = splitComponentsMaker((cell: Cell) => {
      const { row, column } = cell;
      return [
        { row: row + 1, column },
        { row: row - 1, column },
        { row, column: column + 1 },
        { row, column: column - 1 },
        { row: row + 1, column: column + 1 },
        { row: row - 1, column: column + 1 },
        { row: row + 1, column: column - 1 },
        { row: row - 1, column: column - 1 },
      ].filter(isInBounds);
    })(grid);
    let count = 0;
    for (let i = 0; i < components.length; i++) {
      if (components[i].color == color) count++;
    }
    return count <= 1;
  },
  // Must be orthogonally connected to boundary
  "5-6": (color) => (grid, components, health, clues) => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].color == color) {
        let connected = false;
        for (const cell of components[i].cells) {
          if (
            cell.row == 0 ||
            cell.row == 4 ||
            cell.column == 0 ||
            cell.column == 4
          ) {
            connected = true;
            break;
          }
        }
        if (!connected) return false;
      }
    }
    return true;
  },
  // No 2x2 blocks
  "7-8": (color) => (grid, components, health, clues) => {
    for (let r = 0; r < 4; r++) {
      for (let c = 0; c < 4; c++) {
        if (
          grid[r][c] == color &&
          grid[r + 1][c] == color &&
          grid[r][c + 1] == color &&
          grid[r + 1][c + 1] == color
        ) {
          return false;
        }
      }
    }
    return true;
  },
  // Must be rectangles
  "9-10": (color) => (grid, components, health, clues) => {
    for (let r = 0; r < 4; r++) {
      for (let c = 0; c < 4; c++) {
        let count = 0;
        // The condition is equivalent to "No L-shape in a 2x2"
        if (grid[r][c] == color) count++;
        if (grid[r][c + 1] == color) count++;
        if (grid[r + 1][c] == color) count++;
        if (grid[r + 1][c + 1] == color) count++;
        if (count == 3) return false;
      }
    }
    return true;
  },
  // Must be squares
  "11-12": (color) => (grid, components, health, clues) => {
    for (let r = 0; r < 5; r++) {
      for (let c = 0; c < 5; c++) {
        if (grid[r][c] != color) continue;
        // every cell must see the same number of cells horizontally and vertically
        let up, down, left, right;
        up = down = left = right = 0;
        for (let i = 1; i <= r; i++) {
          if (grid[r - i][c] != color) break;
          up++;
        }
        for (let i = 1; i <= 4 - r; i++) {
          if (grid[r + i][c] != color) break;
          down++;
        }
        for (let i = 1; i <= c; i++) {
          if (grid[r][c - i] != color) break;
          left++;
        }
        for (let i = 1; i <= 4 - c; i++) {
          if (grid[r][c + i] != color) break;
          right++;
        }
        if (up + down != left + right) return false;
      }
    }
    return true;
  },
  // Must form paths
  "13-14": (color) => (grid, components, health, clues) => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].color != color) continue;
      let numOnes = 0;
      for (const cell of components[i].cells) {
        const { row: r, column: c } = cell;
        let count = 0;
        if (r > 0 && grid[r - 1][c] == color) count++;
        if (r < 4 && grid[r + 1][c] == color) count++;
        if (c > 0 && grid[r][c - 1] == color) count++;
        if (c < 4 && grid[r][c + 1] == color) count++;
        if (count > 2 || count < 1) return false;
        if (count == 1) numOnes++;
      }
      if (numOnes != 2) return false;
    }
    return true;
  },
  // Must form loops
  "15-16": (color) => (grid, components, health, clues) => {
    for (let r = 0; r < 5; r++) {
      for (let c = 0; c < 5; c++) {
        if (grid[r][c] != color) continue;
        let count = 0;
        if (r > 0 && grid[r - 1][c] == color) count++;
        if (r < 4 && grid[r + 1][c] == color) count++;
        if (c > 0 && grid[r][c - 1] == color) count++;
        if (c < 4 && grid[r][c + 1] == color) count++;
        if (count != 2) return false;
      }
    }
    return true;
  },
  // Must have size N
  "17-18": (color) => (grid, components, health, clues) => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].color != color) continue;
      if (components[i].cells.length != health) return false;
    }
    return true;
  },
  // Contains exactly N clues
  "19-20": (color) => (grid, components, health, clues) => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].color != color) continue;
      let count = 0;
      for (const cell of components[i].cells) {
        if (hasEqualCell(clues, cell)) count++;
      }
      if (count != health) return false;
    }
    return true;
  },
  // Contains up to N clues
  "21-22": (color) => (grid, components, health, clues) => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].color != color) continue;
      let count = 0;
      for (const cell of components[i].cells) {
        if (hasEqualCell(clues, cell)) count++;
      }
      if (count > health) return false;
    }
    return true;
  },
};

const localCheckerMakers: { [id: string]: LocalCheckerMaker } = {
  // Nothing
  "1-2": (color) => (grid, components, health, row, column) => {
    return true;
  },
  // Number of neighboring cells with the same color
  "3-4": (color) => (grid, components, health, row, column) => {
    let count = 0;
    if (row > 0 && grid[row - 1][column] == color) count++;
    if (row < 4 && grid[row + 1][column] == color) count++;
    if (column > 0 && grid[row][column - 1] == color) count++;
    if (column < 4 && grid[row][column + 1] == color) count++;
    return count == health;
  },
  // Number of surrounding cells with opposite color
  "5-6": (color) => (grid, components, health, row, column) => {
    let count = 0;
    for (let r = Math.max(0, row - 1); r <= Math.min(4, row + 1); r++) {
      for (let c = Math.max(0, column - 1); c <= Math.min(4, column + 1); c++) {
        if (grid[r][c] != color) count++;
      }
    }
    return count == health;
  },
  // Number of cells in this column with opposite color
  "7-8": (color) => (grid, components, health, row, column) => {
    let count = 0;
    for (let r = 0; r < 5; r++) {
      if (grid[r][column] != color) count++;
    }
    return count == health;
  },
  // Number of cells in this row with opposite color
  "9-10": (color) => (grid, components, health, row, column) => {
    let count = 0;
    for (let c = 0; c < 5; c++) {
      if (grid[row][c] != color) count++;
    }
    return count == health;
  },
  // Area of this region
  "11-12": (color) => (grid, components, health, row, column) => {
    for (let i = 0; i < components.length; i++) {
      if (hasEqualCell(components[i].cells, { row, column })) {
        return components[i].cells.length == health;
      }
    }
    return false;
  },
  // Sum of areas of all neighboring regions
  "13-14": (color) => (grid, components, health, row, column) => {
    let count = 0;
    for (let i = 0; i < components.length; i++) {
      if (components[i].color == color) continue;
      if (
        hasEqualCell(components[i].cells, { row: row - 1, column: column }) ||
        hasEqualCell(components[i].cells, { row: row + 1, column: column }) ||
        hasEqualCell(components[i].cells, { row: row, column: column - 1 }) ||
        hasEqualCell(components[i].cells, { row: row, column: column + 1 })
      ) {
        count += components[i].cells.length;
      }
    }
    return count == health;
  },
  // Number of cells with the same color that this cell can "see"
  "15-16": (color) => (grid, components, health, row, column) => {
    let up, down, left, right;
    up = down = left = right = 0;
    for (let i = 1; i <= row; i++) {
      if (grid[row - i][column] != color) break;
      up++;
    }
    for (let i = 1; i <= 4 - row; i++) {
      if (grid[row + i][column] != color) break;
      down++;
    }
    for (let i = 1; i <= column; i++) {
      if (grid[row][column - i] != color) break;
      left++;
    }
    for (let i = 1; i <= 4 - column; i++) {
      if (grid[row][column + i] != color) break;
      right++;
    }
    return up + down + left + right + 1 == health;
  },
  // Number of cells with opposite color that this cell can "see"
  "17-18": (color) => (grid, components, health, row, column) => {
    let up, down, left, right;
    up = down = left = right = 0;
    for (let i = 1; i <= row; i++) {
      if (grid[row - i][column] == color) break;
      up++;
    }
    for (let i = 1; i <= 4 - row; i++) {
      if (grid[row + i][column] == color) break;
      down++;
    }
    for (let i = 1; i <= column; i++) {
      if (grid[row][column - i] == color) break;
      left++;
    }
    for (let i = 1; i <= 4 - column; i++) {
      if (grid[row][column + i] == color) break;
      right++;
    }
    return up + down + left + right == health;
  },
};
