import { CounterType } from "engine/types/counters";
import { Permanent, Player } from "engine/types/game-state";
import { Speaker } from "engine/types/keyframes";
import { GameSpecServerFragment, AIContext } from "engine/types/game-specs";
import {
  PermanentEventHandlers,
  PermanentEventType,
} from "engine/server-hooks";

type ExtraState = {
  slimeKilledOrder: string[];
};

// killed slime -> children slimes
// order of this array determines the correct slimeKilledOrder
const slimeSpawnOrder = [
  { parent: "slime", children: ["yellow", "sepia"] }, // *
  { parent: "yellow", children: ["lemon", "amber"] }, // E
  { parent: "amber", children: ["ivory", "orange"] }, // A
  { parent: "lemon", children: ["ebony", "turquoise"] }, // I
  { parent: "ivory", children: ["harlequin"] }, // R
  { parent: "harlequin", children: [] }, // L
  { parent: "ebony", children: ["indigo", "mauve"] }, // S
  { parent: "indigo", children: [] }, // H
  { parent: "sepia", children: ["ecru", "emerald"] }, // T
  { parent: "emerald", children: ["khaki", "apricot"] }, // M
  { parent: "khaki", children: ["eggplant", "lavender"] }, // G
  { parent: "ecru", children: ["auburn", "teal"] }, // N
  { parent: "auburn", children: ["lava", "rust"] }, // D
  { parent: "lava", children: [] }, // B
  { parent: "teal", children: ["white", "saffron"] }, // K
  { parent: "white", children: [] }, // C
  { parent: "apricot", children: [] }, // O
  { parent: "lavender", children: [] }, // Q
  { parent: "turquoise", children: ["silver"] }, // U
  { parent: "silver", children: [] }, // F
  { parent: "mauve", children: [] }, // V
  { parent: "orange", children: ["red", "lilac"] }, // W
  { parent: "lilac", children: [] }, // J
  { parent: "red", children: [] }, // P
  { parent: "rust", children: [] }, // X
  { parent: "saffron", children: [] }, // Y
  { parent: "eggplant", children: [] }, // Z
];

// dot slime
//    adds a poison counter to any unit it attacks
const dotSlimes = [
  "yellow",
  "lemon",
  "ebony",
  "indigo",
  "silver",
  "ivory",
  "harlequin",
  "red",
  "ecru",
  "auburn",
  "lava",
  "white",
  "khaki",
  "eggplant",
];

// dash slime
//   ready on summon. moves each turn
const dashSlimes = [
  "sepia",
  "emerald",
  "apricot",
  "lavender",
  "teal",
  "saffron",
  "rust",
  "amber",
  "orange",
  "lilac",
  "turquoise",
  "mauve",
];

// attack closest creature, then closest structure.
const slimeAttackAI = (unit: Permanent, ctx: AIContext) => {
  const { inspector, ai } = ctx;

  ai.tryAttackFirst(unit, {
    sort: [
      "creaturesFirst",
      // nearest first
      { type: "euclideanDistance", slot: unit.slot },
    ],
  });
};

const dotSlimeAI = (unit: Permanent, ctx: AIContext) => {
  slimeAttackAI(unit, ctx);
};

// dash slimes move randomly each turn, then attack
const dashSlimeAI = (unit: Permanent, ctx: AIContext) => {
  const { engine } = ctx;

  const directions = [
    { x: 0, y: 1 },
    { x: 0, y: -1 },
    { x: 1, y: 0 },
    { x: -1, y: 0 },
  ];

  // pick a random direction to move
  while (directions.length > 0) {
    const idx = Math.floor(Math.random() * directions.length);
    const dir = directions.splice(idx, 1)[0];

    // we use engine.moveRel instead of ai.tryMoveRel since we want slime to remain ready to attack.
    if (engine.moveRel(unit, dir.x, dir.y)) break;
  }

  slimeAttackAI(unit, ctx);
};

const slimeHandler: PermanentEventHandlers = {
  /** TODO: currently, this event triggers before the slime is destroyed.
  /*  so a new slime won't spawn in the same slot as the parent
  /*  imo (amon's opinion), this looks cooler, but in the event of slime flood, 
  /*  we may want to allow this.
  /*/
  [PermanentEventType.BEFORE_DESTROYED]: (event) => {
    const { inspector, engine } = event.ctx;
    // spawn child slimes (usually 2)
    const name = inspector.getCardName(event.permanent);
    const slot = event.permanent.slot;
    slimeSpawnOrder
      .find((s) => name.startsWith(s.parent))
      ?.children.forEach((child) => {
        const emptySlots = inspector.getUnoccupiedSlotsClosestTo(
          Player.P2,
          slot
        );

        let spawnSlot = slot;
        // try to spawn the child in the closest empty slot
        for (const emptySlot of emptySlots) {
          if (engine.spawn(child + "-slime", emptySlot, Player.P2)) {
            spawnSlot = emptySlot;
            break;
          }
        }

        // dot-slimes start frozen for one turn
        if (dotSlimes.includes(child)) {
          const slime = inspector.getPermanentAtIfExists(spawnSlot);
          if (!slime) return;
          engine.addCounter(slime, {
            type: CounterType.FROZEN,
            explanation: "will not ready on its next turn",
            expiry: PermanentEventType.START_TURN,
          });
        }
        // TODO: trigger loss if out of slots?
      });

    // track order of slimes killed
    const ext = engine.getExtraState() as ExtraState;
    const namePrefix = name.split("-")[0];
    ext.slimeKilledOrder.push(namePrefix);
  },
};

export const Puzzle44Spec: GameSpecServerFragment = {
  disableP2NoBasesLossCondition: true,
  counterData: {},

  cardEffects: {
    slime: {
      handlers: slimeHandler,
    },
    ...dotSlimes.reduce(
      (acc: { [key: string]: { handlers: PermanentEventHandlers } }, curr) => {
        acc[curr + "-slime"] = {
          handlers: {
            ...slimeHandler,
            // dotSlime adds +1 poison to any unit it attacks
            [PermanentEventType.AFTER_DEAL_DAMAGE]: (event) => {
              const { defender, ctx } = event;
              const { engine } = ctx;
              engine.mergeValueCounter(
                defender,
                CounterType.POISON,
                1,
                (poison) => `On start of turn, takes ${poison} Damage.`
              );
            },
          },
        };
        return acc;
      },
      {}
    ),
    // TODO: ready dashSlimes on Summon
    ...dashSlimes.reduce(
      (acc: { [key: string]: { handlers: PermanentEventHandlers } }, curr) => {
        acc[curr + "-slime"] = { handlers: slimeHandler };
        return acc;
      },
      {}
    ),
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();
    const units = inspector.getPermanentsOf(Player.P2);
    if (turnNumber == 0) {
      ai.trySpawn("slime", 1, 2);
    }
    // act with all present units
    for (const unit of units) {
      const unitName = inspector.getCardName(unit);
      if (dotSlimes.includes(unitName.split("-")[0])) {
        dotSlimeAI(unit, ctx);
      } else if (dashSlimes.includes(unitName.split("-")[0])) {
        dashSlimeAI(unit, ctx);
      } else if (unitName === "slime") {
        slimeAttackAI(unit, ctx);
      }
    }
  },
  afterSummon: (permanent, ctx) => {
    const { inspector, engine } = ctx;
    const name = inspector.getCardName(permanent);
    if (dashSlimes.includes(name.split("-")[0])) {
      // TODO: ready dashSlimes on Summon
    }
  },
  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      slimeKilledOrder: [],
    } as ExtraState);
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;

    return (
      player === Player.P2 && inspector.getPermanentsOf(player).length === 0
    );
  },
  extraSolveChecks: (player, winner, ctx) => {
    const { engine } = ctx;
    const ext = engine.getExtraState() as ExtraState;
    const slimeKilledOrder = ext.slimeKilledOrder;

    // compare kill order with morse *EAIRLSHTMGNDBKCOQUFVWJPXYZ
    // mapped to ["slime", "yellow", "amber", "lemon", ...]
    const correctOrder = slimeSpawnOrder.map((slime) => slime.parent);
    if (slimeKilledOrder.length !== correctOrder.length) return false;
    for (let i = 0; i < slimeKilledOrder.length; i++) {
      if (slimeKilledOrder[i] !== correctOrder[i]) return false;
    }
    return true;
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine, ai } = ctx;
    if (!(solves[Player.P1] ?? false)) {
      ai.speak(
        "The Slime disintegrated back into the sludge! It looks like you failed to befriend it. How puzzling!",
        {
          hideBattler: true,
          speaker: Speaker.NARRATOR,
        }
      );
    }
  },
};
