import { EffectOptType } from "engine/types/effects";
import { AbilityType } from "engine/types/effects";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { Player } from "engine/types/game-state";

type ExtraState = {
  lastKnownBaseDamage: number;
};

export const Puzzle31Spec: GameSpecServerFragment = {
  // logic
  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      lastKnownBaseDamage: 0,
    });
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    const aiBase = inspector.getPermanents({
      owner: Player.P2,
      isBase: true,
    })[0];
    if (aiBase !== undefined) {
      const ext = engine.getExtraState() as ExtraState;
      if (ext.lastKnownBaseDamage === 0 && aiBase.damage !== 0) {
        ai.speak("i got hit... am i going to l, o, s, e again?");
        ext.lastKnownBaseDamage = aiBase.damage;
      }
    }
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    const units = inspector.getPermanentsOf(Player.P2);
    const aiBase = units.filter(
      (unit) => inspector.getSharedEffects(unit).isBase
    )[0];
    if (aiBase === undefined) return;

    // on turn 0, try to spawn mirrors, and we're done
    if (inspector.getTurnNumber() === 0) {
      ai.speak(
        "if only i could w, i, n just once, i'd be so much braver. if only i had a f, r, i, e, n, d..."
      );
      ai.trySpawn("beeflector", 3, 0);
      ai.trySpawn("beeflector", 3, 1);
      ai.trySpawn("beeflector", 3, 2);
      ai.trySpawn("beeflector", 3, 3);
      ai.trySpawn("beeflector", 3, 4);
      return;
    }

    for (const unit of units) {
      // medics try to special anything that has taken damage
      // and on failure, will attack "smartly"
      if (inspector.getCardName(unit) === "reckless-medisaur") {
        let healed = false;
        // look at permanents in order of opponents > ours, then sorted
        // by how much damage they've taken
        const targets = inspector.getPermanents({
          sort: [(p) => (p.owner === Player.P1 ? 0 : 1), (p) => -p.damage],
        });
        for (const target of targets) {
          healed = ai.tryActivateAbility(unit, AbilityType.SPECIAL, [
            { type: EffectOptType.PERMANENT, permanentId: aiBase.id },
            { type: EffectOptType.PERMANENT, permanentId: target.id },
          ]);
          if (healed) break;
        }
        // attack nearest, prioritizing bases
        if (!healed) {
          ai.tryAttackFirst(unit, {
            sort: [
              "structuresFirst",
              { type: "euclideanDistance", slot: unit.slot },
            ],
          });
        }
      } else if (!inspector.getSharedEffects(unit).isBase) {
        // bb b might have a unit from blancmange, so those should attack too.
        // attack nearest, prioritizing bases
        ai.tryAttackFirst(unit, {
          sort: [
            "structuresFirst",
            { type: "euclideanDistance", slot: unit.slot },
          ],
        });
      }
    }

    // try to spawn a medic
    for (const col of [1, 3, 0, 4, 2]) {
      if (ai.trySpawn("reckless-medisaur", 1, col)) break;
    }
    // try to spawn a mirror in opponent territory
    let mirrorSpawns = 0;
    for (const row of [3, 4, 5]) {
      for (const col of [0, 2, 4, 1, 3]) {
        if (ai.trySpawn("beeflector", row, col)) {
          mirrorSpawns += 1;
        }
        if (mirrorSpawns === 3) break;
      }
      if (mirrorSpawns === 3) break;
    }
  },
  // solved iff ai won
  disableBattleWonSolvedCondition: true,
  extraSolveChecks: (_, winner) => {
    return winner === Player.P2;
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine, ai } = ctx;
    if (!(solves[Player.P1] ?? false)) {
      ai.speak(
        "Before you got the chance to use the uwuand, bb b zipped away crying! Looks like you failed to befriend it.",
        {
          hideBattler: true,
        }
      );
    } else {
      engine.clearKeyframe();
    }
  },
};
