import { Faction, makeInitFactionScores } from "engine/types/factions";
import { SOLVE_COUNTS_MASTERY_ID } from "engine/puzzles/mastery-data";
import { GameServer } from "game-server/GameServer";
import {
  GlobalUpdatesScopeType,
  GlobalUpdateType,
  GlobalStatePuzzleStats,
  ClientSpeedrunTimeRecord,
  SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH,
  RankedLeaderboard,
  TeamNameDisplayDataUpdate,
} from "game-server/global-updates";
import {
  TeamSummaryUpdate,
  teamSummaryUpdateToTeamNameDisplayUpdate,
} from "game-server/monitoring";

export type SpeedrunTimeRecord = {
  teamId: string;
  speedrunTime: number;
};

export type SolveTimeRecord = {
  teamId: string;
  solveTime: number;
};

export class StatsController {
  server: GameServer;

  /** Map from battleGroupName to the number of teams that unlocked it. */
  unlockCounts: Map<string, number>;
  /** Map from battleGroupName to the number of teams that solved it. */
  solveCounts: Map<string, number>;
  /** Total faction scores. */
  factionScores: { [faction in Faction]: number };
  factionHistory: { [key: string]: number };
  factionHistoryUnsynced: { [key: string]: number };

  speedrunLeaderboards: {
    [puzName: string]: RankedLeaderboard<SpeedrunTimeRecord>;
  };
  solveTimeLeaderboards: {
    [puzName: string]: RankedLeaderboard<SolveTimeRecord>;
  };

  constructor(server: GameServer) {
    this.server = server;

    this.unlockCounts = new Map(
      Array.from(this.server.puzzlesDB.battleGroups.keys(), (puzName) => [
        puzName,
        0,
      ])
    );
    this.solveCounts = new Map(
      Array.from(this.server.puzzlesDB.battleGroups.keys(), (puzName) => [
        puzName,
        0,
      ])
    );
    this.factionScores = makeInitFactionScores();
    this.factionHistory = {};
    this.factionHistoryUnsynced = {};

    const { puzzles } = this.server.puzzlesDB;
    // Include the dummy battles, in case we want to keep track
    // of overall battle times too.
    this.speedrunLeaderboards = {};
    this.solveTimeLeaderboards = {};
  }

  getSpeedrunLeaderboard(
    puzName: string
  ): RankedLeaderboard<SpeedrunTimeRecord> {
    this.speedrunLeaderboards[puzName] ??= new RankedLeaderboard(
      (record) => record.teamId,
      (record1, record2) => record1.speedrunTime - record2.speedrunTime
    );
    return this.speedrunLeaderboards[puzName];
  }

  getSolveTimeLeaderboard(puzName: string): RankedLeaderboard<SolveTimeRecord> {
    this.solveTimeLeaderboards[puzName] ??= new RankedLeaderboard(
      (record) => record.teamId,
      (record1, record2) => record1.solveTime - record2.solveTime
    );
    return this.solveTimeLeaderboards[puzName];
  }

  getPuzzleStats(battleGroupName: string): GlobalStatePuzzleStats {
    const unlockCount = this.unlockCounts.get(battleGroupName);
    const solveCount = this.solveCounts.get(battleGroupName);
    if (unlockCount === undefined || solveCount === undefined)
      throw new Error("expect stats to be defined");
    return { unlockCount, solveCount };
  }

  getSpeedrunRank(teamId: string, puzName: string): number | null {
    const speedrunTime = this.server
      .getTeamController(teamId)
      .speedrunTimes.get(puzName);
    if (speedrunTime === undefined) return null;
    const { sortedList } = this.getSpeedrunLeaderboard(puzName);
    const rank = sortedList.findIndex((record) => record.teamId === teamId);
    if (rank === undefined) {
      console.error("expect team to exist in leaderboard");
      // This isn't a critical error; return gracefully.
      return null;
    }
    return rank;
  }

  getSpeedrunLeaderboardSummary(puzName: string): ClientSpeedrunTimeRecord[] {
    const { sortedList } = this.getSpeedrunLeaderboard(puzName);
    return sortedList
      .slice(0, SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH)
      .map(({ teamId, speedrunTime }) => ({
        teamData: this.server.getTeamController(teamId).getTeamSummaryState(),
        speedrunTime,
      }));
  }

  /**
   * Update and broadcast stats/leaderboard updates when a team's
   * stats gets updated. This should only be called after the
   * updates have been merged into TeamController state.
   */
  handleTeamUpdates(
    teamId: string,
    opts: {
      factionScoreChanges?: { [faction in Faction]?: number };
      // This flag lets us distinguish between updates (which should create new
      // history entries) and initialization (which should not).
      updateFactionHistory?: boolean;
      solvesToDelete?: string[];
      unlocksToDelete?: string[];
      solvesToAdd?: string[];
      unlocksToAdd?: string[];
      solveTimes?: { [puzName: string]: number | null };
      speedrunTimes?: { [puzName: string]: number | null };
      /** If set, force the updates even if the team is hidden. */
      forceIfHidden?: boolean;
    }
  ): void {
    const {
      factionScoreChanges,
      updateFactionHistory,
      solvesToDelete,
      unlocksToDelete,
      solvesToAdd,
      unlocksToAdd,
      solveTimes,
      speedrunTimes,
      forceIfHidden = false,
    } = opts;

    const isHidden = this.server.getTeamController(teamId).isHidden;
    // By default, don't update any stats if the team is hidden.
    if (!forceIfHidden && isHidden) return;

    if (factionScoreChanges !== undefined) {
      const timeBucket = Math.floor(Date.now() / 1000 / 60 / 60);
      const factionHistoryToBroadcast: { [key: string]: number } = {};
      for (const [kFaction, diffFactionScore] of Object.entries(
        factionScoreChanges ?? {}
      )) {
        const faction = kFaction as Faction;
        this.factionScores[faction] += diffFactionScore;
        if (updateFactionHistory && diffFactionScore) {
          const key = `${kFaction}.${timeBucket}`;
          const value = (this.factionHistory[key] ?? 0) + diffFactionScore;
          this.factionHistory[key] = value;
          this.factionHistoryUnsynced[key] = value;
          factionHistoryToBroadcast[key] = value;
        }
      }
      const factionScoresToUpdate = Object.fromEntries(
        Object.entries(this.factionScores).filter(
          ([kFaction, score]) =>
            factionScoreChanges[kFaction as Faction] ?? 0 !== 0
        )
      );
      for (const teamController of this.server.teamControllers.values()) {
        teamController.broadcastTeamGlobalUpdate({
          type: GlobalUpdateType.TEAM_UPDATE_STATE,
          factionScores: factionScoresToUpdate,
        });
      }
      if (Object.keys(factionHistoryToBroadcast).length) {
        this.server.broadcastGlobalUpdate(
          { type: GlobalUpdatesScopeType.FACTION_HISTORY },
          {
            type: GlobalUpdateType.FACTION_HISTORY_UPDATE_STATE,
            factionHistory: factionHistoryToBroadcast,
          }
        );
      }
    }

    // Only update the stats of the puzzles we're tracking.
    // For example, some puzNames may be sub-battles, and we don't
    // track stats for those.
    for (const puzName of solvesToDelete ?? []) {
      const solveCount = this.solveCounts.get(puzName);
      if (solveCount === undefined) continue;
      this.solveCounts.set(puzName, solveCount - 1);
    }
    for (const puzName of solvesToAdd ?? []) {
      const solveCount = this.solveCounts.get(puzName);
      if (solveCount === undefined) continue;
      this.solveCounts.set(puzName, solveCount + 1);
    }
    for (const puzName of unlocksToDelete ?? []) {
      const unlockCount = this.unlockCounts.get(puzName);
      if (unlockCount === undefined) continue;
      this.unlockCounts.set(puzName, unlockCount - 1);
    }
    for (const puzName of unlocksToAdd ?? []) {
      const unlockCount = this.unlockCounts.get(puzName);
      if (unlockCount === undefined) continue;
      this.unlockCounts.set(puzName, unlockCount + 1);
    }

    const updatedPuzzles = new Set([
      ...(unlocksToAdd ?? []),
      ...(unlocksToDelete ?? []),
      ...(solvesToAdd ?? []),
      ...(solvesToDelete ?? []),
    ]);
    const stats = Object.fromEntries(
      [...updatedPuzzles].flatMap((puzName) =>
        this.server.puzzlesDB.battleGroups.has(puzName)
          ? [[puzName, this.getPuzzleStats(puzName)]]
          : []
      )
    );
    for (const teamController of this.server.teamControllers.values()) {
      if (
        !teamController.masteryTreeController
          .getEnabledMasteries()
          .has(SOLVE_COUNTS_MASTERY_ID)
      )
        continue;
      teamController.broadcastTeamGlobalUpdate({
        type: GlobalUpdateType.TEAM_UPDATE_PUZZLE_STATS,
        stats: Object.fromEntries(
          Object.entries(stats).filter(([battleGroupName, puzStats]) =>
            teamController.unlockTimes.has(battleGroupName)
          )
        ),
      });
    }

    for (const [puzName, solveTime] of Object.entries(solveTimes ?? {})) {
      const leaderboard = this.getSolveTimeLeaderboard(puzName);
      if (solveTime === null) {
        leaderboard.remove(teamId);
        continue;
      }
      this.addSolveTimeRecord(teamId, puzName, solveTime);
    }

    for (const [puzName, speedrunTime] of Object.entries(speedrunTimes ?? {})) {
      if (speedrunTime === null) this.removeSpeedrunRecord(teamId, puzName);
      else this.addSpeedrunRecord(teamId, puzName, speedrunTime);
    }
  }

  startSync(): { [key: string]: number } {
    return { ...this.factionHistoryUnsynced };
  }

  endSync(snapshot: { [key: string]: number }): void {
    for (const [key, value] of Object.entries(snapshot))
      if (this.factionHistoryUnsynced[key] == value)
        delete this.factionHistoryUnsynced[key];
  }

  private addSolveTimeRecord(
    teamId: string,
    puzName: string,
    solveTime: number
  ) {
    const leaderboard = this.getSolveTimeLeaderboard(puzName);
    leaderboard.insert({ teamId, solveTime }, false);
  }

  private addSpeedrunRecord(
    teamId: string,
    puzName: string,
    speedrunTime: number
  ) {
    const leaderboard = this.getSpeedrunLeaderboard(puzName);
    const { oldIndex, newIndex } = leaderboard.insert(
      { teamId, speedrunTime },
      true
    );

    this.broadcastSpeedrunLeaderboardUpdates(
      puzName,
      newIndex,
      oldIndex === null ? leaderboard.sortedList.length : oldIndex + 1,
      {
        speedrunRecordsToAdd: [
          {
            teamData: this.server
              .getTeamController(teamId)
              .getTeamSummaryState(),
            speedrunTime,
          },
        ],
      }
    );
  }

  private removeSpeedrunRecord(teamId: string, puzName: string): void {
    const leaderboard = this.getSpeedrunLeaderboard(puzName);
    const removeRes = leaderboard.remove(teamId);
    if (removeRes === null) return;

    const { oldIndex } = removeRes;
    const newLast =
      leaderboard.sortedList[SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH - 1];
    this.broadcastSpeedrunLeaderboardUpdates(
      puzName,
      oldIndex,
      leaderboard.sortedList.length,
      {
        speedrunRecordsToAdd:
          newLast === undefined
            ? undefined
            : [
                {
                  teamData: this.server
                    .getTeamController(newLast.teamId)
                    .getTeamSummaryState(),
                  speedrunTime: newLast.speedrunTime,
                },
              ],
        speedrunRecordsToRemove: [teamId],
      }
    );
  }

  /**
   * Hide the team.
   * Warning: Not idempotent. Only call if the team was previously
   * not hidden.
   */
  hideTeam(teamId: string): void {
    const teamController = this.server.getTeamController(teamId);
    this.handleTeamUpdates(teamId, {
      factionScoreChanges: Object.fromEntries(
        Object.entries(teamController.factionScoreContributions).map(
          ([faction, contrib]) => [faction, -contrib]
        )
      ),
      updateFactionHistory: true,
      solvesToDelete: [...teamController.solveTimes.keys()],
      unlocksToDelete: [...teamController.unlockTimes.keys()],
      solveTimes: Object.fromEntries(
        Array.from(teamController.solveTimes.keys(), (puzName) => [
          puzName,
          null,
        ])
      ),
      speedrunTimes: Object.fromEntries(
        Array.from(teamController.speedrunTimes.keys(), (puzName) => [
          puzName,
          null,
        ])
      ),
      forceIfHidden: true,
    });
  }

  /**
   * Unhide the team.
   * Warning: Not idempotent. Only call if the team was previously hidden.
   */
  unhideTeam(teamId: string): void {
    const teamController = this.server.getTeamController(teamId);
    this.handleTeamUpdates(teamId, {
      factionScoreChanges: teamController.factionScoreContributions,
      updateFactionHistory: true,
      solvesToAdd: [...teamController.solveTimes.keys()],
      unlocksToAdd: [...teamController.unlockTimes.keys()],
      solveTimes: Object.fromEntries(teamController.solveTimes.entries()),
      speedrunTimes: Object.fromEntries(teamController.speedrunTimes.entries()),
      forceIfHidden: true,
    });
  }

  /**
   * Broadcast speedrun leaderboard updates, where the changes impact
   * all ranks in the range [rankStart, rankEnd). The actual updates
   * may be broadcast to more teams than the given range, if the affected
   * range includes the summary presented to all teams.
   */
  private broadcastSpeedrunLeaderboardUpdates(
    puzName: string,
    rankStart: number,
    rankEnd: number,
    upds: {
      speedrunRecordsToAdd?: ClientSpeedrunTimeRecord[];
      speedrunRecordsToRemove?: string[];
    }
  ): void {
    const leaderboard = this.getSpeedrunLeaderboard(puzName);
    const needUpdateTopRankings =
      rankStart < SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH;
    const updateStart = needUpdateTopRankings ? 0 : rankStart;
    const updateEnd = needUpdateTopRankings
      ? leaderboard.sortedList.length
      : Math.min(rankEnd, leaderboard.sortedList.length);
    for (
      let updateIndex = updateStart;
      updateIndex < updateEnd;
      updateIndex++
    ) {
      const { teamId: updateTeamId } = leaderboard.sortedList[updateIndex];
      const teamController = this.server.getTeamController(updateTeamId);

      teamController
        .getBattleController(puzName)
        .broadcastTeamSpeedrunLeaderboardSummaryGlobalUpdate({
          type: GlobalUpdateType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY_UPDATE_STATE,
          speedrunRank: updateIndex,
          // No need to send the updates besides the rank if it
          // doesn't affect top rankings.
          ...(needUpdateTopRankings ? upds : undefined),
        });
    }
  }

  handleTeamSummaryUpdates(upds: {
    [teamId: string]: TeamSummaryUpdate;
  }): void {
    for (const [puzName, leaderboard] of Object.entries(
      this.speedrunLeaderboards
    )) {
      const teamNameDisplayUpds: {
        [teamId: string]: TeamNameDisplayDataUpdate;
      } = {};
      for (const { teamId } of leaderboard.sortedList.slice(
        0,
        SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH
      )) {
        const teamUpd = upds[teamId];
        if (teamUpd === undefined) continue;
        const teamNameDisplayUpd =
          teamSummaryUpdateToTeamNameDisplayUpdate(teamUpd);
        if (Object.keys(teamNameDisplayUpd).length === 0) continue;
        teamNameDisplayUpds[teamId] = teamNameDisplayUpd;
      }
      if (Object.keys(teamNameDisplayUpds).length === 0) continue;
      for (const teamController of this.server.teamControllers.values()) {
        teamController
          .getBattleController(puzName)
          .broadcastTeamSpeedrunLeaderboardSummaryGlobalUpdate({
            type: GlobalUpdateType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY_UPDATE_STATE,
            teamNameDisplayUpds,
          });
      }
    }
  }
}
