import {
  CursorEventType,
  CursorEventMove,
  CursorEvent,
  CursorResp,
} from "engine/types/presence";
import { WSRespType } from "game-server/ws";
import { ClientConnection, GameServer } from "game-server/GameServer";

export class CursorsController {
  server: GameServer;
  /**
   * The cursor groups, keyed first by teamId, then by the
   * cursor group name (usually the roomId). A cursor group
   * is represented as a set of client connections, keyed by connId.
   */
  cursorGroups: Map<string, Map<string, CursorGroup>>;

  constructor(server: GameServer) {
    this.server = server;
    this.cursorGroups = new Map();
  }

  private getTeamCursorGroups(teamId: string) {
    const teamCursorGroups = this.cursorGroups.get(teamId);
    if (teamCursorGroups !== undefined) return teamCursorGroups;
    const newTeamCursorGroups = new Map();
    this.cursorGroups.set(teamId, newTeamCursorGroups);
    return newTeamCursorGroups;
  }

  private getCursorGroup(teamId: string, cursorGroupName: string) {
    const teamCursorGroups = this.getTeamCursorGroups(teamId);
    const cursorGroup = teamCursorGroups.get(cursorGroupName);
    if (cursorGroup !== undefined) return cursorGroup;
    const newCursorGroup = new CursorGroup();
    teamCursorGroups.set(cursorGroupName, newCursorGroup);
    return newCursorGroup;
  }

  private getConnCursorGroup(conn: ClientConnection) {
    const { teamId, cursorGroupName } = conn;
    if (teamId === undefined) return null;
    if (cursorGroupName === undefined) return null;
    return this.getCursorGroup(teamId, cursorGroupName);
  }

  removeConnFromGroup(conn: ClientConnection) {
    this.broadcastEvent(conn, {
      type: CursorEventType.REMOVE,
    });

    const { teamId, cursorGroupName } = conn;
    if (teamId === undefined) return;
    if (cursorGroupName === undefined) return;
    const teamCursorGroups = this.cursorGroups.get(teamId);
    if (teamCursorGroups === undefined) {
      console.error("expect team cursor group to exist");
      return;
    }
    const cursorGroup = teamCursorGroups.get(cursorGroupName);
    if (cursorGroup === undefined) {
      console.error("expect cursor group to exist");
      return;
    }
    cursorGroup.conns.delete(conn.connId);
    if (cursorGroup.conns.size === 0) teamCursorGroups.delete(cursorGroupName);
    if (teamCursorGroups.size === 0) this.cursorGroups.delete(teamId);
    delete conn.cursorGroupName;
  }

  addConnToGroup(conn: ClientConnection, cursorGroupName: string) {
    this.removeConnFromGroup(conn);
    const { teamId } = conn;
    if (teamId === undefined) return;
    this.getCursorGroup(teamId, cursorGroupName).conns.set(conn.connId, conn);
    conn.cursorGroupName = cursorGroupName;
  }

  async broadcastEvent(srcConn: ClientConnection, event: CursorEvent) {
    if (srcConn.cursorGroupName === undefined) return;
    const cursorGroup = this.getConnCursorGroup(srcConn);
    if (cursorGroup === null) return;
    cursorGroup.addEvent(srcConn, event);
    if (cursorGroup.locked) return;
    cursorGroup.locked = true;
    await new Promise((resolve) => setTimeout(resolve, 0));
    const queuedEvents: CursorResp[] = cursorGroup.pullEvents();
    for (const dstConn of cursorGroup.conns.values()) {
      const events = queuedEvents.filter(
        ({ connId }) => dstConn.connId !== connId
      );
      if (events.length)
        this.server.sendResp(dstConn, {
          type: WSRespType.CURSOR_EVENT,
          events,
        });
    }
    cursorGroup.locked = false;
  }
}

class CursorGroup {
  conns: Map<string, ClientConnection>;
  queuedMoves: Map<string, CursorEventMove>;
  queuedEvents: [string, CursorEvent][];
  locked: boolean;

  constructor() {
    this.conns = new Map();
    this.queuedMoves = new Map();
    this.queuedEvents = [];
    this.locked = false;
  }

  addEvent({ connId }: ClientConnection, event: CursorEvent) {
    if (event.type == CursorEventType.MOVE) this.queuedMoves.set(connId, event);
    else this.queuedEvents.push([connId, event]);
  }

  pullEvents(): CursorResp[] {
    const events = [...this.queuedMoves.entries(), ...this.queuedEvents];
    this.queuedMoves.clear();
    this.queuedEvents.length = 0;
    return events.map(([connId, event]) => ({ connId, event }));
  }
}
