import { CardEffects } from "engine/cards/card-effects";
import { PermanentEventType } from "engine/server-hooks";
import { CounterType } from "engine/types/counters";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { areSlotsEqual } from "engine/types/game-state";
import { Slot } from "engine/types/game-state";
import { Player } from "engine/types/game-state";
import { StepType } from "engine/types/steps";

const ALL_CARDS = [
  "arvada",
  "burlington",
  "centennial",
  "grover",
  "cow-butte",
  "ramsey-peak",
  "saddle-mountain",
  "finger-rock",
] as ReadonlyArray<string>;
const NUM_UNITS = 8;

const OUR_DESTINATION: { [id: string]: [number, number] } = {
  arvada: [0, 4],
  grover: [2, 0],
  burlington: [0, 2],
  centennial: [3, 4],
  "cow-butte": [3, 3],
  "ramsey-peak": [3, 1],
  "saddle-mountain": [0, 1],
  "finger-rock": [0, 3],
};

const ENEMY_DESTINATION: { [id: string]: [number, number] } = {
  arvada: [1, 3],
  grover: [0, 4],
  burlington: [1, 6],
  centennial: [1, 4],
  "cow-butte": [2, 5],
  "ramsey-peak": [0, 3],
  "saddle-mountain": [2, 1],
  "finger-rock": [0, 2],
};

const toSlot = (a: [number, number]): Slot => ({ row: a[0], column: a[1] });

const BASE_EFFECT: CardEffects = {
  handlers: {
    [PermanentEventType.CREATED]: (event) => {
      event.ctx.engine.addCounter(event.permanent, {
        type: CounterType.BASE_GEMS,
        val: 1,
      });
    },
  },
};

interface ExtraState {
  isAngry: boolean;
}

export const Puzzle90Spec: GameSpecServerFragment = {
  cardEffects: {
    arvada: BASE_EFFECT,
    burlington: BASE_EFFECT,
    centennial: BASE_EFFECT,
  },
  disableNoBasesLossCondition: true,
  afterInitEngine: (ctx) => {
    ctx.engine.initExtraState({
      isAngry: false,
    });
  },
  beforeNewGame: (ctx) => {
    for (const name of ALL_CARDS) {
      ctx.engine.addCardToHand(Player.P1, { name });
      ctx.engine.spawn(name, toSlot(ENEMY_DESTINATION[name]), Player.P2);
    }
  },
  extraLoseChecks: (player, ctx) => {
    const { inspector } = ctx;
    if (player === Player.P1) {
      const stillAlive =
        inspector.getPermanentsOf(player).length +
          inspector.gameState[player].hand.length ===
        NUM_UNITS;
      if (!stillAlive) {
        ctx.ai.speak("...you have no hope of victory now...");
      }
      return !stillAlive;
    }
    // P2
    const cards = inspector.getPermanentsOf(Player.P1);
    const checkDestination = (dest: typeof OUR_DESTINATION) => {
      return (
        cards.length >= NUM_UNITS &&
        // every card...
        cards.every((card) => {
          if (!card.card.name) throw new Error("oops");
          const destination = dest[card.card.name];
          // has a destination and is in that destination
          return destination && areSlotsEqual(card.slot, toSlot(destination));
        })
      );
    };
    if (checkDestination(OUR_DESTINATION)) {
      ctx.ai.speak("");
      return true;
    }
    return false;
  },
  afterStep: (step, ctx) => {
    // anger check
    const extraState = ctx.engine.getExtraState() as ExtraState;
    if (!extraState.isAngry) {
      if (ctx.inspector.getPermanentsOf(Player.P2).length !== NUM_UNITS) {
        ctx.ai.speak(
          "...must coordinate troops... ...partition the land... ...degree by degree..."
        );
        extraState.isAngry = true;
      }
    }

    if (step.type !== StepType.ATTACK) {
      return;
    }
    if (ctx.inspector.getPermanentsOf(Player.P2).length === 0) {
      ctx.ai.speak(
        "...my strength is gone... ...but you won’t take me... ...not with that formation..."
      );
    }
  },
  aiTurn: (ctx) => {
    const { inspector, ai } = ctx;
    if (!(ctx.engine.getExtraState() as ExtraState).isAngry) {
      return;
    }

    // for each unit we own...
    const sneakyPower = 0;
    const totalPower = 0;
    for (const unit of inspector.getPermanentsOf(Player.P2)) {
      if (!unit.ready) {
        continue;
      }
      const power = inspector.getPower(unit);
      if (power <= 0) {
        continue;
      }

      ctx.ai.tryAttackFirst(unit, {
        sort: ["health"],
      });
      // const isSneaky = unit.slot.row >= 2;
      // totalPower += power;
      // if (isSneaky) {
      //   sneakyPower += power;
      // }
    }
  },
};
