import { cloneDeck, DeckSelection } from "engine/types/decks";
import { StepType } from "engine/types/steps";
import { Role } from "engine/types/updates";
import {
  GamePhase,
  Player,
  PlayerStateTeamData,
  FullGameState,
  MutableGameState,
  CheckpointState,
  SerializedCheckpointState,
  isCheckpointSlotValid,
} from "engine/types/game-state";
import { FailedChecks } from "engine/types/action-validation";
import {
  PUZ_NAME_PVP,
  BATTLE_GROUP_NAME_INSTANCER,
  isInstanceable,
} from "engine/puzzles/puzzle-data";
import { StaticInspector } from "engine/Inspector";
import { Engine } from "engine/Engine";
import { WSErrorCode, WSError, WSRespType } from "game-server/ws";
import { ClientConnection, GameServer } from "game-server/GameServer";
import { BackendRoom } from "game-server/backend-interface/BackendInterface";
import {
  SyncTracker,
  SyncObjectType,
} from "game-server/backend-interface/backend-sync";
import {
  GlobalUpdatesScopeType,
  GlobalUpdateType,
  GlobalUpdate,
  GlobalStateBattleRoomStatus,
  ClientActiveGameGlobalState,
  ClientErratum,
  TeamBattleUpdateStateGlobalUpdate,
  CheckpointSummary,
} from "game-server/global-updates";
import { GameRoom, UnjoinedRoomMember, RoomMember } from "game-server/Room";
import { TeamController } from "./team-controller";

type ResolveDeckResult =
  | {
      deckSelection: DeckSelection | null;
      err?: undefined;
    }
  | {
      deckSelection?: undefined;
      err: WSError;
    };

type CommitDeckSelectionResult =
  | {
      deckSelections: { [player in Player]?: DeckSelection };
      err?: undefined;
    }
  | {
      deckSelections?: undefined;
      err: WSError;
    };

/** Controller to manage the room for a team/battle combination. */
export class TeamBattleController {
  server: GameServer;

  teamId: string;
  puzName: string;
  /** Normally equal to puzName, but may be different in instancers. */
  selectedPuzName: string | null;

  /**
   * The selected deck slot in the prep page, before the user has set
   * "ready for battle".
   */
  selectedDeckSlot: number | null;
  /**
   * Details about the pending game, if any. This needs to be separate
   * from the activeGame state, since we want to be able to start
   * setting up a pending game while keeping details about the previous
   * game.
   */
  pendingGame?: {
    deckSelection: DeckSelection | null;
  };

  checkpoints: { [slot: number]: SerializedCheckpointState };
  checkpointsSyncTracker: SyncTracker;

  /**
   * Map of connId to connections for connections subscribed to
   * updates for this battle.
   */
  subscriptions: Map<string, ClientConnection>;
  /**
   * Map of connId to connections for connections subscribed to
   * updates for this battle's speedrun leaderboard summary.
   */
  speedrunLeaderboardSummarySubscriptions: Map<string, ClientConnection>;

  constructor(
    teamId: string,
    puzName: string,
    selectedPuzName: string | null,
    selectedDeckSlot: number | null,
    server: GameServer
  ) {
    this.teamId = teamId;
    this.puzName = puzName;
    this.selectedPuzName = selectedPuzName;
    this.server = server;

    this.selectedDeckSlot = selectedDeckSlot;
    this.checkpoints = {};
    this.checkpointsSyncTracker = new SyncTracker(
      this.server.config,
      SyncObjectType.CHECKPOINTS,
      (dirtyState) => {
        this.server.dirtyStateTracker.update({
          teams: {
            [this.teamId]: {
              checkpoints: {
                [this.puzName]: dirtyState,
              },
            },
          },
        });
      }
    );
    this.subscriptions = new Map();
    this.speedrunLeaderboardSummarySubscriptions = new Map();
  }

  getTeamController(): TeamController {
    return this.server.getTeamController(this.teamId);
  }

  getBattleGroupName(): string {
    return this.server.getBattleGroup(this.puzName).battleGroupName;
  }

  wantsErrataFor(puzzle: string): boolean {
    const { puzName, selectedPuzName } = this;
    if (
      puzzle === puzName ||
      puzzle === this.server.getBattleGroup(puzName).battleGroupName
    )
      return true;
    if (selectedPuzName === null) return false;
    if (
      puzzle === selectedPuzName ||
      puzzle === this.server.getBattleGroup(selectedPuzName).battleGroupName
    )
      return true;
    return false;
  }

  filterErrata(conn: ClientConnection): { [erratumId: string]: ClientErratum } {
    const errata: { [erratumId: string]: ClientErratum } = {};
    for (const [
      erratumId,
      { puzzle, text, timestamp, published },
    ] of Object.entries(this.server.errata))
      if (
        puzzle !== null &&
        text !== "" &&
        this.wantsErrataFor(puzzle) &&
        (published || conn.isAdmin)
      )
        errata[erratumId] = { erratumId, text, timestamp, published };
    return errata;
  }

  broadcastErrataGlobalUpdate(
    upd: Omit<TeamBattleUpdateStateGlobalUpdate, "type">
  ) {
    const subscriptions = this.server.getSubscriptions({
      type: GlobalUpdatesScopeType.TEAM_BATTLE,
      teamId: this.teamId,
      puzName: this.puzName,
    });
    for (const conn of subscriptions.values())
      this.server.sendResp(conn, {
        type: WSRespType.GLOBAL_UPDATE,
        upd: {
          type: GlobalUpdateType.TEAM_BATTLE_UPDATE_STATE,
          errata: this.filterErrata(conn),
          ...upd,
        },
      });
  }

  getActiveRoom(): GameRoom | null {
    return this.server.gameRoomsController.getActiveRoom(
      this.teamId,
      this.puzName
    );
  }

  private setActiveRoom(room: GameRoom): void {
    this.server.gameRoomsController.setActiveRoom(
      this.teamId,
      this.puzName,
      room.roomId
    );
  }

  getRoomStatus(): GlobalStateBattleRoomStatus {
    const room = this.getActiveRoom();
    if (room === null) return GlobalStateBattleRoomStatus.NONE;
    const { gameState } = room.engine;
    if (gameState.phase === GamePhase.ACTIVE)
      return GlobalStateBattleRoomStatus.ACTIVE;
    return GlobalStateBattleRoomStatus.INACTIVE;
  }

  getRoomPuzName(): string | null {
    const room = this.getActiveRoom();
    if (room === null) return null;
    return room.engine.puzName;
  }

  isRoomActive(): boolean {
    return this.getRoomStatus() === GlobalStateBattleRoomStatus.ACTIVE;
  }

  getInspector(): StaticInspector {
    if (this.selectedPuzName === null)
      throw new Error("need puzzle selection to get inspector");
    return this.server.gameRoomsController.getInspector(this.selectedPuzName);
  }

  private getPlayerInGameState(gameState: FullGameState): Player {
    const playerCandidates = [Player.P1, Player.P2].filter((player) => {
      const { teamData } = gameState[player];
      return teamData !== undefined && teamData.teamId === this.teamId;
    });
    // Retain support for vanilla to enable workshop.
    // TODO: If we ever have intra-team PvP, this would instead
    // need to depend on which side of the board we're on.
    if (playerCandidates.length !== 1 && this.selectedPuzName !== "vanilla")
      throw new Error("team not uniquely part of active game");
    return playerCandidates[0];
  }

  getPlayer(): Player {
    const room = this.getActiveRoom();
    if (room === null)
      throw new Error("player not defined if there's no active game");
    const { gameState } = room.engine;
    return this.getPlayerInGameState(gameState);
  }

  getActiveGameTeamData(): PlayerStateTeamData {
    const room = this.getActiveRoom();
    if (room === null)
      throw new Error("tried to get team data without an active game");
    const { teamData } = room.engine.gameState[this.getPlayer()];
    if (teamData === undefined)
      throw new Error("expect team data to be present in active game");
    return teamData;
  }

  getActiveGameDeckSelection(): DeckSelection | null {
    const room = this.getActiveRoom();
    if (room === null)
      throw new Error("tried to get team data without an active game");
    return (
      room.engine.gameState.engineOnly.deckSelections[this.getPlayer()] ?? null
    );
  }

  /**
   * Leave the currently active game. Returns a list of room members
   * kicked from the active game room, if any. This may be only a
   * subset of the members originally in the room if there are
   * multiple teams connected to the room.
   */
  private leaveActiveGame(): RoomMember[] {
    const room = this.getActiveRoom();
    if (room === null) return [];

    const membersToKick = [...room.members.values()].filter((roomMember) => {
      const { teamId, spectateTeamId } = roomMember.conn;
      if (teamId === undefined)
        throw new Error(
          "expect conn to be authenticated if it is joined to a room"
        );
      return teamId === this.teamId || spectateTeamId === this.teamId;
    });
    for (const roomMember of membersToKick) {
      this.server.kickRoomMember(roomMember);
    }

    return membersToKick;
  }

  /**
   * To start a new battle, we first confirm a selected deck, and
   * then start the battle. This separation is only relevant
   * in PvP, where some time may pass between a team committing
   * to a deck and accepting a match.
   * This method commits the deck selection into a pending game,
   * presuming that the team may act as any of the specified players
   * for the battle.
   * Returns the valid player-deck combinations produced.
   */
  commitDeckSelection(
    players: ReadonlyArray<Player>,
    reuseDeck?: boolean,
    allowInvalidDecks?: boolean
  ): CommitDeckSelectionResult {
    if (this.isRoomActive())
      throw new Error(
        "don't expect to set up a new game while a game is active"
      );

    const inspector = this.getInspector();
    const deckPlayers = players.filter(
      (player) => inspector.getDeckOverride(player) === null
    );
    const { deckSelection, err } = this.resolveDeckForBattle(
      deckPlayers,
      reuseDeck,
      allowInvalidDecks
    );
    if (err !== undefined) return { err };

    this.pendingGame = {
      deckSelection,
    };

    const deckSelections: { [player in Player]?: DeckSelection } = {};
    if (deckSelection === null) {
      if (deckPlayers.length > 0 && !inspector.isNoDeckAllowed())
        throw new Error("expect a deck selection if players need decks");
    } else {
      if (deckSelection !== null) {
        for (const player of deckPlayers) {
          deckSelections[player] = deckSelection;
        }
      }
    }

    return { deckSelections };
  }

  /**
   * Move connections from the active game into a new room.
   * Members joining the new room are assigned the specified role,
   * unless they were GOD in the old room.
   */
  private moveConnsIntoRoom(room: GameRoom, role: Role): void {
    const prevMembers = this.leaveActiveGame();
    for (const { conn, role: prevRole } of prevMembers) {
      this.server.joinConnToRoom(
        conn,
        room,
        prevRole === Role.GOD ? prevRole : role
      );
    }
  }

  /**
   * Activate the pending game by moving the team into a new room.
   * The room is provided externally to support sharing the same
   * room across multiple teams in PvP scenarios.
   * The deck selection is saved to support reuse deck on retry.
   *
   * Members joining the new room are assigned the specified role,
   * unless they were GOD in the old room. If not specified, the
   * default role is picked based on the game spec.
   */
  activateGame(room: GameRoom, role?: Role): void {
    const inspector = this.getInspector();
    role ??= inspector.getDefaultRole();

    // Transfer members of this team from the old room to the new one.
    this.moveConnsIntoRoom(room, role);

    if (this.pendingGame === undefined)
      throw new Error("no pending game to activate");
    delete this.pendingGame;

    this.setActiveRoom(room);
    this.handleRoomStatusChange();
  }

  getLockedDeckSelection(): DeckSelection | null {
    // Only use the active game deck selection if the room is active.
    // Otherwise, the deck selection is only there to support retry
    // with same deck, and does not need to be visible to the client.
    if (this.isRoomActive()) return this.getActiveGameDeckSelection();
    return this.pendingGame?.deckSelection ?? null;
  }

  getClientActiveGameState(): ClientActiveGameGlobalState | null {
    const room = this.getActiveRoom();
    if (!this.isRoomActive()) return null;
    if (room === null)
      throw new Error("expect an active game if the room status is active");
    const { enabledMasteries, faction } = this.getActiveGameTeamData();
    return { enabledMasteries, faction };
  }

  broadcastTeamBattleGlobalUpdate(upd: GlobalUpdate): void {
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM_BATTLE,
        teamId: this.teamId,
        puzName: this.puzName,
      },
      upd
    );
  }

  broadcastTeamSpeedrunLeaderboardSummaryGlobalUpdate(upd: GlobalUpdate): void {
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY,
        teamId: this.teamId,
        puzName: this.puzName,
      },
      upd
    );
  }

  handleRoomStatusChange(): void {
    const { teamId, puzName } = this;
    const roomStatus = this.getRoomStatus();
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM,
        teamId,
      },
      {
        type: GlobalUpdateType.TEAM_UPDATE_PUZZLE,
        puzName,
        roomStatus,
      }
    );
    this.broadcastLockedDeckSelection();
    this.broadcastTeamBattleGlobalUpdate({
      type: GlobalUpdateType.TEAM_BATTLE_UPDATE_STATE,
      activeGame: this.getClientActiveGameState(),
      roomPuzName: this.getRoomPuzName(),
    });
    if (puzName === PUZ_NAME_PVP) {
      this.server.handleTeamSummaryUpdate(this.teamId, {
        isPvPGameActive: this.isRoomActive(),
      });
    }
  }

  broadcastLockedDeckSelection(): void {
    this.broadcastTeamBattleGlobalUpdate({
      type: GlobalUpdateType.TEAM_BATTLE_UPDATE_STATE,
      lockedDeckSelection: this.getLockedDeckSelection(),
    });
  }

  summarizeCheckpoints(): { [slot: number]: CheckpointSummary } {
    return Object.fromEntries(
      Object.entries(this.checkpoints).map(
        ([slot, { turnNumber, roomId, timestamp }]) => [
          slot,
          { turnNumber, roomId, timestamp },
        ]
      )
    );
  }

  makeCheckpoint(engine: Engine, slots: number[]): void {
    const { roomId, gameState } = engine;
    const checkpoint: CheckpointState = {
      selectedPuzName: this.selectedPuzName,
      gameState,
    };
    const serialized: SerializedCheckpointState = {
      turnNumber: gameState.turnNumber,
      roomId,
      timestamp: Date.now(),
      gainedScore: gameState.engineOnly.gainedScore,
      serializedState: JSON.stringify(checkpoint),
    };
    for (const slot of slots) this.checkpoints[slot] = serialized;
    this.handleCheckpointsChange();
  }

  restoreCheckpoint(conn: ClientConnection, slot: number): void {
    if (!isCheckpointSlotValid(slot))
      return this.server.sendErr(conn, {
        errCode: WSErrorCode.INVALID_CHECKPOINT_SLOT,
      });
    const room = this.getActiveRoom();
    if (room === null) {
      this.server.sendErr(conn, { errCode: WSErrorCode.NO_ROOM });
      return;
    }
    const { gameState } = room.engine;
    const serializedCheckpoint = this.checkpoints[slot];
    if (serializedCheckpoint === undefined) {
      this.server.sendErr(conn, { errCode: WSErrorCode.NO_CHECKPOINT });
      return;
    }
    const checkpoint = JSON.parse(
      serializedCheckpoint.serializedState
    ) as CheckpointState;

    if (checkpoint.selectedPuzName !== null) {
      const err = this.selectPuzzle(checkpoint.selectedPuzName, true);
      if (err !== null)
        throw new Error("should not encounter error re-selecting the old puzz");
    }

    // Lock the deck selection back to the original deck selection.
    // The old deck contents are derived from the game state and
    // shown to the player separately as part of the active game
    // state broadcast in the room status update handler.
    const player = this.getPlayerInGameState(checkpoint.gameState);
    const deck = checkpoint.gameState.engineOnly.deckSelections[player];
    const deckSlot = deck?.slot ?? null;
    if (deckSlot !== null) {
      const err = this.selectDeck(deckSlot, true);
      if (err !== null)
        throw new Error("should not encounter error re-selecting the old deck");
    }

    // HACK: It might be possible to restore an old checkpoint from before
    // solving after having solved a battle. Fix up the isReplay state.
    for (const player of [Player.P1, Player.P2]) {
      const { teamData } = checkpoint.gameState[player];
      if (teamData === undefined) continue;
      teamData.isReplay = this.server
        .getTeamController(teamData.teamId)
        .solveTimes.has(checkpoint.gameState.puzName);
    }

    checkpoint.gameState.engineOnly.gainedScore =
      serializedCheckpoint.gainedScore;

    // We might restore from a different puzzle in instancer rooms.
    const isSamePuzzle = gameState.puzName === checkpoint.gameState.puzName;

    // HACK: Create a fake BackendRoom and restore it. No need to set
    // optional fields that aren't used by restoreSavedRoom.
    // If we're restoring from the same puzzle, initialize the new room
    // with the current game state first and then send an override, in
    // order to animate the restoration process.
    const newRoom = this.restoreSavedRoom({
      roomId: this.server.genGameRoomId(),
      puzName: checkpoint.gameState.puzName,
      hostPuzName: this.puzName,
      createTime: new Date().getTime(),
      savedRoom: { gameState: isSamePuzzle ? gameState : checkpoint.gameState },
    });
    if (newRoom === null) throw new Error("expect restore room to succeed");
    if (isSamePuzzle) {
      this.server.useUpdatesContext(newRoom, () => {
        newRoom.engine.broadcastOverrideState(checkpoint.gameState);
      });
    }
    this.handleRoomStatusChange();

    // Update the roomIds of all checkpoints attributed to the same
    // room as the restored checkpoint, so that they show to the user
    // as belonging to the currently active room.
    const oldRoomId = serializedCheckpoint.roomId;
    for (const [slot, checkpoint] of Object.entries(this.checkpoints)) {
      if (checkpoint.roomId === oldRoomId) {
        this.checkpoints[Number(slot)] = {
          ...checkpoint,
          roomId: newRoom.roomId,
        };
      }
    }
    this.handleCheckpointsChange();
  }

  private handleCheckpointsChange() {
    this.checkpointsSyncTracker.handleModify();
    this.broadcastTeamBattleGlobalUpdate({
      type: GlobalUpdateType.TEAM_BATTLE_UPDATE_STATE,
      checkpointSummaries: this.summarizeCheckpoints(),
    });
  }

  handleRoomComplete(roomId: string, gainedScore: number) {
    for (const [slot, checkpoint] of Object.entries(this.checkpoints))
      if (checkpoint.roomId === roomId)
        this.checkpoints[Number(slot)] = { ...checkpoint, gainedScore };
    this.handleCheckpointsChange();
    this.handleRoomStatusChange();
  }

  resign(): WSError | null {
    const room = this.getActiveRoom();
    if (room === null) return { errCode: WSErrorCode.NO_ROOM };
    if (!this.isRoomActive()) {
      // This might be caused by a race condition, so just return
      // silently.
      return null;
    }

    this.server.sendSystemStep(room, {
      type: StepType.SYSTEM_RESIGN,
      player: this.getPlayer(),
    });
    return null;
  }

  handleEnterGameRoom(unjoinedMember: UnjoinedRoomMember): void {
    const { conn, role } = unjoinedMember;
    const room = this.getActiveRoom();

    // The client should only request to join the game room once
    // a game has been set up.
    if (room === null) {
      this.server.sendEnterRoomErr(unjoinedMember, {
        errCode: WSErrorCode.NO_ROOM,
      });
      return;
    }

    // Only allow arbitrary roles as admin.
    if (role !== undefined && !conn.isAdmin) {
      this.server.sendEnterRoomErr(unjoinedMember, {
        errCode: WSErrorCode.PERMISSION_DENIED,
      });
      return;
    }

    // Currently, the default role is always captured in what player
    // the team is joined to the room as.
    const defaultRole = ((): Role => {
      switch (this.getPlayer()) {
        case Player.P1:
          return Role.P1;
        case Player.P2:
          return Role.P2;
      }
    })();

    this.server.completeEnterRoom(
      {
        ...unjoinedMember,
        role: role ?? defaultRole,
      },
      room
    );
  }

  selectPuzzle(
    selectedPuzName: string | null,
    restoring?: boolean
  ): WSError | null {
    // Early out to avoid broadcasting an unnecessary update.
    if (
      this.selectedPuzName !== null &&
      this.selectedPuzName === selectedPuzName
    )
      return null;

    if (this.getBattleGroupName() !== BATTLE_GROUP_NAME_INSTANCER)
      return { errCode: WSErrorCode.INVALID_PUZZLE };
    if (this.isRoomActive() && !restoring)
      return { errCode: WSErrorCode.LOCKED_BY_BATTLE };

    if (selectedPuzName !== null) {
      const puzData = this.server.puzzlesDB.tryGetPuzzle(selectedPuzName);
      const battleGroupData =
        this.server.puzzlesDB.tryGetBattleGroup(selectedPuzName);
      if (puzData === null || battleGroupData === null)
        return { errCode: WSErrorCode.INVALID_PUZZLE };
      if (
        !isInstanceable(
          puzData.answer !== null,
          puzData.puzName,
          puzData.battleGroupName,
          battleGroupData.numPuzzles
        )
      )
        return { errCode: WSErrorCode.INVALID_PUZZLE };
    }

    this.selectedPuzName = selectedPuzName;
    this.getTeamController().mergeSlowSyncPacket({
      puzzleSelections: {
        [this.puzName]: selectedPuzName,
      },
    });
    this.server.bigBoardStateTracker.updateTeam(this.teamId, {
      selectedPuzzles: {
        [this.puzName]: selectedPuzName,
      },
    });

    this.broadcastErrataGlobalUpdate({ selectedPuzName });
    return null;
  }

  selectDeck(slot: number, restoring?: boolean): WSError | null {
    // Don't select the deck if no change is required.
    if (slot === this.selectedDeckSlot) return null;

    if (this.isRoomActive() && !restoring)
      return { errCode: WSErrorCode.LOCKED_BY_BATTLE };

    this.selectedDeckSlot = slot;
    this.getTeamController().mergeSlowSyncPacket({
      preferredDecks: {
        [this.puzName]: slot,
      },
    });
    this.broadcastTeamBattleGlobalUpdate({
      type: GlobalUpdateType.TEAM_BATTLE_UPDATE_STATE,
      selectedDeckSlot: slot,
    });
    return null;
  }

  /**
   * Resolve the team's deck as players for the battle. If resolve fails,
   * send an error to conn and return null.
   * Allow multiple players to support joining as both players in dev.
   */
  private resolveDeckForBattle(
    /** Players that will be assigned the resolved deck, if any. */
    players: Player[],
    reuseDeck?: boolean,
    allowInvalidDecks?: boolean
  ): ResolveDeckResult {
    // If there are no players that need a deck, then don't get one.
    // In particular, battles with assigned P1 decks should not
    // require the user to make deck selections.
    if (players.length <= 0) return { deckSelection: null };

    const { selectedDeckSlot } = this;
    const room = this.getActiveRoom();
    const inspector = this.getInspector();

    if ((reuseDeck ?? false) && room !== null) {
      return { deckSelection: this.getActiveGameDeckSelection() };
    }

    if (selectedDeckSlot === null) {
      if (inspector.isNoDeckAllowed()) return { deckSelection: null };
      if (allowInvalidDecks ?? false) {
        return {
          deckSelection: {
            slot: null,
            deck: {
              displayName: "autogen deck",
              cards: {},
            },
          },
        };
      }

      return { err: { errCode: WSErrorCode.NEED_DECK } };
    }

    const deck =
      this.getTeamController().getDeckController(selectedDeckSlot).deck;
    for (const player of players) {
      const failedChecks: FailedChecks = new Set();
      inspector.validateDeck(failedChecks, player, deck);
      if (!(allowInvalidDecks ?? false) && failedChecks.size !== 0) {
        return { err: { errCode: WSErrorCode.INVALID_DECK } };
      }
    }

    return {
      deckSelection: {
        slot: selectedDeckSlot,
        deck: cloneDeck(deck),
      },
    };
  }

  restoreSavedRoom(roomEntry: BackendRoom): GameRoom | null {
    const { roomId, hostPuzName, puzName, savedRoom } = roomEntry;
    if (savedRoom === undefined) return null;
    this.selectPuzzle(puzName);
    // Check if the room already exists, which may be the case when
    // restoring PvP rooms.
    const existingRoomTracker =
      this.server.gameRoomsController.rooms.get(roomId);
    const room =
      existingRoomTracker?.room ??
      this.server.gameRoomsController.createRoomFromGameState(
        puzName,
        hostPuzName,
        // Warning: This is necessary even if we're not concerned about
        // type safety, since if this is called from restoreCheckpoint,
        // and the previous game has ended, the game state might not
        // have been synced to backend yet and so should be treated as
        // immutable. If we want to remove this deep copy, then
        // restoreCheckpoint should be updated to deep copy the game
        // state if the game has ended.
        JSON.parse(JSON.stringify(savedRoom.gameState)) as MutableGameState,
        roomId
      );
    const defaultRole = ((): Role => {
      switch (this.getPlayerInGameState(room.engine.gameState)) {
        case Player.P1:
          return Role.P1;
        case Player.P2:
          return Role.P2;
      }
    })();
    this.moveConnsIntoRoom(room, defaultRole);
    this.setActiveRoom(room);
    return room;
  }
}
