import {
  CreatedPermanentEvent,
  EndTurnPermanentEvent,
  PermanentEventType,
} from "engine/server-hooks";
import { CardType } from "engine/types/card-data";
import { CounterType } from "engine/types/counters";
import { AIContext, GameSpecServerFragment } from "engine/types/game-specs";
import { Player } from "engine/types/game-state";
import { UpdateType } from "engine/types/updates";
import { CardEffects } from "engine/cards/card-effects";
import { Puzzle76SharedExtraState } from "engine/game-specs/puzzle-76-shared";

const LAST_WAVE = 6;

// make sure to modify the card's text in Deck sheet as well
const DINOSAUR_TAIL_TURNS = 5;
const BOAR_TUSKS_TURNS = 2;
const DRYAD_HORNS_TURNS = 14;
const COW_UDDER_TURNS = 4;
const BEE_WINGS_TURNS = 3;
const KERO_TURNS = 5;

const announceDoom = (ctx: AIContext, dialogue: string, turnsLeft: number) => {
  const { ai } = ctx;
  ai.speak(`${dialogue}\n${Array(turnsLeft).fill("⏳").join(" ")}`);
};

const summonDoomHandler = (turns: number) => (event: CreatedPermanentEvent) => {
  const { permanent, ctx } = event;
  const { engine } = ctx;

  engine.addCounter(permanent, {
    type: CounterType.DOOM,
    val: turns,
  });
};

const endTurnDoomHandler =
  (turns: number) => (event: EndTurnPermanentEvent) => {
    // note this triggers before the counter itself decrements
    // check for doom counter
    const { permanent, ctx } = event;
    const { inspector, engine, ai } = ctx;
    const sharedExt =
      inspector.getSharedExtraState() as Puzzle76SharedExtraState;

    if (inspector.doesPermanentHaveCounterType(permanent, CounterType.DOOM)) {
      const doomValue = inspector.getCounterValSum(permanent, CounterType.DOOM);
      // console.log(`doom counter exists with ${doomValue}`);

      const beforeMyCardDestroysYou = `before my ${inspector.getCardDisplayName(
        permanent
      )} destroys you!`;

      if (doomValue === turns) {
        // ai.speak(
        //   `Wave ${sharedExt.keroPhase}: ${doomValue} turns left ${beforeMyCardDestroysYou}`
        // );
      } else if (doomValue > 1) {
        // ai.speak(`${doomValue} turns left ${beforeMyCardDestroysYou}`);
      } else if (doomValue === 1) {
        // ai.speak(`Last turn ${beforeMyCardDestroysYou}`);
      } else if (doomValue === 0) {
        // destroy all enemy units
        const enemyUnits = inspector.getPermanents({
          ownedByOpponentOf: permanent.owner,
        });

        enemyUnits.forEach((unit) => {
          engine.destroy(permanent, unit);
        });

        // destroy counter
        engine.removeCounterType(permanent, CounterType.DOOM);
        engine.announce(
          `${inspector.getCardDisplayName(permanent)} destroys all enemy units`
        );
      }
    }
  };

const cowLegEffects: CardEffects = {
  // Whenever an allied unit dies, gain +0/+1.
  handlers: {
    [PermanentEventType.AFTER_DAMAGED]: (event) => {
      const { permanent, attacker, ctx } = event;
      const { inspector, engine } = ctx;
      // Attack back only if the damage comes from an attacker.
      if (attacker) {
        engine.announce(
          `${inspector.getCardDisplayName(
            permanent
          )} attacks back for 1 damage!`
        );
        // Do not attribute damage to prevent triggering other card effects.
        engine.damageUnattributed(attacker, 1);
      }
    },
    [PermanentEventType.OTHER_UNIT_DESTROYED]: (event) => {
      const { permanent, destroyedPermanent, ctx } = event;
      const { inspector, engine } = ctx;

      if (permanent.owner === destroyedPermanent.owner) {
        engine.adjustPermanentStats(permanent, 0, 1);
      }
      engine.announce(
        `${inspector.getCardDisplayName(permanent)} gains +1 max health`
      );
    },
  },
};

export const Puzzle76Spec: GameSpecServerFragment = {
  // counterData: {},
  cardEffects: {
    "kero-dos": {
      handlers: {
        [PermanentEventType.CREATED]: (ev) => {
          const { permanent, ctx } = ev;
          const { engine } = ctx;
          engine.addCounter(permanent, { type: CounterType.INVULNERABLE });
        },
      },
    },
    // wave 1
    "dinosaur-tail": {
      // Takes at most 1 damage from any single source of damage.
      adjustDamageReceived: (permanent, damage, attacker, ctx) => {
        if (damage >= 1) return 1;
        return damage;
      },
      // After 3 turns, destroy all enemy units.
      handlers: {
        [PermanentEventType.CREATED]: summonDoomHandler(DINOSAUR_TAIL_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(DINOSAUR_TAIL_TURNS),
      },
    },
    // wave 2
    "boar-tusks": {
      // Creatures that do damage to this Creature take 3 damage.
      // After 2 turns, destroy all enemy units.
      handlers: {
        [PermanentEventType.AFTER_DAMAGED]: (event) => {
          const { permanent, attacker, ctx } = event;
          const { inspector, engine } = ctx;
          // Attack back only if the damage comes from an attacker.
          if (attacker) {
            engine.announce(
              `${inspector.getCardDisplayName(
                permanent
              )} attacks back for 3 damage!`
            );
            // Do not attribute damage to prevent triggering other card effects.
            engine.damageUnattributed(attacker, 3);
          }
        },
        [PermanentEventType.CREATED]: summonDoomHandler(BOAR_TUSKS_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(BOAR_TUSKS_TURNS),
      },
    },
    // wave 3
    "dryad-horns": {
      handlers: {
        [PermanentEventType.CREATED]: summonDoomHandler(DRYAD_HORNS_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(DRYAD_HORNS_TURNS),
      },
    },
    // wave 4
    "cow-leg-white": cowLegEffects,
    "cow-leg-black": cowLegEffects,
    "cow-udder": {
      handlers: {
        [PermanentEventType.CREATED]: summonDoomHandler(COW_UDDER_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(COW_UDDER_TURNS),
        // Whenever an allied unit dies, gain +0/+1.
        [PermanentEventType.AFTER_DAMAGED]: (event) => {
          const { permanent, attacker, ctx } = event;
          const { inspector, engine } = ctx;
          // Attack back only if the damage comes from an attacker.
          if (attacker) {
            engine.announce(
              `${inspector.getCardDisplayName(
                permanent
              )} attacks back for 1 damage!`
            );
            // Do not attribute damage to prevent triggering other card effects.
            engine.damageUnattributed(attacker, 1);
          }
        },
        [PermanentEventType.OTHER_UNIT_DESTROYED]: (event) => {
          const { permanent, destroyedPermanent, ctx } = event;
          const { inspector, engine } = ctx;

          if (permanent.owner === destroyedPermanent.owner) {
            engine.adjustPermanentStats(permanent, 0, 1);
          }
          engine.announce(
            `${inspector.getCardDisplayName(permanent)} gains +1 max health`
          );
        },
      },
    },
    // wave 5
    "bee-wings": {
      handlers: {
        [PermanentEventType.CREATED]: summonDoomHandler(BEE_WINGS_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(BEE_WINGS_TURNS),
      },
      // Platinum Archangel doesn't take damage from enemy units.
      insteadOfDamaged: (permanent, damage, attacker, ctx) => {
        const { inspector } = ctx;
        if (
          attacker &&
          permanent.owner === inspector.getOpponentOf(attacker.owner)
        )
          return false;
        return true;
      },
    },
    // wave 6
    kero: {
      // After this creature takes damage, double its remaining health.
      handlers: {
        [PermanentEventType.CREATED]: summonDoomHandler(KERO_TURNS),
        [PermanentEventType.END_TURN]: endTurnDoomHandler(KERO_TURNS),
        [PermanentEventType.AFTER_DAMAGED]: (event) => {
          const { permanent, ctx } = event;
          const { inspector, engine } = ctx;
          const me = inspector.getPermanents({
            name: "kero",
            owner: Player.P2,
          })[0];
          if (!me) {
            return;
          }
          const health = inspector.getHealth(me);
          engine.adjustPermanentStats(me, 0, health);
          engine.announce(`Kero increases its Health by ${health}`);
        },
      },
    },
  },
  afterInitEngine: (ctx) => {
    const { inspector, engine } = ctx;
    engine.initSharedExtraState({
      keroPhase: 0,
    } as Puzzle76SharedExtraState);
  },
  afterStep: (step, ctx) => {
    const { inspector, engine } = ctx;
    const sharedExt =
      inspector.getSharedExtraState() as Puzzle76SharedExtraState;
    if (sharedExt.keroPhase === 4) {
      // cow leg phase only
      const udder = inspector.getPermanents({
        owner: Player.P2,
        name: "cow-udder",
      })[0];
      if (!udder) {
        return;
      }
      const numLegs =
        inspector.getPermanents({
          owner: Player.P2,
          name: "cow-leg-white",
        }).length +
        inspector.getPermanents({
          owner: Player.P2,
          name: "cow-leg-black",
        }).length;
      if (numLegs === 0) {
        engine.removeCounterType(udder, CounterType.INVULNERABLE);
      }
    }

    // If Kero Dos (and Kero) has been destroyed, the battle has ended.
    const enemyUnits = inspector.getPermanentsOf(Player.P2);
    if (enemyUnits.length === 0) {
      engine.modifySharedExtraState({
        keroPhase: sharedExt.keroPhase + 1,
      } as Puzzle76SharedExtraState);
      engine.getBackdoorController().broadcastAndApply({
        type: UpdateType.WAIT_ANIMATE,
        waitTime: 4000,
      });
    }
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const units = inspector.getPermanentsOf(Player.P2);
    const sharedExt =
      inspector.getSharedExtraState() as Puzzle76SharedExtraState;

    const hasCreatures = units.some(
      (unit) => inspector.getCardType(unit) === CardType.CREATURE
    );

    // new round if no Creatures left
    if (!hasCreatures) {
      engine.modifySharedExtraState({
        keroPhase: sharedExt.keroPhase + 1,
      } as Puzzle76SharedExtraState);

      // Only unit left is Kero Dos.
      const base = units[0];

      if (sharedExt.keroPhase > 1) {
        // deal 1 damage to kero dos
        engine.damageUnattributed(
          base,
          Math.round(inspector.getMaxHealth(base) / 6),
          true
        );
      }

      // set up new round
      // clear board of player's creatures
      inspector.getPermanentsOf(Player.P1).forEach((permanent) => {
        if (inspector.getCardType(permanent) === CardType.CREATURE) {
          engine.despawn(permanent);
        }
      });

      switch (sharedExt.keroPhase) {
        case 1:
          // // TEST to draw all cards
          // while (inspector.getDrawPile(Player.P1).length > 0)
          //   engine.drawCard(Player.P1);
          ai.trySpawn("dinosaur-tail", 1, 2);
          break;
        case 2:
          ai.trySpawn("boar-tusks", 1, 2);
          break;
        case 3:
          ai.trySpawn("dryad-horns", 1, 2);
          break;
        case 4: {
          ai.trySpawn("cow-udder", 1, 2);
          for (let row = 1; row < 3; row++) {
            for (let col = 0; col < 5; col++) {
              ai.trySpawn(
                (row + col) % 2 === 0 ? "cow-leg-white" : "cow-leg-black",
                row,
                col
              );
            }
          }
          const udder = inspector.getPermanents({
            owner: Player.P2,
            name: "cow-udder",
          })[0];
          if (udder) {
            engine.addCounter(udder, { type: CounterType.INVULNERABLE });
          }
          break;
        }
        case 5:
          ai.trySpawn("bee-wings", 1, 2);
          break;
        case 6: {
          engine.despawn(base);
          engine.spawn("kero", 0, 2, Player.P2);
          break;
        }
      }
    }

    const doomUnit = inspector.getPermanents({
      hasCounter: CounterType.DOOM,
    })[0];
    const turnsToDoom =
      doomUnit === undefined
        ? 0
        : inspector.getCounterValSum(doomUnit, CounterType.DOOM);
    switch (sharedExt.keroPhase) {
      case 1: {
        announceDoom(
          ctx,
          "How dare y'all turn against me?\nAfter everything we've been thruwu!\nHand over the cards, or...\nI'll smash all of you to bits with my tail! ( •̀_•́)💢",
          turnsToDoom
        );
        break;
      }
      case 2: {
        announceDoom(
          ctx,
          "You... destroyed my tail? How is that possible?!\nThe power of all our Dino friends was stored within it!\nNo matter. I still have power from all the other Factions!\nI'll destroy youwu with my tusks!! (╬ Ò﹏Ó)",
          turnsToDoom
        );
        break;
      }
      case 3: {
        announceDoom(
          ctx,
          "STOP IT!!! I'll destroy ALL of you! ヽ(#`Д´)ﾉ\nI'll destroy the whole uwuorld!\nIf these horns would just — *cough* 🌷 — charge up a little faster...\nAll the cards... will be MINE! MY FRIENDS!!! FOREVER!!!!",
          turnsToDoom
        );
        break;
      }
      case 4: {
        announceDoom(
          ctx,
          "What, no!!! GIVE THEM BACK!!! (ง ‵□′)ง\nYou were my FRIEND! ALL of you were my FRIENDS!!!\nWe were so close... the UWU... Just... give up already!!!\nI'll- I'll share some of the UWU with you, I promise!!!",
          turnsToDoom
        );
        break;
      }
      case 5: {
        announceDoom(
          ctx,
          "No... (๑′°︿°๑) I don't wanna...\nI don't wanna go back to being a fish again...\nI don't... I CAN'T go back in that box again...\nNot another 1000 years... alone...",
          turnsToDoom
        );
        break;
      }
      case 6: {
        announceDoom(
          ctx,
          "Nouwu~! Everything... EVERYTHING IS RUWUINED!!! (ಥ﹏ಥ)\nI... No, I can't give up, not nouwu...!\nI don't need you, I NEVER needed you!\nNot youwu, not cards... just the power... of UWU...",
          turnsToDoom
        );
        break;
      }
    }

    // TODO: uncomment
    // for (const unit of units) {
    //   // act with all present units, just use a basic attack
    //   ai.tryAttackFirst(unit);
    // }
  },
  disableP2NoBasesLossCondition: true,
  extraLoseChecks: (player, ctx) => {
    const { inspector, engine } = ctx;
    const sharedExt =
      inspector.getSharedExtraState() as Puzzle76SharedExtraState;
    return player === Player.P2 && sharedExt.keroPhase === 7;
  },
};
