export type GameServerConfig = {
  /**
   * Maximum number of completed rooms to batch into one request when
   * syncing to backend.
   */
  completedRoomsSyncLimit: number;
  /**
   * Maximum number of active rooms to batch into one request when syncing
   * to backend.
   */
  activeRoomsSyncLimit: number;
  /**
   * Maximum number of teams to batch into one request when syncing
   * decks and mastery trees to backend.
   */
  decksAndMasteryTreesTeamsSyncLimit: number;
  /**
   * Maximum number of teams to batch into one request when syncing
   * checkpoints to backend.
   */
  checkpointsSyncLimit: number;
  /**
   * Maximum number of teams to batch into one request when syncing
   * misc team state to backend.
   */
  miscTeamStateTeamsSyncLimit: number;
  /** Maximum number of parallel sync backend requests. */
  syncThrottleParallelism: number;
  /**
   * Amount of time to throttle the slow sync, depending on the
   * slow sync type.
   */
  slowSyncThrottle: { [syncObjectType in SyncObjectType]: number };
};

type GameServerOpts = {
  backend: BackendInterface;
  dumpWsMessages: boolean;
  dumpBackendRequests: boolean;
  periodicSyncInterval?: number;
  authInterface: AuthenticationInterface;
  parseWSReq: (req: object) => req is WSReq;
  cursorsMode?: boolean;
  /**
   * Dev backdoor called after each step if provided; used to
   * dump highest mem game states.
   */
  afterStepBackdoor?: (room: GameRoom) => void;
  logMetrics?: (msg: string) => Promise<void>;
  logCursorsMetrics?: (msg: string) => Promise<void>;
  metricsDumpInterval?: number;
  getCpuUsage?: () => {
    user: number;
    system: number;
  };
};

type ClientInterface = {
  send: (msg: string) => void;
  close: () => void;
};

export type ClientConnection = {
  connId: string;
  client: ClientInterface;
  // teamId only gets set once the connection is authenticated,
  // so it can be used to check if a connection is authenticated.
  teamId?: string;
  spectateTeamId?: string;
  /**
   * Whether the connection is an admin connection. An admin can be
   * connected as a non-admin team through impersonation.
   */
  isAdmin: boolean;
  // In the current design, each connection can only control
  // one room client at a time.
  roomMember?: RoomMember;
  /**
   * The cursor group that the client is in, denoted by puzName.
   * A client can only be part of one cursor group at a time.
   */
  cursorGroupName?: string;
  /** Global updates scopes that the connection is subscribed to. */
  subscriptions: Map<GlobalUpdatesScopeType, GlobalUpdatesScope>;
};

export class GameServer {
  /** Unique identifier from the backend for this instance. */
  gameServerId: string;
  cardsDB: CardsDB;
  puzzlesDB: PuzzlesDB;
  masteriesDB: MasteriesDB;
  keyframes: Keyframe[];
  opts: GameServerOpts;
  /**
   * Functions to be called once the GameServer is ready to
   * accept connections.
   */
  onReadyCallbacks: (() => void)[];

  // Static hunt config synced from Django on startup.

  maxGuessesPerPuzzle: number;
  huntStartTime: number;
  huntEndTime: number;

  backend: BackendInterfaceController;
  numActiveConns: number;
  /**
   * Map of team ID to the client connections authenticated for it,
   * indexed by connId.
   */
  teamConns: Map<string, Map<string, ClientConnection>>;
  /** Map of team ID to team controller. */
  teamControllers: Map<string, TeamController>;
  gameRoomsController: GameRoomsController;
  pvpLobbyController: PvPLobbyController;
  statsController: StatsController;
  cursorsController: CursorsController;
  dirtyStateTracker: ServerDirtyStateTracker;
  bigBoardStateTracker: BigBoardStateTracker;
  teamListStateTracker: TeamListStateTracker;
  teamMetricsTracker: TeamMetricsTracker;

  fastSyncMetrics: IntervalMetrics;
  slowSyncMetrics: IntervalMetrics;
  checkAnswerMetrics: IntervalMetrics;
  cursorEventsMetrics: IntervalMetrics;
  numAuthsMetrics: IntervalMetrics;
  numRequestsMetrics: IntervalMetrics;

  metricsDumpTracking: {
    lastDumpTime: number;
    lastUserTime: number;
    lastSystemTime: number;
    userTimeUsage: number;
    systemTimeUsage: number;
  };

  /**
   * Map of connId to connections for connections subscribed to
   * server updates.
   */
  subscriptions: Map<string, ClientConnection>;
  /**
   * Map of connId to connections for connections subscribed to
   * team list updates.
   */
  teamListSubscriptions: Map<string, ClientConnection>;
  /**
   * Map of connId to connections for connections subscribed to
   * faction history updates.
   */
  factionHistorySubscriptions: Map<string, ClientConnection>;
  /**
   * Map of connId to connections for connections subscribed to
   * server health updates.
   */
  serverHealthSubscriptions: Map<string, ClientConnection>;
  /**
   * Map of connId to connections for connections subscribed to
   * big board updates.
   */
  bigBoardSubscriptions: Map<string, ClientConnection>;

  /** Whether a init backend request is pending. */
  isInitPending: boolean;
  /** Whether the server is ready to accept requests. */
  isReady: boolean;
  /** Whether the server has been torn down. */
  isStopped: boolean;

  /** teamIds of teams that need init. */
  queuedTeamInits: Set<string>;
  isTeamInitInFlight: boolean;

  nextConnId: number;
  nextRoomMemberId: number;
  nextGameRoomId: number;
  nextAnswerSubmissionId: number;

  /** Prevent opening too many connections at a time. */
  syncThrottle: {
    /** Number of syncs currently in flight. */
    numSyncsInFlight: number;
    /**
     * Callbacks to trigger when there's capacity,
     * from earliest to latest.
     */
    queue: (() => void)[];
  };

  /**
   * Tracking state for the periodic backend sync.
   */
  slowSyncTracking: {
    isDownloadInFlight: boolean;
    decksAndMasteryTreesSyncController: DecksAndMasteryTreesSyncController;
    checkpointsSyncController: CheckpointsSyncController;
    miscTeamStateSyncController: MiscTeamStateSyncController;
    /** The notif index to start the next sync from. */
    notifsNextIndex: number;
  };

  numActivePeriodicTasksHandlers: number;
  periodicSyncInterval: number;
  /** Timer handle for periodic tasks. */
  periodicTasksTimer?: ReturnType<typeof setInterval>;

  numPendingSubmissionTasks: number;

  errata: { [erratumId: string]: BackendErratum };
  unclaimedHints: number;

  jwtCache: Map<string, HuntJwtPayload>;

  config: GameServerConfig;

  constructor(
    puzzlesDB: PuzzlesDB,
    cardsDB: CardsDB,
    masteriesDB: MasteriesDB,
    keyframes: Keyframe[],
    opts: GameServerOpts
  ) {
    // Unused data to initialize the fields with.
    // These should only be used after the game server is initialized
    // with the first getInitData backend call.
    // TODO: Probably wrap this in something that does the initialization
    // and passes these in during construction, to ensure that they
    // are only ever used after initialization.
    this.gameServerId = "__UNINITIALIZED__";
    this.maxGuessesPerPuzzle = 0;
    this.huntStartTime = 0;
    this.huntEndTime = 0;

    this.puzzlesDB = puzzlesDB;
    this.cardsDB = cardsDB;
    this.masteriesDB = masteriesDB;
    this.keyframes = keyframes;
    this.opts = opts;
    this.onReadyCallbacks = [];

    const backendOpts = {
      dumpBackendRequests: this.opts.dumpBackendRequests,
    };
    this.backend = new BackendInterfaceController({
      backend: this.opts.backend,
      dumpBackendRequests: this.opts.dumpBackendRequests,
    });
    this.numActiveConns = 0;
    this.teamConns = new Map();
    this.teamControllers = new Map();
    this.gameRoomsController = new GameRoomsController(this);
    this.pvpLobbyController = new PvPLobbyController(this);
    this.statsController = new StatsController(this);
    this.cursorsController = new CursorsController(this);
    this.dirtyStateTracker = new ServerDirtyStateTracker();
    this.bigBoardStateTracker = new BigBoardStateTracker();
    this.teamListStateTracker = new TeamListStateTracker();
    this.teamMetricsTracker = new TeamMetricsTracker(this);
    this.fastSyncMetrics = new IntervalMetrics(HEALTH_MONITORING_INTERVAL_MS);
    this.slowSyncMetrics = new IntervalMetrics(HEALTH_MONITORING_INTERVAL_MS);
    this.checkAnswerMetrics = new IntervalMetrics(
      HEALTH_MONITORING_INTERVAL_MS
    );
    this.cursorEventsMetrics = new IntervalMetrics(
      CURSOR_EVENTS_MONITORING_INTERVAL_MS
    );
    this.numAuthsMetrics = new IntervalMetrics(HEALTH_MONITORING_INTERVAL_MS);
    this.numRequestsMetrics = new IntervalMetrics(
      HEALTH_MONITORING_INTERVAL_MS
    );
    this.metricsDumpTracking = {
      lastDumpTime: 0,
      lastUserTime: 0,
      lastSystemTime: 0,
      userTimeUsage: 0,
      systemTimeUsage: 0,
    };
    this.subscriptions = new Map();
    this.teamListSubscriptions = new Map();
    this.factionHistorySubscriptions = new Map();
    this.serverHealthSubscriptions = new Map();
    this.bigBoardSubscriptions = new Map();
    this.isInitPending = false;
    this.isReady = false;
    this.isStopped = false;
    this.queuedTeamInits = new Set();
    this.isTeamInitInFlight = false;
    this.nextConnId = 0;
    this.nextRoomMemberId = 0;
    this.nextGameRoomId = 0;
    this.nextAnswerSubmissionId = 0;
    this.syncThrottle = {
      numSyncsInFlight: 0,
      queue: [],
    };
    this.slowSyncTracking = {
      isDownloadInFlight: false,
      decksAndMasteryTreesSyncController:
        new DecksAndMasteryTreesSyncController(this),
      checkpointsSyncController: new CheckpointsSyncController(this),
      miscTeamStateSyncController: new MiscTeamStateSyncController(this),
      notifsNextIndex: 0,
    };

    this.numActivePeriodicTasksHandlers = 0;
    // 3 seconds by default
    this.periodicSyncInterval = this.opts.periodicSyncInterval ?? 3000;
    this.periodicTasksTimer = setInterval(() => {
      this.runPeriodicTasks();
    }, this.periodicSyncInterval);

    this.numPendingSubmissionTasks = 0;

    this.errata = {};
    this.unclaimedHints = 0;

    this.jwtCache = new Map();

    this.config = {
      completedRoomsSyncLimit: 10,
      activeRoomsSyncLimit: 10,
      decksAndMasteryTreesTeamsSyncLimit: 10,
      checkpointsSyncLimit: 10,
      miscTeamStateTeamsSyncLimit: 10,
      syncThrottleParallelism: 2,
      slowSyncThrottle: {
        [SyncObjectType.FAST]: 0,
        [SyncObjectType.MISC]: 0,
        [SyncObjectType.MASTERY_TREE]: 0,
        [SyncObjectType.DECK]: 0,
        [SyncObjectType.ROOM]: 0,
        [SyncObjectType.CHECKPOINTS]: 0,
        [SyncObjectType.ANSWER]: 0,
      },
    };
  }

  /**
   * TODO: This is just a test utility to remove pending tasks and
   * allow tests to end gracefully. If this is to be used in a
   * production server, it needs to ensure that all pending promises
   * are responded to (e.g. onReadyCallbacks).
   *
   * This is now also used for the shared worker mock server. There
   * are memory leaks with pending promises, but it should not be
   * okay for now since users are not expected to reset repeatedly.
   */
  teardown(): void {
    if (this.periodicTasksTimer !== undefined)
      clearInterval(this.periodicTasksTimer);
    for (const teamConnsForTeam of this.teamConns.values()) {
      for (const conn of teamConnsForTeam.values()) {
        conn.client.close();
      }
    }
    this.isStopped = true;
  }

  genConnId() {
    const connId = this.nextConnId;
    this.nextConnId++;
    return `${connId}`;
  }

  genRoomMemberId() {
    const memberId = this.nextRoomMemberId;
    this.nextRoomMemberId++;
    return `${memberId}`;
  }

  genGameRoomId() {
    const roomId = `${this.gameServerId}_room_${this.nextGameRoomId}`;
    this.nextGameRoomId++;
    return roomId;
  }

  getTeamController(teamId: string): TeamController {
    const teamController = this.teamControllers.get(teamId);
    if (teamController === undefined)
      throw new Error(`team ${teamId} not initialized yet`);
    return teamController;
  }

  getBattleController(teamId: string, puzName: string): TeamBattleController {
    return this.getTeamController(teamId).getBattleController(puzName);
  }

  getDeckController(teamId: string, slot: number): DeckController {
    return this.getTeamController(teamId).getDeckController(slot);
  }

  getMasteryTreeController(teamId: string): TeamMasteryTreeController {
    return this.getTeamController(teamId).masteryTreeController;
  }

  isValidPuzzle(teamController: TeamController, puzName: string): boolean {
    return (
      this.puzzlesDB.puzzles.has(puzName) &&
      teamController.isPuzzleUnlocked(puzName)
    );
  }

  isValidDeckSlot(slot: number) {
    return Number.isInteger(slot) && slot >= 0 && slot < NUM_DECK_SLOTS;
  }

  async waitForReadyAsync(): Promise<void> {
    if (this.isReady) return;
    await new Promise<void>((resolve, reject) => {
      this.onReadyCallbacks.push(resolve);
    });
  }

  async tryInitAsync(): Promise<void> {
    if (this.isReady) return;
    if (this.isInitPending) return;

    this.isInitPending = true;
    try {
      const resp = await (async () => {
        try {
          return await this.backend.getInitData();
        } catch (err) {
          // If we fail, the periodic tasks handler will call this again.
          console.error(err);
        }
      })();

      if (resp !== undefined) {
        const {
          gameServerId,
          teams,
          factionHistory,
          maxGuessesPerPuzzle,
          huntStartTime,
          huntEndTime,
          notifsNextIndex,
          errata,
          unclaimedHints,
        } = resp;

        this.gameServerId = gameServerId;
        this.maxGuessesPerPuzzle = maxGuessesPerPuzzle;
        this.huntStartTime = huntStartTime;
        this.huntEndTime = huntEndTime;
        this.slowSyncTracking.notifsNextIndex = notifsNextIndex;
        this.errata = errata;
        this.unclaimedHints = unclaimedHints;
        for (const [teamId, teamData] of Object.entries(teams)) {
          await this.createTeamAsync(teamId, teamData);
        }
        Object.assign(this.statsController.factionHistory, factionHistory);

        this.isReady = true;
        for (const callback of this.onReadyCallbacks) {
          callback();
        }
        this.onReadyCallbacks = [];
        console.log("Game server init complete.");
      }
    } finally {
      this.isInitPending = false;
    }
  }

  async initAsync(): Promise<void> {
    if (this.isReady || this.isInitPending)
      throw new Error("don't expect to request init more than once");
    if (this.opts.cursorsMode ?? false) {
      this.isReady = true;
      return;
    }
    await this.tryInitAsync();
    await this.waitForReadyAsync();
  }

  /**
   * Returns an interface for the added client, containing
   * a function to handle received messages.
   * For development, a particular connId may be specified
   * to aid debugging.
   */
  addClient(client: ClientInterface, connId?: string) {
    this.numActiveConns++;

    const conn = {
      connId: connId ?? this.genConnId(),
      client: client,
      // The connection only ever gets marked as admin after auth.
      isAdmin: false,
      subscriptions: new Map(),
    };

    return {
      handleMsg: (msg: string) => {
        (async () => {
          try {
            await this.handleMsg(conn, msg);
          } catch (err) {
            console.error(err);
            this.sendErr(conn, { errCode: WSErrorCode.SERVER_ERROR });
          }
        })();
      },
      removeClient: () => {
        this.removeClient(conn);
      },
    };
  }

  removeClient(conn: ClientConnection) {
    this.numActiveConns--;

    const { connId, teamId, roomMember, subscriptions } = conn;
    for (const scopeType of subscriptions.keys()) {
      this.unsubscribeFromUpdates(conn, scopeType);
    }
    this.cursorsController.removeConnFromGroup(conn);
    if (roomMember !== undefined) {
      this.kickRoomMember(roomMember);
    }
    if (teamId !== undefined) {
      const teamConnsForTeam = this.teamConns.get(teamId);
      if (teamConnsForTeam === undefined)
        throw new Error("expect team conns to be defined for team");
      teamConnsForTeam.delete(connId);
    }
  }

  async createTeamAsync(teamId: string, teamData: GetInitTeamDataBackendResp) {
    // Create the team controller. This signals that the team
    // has been initialized.
    const teamController = new TeamController(teamId, teamData, this);
    this.teamControllers.set(teamId, teamController);
    teamController.initStats();
    teamController.initRooms(teamData.rooms);

    this.broadcastAdminGlobalUpdate({
      type: GlobalUpdateType.SERVER_ADD_TEAM,
      teamId: teamController.teamId,
      teamAdminData: teamController.getClientAdminData(),
    });
    // We add the team even if it's hidden. We will filter the hidden teams
    // downstream.
    this.handleTeamListAddTeam(teamId);

    const bigBoardState = teamController.getClientBigBoardState();
    this.bigBoardStateTracker.addTeam(teamId, bigBoardState);
    this.broadcastBigBoardGlobalUpdate({
      type: GlobalUpdateType.BIG_BOARD_UPDATE_STATE,
      addTeams: { [teamId]: bigBoardState },
    });

    // Ensure consistency between solves and unlocks, and also handle
    // the initial unlocks.
    await teamController.handleTeamUpdatesAsync({});
  }

  private async triggerTeamInits() {
    if (this.isTeamInitInFlight) return;
    this.isTeamInitInFlight = true;
    for (const teamId of [...this.queuedTeamInits]) {
      try {
        await this.initTeamAsync(teamId);
      } catch (err) {
        console.error(err);
      }
    }
    this.isTeamInitInFlight = false;
  }

  private async initTeamAsync(teamId: string) {
    let resp: GetInitTeamDataBackendResp | null = null;
    try {
      resp = await this.backend.getInitTeamData(teamId);
    } catch (err) {
      console.error(err);
    }
    if (resp === null) return;

    this.queuedTeamInits.delete(teamId);
    await this.createTeamAsync(teamId, resp);

    if (!(this.opts.cursorsMode ?? false)) {
      const teamController = this.getTeamController(teamId);
      for (let i = 0; i < (this.teamConns.get(teamId)?.size ?? 0); i++) {
        teamController.numAuthsMetrics.record();
        this.teamMetricsTracker.recordAuth(teamId);
      }
    }

    // Tell all connections for the team that we're ready to
    // accept input.
    this.broadcastRespToTeam(teamId, {
      type: WSRespType.AUTH_SUCCESS,
    });
  }

  /**
   * Room-agnostic preparation, called immediately after any
   * room construction.
   */
  prepareRoom(room: Room): void {
    // Flush out any updates generated during construction.
    // At this point, there aren't any joined members, so
    // we only have system updates.
    this.broadcastUpdates(room);
  }

  completeEnterRoom(
    { reqId, conn, role: reqRole }: UnjoinedRoomMember,
    room: Room
  ): void {
    const { puzName } = room.engine;
    const role = reqRole ?? Role.GOD;

    this.sendResp(conn, {
      type: WSRespType.ENTERED_ROOM,
      reqId,
      params: {
        puzName,
        role,
      },
    });

    this.joinConnToRoom(conn, room, role);
  }

  joinConnToRoom(conn: ClientConnection, room: Room, role: Role): void {
    const roomMember = {
      id: this.genRoomMemberId(),
      conn,
      room,
      role,
      enabled: false,
    };
    conn.roomMember = roomMember;
    room.members.set(roomMember.id, roomMember);

    const teamId = conn.teamId;
    if (teamId === undefined) throw new Error("expect client to be authed");

    this.gameRoomsController.handleRoomMembersUpdate(teamId, room.hostPuzName);

    this.sendSystemStep(room, {
      type: StepType.SYSTEM_JOIN,
      teamId,
      roomMember,
      role,
    });
  }

  kickRoomMember(roomMember: RoomMember): void {
    const { id: memberId, room, conn } = roomMember;
    room.members.delete(memberId);
    delete conn.roomMember;

    const teamId = conn.teamId;
    if (teamId === undefined) throw new Error("expect client to be authed");
    this.gameRoomsController.handleRoomMembersUpdate(teamId, room.hostPuzName);
  }

  /** Get the battle group that a puzzle belongs to. */
  tryGetBattleGroup(puzName: string): BattleGroupData | null {
    return this.puzzlesDB.tryGetBattleGroup(puzName);
  }

  /** Get the battle group that a puzzle belongs to. */
  getBattleGroup(puzName: string): BattleGroupData {
    return this.puzzlesDB.getBattleGroup(puzName);
  }

  /**
   * Returns a map of connId to connections subscribed to a given global
   * updates scope.
   */
  getSubscriptions(scope: GlobalUpdatesScope): Map<string, ClientConnection> {
    switch (scope.type) {
      case GlobalUpdatesScopeType.SERVER:
        return this.subscriptions;
      case GlobalUpdatesScopeType.SERVER_HEALTH:
        return this.serverHealthSubscriptions;
      case GlobalUpdatesScopeType.BIG_BOARD:
        return this.bigBoardSubscriptions;
      case GlobalUpdatesScopeType.PVP:
        return this.pvpLobbyController.subscriptions;
      case GlobalUpdatesScopeType.TEAM_LIST:
        return this.teamListSubscriptions;
      case GlobalUpdatesScopeType.TEAM_MEMBERS:
        return this.getTeamController(scope.teamId).membersSubscriptions;
      case GlobalUpdatesScopeType.FACTION_HISTORY:
        return this.factionHistorySubscriptions;
      case GlobalUpdatesScopeType.TEAM:
        return this.getTeamController(scope.teamId).subscriptions;
      case GlobalUpdatesScopeType.TEAM_BATTLE:
        return this.getTeamController(scope.teamId).getBattleController(
          scope.puzName
        ).subscriptions;
      case GlobalUpdatesScopeType.TEAM_FISH_PUZZLE:
        return this.getTeamController(scope.teamId).getFishPuzzleController(
          scope.puzName
        ).subscriptions;
      case GlobalUpdatesScopeType.TEAM_DECKS:
        return this.getTeamController(scope.teamId).decksSubscriptions;
      case GlobalUpdatesScopeType.TEAM_MASTERY_TREE:
        return this.getMasteryTreeController(scope.teamId).subscriptions;
      case GlobalUpdatesScopeType.TEAM_PVP:
        return this.getTeamController(scope.teamId).pvpController.subscriptions;
      case GlobalUpdatesScopeType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY:
        return this.getTeamController(scope.teamId).getBattleController(
          scope.puzName
        ).speedrunLeaderboardSummarySubscriptions;
    }
  }

  unsubscribeFromUpdates(
    conn: ClientConnection,
    scopeType: GlobalUpdatesScopeType
  ): void {
    const scope = conn.subscriptions.get(scopeType);
    if (scope === undefined) return;
    this.getSubscriptions(scope).delete(conn.connId);
  }

  validateSubscribeToUpdates(
    conn: ClientConnection,
    scope: GlobalUpdatesScope
  ): WSError | null {
    switch (scope.type) {
      case GlobalUpdatesScopeType.SERVER:
      case GlobalUpdatesScopeType.SERVER_HEALTH:
      case GlobalUpdatesScopeType.BIG_BOARD: {
        if (!conn.isAdmin) return { errCode: WSErrorCode.PERMISSION_DENIED };
        return null;
      }
      case GlobalUpdatesScopeType.PVP: {
        // No reason to check for PvP mastery here, but if we do
        // we must make sure to guard the subscription from the
        // client side behind the mastery too.
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_LIST: {
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_MEMBERS: {
        const { teamId } = scope;
        // Any team can see any other team's members, as long as the
        // teamId is valid.
        const teamController = this.teamControllers.get(teamId);
        if (teamController === undefined)
          return { errCode: WSErrorCode.INVALID_TEAM };
        return null;
      }
      case GlobalUpdatesScopeType.FACTION_HISTORY: {
        return null;
      }
      case GlobalUpdatesScopeType.TEAM: {
        const { teamId } = scope;
        if (conn.teamId !== teamId && !conn.isAdmin)
          return { errCode: WSErrorCode.PERMISSION_DENIED };
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_FISH_PUZZLE: {
        const { teamId, puzName } = scope;
        if (conn.teamId !== teamId && !conn.isAdmin)
          return { errCode: WSErrorCode.PERMISSION_DENIED };
        const teamController = this.getTeamController(teamId);
        if (!teamController.isValidFishPuzzle(puzName))
          return { errCode: WSErrorCode.INVALID_PUZZLE };
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_BATTLE: {
        const { teamId, puzName } = scope;
        if (conn.teamId !== teamId && !conn.isAdmin)
          return { errCode: WSErrorCode.PERMISSION_DENIED };
        const teamController = this.getTeamController(teamId);
        if (!teamController.isValidBattle(puzName))
          return { errCode: WSErrorCode.INVALID_PUZZLE };
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_DECKS:
      case GlobalUpdatesScopeType.TEAM_MASTERY_TREE:
      case GlobalUpdatesScopeType.TEAM_PVP: {
        const { teamId } = scope;
        if (conn.teamId !== teamId && !conn.isAdmin)
          return { errCode: WSErrorCode.PERMISSION_DENIED };
        return null;
      }
      case GlobalUpdatesScopeType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY: {
        const { teamId, puzName } = scope;
        if (conn.teamId !== teamId && !conn.isAdmin)
          return { errCode: WSErrorCode.PERMISSION_DENIED };
        const teamController = this.getTeamController(teamId);
        if (!teamController.isValidBattle(puzName))
          return { errCode: WSErrorCode.INVALID_PUZZLE };
        return null;
      }
      // Don't include a default case to ensure this is exhaustive.
    }
  }

  subscribeToUpdates(conn: ClientConnection, scope: GlobalUpdatesScope): void {
    const err = this.validateSubscribeToUpdates(conn, scope);
    if (err !== null) return this.sendErr(conn, err);

    // Connections are only allowed to maintain at most one
    // subscription per scope type.
    this.unsubscribeFromUpdates(conn, scope.type);

    const subscriptions = this.getSubscriptions(scope);
    subscriptions.set(conn.connId, conn);
    conn.subscriptions.set(scope.type, scope);

    // Broadcast the initial state.
    switch (scope.type) {
      case GlobalUpdatesScopeType.SERVER: {
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.SERVER_OVERRIDE_STATE,
          teamAdminData: Object.fromEntries(
            Array.from(this.teamControllers.values(), (teamController) => [
              teamController.teamId,
              teamController.getClientAdminData(),
            ])
          ),
          puzzles: Object.fromEntries(this.puzzlesDB.puzzles.entries()),
          battleGroups: Object.fromEntries(
            this.puzzlesDB.battleGroups.entries()
          ),
          masteries: Object.fromEntries(
            Object.values(this.masteriesDB).map(
              ({ masteryId, displayName }) => [masteryId, { displayName }]
            )
          ),
          unclaimedHints: this.unclaimedHints,
        });
        break;
      }
      case GlobalUpdatesScopeType.SERVER_HEALTH: {
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.SERVER_HEALTH_OVERRIDE_STATE,
          state: {
            slowSyncMetrics: this.slowSyncMetrics.getSummary(),
            fastSyncMetrics: this.fastSyncMetrics.getSummary(),
            checkAnswerMetrics: this.checkAnswerMetrics.getSummary(),
            numAuthsMetrics: this.numAuthsMetrics.getSummary(),
            numRequestsMetrics: this.numRequestsMetrics.getSummary(),
            teamNumAuthsMetrics: this.teamMetricsTracker.numAuthsSummaries,
            teamNumRequestsMetrics:
              this.teamMetricsTracker.numRequestsSummaries,
            fastSyncQueueLength: this.syncThrottle.queue.length,
            numActivePeriodicTasksHandlers: this.numActivePeriodicTasksHandlers,
            numPendingSubmissionTasks: this.numPendingSubmissionTasks,
            numCompletedRoomsToSync:
              this.gameRoomsController.slowSyncTracking
                .completedRoomsSyncController.roomsToSync.length,
            teamInitQueueLength: this.queuedTeamInits.size,
            numActiveConns: this.numActiveConns,
            userTimeUsage: this.metricsDumpTracking.userTimeUsage,
            systemTimeUsage: this.metricsDumpTracking.systemTimeUsage,
            dirtyState: this.dirtyStateTracker.dirtyState,
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.BIG_BOARD: {
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.BIG_BOARD_OVERRIDE_STATE,
          state: {
            teams: Object.fromEntries(
              Array.from(this.teamControllers.values(), (teamController) => [
                teamController.teamId,
                teamController.getClientBigBoardState(),
              ])
            ),
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.PVP: {
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.PVP_OVERRIDE_STATE,
          state: {
            ring: this.pvpLobbyController.ring,
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_LIST: {
        this.sendRespRaw(
          conn,
          this.teamListStateTracker.getSerializedOverrideGlobalUpdate()
        );
        break;
      }
      case GlobalUpdatesScopeType.TEAM_MEMBERS: {
        const { teamId } = scope;
        const teamController = this.getTeamController(teamId);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_MEMBERS_OVERRIDE_STATE,
          state: {
            teamId,
            members: teamController.members,
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.FACTION_HISTORY: {
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.FACTION_HISTORY_OVERRIDE_STATE,
          factionHistory: this.statsController.factionHistory,
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM: {
        const { teamId } = scope;
        const teamController = this.getTeamController(teamId);
        const {
          displayName,
          faction,
          factionScoreContributions,
          solveTimes,
          unlockTimes,
          cardUnlocks,
          speedrunTimes,
        } = teamController;
        const enabledMasteries =
          teamController.masteryTreeController.getEnabledMasteries();
        const solvesDict = Object.fromEntries(
          Array.from(solveTimes.keys(), (puzName) => [puzName, true])
        );
        const puzzlesDict = Object.fromEntries(
          [...this.puzzlesDB.puzzles.keys()].flatMap((puzName) => {
            if (!unlockTimes.has(puzName)) return [];
            return [[puzName, teamController.getClientPuzzleState(puzName)]];
          })
        );
        const battleGroupsDict = Object.fromEntries(
          [...this.puzzlesDB.battleGroups.entries()].flatMap(
            ([battleGroupName, battleGroup]) => {
              if (!unlockTimes.has(battleGroupName)) return [];
              return [
                [
                  battleGroupName,
                  teamController.getClientBattleGroupState(battleGroupName),
                ],
              ];
            }
          )
        );
        const masteriesDict: { [masteryId: string]: ClientMasteryGlobalState } =
          Object.fromEntries(
            Object.values(this.masteriesDB).map(
              ({ masteryId, order, x, y, displayName, effectText }) => {
                const masteryData = {
                  masteryId,
                  order,
                  x,
                  y,
                  isEnabled: enabledMasteries.has(masteryId),
                };
                if (teamController.solveTimes.has(masteryId))
                  Object.assign(masteryData, { displayName, effectText });
                return [masteryId, masteryData];
              }
            )
          );
        const { hintsTotal, hintsUsed } = teamController.backendOwned;
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_OVERRIDE_STATE,
          state: {
            teamId,
            displayName,
            faction,
            factionScoreContributions,
            factionScores: this.statsController.factionScores,
            puzzles: puzzlesDict,
            battleGroups: battleGroupsDict,
            cardUnlocks: Object.fromEntries(
              Array.from(cardUnlocks.entries(), ([cardName, puzName]) => [
                cardName,
                {
                  cardName,
                  puzName,
                },
              ])
            ),
            masteries: masteriesDict,
            hintsInfo: {
              numHintsTotal: hintsTotal,
              numHintsUsed: hintsUsed.length,
            },
            huntEndTime: this.huntEndTime,
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_FISH_PUZZLE: {
        const { teamId, puzName } = scope;
        const teamController = this.getTeamController(teamId);
        const fishPuzzleController =
          teamController.getFishPuzzleController(puzName);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_FISH_PUZZLE_OVERRIDE_STATE,
          state: {
            teamId,
            puzName,
            numGuessesRemaining: fishPuzzleController.getNumGuessesRemaining(),
            previousGuesses: fishPuzzleController.recentSubmissions.map(
              ({ answer, timestamp }) => ({
                submittedAnswer: answer,
                timestamp,
              })
            ),
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_BATTLE: {
        const { teamId, puzName } = scope;
        const battleController = this.getBattleController(teamId, puzName);
        const { masteryTreeController } = this.getTeamController(teamId);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_BATTLE_OVERRIDE_STATE,
          state: {
            teamId,
            puzName,
            selectedPuzName: battleController.selectedPuzName,
            roomPuzName: battleController.getRoomPuzName(),
            selectedDeckSlot: battleController.selectedDeckSlot,
            lockedDeckSelection: battleController.getLockedDeckSelection(),
            activeGame: battleController.getClientActiveGameState(),
            errata: battleController.filterErrata(conn),
            checkpointSummaries: battleController.summarizeCheckpoints(),
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_DECKS: {
        const { teamId } = scope;
        const { deckControllers } = this.getTeamController(teamId);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_DECKS_OVERRIDE_STATE,
          state: {
            teamId,
            decks: deckControllers.map((deckController) => {
              if (deckController === null) return null;
              return deckController.deck;
            }),
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_MASTERY_TREE: {
        const { teamId } = scope;
        const { masteryTree } = this.getMasteryTreeController(teamId);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_MASTERY_TREE_OVERRIDE_STATE,
          state: {
            teamId,
            masteryTree,
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_PVP: {
        const { teamId } = scope;
        const pvpController = this.getTeamController(teamId).pvpController;
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_PVP_OVERRIDE_STATE,
          state: {
            teamId,
            outgoingRequests: Object.fromEntries(
              pvpController.outgoingRequests
            ),
            incomingRequests: Object.fromEntries(
              pvpController.incomingRequests
            ),
            blockedTeams: Object.fromEntries(
              Array.from(pvpController.blockedTeams, (k) => [k, true])
            ),
            blockedByTeams: Object.fromEntries(
              [...this.teamControllers.values()]
                .filter(({ pvpController }) =>
                  pvpController.blockedTeams.has(teamId)
                )
                .map(({ teamId }) => [teamId, true])
            ),
          },
        });
        break;
      }
      case GlobalUpdatesScopeType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY: {
        const { teamId, puzName } = scope;
        const battleController = this.getBattleController(teamId, puzName);
        this.respGlobalUpdate(conn, {
          type: GlobalUpdateType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY_OVERRIDE_STATE,
          state: {
            teamId,
            puzName,
            speedrunRank:
              this.statsController.getSpeedrunRank(teamId, puzName) ??
              undefined,
            speedrunLeaderboardSummary:
              this.statsController.getSpeedrunLeaderboardSummary(puzName),
          },
        });
        break;
      }
    }
  }

  sendRespRaw(conn: ClientConnection, resp: string): void {
    if (this.opts.dumpWsMessages) {
      console.log(
        `${conn.connId} < ${JSON.stringify(JSON.parse(resp), null, 2)}`
      );
    }
    conn.client.send(resp);
  }

  sendResp(conn: ClientConnection, resp: WSResp): void {
    this.sendRespRaw(conn, JSON.stringify(resp));
  }

  sendRespMultiple(conns: Iterable<ClientConnection>, resp: WSResp): void {
    const serializedResp = JSON.stringify(resp);
    for (const conn of conns) {
      this.sendRespRaw(conn, serializedResp);
    }
  }

  sendEnterRoomErr({ reqId, conn }: UnjoinedRoomMember, err: WSError) {
    this.sendResp(conn, {
      type: WSRespType.ENTER_ROOM_ERROR,
      reqId,
      err,
    });
  }

  sendErr(conn: ClientConnection, err: WSError) {
    this.sendResp(conn, {
      type: WSRespType.ERROR,
      err,
    });
  }

  respBadRequest(conn: ClientConnection) {
    this.sendErr(conn, { errCode: WSErrorCode.BAD_REQUEST });
  }

  /**
   * If conn is not admin, responds with PERMISSION_DENIED.
   * Returns if conn is admin.
   */
  checkIsAdmin(conn: ClientConnection): boolean {
    if (!conn.isAdmin) {
      this.sendErr(conn, { errCode: WSErrorCode.PERMISSION_DENIED });
      return false;
    }
    return true;
  }

  async handleMsg(conn: ClientConnection, msg: string) {
    // If we are stopped, disconnect connections on auth.
    if (this.isStopped) {
      conn.client.close();
      return;
    }

    this.numRequestsMetrics.record();

    const req = JSON.parse(msg.toString());

    if (typeof req !== "object" || typeof req.type !== "string")
      return this.respBadRequest(conn);

    if (this.opts.dumpWsMessages) {
      console.log(`${conn.connId} > ${JSON.stringify(req, null, 2)}`);
    }

    if (req.type === WSReqType.AUTH) {
      const { jwt } = req;
      if (typeof jwt !== "string") return this.respBadRequest(conn);

      this.numAuthsMetrics.record();
      const payload =
        this.jwtCache.get(jwt) ?? this.opts.authInterface.verify(jwt);
      if (payload === null) {
        this.sendErr(conn, { errCode: WSErrorCode.AUTH_ERROR });
        return;
      }
      const { teamId, isAdmin = false } = payload;

      conn.teamId = teamId;
      conn.isAdmin = isAdmin;

      const teamConnsForTeam = this.teamConns.get(teamId);
      if (teamConnsForTeam === undefined) {
        const conns: Map<string, ClientConnection> = new Map();
        conns.set(conn.connId, conn);
        this.teamConns.set(teamId, conns);
      } else {
        teamConnsForTeam.set(conn.connId, conn);
      }

      if (!(this.opts.cursorsMode ?? false)) {
        const teamController = this.teamControllers.get(teamId);
        if (teamController === undefined) {
          // Delay the AUTH_SUCCESS until the team state is initialized.
          // This needs to be awaited on to ensure that errors are
          // properly contained.
          if (this.queuedTeamInits.has(teamId)) return;
          this.queuedTeamInits.add(teamId);
          await this.triggerTeamInits();
          return;
        }
        this.teamMetricsTracker.recordAuth(teamId);
        teamController.numAuthsMetrics.record();
      }

      this.sendResp(conn, {
        type: WSRespType.AUTH_SUCCESS,
      });
      return;
    }

    const teamId = conn.teamId;
    if (teamId === undefined) {
      // All other requests, including pings, should be rejected
      // if the client is not yet authenticated.
      this.sendErr(conn, { errCode: WSErrorCode.PERMISSION_DENIED });
      conn.client.close();
      return;
    }

    // Requests that don't depend on teamController.
    // This special-casing is necessary since the cursors server
    // does not set up TeamControllers.
    //
    // Make sure that all these cases end with a return instead of
    // a break!
    switch (req.type) {
      case WSReqType.PING: {
        this.sendResp(conn, {
          type: WSRespType.PONG,
        });
        return;
      }
      case WSReqType.SET_CURSOR_GROUP: {
        const { reqId, cursorGroupName } = req;
        if (!(cursorGroupName === null || typeof cursorGroupName === "string"))
          return this.respBadRequest(conn);
        if (cursorGroupName === null)
          this.cursorsController.removeConnFromGroup(conn);
        else this.cursorsController.addConnToGroup(conn, cursorGroupName);
        this.sendResp(conn, {
          type: WSRespType.SET_CURSOR_GROUP_ACK,
          reqId,
        });
        return;
      }
      case WSReqType.CURSOR_EVENT: {
        const { event } = req;
        if (typeof event !== "object") return this.respBadRequest(conn);
        const parsedEvent = safeParseCursorEvent(event);
        if (parsedEvent === null) return this.respBadRequest(conn);
        this.cursorsController.broadcastEvent(conn, parsedEvent);
        this.cursorEventsMetrics.record();
        return;
      }
    }

    const teamController = this.teamControllers.get(teamId);
    if (teamController === undefined) {
      // Any further requests after AUTH should be rejected until the
      // team state is initialized. Normally, this is enforced since
      // we delay AUTH_SUCCESS until the team state is initialized,
      // but we may still get this from a faulty or modded client.
      this.sendErr(conn, { errCode: WSErrorCode.PERMISSION_DENIED });
      conn.client.close();
      return;
    }
    if (teamController.isInactive && !conn.isAdmin) {
      // If the team is inactive, block all connections
      // (except admin impersonators).
      this.sendErr(conn, { errCode: WSErrorCode.TEAM_INACTIVE });
      conn.client.close();
      return;
    }
    if (teamController.isTempBlocked && !conn.isAdmin) {
      // If the team is temporarily blocked for maintenance,
      // block all connections (except admin impersonators).
      this.sendErr(conn, { errCode: WSErrorCode.TEAM_TEMP_BLOCKED });
      conn.client.close();
      return;
    }
    if (
      conn.spectateTeamId !== undefined &&
      req.type !== WSReqType.ENTER_ROOM &&
      req.type !== WSReqType.SUBSCRIBE_TO_UPDATES
    ) {
      // Spectators aren't allowed to take any actions.
      this.sendErr(conn, { errCode: WSErrorCode.SPECTATE_ACTION });
      return;
    }

    this.teamMetricsTracker.recordRequest(teamId);
    teamController.numRequestsMetrics.record();
    const parseResult = this.opts.parseWSReq(req);
    if (!parseResult) {
      // req is a malformed WSReq
      this.sendErr(conn, { errCode: WSErrorCode.BAD_REQUEST });
      return;
    }
    await this.handleZodReq(conn, req, teamController);
  }

  async handleZodReq(
    conn: ClientConnection,
    req: WSReq,
    teamController: TeamController
  ) {
    switch (req.type) {
      case WSReqType.SET_FACTION: {
        const { faction } = req;
        teamController.handleSetFaction(conn, faction);
        break;
      }
      case WSReqType.ENTER_ROOM: {
        const { reqId, params } = req;
        // Leave the room the connection is currently joined to, if any
        if (conn.roomMember !== undefined) this.kickRoomMember(conn.roomMember);
        if (params === null) {
          // If we're not part of a room, just return silently
          // since this might be a holdover from a previous cleanup.
          break;
        }
        const unjoinedMember: UnjoinedRoomMember = {
          reqId,
          conn,
          role: params.role,
        };
        if (params.spectateTeamId === undefined) {
          teamController.handleEnterRoom(unjoinedMember, params);
          break;
        }
        // TODO: turn this on for everyone after hunt end?
        if (!conn.isAdmin) {
          this.sendEnterRoomErr(unjoinedMember, {
            errCode: WSErrorCode.PERMISSION_DENIED,
          });
          break;
        }
        const spectateTeamController = this.teamControllers.get(
          params.spectateTeamId ?? ""
        );
        // Forbid joining with an invalid team id.
        if (spectateTeamController === undefined) {
          this.sendEnterRoomErr(unjoinedMember, {
            errCode: WSErrorCode.SPECTATE_INVALID,
          });
          break;
        }
        conn.spectateTeamId = params.spectateTeamId;
        spectateTeamController.handleEnterRoom(unjoinedMember, params);
        break;
      }
      case WSReqType.STEP: {
        const roomMember = conn.roomMember;
        if (roomMember === undefined) {
          this.sendErr(conn, { errCode: WSErrorCode.NO_ROOM });
          return;
        }
        const { room } = roomMember;
        room.tracker.handleModify();

        this.handleStep(roomMember, room, req.step);
        this.opts.afterStepBackdoor?.(room);
        break;
      }
      case WSReqType.DECKBUILDING_STEP: {
        const { slot, deckUpd } = req;
        teamController.getDeckController(slot).handleDeckbuildingStep(deckUpd);
        break;
      }
      case WSReqType.MASTERY_TREE_STEP: {
        const { step } = req;
        teamController.masteryTreeController.handleMasteryTreeStep(step);
        break;
      }
      case WSReqType.NEXT_BATTLE: {
        const roomMember = conn.roomMember;
        if (roomMember === undefined) {
          this.sendErr(conn, { errCode: WSErrorCode.NO_ROOM });
          return;
        }

        const { room } = roomMember;
        for (const destMember of room.members.values()) {
          // Do not pull spectators along; we don't have a key for them yet
          if (destMember.conn.spectateTeamId !== undefined) continue;
          this.sendResp(destMember.conn, {
            type: WSRespType.NEXT_BATTLE,
            roomId: room.roomId,
          });
        }
        break;
      }
      case WSReqType.SELECT_PUZZLE: {
        const { hostPuzName, selectedPuzName } = req;
        if (
          !teamController.isValidBattle(hostPuzName) ||
          !teamController.isValidBattle(selectedPuzName)
        ) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }

        const err = teamController
          .getBattleController(hostPuzName)
          .selectPuzzle(selectedPuzName);
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.SELECT_DECK_FOR_BATTLE: {
        const { puzName, slot } = req;
        if (!teamController.isValidBattle(puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }
        if (!this.isValidDeckSlot(slot)) {
          this.sendErr(conn, { errCode: WSErrorCode.DECK_SLOT_OUT_OF_BOUNDS });
          return;
        }

        const err = teamController
          .getBattleController(puzName)
          .selectDeck(slot);
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.START_AI_BATTLE: {
        const { puzName, slot, restart, reuseDeck, devKnobs } = req;
        if (!teamController.isValidBattle(puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }
        if (slot !== undefined && !this.isValidDeckSlot(slot)) {
          this.sendErr(conn, { errCode: WSErrorCode.DECK_SLOT_OUT_OF_BOUNDS });
          return;
        }
        teamController.startAIBattle(
          conn,
          puzName,
          slot,
          restart,
          reuseDeck,
          // A lot of client code has been passing in empty devKnobs
          // for non-admin connections, rather than omitting it
          // entirely. To reduce the possiblity of regression, just
          // silently drop devKnobs if non-admin.
          conn.isAdmin ? devKnobs : undefined
        );
        break;
      }
      case WSReqType.RESTORE_CHECKPOINT: {
        const { puzName, slot } = req;
        if (!teamController.isValidBattle(puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }
        if (!this.isValidDeckSlot(slot)) {
          this.sendErr(conn, { errCode: WSErrorCode.DECK_SLOT_OUT_OF_BOUNDS });
          return;
        }

        teamController
          .getBattleController(puzName)
          .restoreCheckpoint(conn, slot);
        break;
      }
      case WSReqType.RESIGN: {
        const { player, puzName } = req;
        if (!teamController.isValidBattle(puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }

        const err = teamController.getBattleController(puzName).resign();
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_CLAIM_TIME_VICTORY: {
        const err = teamController.pvpController.claimTimeVictory();
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_UNLOCK_DECK: {
        const err = teamController.pvpController.handleUnlockDeck(conn);
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_MAKE_REQUEST: {
        const { targetTeamId, autoAccept } = req;
        if (autoAccept !== undefined && !this.checkIsAdmin(conn)) break;
        const err = teamController.pvpController.handleMakePvPRequest(
          targetTeamId,
          autoAccept
        );
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_CANCEL_REQUEST: {
        const { targetTeamId } = req;
        const err = teamController.pvpController.handleCancelPvPRequest(
          conn,
          targetTeamId
        );
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_ACCEPT_REQUEST: {
        const { targetTeamId, devKnobs } = req;
        const err = teamController.pvpController.acceptPvPRequest(
          targetTeamId,
          // A lot of client code has been passing in empty devKnobs
          // for non-admin connections, rather than omitting it
          // entirely. To reduce the possiblity of regression, just
          // silently drop devKnobs if non-admin.
          conn.isAdmin ? devKnobs : undefined
        );
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_SET_IS_BLOCKED: {
        const { targetTeamId, isBlocked } = req;
        const err = teamController.pvpController.setIsTeamBlocked(
          conn,
          targetTeamId,
          isBlocked
        );
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_ENTER_RING: {
        const err = teamController.pvpController.enterRing();
        if (err !== null) this.sendErr(conn, err);
        break;
      }
      case WSReqType.PVP_LEAVE_RING: {
        teamController.pvpController.leaveRing();
        break;
      }
      case WSReqType.VIEW_PUZZLE: {
        const { puzName } = req;
        if (!this.isValidPuzzle(teamController, puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          return;
        }
        teamController.viewPuzzle(conn, puzName);
        break;
      }
      case WSReqType.SUBMIT_ANSWER: {
        const { teamId, puzName, answer } = req;
        const teamController = this.getTeamController(teamId);
        if (teamController.teamId !== teamId && !conn.isAdmin) {
          this.sendErr(conn, { errCode: WSErrorCode.PERMISSION_DENIED });
          return;
        }
        if (!teamController.isValidFishPuzzle(puzName)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          break;
        }
        await teamController
          .getFishPuzzleController(puzName)
          .handleSubmitAnswerAsync(conn, answer);
        break;
      }
      case WSReqType.MYOSB_REQUEST: {
        const { teamId, components } = req;
        const teamController = this.getTeamController(teamId);
        if (teamController.teamId !== teamId && !conn.isAdmin) {
          this.sendErr(conn, { errCode: WSErrorCode.PERMISSION_DENIED });
          return;
        }
        if (!teamController.isValidFishPuzzle(PUZ_NAME_MYOSB)) {
          this.sendErr(conn, { errCode: WSErrorCode.INVALID_PUZZLE });
          break;
        }

        await teamController
          .getFishPuzzleController(PUZ_NAME_MYOSB)
          .handleMyosbRequest(conn, components);
        break;
      }
      case WSReqType.ADMIN_MODIFY: {
        if (!this.checkIsAdmin(conn)) break;
        for (const [teamId, teamUpds] of Object.entries(req.teams ?? {})) {
          await this.getTeamController(teamId).adminModifyTeamAsync(teamUpds);
        }
        if (req.config !== undefined) Object.assign(this.config, req.config);
        break;
      }
      case WSReqType.SUBSCRIBE_TO_UPDATES: {
        this.subscribeToUpdates(conn, req.scope);
        break;
      }
    }
  }

  /**
   * Used to wrap operations that might generate updates, to ensure that
   * the updates are sent out afterwards.
   */
  useUpdatesContext(room: Room, func: () => void) {
    if (room.multicaster.updates.length !== 0)
      throw new Error("updates leakage from a previous step or async event");
    try {
      func();
    } catch (err) {
      const { gameState } = room.engine;
      console.error(
        `Error in room ${room.roomId} (team ${
          gameState[Player.P1].teamData?.teamId ?? "unknown"
        }, puzzle ${gameState.puzName})`
      );
      throw err;
    } finally {
      this.broadcastUpdates(room);
    }
  }

  handleStep(sender: RoomMember, room: Room, step: Step): void {
    this.useUpdatesContext(room, () => {
      room.engine.applyStep(sender, step);
    });
  }

  sendSystemStep(room: Room, step: SystemStep): void {
    this.useUpdatesContext(room, () => {
      room.engine.applySystemStep(step);
    });
  }

  handleSystemUpdate(room: Room, upd: Update): void {
    switch (upd.type) {
      case UpdateType.SYSTEM_ROOM_COMPLETE: {
        const { solvedTeamIds, cardUnlocks } = upd;
        this.gameRoomsController.handleRoomCompleteAsync(
          room,
          solvedTeamIds,
          cardUnlocks
        );
        break;
      }
    }
  }

  broadcastUpdates(room: Room): void {
    const upds = room.multicaster.readOutUpdates();
    if (upds.length === 0) return;
    for (const [destMemberId, destMember] of room.members) {
      const updsToSend = [];
      for (const updSpec of upds) {
        const { upd } = updSpec;
        // Only send updates after a member is enabled.
        if (upd.type === UpdateType.SYSTEM_ENABLE_MEMBER) {
          destMember.enabled = true;
          continue;
        }
        const shouldSend =
          destMember.enabled &&
          (() => {
            switch (updSpec.type) {
              case UpdateBroadcastSpecType.SYSTEM:
                return false;
              case UpdateBroadcastSpecType.ROOM_MEMBER_ID:
                return destMemberId === updSpec.roomMemberId;
              case UpdateBroadcastSpecType.BROADCAST:
                return true;
              case UpdateBroadcastSpecType.ROLE:
                return destMember.role === updSpec.role;
              case UpdateBroadcastSpecType.ROLE_FILTER:
                return updSpec.pred(destMember.role);
            }
          })();
        if (shouldSend) {
          updsToSend.push(upd);
        }
      }
      if (updsToSend.length > 0) {
        this.sendResp(destMember.conn, {
          type: WSRespType.UPDATES,
          updates: updsToSend,
        });
      }
    }

    // Process system updates.
    for (const updSpec of upds) {
      if (updSpec.type !== UpdateBroadcastSpecType.SYSTEM) continue;
      this.handleSystemUpdate(room, updSpec.upd);
    }
  }

  respGlobalUpdate(conn: ClientConnection, upd: GlobalUpdate): void {
    this.sendResp(conn, {
      type: WSRespType.GLOBAL_UPDATE,
      upd,
    });
  }

  broadcastGlobalUpdate(scope: GlobalUpdatesScope, upd: GlobalUpdate): void {
    const subscriptions = this.getSubscriptions(scope);
    const resp: WSResp = {
      type: WSRespType.GLOBAL_UPDATE,
      upd,
    };
    this.sendRespMultiple(subscriptions.values(), resp);
  }

  broadcastAdminGlobalUpdate(upd: GlobalUpdate): void {
    return this.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.SERVER,
      },
      upd
    );
  }

  broadcastServerHealthGlobalUpdate(upd: GlobalUpdate): void {
    return this.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.SERVER_HEALTH,
      },
      upd
    );
  }

  handleTeamSummaryUpdate(teamId: string, upd: TeamSummaryUpdate): void {
    // Broadcast team list updates even for hidden teams. We will filter
    // hidden teams downstream.
    this.teamListStateTracker.updateTeam(teamId, upd);
  }

  handleTeamListAddTeam(teamId: string) {
    const teamController = this.getTeamController(teamId);
    const teamListState = teamController.getTeamSummaryState();
    this.teamListStateTracker.addTeam(teamId, teamListState);
    this.broadcastTeamListGlobalUpdate({
      type: GlobalUpdateType.TEAM_LIST_UPDATE_STATE,
      addTeams: { [teamId]: teamListState },
    });
  }

  handleTeamListRemoveTeam(teamId: string) {
    this.teamListStateTracker.removeTeam(teamId);
    this.broadcastTeamListGlobalUpdate({
      type: GlobalUpdateType.TEAM_LIST_UPDATE_STATE,
      removeTeams: { [teamId]: true },
    });
  }

  private broadcastTeamListGlobalUpdate(upd: GlobalUpdate): void {
    return this.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM_LIST,
      },
      upd
    );
  }

  private broadcastAndCommitTeamSummaryUpdates() {
    const { pendingUpds } = this.teamListStateTracker;
    if (Object.keys(pendingUpds).length <= 0) return;
    this.broadcastTeamListGlobalUpdate({
      type: GlobalUpdateType.TEAM_LIST_UPDATE_STATE,
      updTeams: pendingUpds,
    });
    this.statsController.handleTeamSummaryUpdates(pendingUpds);
    this.teamListStateTracker.commitUpds();
  }

  broadcastBigBoardGlobalUpdate(upd: GlobalUpdate): void {
    return this.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.BIG_BOARD,
      },
      upd
    );
  }

  broadcastRespToTeam(teamId: string, resp: WSResp): void {
    const teamConnsForTeam = this.teamConns.get(teamId);
    if (teamConnsForTeam === undefined) return;

    this.sendRespMultiple(teamConnsForTeam.values(), resp);
  }

  broadcastNotifToTeam(teamId: string, notif: HuntNotification) {
    this.broadcastRespToTeam(teamId, {
      type: WSRespType.NOTIFICATION,
      notif,
    });
  }

  processFastSyncQueue(): void {
    while (
      this.syncThrottle.numSyncsInFlight < this.config.syncThrottleParallelism
    ) {
      const task = this.syncThrottle.queue.shift();
      if (task === undefined) break;
      this.syncThrottle.numSyncsInFlight++;
      task();
    }
  }

  private async waitSyncThrottleAsync() {
    await new Promise<void>((resolve, reject) => {
      this.syncThrottle.queue.push(resolve);
      this.processFastSyncQueue();
    });
  }

  private signalSyncThrotleAsync() {
    this.syncThrottle.numSyncsInFlight--;
    this.processFastSyncQueue();
  }

  async runWithSyncThrottleAsync<T>(funcAsync: () => Promise<T>): Promise<T> {
    let needRelease = false;
    try {
      await this.waitSyncThrottleAsync();
      needRelease = true;
      return await funcAsync();
    } finally {
      // Release the lock, but propagate the error.
      if (needRelease) this.signalSyncThrotleAsync();
    }
  }

  async syncDataThrottledAsync(req: SyncDataBackendReq) {
    return await this.runWithSyncThrottleAsync(async () => {
      return await this.backend.syncData(req);
    });
  }

  async triggerSlowSyncDownloadAsync(): Promise<void> {
    if (this.slowSyncTracking.isDownloadInFlight) return;
    const factionHistorySnapshot = this.statsController.startSync();

    const syncDataReq = {
      factionHistory: factionHistorySnapshot,
      // The presence of this field in general indicates that this is
      // a slow, periodic update, and is used for things like getting
      // notifications and updating hint counts
      notifsStartIndex: this.slowSyncTracking.notifsNextIndex,
    };

    this.slowSyncTracking.isDownloadInFlight = true;
    const resp = await (async () => {
      try {
        return await this.syncDataThrottledAsync(syncDataReq);
      } catch (err) {
        // If we encounter an error, just log and try again later.
        console.error(err);
      } finally {
        this.slowSyncTracking.isDownloadInFlight = false;
      }
    })();

    if (resp === undefined) return;

    const { notifs, notifsNextIndex } = resp;

    this.statsController.endSync(factionHistorySnapshot);

    if (notifsNextIndex !== undefined)
      this.slowSyncTracking.notifsNextIndex = notifsNextIndex;
    for (const data of notifs ?? []) {
      try {
        switch (data.type) {
          case BackendNotificationDataType.HINT_ANSWERED: {
            // Translate the backend notif into a client notif.
            const { teamId, puzName } = data;
            this.broadcastNotifToTeam(teamId, {
              type: HuntNotificationType.HINT_ANSWERED,
              puzName,
            });
            break;
          }
          case BackendNotificationDataType.TEAM_MEMBERS: {
            const { teamId, members } = data;
            this.teamControllers.get(teamId)?.handleSetMembers(members);
            break;
          }
          case BackendNotificationDataType.ERRATUM: {
            const { erratumId, puzzle, published } = data;
            const newlyPublished =
              published && !this.errata[erratumId]?.published;
            this.errata[erratumId] = data;
            for (const [teamId, team] of this.teamControllers) {
              // Imperfectly replicate Django logic for when errata are
              // visible. Hopefully it's close enough.
              if (newlyPublished && (!puzzle || team.unlockTimes.has(puzzle)))
                this.broadcastNotifToTeam(teamId, {
                  type: HuntNotificationType.ERRATUM,
                });
              for (const [puzName, battle] of team.battleControllers)
                battle.broadcastErrataGlobalUpdate({});
            }
            break;
          }
        }
      } catch (err) {
        // Isolate errors to individual notifications.
        console.error(err);
      }
    }

    if (
      resp.unclaimedHints !== undefined &&
      resp.unclaimedHints !== this.unclaimedHints
    ) {
      this.unclaimedHints = resp.unclaimedHints;
      this.broadcastAdminGlobalUpdate({
        type: GlobalUpdateType.SERVER_UNCLAIMED_HINTS,
        unclaimedHints: resp.unclaimedHints,
      });
    }

    // Update hint counts and extra guesses if changed
    const { teams } = resp;
    for (const [teamId, teamUpdate] of Object.entries(teams ?? {})) {
      try {
        const teamController = this.teamControllers.get(teamId);
        // If we don't have the team initialized yet, drop the update
        // as we should get it with the init later.
        if (teamController === undefined) continue;

        // Check to see if number of hints changed
        const { hintsTotal, hintsUsed } = teamUpdate;
        if (
          teamController.backendOwned.hintsTotal !== hintsTotal ||
          teamController.backendOwned.hintsUsed.length !== hintsUsed.length
        ) {
          teamController.backendOwned = teamUpdate;

          teamController.broadcastTeamGlobalUpdate({
            type: GlobalUpdateType.TEAM_UPDATE_STATE,
            hintsInfo: {
              numHintsTotal: hintsTotal,
              numHintsUsed: hintsUsed.length,
            },
          });
        }
        // Note that we shouldn't put this in the above diff since
        // the hint used allocation may change even if the total
        // number of hitns used stays the same.
        const hintsUsedMap = teamController.getHintsUsedMap();
        this.bigBoardStateTracker.updateTeam(teamId, {
          numHintsTotal: hintsTotal,
          unlocks: Object.fromEntries(
            Object.entries(hintsUsedMap).map(([puzName, numHintsUsed]) => [
              puzName,
              { numHintsUsed },
            ])
          ),
        });

        // Check to see if number of guesses per puzzle has changed. This will
        // not catch cases in which a guess grant for a puzzle has completely
        // been removed, i.e the number of extra guesses is amended to 0.
        const { extraGuesses } = teamUpdate;
        for (const [puzName, numExtraGuesses] of Object.entries(
          extraGuesses ?? {}
        )) {
          const fishPuzzleController =
            teamController.getFishPuzzleController(puzName);
          if (fishPuzzleController.numExtraGuesses !== numExtraGuesses) {
            fishPuzzleController.numExtraGuesses = numExtraGuesses;
            fishPuzzleController.handleUpdateNumGuessesRemaining();
          }
        }
      } catch (err) {
        // Isolate errors to the team being synced.
        console.error(err);
      }
    }

    // Check to see if any extra guesses have gone down to 0
    for (const teamController of this.teamControllers.values()) {
      try {
        const teamUpdate = teams ? teams[teamController.teamId] : undefined;
        const extraGuesses = teamUpdate ? teamUpdate.extraGuesses : {};
        for (const fishPuzzleController of teamController.fishPuzzleControllers.values()) {
          const newExtraGuesses =
            extraGuesses[fishPuzzleController.puzName] ?? 0;
          if (fishPuzzleController.numExtraGuesses !== newExtraGuesses) {
            fishPuzzleController.numExtraGuesses = newExtraGuesses;
            fishPuzzleController.handleUpdateNumGuessesRemaining();
          }
        }
      } catch (err) {
        console.error(err);
      }
    }
  }

  async triggerSlowSyncUpload(): Promise<void> {
    await this.gameRoomsController.triggerSlowSyncAsync();
    await this.slowSyncTracking.decksAndMasteryTreesSyncController.syncLoopController.triggerLoopAsync();
    await this.slowSyncTracking.checkpointsSyncController.syncLoopController.triggerLoopAsync();
    await this.slowSyncTracking.miscTeamStateSyncController.syncLoopController.triggerLoopAsync();
  }

  private async runPeriodicSyncTasks(): Promise<void> {
    // If a fast sync fails, the periodic task handler is responsible
    // for kicking it off again after a while.
    for (const [teamId, teamController] of this.teamControllers) {
      try {
        await teamController.fastSyncTracking.syncLoopController.triggerLoopAsync();
      } catch (err) {
        // Isolate fast sync problems to within individual teams.
        console.error(err);
      }
    }

    try {
      await this.triggerSlowSyncUpload();
    } catch (err) {
      // Isolate errors for the slow sync upload.
      console.error(err);
    }

    try {
      await this.triggerSlowSyncDownloadAsync();
    } catch (err) {
      // Isolate errors for the slow sync download.
      console.error(err);
    }
  }

  runPeriodicTasks(): void {
    // Allow multiple periodic tasks runners to run at once,
    // so that if any gets stuck, future ones may pick up where
    // earlier ones left off.
    (async () => {
      if (!this.isReady) {
        // If init failed, try again.
        await this.tryInitAsync();
        return;
      }

      try {
        const nowTime = Date.now();
        const { lastDumpTime, lastUserTime, lastSystemTime } =
          this.metricsDumpTracking;
        // default interval of 30s
        if (
          nowTime >
          lastDumpTime + (this.opts.metricsDumpInterval ?? 30 * 1000)
        ) {
          const { user: userTime, system: systemTime } =
            this.opts.getCpuUsage?.() ?? { user: 0, system: 0 };
          const userTimeUsage =
            (userTime - lastUserTime) / 1000 / (nowTime - lastDumpTime);
          const systemTimeUsage =
            (systemTime - lastSystemTime) / 1000 / (nowTime - lastDumpTime);
          this.metricsDumpTracking = {
            lastDumpTime: nowTime,
            lastUserTime: userTime,
            lastSystemTime: systemTime,
            userTimeUsage,
            systemTimeUsage,
          };
          this.opts.logMetrics?.(
            `[${nowTime}] Metrics: ${JSON.stringify([
              0, // version
              this.fastSyncMetrics.getSummary().totNumEvents,
              this.slowSyncMetrics.getSummary().totNumEvents,
              this.checkAnswerMetrics.getSummary().totNumEvents,
              this.numAuthsMetrics.getSummary().totNumEvents,
              this.numRequestsMetrics.getSummary().totNumEvents,
              this.syncThrottle.queue.length,
              this.numActivePeriodicTasksHandlers,
              this.numPendingSubmissionTasks,
              this.gameRoomsController.slowSyncTracking
                .completedRoomsSyncController.roomsToSync.length,
              this.queuedTeamInits.size,
              userTime,
              systemTime,
            ])}`
          );
        }
      } catch (err) {
        console.error(err);
      }

      this.numActivePeriodicTasksHandlers++;
      try {
        if (this.cursorEventsMetrics.prevNumEvents > 0) {
          this.opts.logCursorsMetrics?.(
            `[${new Date().toLocaleString()}] Cursor events: ${this.cursorEventsMetrics.getSummaryString()}`
          );
        }
      } catch (err) {
        console.error(err);
      }

      if (!(this.opts.cursorsMode ?? false)) {
        try {
          this.broadcastServerHealthGlobalUpdate({
            type: GlobalUpdateType.SERVER_HEALTH_UPDATE_STATE,
            slowSyncMetrics: this.slowSyncMetrics.getSummary(),
            fastSyncMetrics: this.fastSyncMetrics.getSummary(),
            checkAnswerMetrics: this.checkAnswerMetrics.getSummary(),
            numAuthsMetrics: this.numAuthsMetrics.getSummary(),
            numRequestsMetrics: this.numRequestsMetrics.getSummary(),
            ...this.teamMetricsTracker.getAndCommitUpds(),
            fastSyncQueueLength: this.syncThrottle.queue.length,
            numActivePeriodicTasksHandlers: this.numActivePeriodicTasksHandlers,
            numPendingSubmissionTasks: this.numPendingSubmissionTasks,
            numCompletedRoomsToSync:
              this.gameRoomsController.slowSyncTracking
                .completedRoomsSyncController.roomsToSync.length,
            teamInitQueueLength: this.queuedTeamInits.size,
            numActiveConns: this.numActiveConns,
            userTimeUsage: this.metricsDumpTracking.userTimeUsage,
            systemTimeUsage: this.metricsDumpTracking.systemTimeUsage,
            dirtyStateUpd: this.dirtyStateTracker.pendingDirtyStateUpd,
          });
          this.dirtyStateTracker.commitUpds();
        } catch (err) {
          // Isolate problems with server health reporting.
          console.error(err);
        }

        try {
          this.broadcastAndCommitTeamSummaryUpdates();
        } catch (err) {
          // Isolate problems with team list reporting.
          console.error(err);
        }

        try {
          const { pendingUpds } = this.bigBoardStateTracker;
          if (Object.keys(pendingUpds).length > 0) {
            this.broadcastBigBoardGlobalUpdate({
              type: GlobalUpdateType.BIG_BOARD_UPDATE_STATE,
              updTeams: pendingUpds,
            });
            this.bigBoardStateTracker.commitUpds();
          }
        } catch (err) {
          // Isolate problems with big board reporting.
          console.error(err);
        }

        await this.runPeriodicSyncTasks();

        try {
          // This involves checking all PvP requests for expiry.
          // While this can theoretically grow as large as quadratic
          // in the number of teams, realisitically it is very
          // unlikely for us to reach that since requests are
          // short-lived.
          for (const teamController of this.teamControllers.values()) {
            teamController.pvpController.unlockDeckIfUnneeded();
          }
        } catch (err) {
          // Isolate problems with auto-unlocking decks.
          console.error(err);
        }
      }
      this.numActivePeriodicTasksHandlers--;
    })();
  }

  recordSlowSyncPacket(): void {
    this.slowSyncMetrics.record();
  }
}
