import {
  SyncDataBackendReq,
  CheckAnswerBackendReq,
  GetInitDataBackendResp,
  GetInitTeamDataBackendResp,
  CheckAnswerBackendResp,
  BackendInterface,
} from "game-server/backend-interface/BackendInterface";

export type BackendInterfaceControllerOpts = {
  backend: BackendInterface;
  dumpBackendRequests: boolean;
};

export class BackendInterfaceController implements BackendInterface {
  dumpRequests: boolean;
  backend: BackendInterface;

  constructor(opts: BackendInterfaceControllerOpts) {
    this.dumpRequests = opts.dumpBackendRequests;
    this.backend = opts.backend;
  }

  async getInitData(): Promise<GetInitDataBackendResp> {
    if (this.dumpRequests) {
      console.log(`req getInitData`);
    }
    const resp = await this.backend.getInitData();
    if (this.dumpRequests) {
      console.log(`resp getInitData`);
    }
    return resp;
  }

  async getInitTeamData(teamId: string): Promise<GetInitTeamDataBackendResp> {
    if (this.dumpRequests) {
      console.log(`req getInitTeamData`);
    }
    const resp = await this.backend.getInitTeamData(teamId);
    if (this.dumpRequests) {
      console.log(`resp getInitTeamData`);
    }
    return resp;
  }

  async syncData(req: SyncDataBackendReq) {
    if (this.dumpRequests) {
      console.log(`req syncData`);
    }
    const resp = await this.backend.syncData(req);
    if (this.dumpRequests) {
      console.log(`resp syncData`);
    }
    return resp;
  }

  async checkAnswer(
    req: CheckAnswerBackendReq
  ): Promise<CheckAnswerBackendResp> {
    if (this.dumpRequests) {
      console.log(`req solve`);
    }
    const isCorrect = await this.backend.checkAnswer(req);
    if (this.dumpRequests) {
      console.log(`resp solve`);
    }
    return isCorrect;
  }
}
