import { CreatedPermanentEvent, PermanentEventType } from "engine/server-hooks";
import { CounterType } from "engine/types/counters";
import { EngineContext, GameSpecServerFragment } from "engine/types/game-specs";
import {
  ALL_AUTO_BATTLERS,
  FIGHTER_ALLY_NAME,
  WALL_NAME,
  BOX_NAME,
  LEVEL_INITERS,
  LEVEL_INDICES,
  ALL_PUSHERS,
  EXPLOSIVES_NAME,
  ZAP_PUSHER_NAME,
  ARMORED_CAMP_NAME,
  ARMORED_FIGHTER_ENEMY_NAME,
} from "./puzzle-22-data";
import { SokobanState, SokobanUpdateType } from "./puzzle-22-logic";
import { Permanent } from "engine/types/game-state";
import { CardEffects } from "engine/cards/card-effects";
import { PermanentEffectOptResolved } from "engine/types/effects";
import { StepType } from "engine/types/steps";

const CARD_EFFECTS_BASE = {
  [WALL_NAME]: {
    handlers: {
      [PermanentEventType.CREATED]: initWallCounters,
    },
  },
  [FIGHTER_ALLY_NAME]: {
    handlers: {
      [PermanentEventType.CREATED]: initFriendlyUnitCounters,
    },
  },
  [EXPLOSIVES_NAME]: {
    handlers: {
      [PermanentEventType.CREATED]: (ev) => {
        ev.ctx.engine.addCounter(ev.permanent, {
          type: CounterType.CANNOT_MOVE,
        });
        ev.ctx.engine.addCounter(ev.permanent, {
          type: CounterType.CANNOT_ATTACK,
        });
        ev.ctx.engine.addCounter(ev.permanent, {
          type: CounterType.CANNOT_CREATE,
        });
      },
      [PermanentEventType.AFTER_DESTROYED]: (ev) => {
        const { inspector, engine } = ev.ctx;

        engine.announce("Explosives... explode!");
        for (const perm of inspector.getPermanents({
          touching: ev.permanent.slot,
        })) {
          engine.damageUnattributed(perm, 2);
        }
      },
    },
  },
  [ZAP_PUSHER_NAME]: {
    onRepeatable(step, ctx) {
      const effectOpt = step
        .effectOptsResolved[0] as PermanentEffectOptResolved;
      ctx.engine.damage(
        step.permanent,
        effectOpt.permanent,
        ctx.inspector.getPower(step.permanent)
      );
      ctx.engine.damage(step.permanent, step.permanent, 1);
    },
  },
  [ARMORED_CAMP_NAME]: {
    adjustDamageReceived(_, damage) {
      return Math.max(damage - 1, 0);
    },
  },
  [ARMORED_FIGHTER_ENEMY_NAME]: {
    adjustDamageReceived(_, damage) {
      return Math.max(damage - 1, 0);
    },
  },
} as { [cardName: string]: CardEffects };
for (const pusherName of ALL_PUSHERS) {
  CARD_EFFECTS_BASE[pusherName] ||= {};
  CARD_EFFECTS_BASE[pusherName].handlers = {
    [PermanentEventType.CREATED]: initPusherCounters,
  };
}

const GAME_SPEC_BASE = {
  cardEffects: CARD_EFFECTS_BASE,

  afterTurnEnd: (player, ctx) => {
    const { ai, inspector, engine } = ctx;
    if (inspector.getTurnNumber() <= 1) {
      return;
    }

    // Perform glorious combat.
    const playerUnitsInOrder = inspector.getPermanents({
      owner: player,
      sort: [
        (perm: Permanent) =>
          -100 * inspector.getNthNearestRow(perm.slot.row, player) -
          perm.slot.column,
      ],
    });

    for (const playerUnit of playerUnitsInOrder) {
      if (!ALL_AUTO_BATTLERS.has(playerUnit.card.name || "")) {
        continue;
      }
      if (inspector.getPower(playerUnit) <= 0) {
        continue;
      }
      const targetUnits = inspector.getPermanents({
        column: playerUnit.slot.column,
        rowInFrontOf: {
          row: playerUnit.slot.row,
          perspective: player,
        },
        sort: [
          {
            type: "frontToBack",
            perspective: inspector.getOpponentOf(player),
          },
        ],
      });
      // Hit the first enemy unit seen.
      for (const potentialTarget of targetUnits) {
        // Boxes block all combat.
        if (potentialTarget.card.name === BOX_NAME) {
          break;
        }
        // Walls are ignored in combat (you can attack over them)
        if (potentialTarget.card.name === WALL_NAME) {
          continue;
        }
        // Skip past allied units.
        if (potentialTarget.owner === player) {
          continue;
        }

        engine.damage(
          playerUnit,
          potentialTarget,
          inspector.getPower(playerUnit)
        );
        break;
      }
    }
  },
  overrideStep(step, ctx) {
    // Only override move steps.
    if (step.type !== StepType.MOVE) return false;

    const { inspector, engine } = ctx;
    const pusher = step.permanent;
    if (!ALL_PUSHERS.has(pusher.card.name || "")) {
      // Use normal move rules for non-pushers.
      return false;
    }

    const oldSlot = pusher.slot;
    const newSlot = step.slot;
    const rowDiff = newSlot.row - oldSlot.row;
    const colDiff = newSlot.column - oldSlot.column;

    const state = SokobanState.makeStateFromInspector(inspector);
    const updates = state.getPushResult(
      1, // box weight that can be pushed
      pusher,
      rowDiff,
      colDiff
    );

    for (const update of updates) {
      if (update.type === SokobanUpdateType.MOVE) {
        engine.moveRel(update.permanent, update.rowDiff, update.colDiff);
      } else if (update.type === SokobanUpdateType.APPLY_POWERUP) {
        // TODO: omnomnom
      }
    }

    if (updates.length === 0) {
      engine.announce("bonk.");
    }

    return true;
  },
} as GameSpecServerFragment;

export const Puzzle22Specs: GameSpecServerFragment[] = LEVEL_INDICES.map(
  (i) => {
    const val = {
      ...GAME_SPEC_BASE,

      afterInitEngine(ctx: EngineContext) {
        LEVEL_INITERS[i](ctx);
      },
    };
    // Special cases, if any.
    return val;
  }
);

function initPusherCounters(ev: CreatedPermanentEvent) {
  const { permanent, ctx } = ev;
  const { engine } = ctx;
  engine.addCounter(permanent, { type: CounterType.CANNOT_ATTACK });
  engine.addCounter(permanent, { type: CounterType.CANNOT_CREATE });
}

function initFriendlyUnitCounters(ev: CreatedPermanentEvent) {
  const { permanent, ctx } = ev;
  const { engine } = ctx;
  engine.addCounter(permanent, { type: CounterType.CANNOT_ATTACK });
  engine.addCounter(permanent, { type: CounterType.CANNOT_CREATE });
  engine.addCounter(permanent, { type: CounterType.CANNOT_MOVE });
}

function initWallCounters(ev: CreatedPermanentEvent) {
  const { permanent, ctx } = ev;
  const { engine } = ctx;
  engine.addCounter(permanent, { type: CounterType.DOES_NOT_PROTECT });
}
