import { StepType } from "engine/types/steps";
import { Role } from "engine/types/updates";
import { Player, DevKnobs, opponentOf } from "engine/types/game-state";
import { PUZ_NAME_PVP, PVP_TURN_TIME_LIMIT } from "engine/puzzles/puzzle-data";
import { PVP_MASTERY_ID } from "engine/puzzles/mastery-data";
import { WSErrorCode, WSError, WSRespType } from "game-server/ws";
import { HuntNotificationType } from "game-server/notifications";
import { ClientConnection, GameServer } from "game-server/GameServer";
import {
  GlobalUpdatesScopeType,
  GlobalUpdateType,
  GlobalUpdate,
} from "game-server/global-updates";

/**
 * PvP requests expire after some amount of time, to make it more
 * likely that the requesting team is still active when the request
 * gets accepted.
 */
export const PVP_REQUEST_EXPIRY_TIME_MS = 60 * 1000;

export type PvPRequest = {
  fromTeamId: string;
  toTeamId: string;
  expiryTime: number;
};

const isPvPRequestExpired = (pvpReq: PvPRequest): boolean => {
  const timeNow = new Date().getTime();
  const { expiryTime } = pvpReq;
  return timeNow > expiryTime;
};

export class TeamPvPController {
  teamId: string;
  server: GameServer;

  /** Battle requests that the team has made, indexed by team ID. */
  outgoingRequests: Map<string, PvPRequest>;
  /** Battle requests made to the team, indexed by team ID. */
  incomingRequests: Map<string, PvPRequest>;
  /** Team IDs of teams blocked by the team. */
  blockedTeams: Set<string>;

  /**
   * Map of connId to connections for connections subscribed to
   * updates for this team's PvP state.
   */
  subscriptions: Map<string, ClientConnection>;

  constructor(
    teamId: string,
    blockedTeams: ReadonlyArray<string>,
    server: GameServer
  ) {
    this.teamId = teamId;
    this.server = server;
    this.outgoingRequests = new Map();
    this.incomingRequests = new Map();
    this.blockedTeams = new Set(blockedTeams);
    this.subscriptions = new Map();
  }

  getTeamController() {
    return this.server.getTeamController(this.teamId);
  }

  getBattleController() {
    return this.getTeamController().getBattleController(PUZ_NAME_PVP);
  }

  claimTimeVictory(): WSError | null {
    const room = this.getBattleController().getActiveRoom();
    if (room === null) {
      return { errCode: WSErrorCode.NO_ROOM };
    }
    const { gameState } = room.engine;
    const opponent = opponentOf(this.getBattleController().getPlayer());
    const { teamData, startTurnTime } = gameState[opponent] ?? {};
    if (!teamData) throw new Error("pvp opponent not found");
    if (!startTurnTime || Date.now() - startTurnTime < PVP_TURN_TIME_LIMIT) {
      // client sent request when stale; return silently
      return null;
    }
    this.server.sendSystemStep(room, {
      type: StepType.SYSTEM_TIME_OUT,
      player: opponent,
    });
    return null;
  }

  getHasMastery(): boolean {
    return this.getTeamController()
      .masteryTreeController.getEnabledMasteries()
      .has(PVP_MASTERY_ID);
  }

  broadcastTeamPvPGlobalUpdate(upd: GlobalUpdate) {
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.TEAM_PVP,
        teamId: this.teamId,
      },
      upd
    );
  }

  isGameActive(): boolean {
    return this.getBattleController().isRoomActive();
  }

  /**
   * Validate that a team ID is a valid opponent, and get its
   * PvP controller.
   */
  private validateOpponentTeamId(
    targetTeamId: string
  ): TeamPvPController | null {
    // A team cannot battle itself.
    if (targetTeamId === this.teamId) return null;
    const targetTeamController = this.server.teamControllers.get(targetTeamId);
    if (targetTeamController === undefined) return null;
    return targetTeamController.pvpController;
  }

  private commitDeckSelection(): WSError | null {
    const battleController = this.getBattleController();

    // If we've already committed the deck, don't commit it again.
    if (battleController.pendingGame !== undefined) return null;

    const { err } = battleController.commitDeckSelection([
      Player.P1,
      Player.P2,
    ]);
    return err ?? null;
  }

  handleUnlockDeck(conn: ClientConnection): WSError | null {
    if (this.isGameActive()) {
      return { errCode: WSErrorCode.LOCKED_BY_BATTLE };
    }

    this.clearOutgoingRequests();
    const battleController = this.getBattleController();
    delete battleController.pendingGame;
    battleController.broadcastLockedDeckSelection();
    return null;
  }

  private makePvPRequest(targetTeamId: string): WSError | null {
    const targetController = this.validateOpponentTeamId(targetTeamId);
    if (targetController === null) {
      return { errCode: WSErrorCode.INVALID_TEAM };
    }
    if (this.blockedTeams.has(targetTeamId)) {
      return { errCode: WSErrorCode.BLOCKED_BY_REQUESTER };
    }
    if (targetController.blockedTeams.has(this.teamId)) {
      return { errCode: WSErrorCode.BLOCKED_BY_TARGET };
    }
    if (this.isGameActive()) {
      return { errCode: WSErrorCode.ROOM_BUSY };
    }
    if (!this.getHasMastery()) {
      return { errCode: WSErrorCode.NO_MASTERY };
    }
    if (!targetController.getHasMastery()) {
      return { errCode: WSErrorCode.TARGET_NO_MASTERY };
    }

    const existingReq = this.outgoingRequests.get(targetTeamId);
    if (existingReq !== undefined) {
      if (isPvPRequestExpired(existingReq)) {
        this.clearRequest(existingReq, false);
      } else {
        return { errCode: WSErrorCode.DUPLICATE_REQUEST };
      }
    }

    const commitDeckSelectionErr = this.commitDeckSelection();
    if (commitDeckSelectionErr !== null) return commitDeckSelectionErr;

    const pvpReq = {
      fromTeamId: this.teamId,
      toTeamId: targetTeamId,
      expiryTime: new Date().getTime() + PVP_REQUEST_EXPIRY_TIME_MS,
    };
    this.outgoingRequests.set(targetTeamId, pvpReq);
    targetController.incomingRequests.set(this.teamId, pvpReq);

    this.getBattleController().broadcastLockedDeckSelection();
    this.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      outgoingRequestsToAdd: [pvpReq],
    });
    targetController.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      incomingRequestsToAdd: [pvpReq],
    });

    this.server.broadcastRespToTeam(targetTeamId, {
      type: WSRespType.NOTIFICATION,
      notif: { type: HuntNotificationType.PVP_REQUEST_RECEIVED },
    });

    return null;
  }

  handleMakePvPRequest(
    targetTeamId: string,
    autoAccept?: boolean
  ): WSError | null {
    const makePvPRequestErr = this.makePvPRequest(targetTeamId);
    if (makePvPRequestErr !== null) return makePvPRequestErr;

    if (autoAccept ?? false) {
      const targetController = this.validateOpponentTeamId(targetTeamId);
      if (targetController === null)
        return { errCode: WSErrorCode.INVALID_TEAM };
      const acceptPvPRequestErr = targetController.acceptPvPRequest(
        this.teamId,
        undefined,
        true
      );
      if (acceptPvPRequestErr !== null) return acceptPvPRequestErr;
    }

    return null;
  }

  handleCancelPvPRequest(
    conn: ClientConnection,
    targetTeamId: string
  ): WSError | null {
    const targetController = this.validateOpponentTeamId(targetTeamId);
    if (targetController === null) {
      return { errCode: WSErrorCode.INVALID_TEAM };
    }

    const existingReq = this.outgoingRequests.get(targetTeamId);
    // Don't return an error if the request doesn't exist, since
    // that might be due to a race condition.
    if (existingReq !== undefined) {
      this.clearRequest(existingReq, true);
    }

    this.unlockDeckIfUnneeded();
    return null;
  }

  setIsTeamBlocked(
    conn: ClientConnection,
    targetTeamId: string,
    isBlocked: boolean
  ): WSError | null {
    const targetController = this.validateOpponentTeamId(targetTeamId);
    if (targetController === null) {
      return { errCode: WSErrorCode.INVALID_TEAM };
    }

    if (isBlocked) {
      this.blockedTeams.add(targetTeamId);
      const existingOutgoingReq = this.outgoingRequests.get(targetTeamId);
      if (existingOutgoingReq !== undefined)
        this.clearRequest(existingOutgoingReq, true);
      const existingIncomingReq = this.incomingRequests.get(targetTeamId);
      if (existingIncomingReq !== undefined)
        this.clearRequest(existingIncomingReq, true);
    } else {
      this.blockedTeams.delete(targetTeamId);
    }

    this.getTeamController().mergeSlowSyncPacket({
      blockedTeams: {
        [targetTeamId]: isBlocked,
      },
    });

    this.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      blockedTeamsToAdd: isBlocked ? [targetTeamId] : undefined,
      blockedTeamsToDelete: isBlocked ? undefined : [targetTeamId],
    });
    targetController.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      blockedByTeamsToAdd: isBlocked ? [this.teamId] : undefined,
      blockedByTeamsToDelete: isBlocked ? undefined : [this.teamId],
    });

    return null;
  }

  /**
   * Enter the ring. Note that a team may "re-enter" the ring after
   * some time to re-send battle requests.
   */
  enterRing(): WSError | null {
    if (!this.getHasMastery()) {
      return { errCode: WSErrorCode.NO_MASTERY };
    }

    const { ring } = this.server.pvpLobbyController;
    const isNew = ring.indexOf(this.teamId) === -1;
    if (isNew) {
      ring.push(this.teamId);
      this.getTeamController().mergeSlowSyncPacket({ isInRing: true });
      this.server.pvpLobbyController.broadcastPvPGlobalUpdate({
        type: GlobalUpdateType.PVP_UPDATE_STATE,
        ringTeamsToAdd: [this.teamId],
      });
    }

    // Broadcast requests to anyone who would accept it, and only
    // if the current deck selection is valid.
    for (const targetTeamId of ring) {
      this.makePvPRequest(targetTeamId);
    }

    return null;
  }

  leaveRing() {
    const { ring } = this.server.pvpLobbyController;
    const indexInRing = ring.indexOf(this.teamId);
    if (indexInRing === -1) return;
    ring.splice(indexInRing, 1);
    this.getTeamController().mergeSlowSyncPacket({ isInRing: false });

    this.server.pvpLobbyController.broadcastPvPGlobalUpdate({
      type: GlobalUpdateType.PVP_UPDATE_STATE,
      ringTeamsToDelete: [this.teamId],
    });
  }

  acceptPvPRequest(
    targetTeamId: string,
    devKnobs?: DevKnobs,
    forceP1?: boolean
  ): WSError | null {
    this.clearExpiredRequests();

    const targetController = this.validateOpponentTeamId(targetTeamId);
    if (targetController === null) {
      return { errCode: WSErrorCode.INVALID_TEAM };
    }
    const pvpReq = this.incomingRequests.get(targetTeamId);
    if (pvpReq === undefined) {
      return { errCode: WSErrorCode.NO_PVP_REQUEST };
    }
    if (this.isGameActive()) {
      return { errCode: WSErrorCode.ROOM_BUSY };
    }
    if (!this.getHasMastery()) {
      return { errCode: WSErrorCode.NO_MASTERY };
    }
    if (!targetController.getHasMastery()) {
      return { errCode: WSErrorCode.TARGET_NO_MASTERY };
    }

    const commitDeckSelectionErr = this.commitDeckSelection();
    if (commitDeckSelectionErr !== null) return commitDeckSelectionErr;

    this.clearAllRequestsAndLeaveRing();
    targetController.clearAllRequestsAndLeaveRing();

    const battleController = this.getBattleController();
    const targetBattleController = targetController.getBattleController();
    if (
      battleController.pendingGame === undefined ||
      targetBattleController.pendingGame === undefined
    )
      throw new Error("expect game setup for pvp teams to be ready");
    const selfDeckSelection = battleController.pendingGame.deckSelection;
    const oppDeckSelection = targetBattleController.pendingGame.deckSelection;
    if (selfDeckSelection === null || oppDeckSelection === null)
      throw new Error("expect deck selections pvp teams to be defined");

    const isP1 = forceP1 ?? false ? true : Math.random() * 2 < 1;
    const room = this.server.gameRoomsController.createRoom(
      PUZ_NAME_PVP,
      PUZ_NAME_PVP,
      {
        [Player.P1]: isP1 ? this.teamId : targetTeamId,
        [Player.P2]: isP1 ? targetTeamId : this.teamId,
      },
      {
        [Player.P1]: isP1 ? selfDeckSelection : oppDeckSelection,
        [Player.P2]: isP1 ? oppDeckSelection : selfDeckSelection,
      },
      devKnobs
    );
    battleController.activateGame(room, isP1 ? Role.P1 : Role.P2);
    targetBattleController.activateGame(room, isP1 ? Role.P2 : Role.P1);

    this.server.broadcastRespToTeam(targetTeamId, {
      type: WSRespType.NOTIFICATION,
      notif: { type: HuntNotificationType.PVP_REQUEST_ACCEPTED },
    });

    return null;
  }

  private clearRequest(pvpReq: PvPRequest, broadcast: boolean) {
    const { fromTeamId, toTeamId } = pvpReq;
    const fromController =
      this.server.getTeamController(fromTeamId).pvpController;
    const toController = this.server.getTeamController(toTeamId).pvpController;
    fromController.outgoingRequests.delete(toTeamId);
    toController.incomingRequests.delete(fromTeamId);

    if (broadcast) {
      fromController.broadcastTeamPvPGlobalUpdate({
        type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
        outgoingRequestsToDelete: [toTeamId],
      });
      toController.broadcastTeamPvPGlobalUpdate({
        type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
        incomingRequestsToDelete: [fromTeamId],
      });
    }
  }

  clearExpiredRequests() {
    for (const pvpReq of [
      ...this.incomingRequests.values(),
      ...this.outgoingRequests.values(),
    ].filter(isPvPRequestExpired)) {
      this.clearRequest(pvpReq, false);
    }
  }

  clearOutgoingRequests() {
    for (const pvpReq of this.outgoingRequests.values()) {
      this.clearRequest(pvpReq, false);
    }
    this.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      clearOutgoingRequests: true,
    });
  }

  clearAllRequestsAndLeaveRing() {
    for (const pvpReq of [
      ...this.incomingRequests.values(),
      ...this.outgoingRequests.values(),
    ]) {
      this.clearRequest(pvpReq, false);
    }

    this.broadcastTeamPvPGlobalUpdate({
      type: GlobalUpdateType.TEAM_PVP_UPDATE_STATE,
      clearOutgoingRequests: true,
      clearIncomingRequests: true,
    });

    this.leaveRing();
  }

  unlockDeckIfUnneeded(): void {
    if (this.isGameActive()) return;
    this.clearExpiredRequests();
    if (this.outgoingRequests.size > 0) return;

    // If there is no active game and no active outgoing requests,
    // unlock the deck.
    const battleController = this.getBattleController();
    if (battleController.pendingGame === undefined) return;
    delete battleController.pendingGame;
    battleController.broadcastLockedDeckSelection();
  }
}

export class PvPLobbyController {
  server: GameServer;

  /**
   * Teams may enter the "ring" to indicate intent to battle any
   * available team.
   * This tracks the team IDs of teams in the ring in the order
   * that they joined, from least to most recent.
   */
  ring: string[];

  /**
   * Map of connId to connections for connections subscribed to
   * global (non-team-specific) PvP updates.
   */
  subscriptions: Map<string, ClientConnection>;

  constructor(server: GameServer) {
    this.server = server;
    this.ring = [];
    this.subscriptions = new Map();
  }

  broadcastPvPGlobalUpdate(upd: GlobalUpdate) {
    this.server.broadcastGlobalUpdate(
      {
        type: GlobalUpdatesScopeType.PVP,
      },
      upd
    );
  }
}
