import { Player } from "engine/types/game-state";
import { CounterType } from "engine/types/counters";
import { StepType } from "engine/types/steps";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { ColorSymbol } from "engine/types/card-data";

export const Tutorial1Spec: GameSpecServerFragment = {
  disableNoBasesLossCondition: true,
  beforeNewGame: (ctx) => {
    const { inspector, engine, ai } = ctx;
    engine.spawn("new-boarn", 0, 0, Player.P2);

    const enemyUnit = inspector.getPermanents({
      name: "new-boarn",
    })[0];
    engine.damageUnattributed(enemyUnit, 3);
    engine.addCounter(enemyUnit, { type: CounterType.CANNOT_MOVE });
    engine.addCounter(enemyUnit, { type: CounterType.CANNOT_CREATE });
    ai.setKeyframe("tutorial1-start-1");
  },
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    if (inspector.getTurnNumber() === 0) return;

    const enemyUnits = inspector.getPermanentsOf(Player.P2);
    if (enemyUnits.length !== 1)
      throw new Error("expect one enemy unit in play");
    ai.tryAttackFirst(enemyUnits[0]);

    switch (inspector.getTurnNumber()) {
      case 2: {
        ai.setKeyframe("tutorial1-start-2");
        break;
      }
    }
  },
  beforeStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    if (
      step.type === StepType.ADVANCE_KEYFRAME &&
      step.keyframeId === "tutorial1-start-1"
    ) {
      engine.spawn("yoshisaur-munchakoopas", 1, 0, Player.P1);
      const playerUnit = inspector.getPermanents({
        name: "yoshisaur-munchakoopas",
      })[0];
      engine.addCounter(playerUnit, { type: CounterType.CANNOT_MOVE });
      engine.addCounter(playerUnit, { type: CounterType.CANNOT_CREATE });
    }
    if (step.type === StepType.END_TURN && inspector.getTurnNumber() === 1) {
      engine.clearKeyframe();
    }
    if (
      step.type === StepType.ADVANCE_KEYFRAME &&
      step.keyframeId === "tutorial1-before-give-food"
    ) {
      engine.addGems(Player.P1, [ColorSymbol.GREEN]);
    }
    if (step.type === StepType.ATTACK && inspector.getTurnNumber() === 3) {
      engine.clearKeyframe();
    }
    return true;
  },
  afterStep: (step, ctx) => {
    const { inspector, engine, ai } = ctx;
    if (step.type === StepType.ATTACK && inspector.getTurnNumber() === 3) {
      ai.setKeyframe("tutorial1-start-3");
    }
  },
  afterGameEnd: (winner, solves, ctx) => {
    const { engine } = ctx;
    engine.clearKeyframe();
  },
};
