import { Player } from "engine/types/game-state";

import { GameServer } from "game-server/GameServer";
import { BackendRoom } from "game-server/backend-interface/BackendInterface";
import {
  BackendSyncReqPacket,
  SyncLoopController,
  SyncTracker,
} from "game-server/backend-interface/backend-sync";

export class MiscTeamStateSyncController {
  server: GameServer;
  /**
   * Team IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop.
   */
  seenTeamIds: Set<string>;
  syncLoopController: SyncLoopController;

  constructor(server: GameServer) {
    this.server = server;
    this.seenTeamIds = new Set();
    this.syncLoopController = new SyncLoopController(async () => {
      return await this.runSyncAsync();
    });
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. we have seen all
   * the teams).
   */
  private async runSyncAsync(): Promise<boolean> {
    const syncPacket: { [teamId: string]: BackendSyncReqPacket } = {};
    for (const teamController of this.server.teamControllers.values()) {
      if (this.seenTeamIds.has(teamController.teamId)) continue;
      if (
        Object.keys(syncPacket).length >=
        this.server.config.miscTeamStateTeamsSyncLimit
      )
        break;

      const {
        teamId,
        slowSyncTracking: { syncController },
      } = teamController;
      const snapshot = syncController.startSync();
      if (snapshot === null) continue;
      this.seenTeamIds.add(teamId);
      syncPacket[teamId] = snapshot;
    }
    if (Object.keys(syncPacket).length === 0) {
      // Reset the seen team IDs to try them again on the next loop.
      this.seenTeamIds.clear();
      return true;
    }

    this.server.recordSlowSyncPacket();
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          teamState: syncPacket,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();

    for (const [teamId, snapshot] of Object.entries(syncPacket)) {
      const teamController = this.server.getTeamController(teamId);
      const { syncController } = teamController.slowSyncTracking;
      syncController.endSync(didSyncSucceed ? snapshot : null);
    }
    return false;
  }
}

export class DecksAndMasteryTreesSyncController {
  server: GameServer;
  /**
   * Team IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop.
   */
  seenTeamIds: Set<string>;
  syncLoopController: SyncLoopController;

  constructor(server: GameServer) {
    this.server = server;
    this.seenTeamIds = new Set();
    this.syncLoopController = new SyncLoopController(async () => {
      return await this.runSyncAsync();
    });
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. we have seen all
   * the teams).
   */
  private async runSyncAsync(): Promise<boolean> {
    const syncPacket: { [teamId: string]: BackendSyncReqPacket } = {};
    for (const teamController of this.server.teamControllers.values()) {
      if (this.seenTeamIds.has(teamController.teamId)) continue;
      if (
        Object.keys(syncPacket).length >=
        this.server.config.decksAndMasteryTreesTeamsSyncLimit
      )
        break;

      const { teamId, deckControllers, masteryTreeController } = teamController;
      const teamSyncPacket: BackendSyncReqPacket = {};
      for (const [slot, deckController] of deckControllers.entries()) {
        if (deckController === null) continue;
        const {
          deck,
          slowSyncTracking: { tracker },
        } = deckController;
        if (tracker.snapshot()) {
          teamSyncPacket.decks ??= {};
          teamSyncPacket.decks[slot] = { teamId, slot, deck };
        }
      }

      const {
        masteryTree,
        slowSyncTracking: { tracker: masteryTreeTracker },
      } = masteryTreeController;
      if (masteryTreeTracker.snapshot()) {
        teamSyncPacket.masteryTree = masteryTree;
      }

      this.seenTeamIds.add(teamId);
      if (Object.keys(teamSyncPacket).length === 0) continue;
      syncPacket[teamId] = teamSyncPacket;
    }
    if (Object.keys(syncPacket).length === 0) {
      // Reset the seen team IDs to try them again on the next loop.
      this.seenTeamIds.clear();
      return true;
    }

    this.server.recordSlowSyncPacket();
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          teamState: syncPacket,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();

    for (const [teamId, snapshot] of Object.entries(syncPacket)) {
      const teamController = this.server.getTeamController(teamId);
      for (const { slot, deck } of Object.values(snapshot.decks ?? {})) {
        const { tracker } =
          teamController.getDeckController(slot).slowSyncTracking;
        tracker.handleSyncResult(didSyncSucceed);
      }
      if (snapshot.masteryTree !== undefined) {
        const { tracker } =
          teamController.masteryTreeController.slowSyncTracking;
        tracker.handleSyncResult(didSyncSucceed);
      }
    }
    return false;
  }
}

export class CheckpointsSyncController {
  server: GameServer;
  /**
   * Team IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop.
   */
  seenTeamIds: Set<string>;
  syncLoopController: SyncLoopController;

  constructor(server: GameServer) {
    this.server = server;
    this.seenTeamIds = new Set();
    this.syncLoopController = new SyncLoopController(async () => {
      return await this.runSyncAsync();
    });
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. we have seen all
   * the teams).
   */
  private async runSyncAsync(): Promise<boolean> {
    const syncPacket: { [teamId: string]: BackendSyncReqPacket } = {};
    let numCheckpointsInPacket = 0;
    for (const teamController of this.server.teamControllers.values()) {
      if (this.seenTeamIds.has(teamController.teamId)) continue;
      if (numCheckpointsInPacket >= this.server.config.checkpointsSyncLimit)
        break;

      const { teamId, battleControllers } = teamController;
      const teamSyncPacket: BackendSyncReqPacket = {};
      for (const [puzName, battleController] of battleControllers.entries()) {
        const { checkpoints, checkpointsSyncTracker: tracker } =
          battleController;
        if (tracker.snapshot()) {
          teamSyncPacket.checkpoints ??= {};
          teamSyncPacket.checkpoints[puzName] = checkpoints;
          numCheckpointsInPacket += Object.keys(checkpoints).length;
        }
      }

      this.seenTeamIds.add(teamId);
      if (Object.keys(teamSyncPacket).length === 0) continue;
      syncPacket[teamId] = teamSyncPacket;
    }
    if (Object.keys(syncPacket).length === 0) {
      // Reset the seen team IDs to try them again on the next loop.
      this.seenTeamIds.clear();
      return true;
    }

    this.server.recordSlowSyncPacket();
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          teamState: syncPacket,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();

    for (const [teamId, snapshot] of Object.entries(syncPacket)) {
      const teamController = this.server.getTeamController(teamId);
      for (const [puzName, checkpoints] of Object.entries(
        snapshot.checkpoints ?? {}
      )) {
        const { checkpointsSyncTracker: tracker } =
          teamController.getBattleController(puzName);
        tracker.handleSyncResult(didSyncSucceed);
      }
    }
    return false;
  }
}

export class ActiveRoomsSyncController {
  server: GameServer;
  /**
   * Room IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop.
   */
  seenRoomIds: Set<string>;
  /**
   * Team IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop.
   */
  seenTeamIds: Set<string>;
  syncLoopController: SyncLoopController;

  constructor(server: GameServer) {
    this.server = server;
    this.seenRoomIds = new Set();
    this.seenTeamIds = new Set();
    this.syncLoopController = new SyncLoopController(async () => {
      return await this.runSyncAsync();
    });
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. we have seen all
   * the rooms).
   */
  private async runSyncAsync(): Promise<boolean> {
    const syncPacket: { [roomId: string]: BackendRoom } = {};
    const syncTrackers: SyncTracker[] = [];
    for (const teamController of this.server.teamControllers.values()) {
      const { teamId, battleControllers } = teamController;
      if (this.seenTeamIds.has(teamId)) continue;
      if (
        Object.keys(syncPacket).length >=
        this.server.config.activeRoomsSyncLimit
      )
        break;

      let isPartialTeamSync = false;
      for (const [puzName, battleController] of battleControllers) {
        if (
          Object.keys(syncPacket).length >=
          this.server.config.activeRoomsSyncLimit
        ) {
          isPartialTeamSync = true;
          break;
        }
        const room = battleController.getActiveRoom();
        if (room === null) continue;
        // If the room is inactive, then it should already have
        // been added to slow sync, so don't sync it here.
        if (!battleController.isRoomActive()) continue;
        // Only sync for the primary player, so that we don't get
        // duplicate syncs. We are guaranteed that every game room
        // has a real team playing as P1.
        if (battleController.getPlayer() !== Player.P1) continue;
        const { engine, roomId, hostPuzName, createTime, tracker } = room;
        if (this.seenRoomIds.has(roomId)) continue;
        if (tracker.snapshot()) {
          const { puzName, gameState } = engine;
          const p1TeamData = gameState[Player.P1].teamData;
          const p2TeamData = gameState[Player.P2].teamData;
          const p1TeamId = p1TeamData?.teamId;
          const p2TeamId = p2TeamData?.teamId;
          this.seenRoomIds.add(roomId);
          syncPacket[roomId] = {
            roomId,
            puzName,
            hostPuzName,
            createTime,
            p1TeamId,
            p2TeamId,
            p1Faction: p1TeamData?.faction ?? undefined,
            p2Faction: p2TeamData?.faction ?? undefined,
            savedRoom: { gameState },
          };
          syncTrackers.push(tracker);
        }
      }
      if (!isPartialTeamSync) this.seenTeamIds.add(teamId);
    }
    if (Object.keys(syncPacket).length === 0) {
      // Reset the seen room/team IDs to try them again on the next loop.
      this.seenRoomIds.clear();
      this.seenTeamIds.clear();
      return true;
    }

    const { lockedRoomIds } =
      this.server.gameRoomsController.slowSyncTracking
        .completedRoomsSyncController;
    for (const roomId of Object.keys(syncPacket)) {
      lockedRoomIds.add(roomId);
    }
    this.server.recordSlowSyncPacket();
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          rooms: syncPacket,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();
    for (const roomId of Object.keys(syncPacket)) {
      lockedRoomIds.delete(roomId);
    }

    if (lockedRoomIds.size !== 0) {
      // Not a critical failure, so don't throw.
      // This might mean that there are multiple sync loops running
      // at once, which should not be happening.
      console.error(
        "locked rooms found in active rooms sync not owned by current sync loop"
      );
    }

    for (const tracker of syncTrackers) {
      tracker.handleSyncResult(didSyncSucceed);
    }
    return false;
  }
}

export class CompletedRoomsSyncController {
  server: GameServer;
  roomsToSync: BackendRoom[];
  /**
   * Room IDs that should not be synced here as they are actively being
   * synced in a previous slow sync.
   */
  lockedRoomIds: Set<string>;
  /**
   * Room IDs that have been seen in a previous iteration of the sync,
   * in the current sync loop. This prevents the sync loop from
   * repeatedly retrying errored rooms if it hits an error.
   */
  seenRoomIds: Set<string>;
  syncLoopController: SyncLoopController;

  constructor(server: GameServer) {
    this.server = server;
    this.roomsToSync = [];
    this.lockedRoomIds = new Set();
    this.seenRoomIds = new Set();
    this.syncLoopController = new SyncLoopController(async () => {
      return await this.runSyncAsync();
    });
  }

  addRoom(room: BackendRoom) {
    this.roomsToSync.push(room);
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. there are no more
   * rooms in the buffers to sync).
   */
  private async runSyncAsync(): Promise<boolean> {
    const syncPacket: { [roomId: string]: BackendRoom } = {};
    for (const room of this.roomsToSync) {
      const { roomId } = room;
      if (
        Object.keys(syncPacket).length >=
        this.server.config.completedRoomsSyncLimit
      )
        break;
      // Skip locked rooms. They can be synced next iteration if
      // their other syncs has completed by then.
      if (this.lockedRoomIds.has(roomId)) continue;
      if (this.seenRoomIds.has(roomId)) continue;
      this.seenRoomIds.add(roomId);
      syncPacket[roomId] = room;
    }
    if (Object.keys(syncPacket).length === 0) {
      // Reset the seen room IDs to try them again on the next loop.
      this.seenRoomIds.clear();
      return true;
    }
    this.server.recordSlowSyncPacket();
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          rooms: syncPacket,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();
    if (!didSyncSucceed) return false;

    this.roomsToSync = this.roomsToSync.filter(
      (room) => syncPacket[room.roomId] === undefined
    );
    return false;
  }
}
