import { Player } from "engine/types/game-state";
import { GameSpecServerFragment } from "engine/types/game-specs";
import { CardType } from "engine/types/card-data";

export const Tutorial4Spec: GameSpecServerFragment = {
  aiTurn: (ctx) => {
    const { inspector, engine, ai } = ctx;
    const turnNumber = inspector.getTurnNumber();

    const playerCreatures = inspector.getPermanents({
      owner: Player.P1,
      type: CardType.CREATURE,
    });
    const aiCreatures = inspector.getPermanents({
      owner: Player.P2,
      type: CardType.CREATURE,
    });

    switch (turnNumber) {
      case 0: {
        const base = inspector.getBases(Player.P2)[0];
        engine.adjustPermanentStats(base, 0, -2);
        ai.setKeyframe("tutorial4-start-1");
        return;
      }
      case 2: {
        engine.spawn("pbee---j", 1, 0, Player.P2);
        engine.spawn("pbee---j", 1, 1, Player.P2);
        engine.spawn("two-bees", 2, 2, Player.P2);
        engine.spawn("pbee---j", 1, 3, Player.P2);
        engine.spawn("pbee---j", 1, 4, Player.P2);
        if (playerCreatures.length === 2) ai.setKeyframe("tutorial4-start-2");
        else ai.setKeyframe("tutorial4-start-2-not-all-summon");
        return;
      }
    }

    // creature first attack
    aiCreatures.forEach((creature) => {
      ai.tryAttackFirst(creature, {
        sort: [
          "creaturesFirst",
          "health",
          { type: "euclideanDistance", slot: creature.slot },
        ],
      });
    });
  },
};
