import { MockServerInterface } from "game-server/server-interface/MockServerInterface";
import {
  ServerInterface,
  ServerInterfaceType,
} from "game-server/server-interface/ServerInterface";
import { mockServerOpts } from "settings";
import makeMockServer from "makeMockServer";

const makeClientMockServerInterface = async (
  type: ServerInterfaceType
): Promise<ServerInterface> => {
  return new MockServerInterface(await makeMockServer(mockServerOpts));
};
export default makeClientMockServerInterface;
